/* eslint-disable react-refresh/only-export-components */
import { useContext, useEffect, useState, type ReactNode } from 'react'
import { wharfKitService } from '../services/wharfkit'
import WalletContext, { type WalletContextType } from './WalletContextDefinition.ts'

interface WalletProviderProps {
  children: ReactNode
}

export const WalletProvider = ({ children }: WalletProviderProps) => {
  const [walletState, setWalletState] = useState<WalletContextType>({
    isConnected: false,
    account: null,
    session: null,
    login: async () => null,
    logout: async () => {},
    transact: async () => null,
    purchaseNFT: async () => null,
    batchPurchaseNFTs: async () => null,
    executeCraftingTransaction: async () => null,
    executeTransaction: async () => { throw new Error('Wallet not connected') },
    isLoading: true
  })

  useEffect(() => {
    // Subscribe to wallet state changes
    const unsubscribe = wharfKitService.subscribe((context) => {
      setWalletState(context)
    })

    // Cleanup subscription on unmount
    return unsubscribe
  }, [])

  return (
    <WalletContext.Provider value={walletState}>
      {children}
    </WalletContext.Provider>
  )
}

export const useWallet = (): WalletContextType => {
  const context = useContext(WalletContext)
  if (context === undefined) {
    throw new Error('useWallet must be used within a WalletProvider')
  }
  return context
}

export default WalletContext