import React, { useState, useEffect, useCallback } from 'react'
import { useParams, useNavigate, useSearchParams } from 'react-router-dom'
import { apiService } from '../services/api'
import { balanceService } from '../services/balanceService'
import type { NFTAsset, WaxAccountInfo, WaxCurrencyBalance } from '../services/api'
import { badgeService } from '../services/badgeService'
import type { UserBadge } from '../services/badgeService'
import { log } from '../services/loggingService'
import NFTDetailsModal from './NFTDetailsModal'
import '../css/user-profile-lookup.css'


interface UserProfileData {
  account: string
  nftsOwned: NFTAsset[]
  totalNFTs: number
  collections: string[]
  joinDate: string
  isVerified: boolean
  badges: string[]
}

interface UserStats {
  totalAssets: number
  uniqueCollections: number
  totalValue: string
  rareAssets: number
}

interface ResourceData {
  cpu: { used: number; available: number; max: number }
  net: { used: number; available: number; max: number }
  ram: { used: number; available: number; max: number }
}

// Helper function to get account creation date from blockchain
const getAccountCreationDate = async (account: string): Promise<string> => {
  try {
    // Try to get account info from WAX API
    const accountInfo = await apiService.getWaxAccountInfo(account);
    if (accountInfo && accountInfo.created) {
      return new Date(accountInfo.created).toISOString().split('T')[0];
    }
  } catch (error) {
    log.warn('Could not fetch account creation date:', 'UserProfileLookup.getAccountCreationDate', error);
  }
  
  try {
    // Fallback: try to get first transaction date
    const transactions = await apiService.getUserTransactions(account, 1);
    if (transactions && transactions.length > 0) {
      const firstTx = transactions[transactions.length - 1];
      if (firstTx.block_time) {
        return new Date(firstTx.block_time).toISOString().split('T')[0];
      }
    }
  } catch (error) {
    log.warn('Could not fetch first transaction date:', 'UserProfileLookup.getAccountCreationDate', error);
  }
  
  // Final fallback: return a reasonable default
  return '2023-01-01';
};

const UserProfileLookup: React.FC = () => {
  const { account: paramAccount } = useParams<{ account: string }>()
  const [searchParams] = useSearchParams()
  const navigate = useNavigate()
  
  // Get account from either URL params or query params
  const account = paramAccount || searchParams.get('user')
  const [profileData, setProfileData] = useState<UserProfileData | null>(null)
  const [userStats, setUserStats] = useState<UserStats | null>(null)
  const [accountInfo, setAccountInfo] = useState<WaxAccountInfo | null>(null)
  const [waxBalance, setWaxBalance] = useState<string>('0.0000')
  const [tokenBalances, setTokenBalances] = useState<(WaxCurrencyBalance & { value: string })[]>([])
  const [resourceData, setResourceData] = useState<ResourceData | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [selectedNFT, setSelectedNFT] = useState<NFTAsset | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [collectionAvatars, setCollectionAvatars] = useState<Record<string, string>>({})
  const [currentPage, setCurrentPage] = useState(1)
  const [filter, setFilter] = useState<'all' | 'pixals' | 'waxrock' | 'ingredients'>('all')
  const [userBadges, setUserBadges] = useState<UserBadge[]>([])
  const itemsPerPage = 20
  
  // Pixal metadata filters
  const [pixalFilters, setPixalFilters] = useState({
    Body: '',
    Eyes: '',
    Head: '',
    Mouth: '',
    WaxRock: '',
    PixelPet: '',
    Background: '',
    'Hats and Hair': '',
    'Glasses and Masks': '',
    'Special Accessories': ''
  })
  const [availableTraits, setAvailableTraits] = useState<Record<string, string[]>>({})

  const fetchTokenBalances = useCallback(async (userAccount: string) => {
    try {
      const tokenContracts = ['token.pxj', 'eosio.token']
      const balances: (WaxCurrencyBalance & { value: string })[] = []
      
      for (const contract of tokenContracts) {
        try {
          const contractBalances = await apiService.getWaxCurrencyBalance(userAccount, contract)
          const balancesWithValue = await Promise.all(contractBalances.map(async balance => ({
            ...balance,
            contract: contract,
            value: await calculateTokenValue(balance.balance, balance.symbol)
          })))
          balances.push(...balancesWithValue)
        } catch (error) {
          log.error(`Failed to fetch balance for ${contract}:`, 'UserProfileLookup.fetchTokenBalances', error as Error)
        }
      }
      
      setTokenBalances(balances)
    } catch (error) {
      log.error('Error fetching token balances:', 'UserProfileLookup.fetchTokenBalances', error as Error)
    }
  }, [])

  const loadUserProfile = useCallback(async () => {
    if (!account) return
    
    setLoading(true)
    setError(null)
    
    try {
      // Load user's NFT inventory
      const inventory = await apiService.getUserInventory(account)
      
      // Get unique collections
      const collections = [...new Set(inventory.map(nft => nft.collection))]
      
      // Check for user badges
        const badgeResult = await badgeService.checkUserBadges(account)
        setUserBadges(badgeResult.badges)
      
      // Extract available traits from Pixal NFTs
      const pixalNFTs = inventory.filter(nft => nft.collection === 'pixeljourney' && nft.schema === 'pixals')
      const traits: Record<string, Set<string>> = {
        Body: new Set(),
        Eyes: new Set(),
        Head: new Set(),
        Mouth: new Set(),
        WaxRock: new Set(),
        PixelPet: new Set(),
        Background: new Set(),
        'Hats and Hair': new Set(),
        'Glasses and Masks': new Set(),
        'Special Accessories': new Set()
      }
      
      pixalNFTs.forEach(nft => {
        Object.keys(traits).forEach(trait => {
          const value = nft.data?.[trait] as string
          if (value && value !== 'none' && value !== '') {
            traits[trait].add(value)
          }
        })
      })
      
      const availableTraitsObj: Record<string, string[]> = {}
      Object.entries(traits).forEach(([trait, values]) => {
        availableTraitsObj[trait] = Array.from(values).sort()
      })
      setAvailableTraits(availableTraitsObj)
      
      // Load WAX Chain API data
      await Promise.all([
        fetchWaxAccountInfo(account),
        fetchWaxBalance(account),
        fetchTokenBalances(account)
      ])
      
      // Get real account creation date from blockchain
      const joinDate = await getAccountCreationDate(account);
      
      const profileData: UserProfileData = {
          account,
          nftsOwned: inventory,
          totalNFTs: inventory.length,
          collections,
          joinDate,
          isVerified: badgeResult.badges.some(badge => badge.id === 'pixal-pfp'),
          badges: badgeResult.badges.map(badge => badge.name)
        }
      
      // Calculate user stats
      const totalValue = await calculateTotalValue(inventory)
      const stats: UserStats = {
        totalAssets: inventory.length,
        uniqueCollections: collections.length,
        totalValue,
        rareAssets: inventory.filter(nft => nft.template_mint && nft.template_mint <= 100).length
      }
      
      setProfileData(profileData)
      setUserStats(stats)
      
      // Fetch collection avatars
      if (collections.length > 0) {
        fetchCollectionAvatars(collections)
      }
      

    } catch (err) {
      log.error('Error loading user profile:', 'UserProfileLookup.loadUserProfile', err as Error)
      setError('Failed to load user profile. Please check the account name and try again.')
    } finally {
      setLoading(false)
    }
  }, [account, fetchTokenBalances])

  useEffect(() => {
    if (account) {
      loadUserProfile()
    }
  }, [account, loadUserProfile])

  const fetchWaxAccountInfo = async (userAccount: string) => {
    try {
      const accountInfo = await apiService.getWaxAccountInfo(userAccount)
      setAccountInfo(accountInfo)
      
      // Extract resource data
      if (accountInfo) {
        const resources: ResourceData = {
          cpu: {
            used: accountInfo.cpu_limit.used,
            available: accountInfo.cpu_limit.available,
            max: accountInfo.cpu_limit.max
          },
          net: {
            used: accountInfo.net_limit.used,
            available: accountInfo.net_limit.available,
            max: accountInfo.net_limit.max
          },
          ram: {
            used: accountInfo.ram_usage,
            available: accountInfo.ram_quota - accountInfo.ram_usage,
            max: accountInfo.ram_quota
          }
        }
        setResourceData(resources)
      }
    } catch (error) {
      log.error('Failed to fetch WAX account info:', 'UserProfileLookup.fetchWaxAccountInfo', error as Error)
    }
  }

  const fetchWaxBalance = async (userAccount: string) => {
    try {
      const balance = await balanceService.getWaxBalance(userAccount)
      setWaxBalance(balance)
    } catch (error) {
      log.error('Failed to fetch WAX balance:', 'UserProfileLookup.fetchWaxBalance', error as Error)
    }
  }



  const calculateTokenValue = async (balance: string, symbol: string): Promise<string> => {
    const amount = parseFloat(balance.split(' ')[0])
    
    try {
      // Get real token price from Alcor Exchange
      const tokenContracts: Record<string, string> = {
        'TLM': 'alien.worlds',
        'NEFTY': 'neftyblocksp',
        'SEST': 'sest.token'
      }
      
      const contract = tokenContracts[symbol]
      if (contract) {
        const priceData = await apiService.getAlcorTokenPrice(symbol, contract)
        if (priceData && priceData.usd_price) {
          return `$${(amount * priceData.usd_price).toFixed(4)}`
        }
      }
      
      // Fallback prices if API fails
      const fallbackPrices: Record<string, number> = {
        'TLM': 0.02,
        'NEFTY': 0.001,
        'SEST': 0.0001
      }
      const price = fallbackPrices[symbol] || 0.001
      return `$${(amount * price).toFixed(4)}`
    } catch (error) {
      log.error(`Error fetching price for ${symbol}:`, 'UserProfileLookup.fetchWaxAccountInfo', error as Error)
      const fallbackPrices: Record<string, number> = {
        'TLM': 0.02,
        'NEFTY': 0.001,
        'SEST': 0.0001
      }
      const price = fallbackPrices[symbol] || 0.001
      return `$${(amount * price).toFixed(4)}`
    }
  }



  const calculateTotalValue = async (inventory: NFTAsset[]): Promise<string> => {
    if (inventory.length === 0) return '0.00 WAX'
    
    try {
      // Group NFTs by collection for batch analysis
      const collectionGroups = inventory.reduce((groups, nft) => {
        const collection = nft.collection
        if (!groups[collection]) groups[collection] = []
        groups[collection].push(nft)
        return groups
      }, {} as Record<string, NFTAsset[]>)
      
      let totalValue = 0
      
      // Calculate value for each collection
      for (const [collection, nfts] of Object.entries(collectionGroups)) {
        try {
          // Get collection stats including floor price
          const stats = await apiService.getCollectionStats(collection)
          let collectionFloorPrice = 0
          
          if (stats && stats.floor_price) {
            collectionFloorPrice = parseFloat(stats.floor_price)
          } else {
            // Fallback: get recent sales data for the collection
            const salesResponse = await fetch(
              `${apiService.atomicMarketAPI}/atomicmarket/v1/sales?state=3&collection_name=${collection}&limit=50&order=desc&sort=updated`
            )
            const salesData = await salesResponse.json()
            
            if (salesData.success && salesData.data?.length > 0) {
              interface SaleData {
                price?: {
                  amount: string
                  token_precision?: number
                }
              }
              
              const prices = salesData.data
                .filter((sale: SaleData) => sale.price?.amount)
                .map((sale: SaleData) => parseFloat(sale.price!.amount) / Math.pow(10, sale.price!.token_precision || 8))
              
              if (prices.length > 0) {
                collectionFloorPrice = Math.min(...prices)
              }
            }
          }
          
          // If we still don't have a floor price, use a conservative estimate
          if (collectionFloorPrice === 0) {
            collectionFloorPrice = 1.0 // 1 WAX default for unknown collections
          }
          
          // Calculate value for this collection's NFTs
          // Use floor price as base, with adjustments for rarity
          nfts.forEach(nft => {
            let nftValue = collectionFloorPrice
            
            // Adjust for rarity based on template mint number
            if (nft.template_mint) {
              if (nft.template_mint <= 10) {
                nftValue *= 3.0 // Very rare (top 10 mints)
              } else if (nft.template_mint <= 50) {
                nftValue *= 2.0 // Rare (top 50 mints)
              } else if (nft.template_mint <= 100) {
                nftValue *= 1.5 // Uncommon (top 100 mints)
              }
            }
            
            totalValue += nftValue
          })
          
        } catch (error) {
          log.warn(`Failed to get market data for collection ${collection}, using fallback pricing:`, 'UserProfileLookup.calculateTotalValue', error as Error)
          // Fallback: use conservative estimate of 1.5 WAX per NFT
          totalValue += nfts.length * 1.5
        }
      }
      
      return `${totalValue.toFixed(2)} WAX`
      
    } catch (error) {
      log.error('Error calculating total portfolio value:', 'UserProfileLookup.calculateTotalValue', error as Error)
      // Ultimate fallback to mock calculation
      const fallbackValue = inventory.length * 2.0
      return `${fallbackValue.toFixed(2)} WAX`
    }
  }

  const getFilteredNFTs = () => {
    if (!profileData) return []
    
    let filtered = profileData.nftsOwned
    
    switch (filter) {
      case 'pixals':
        filtered = filtered.filter(nft => nft.collection === 'pixeljourney' && nft.schema === 'pixals')
        
        // Apply Pixal metadata filters
        Object.entries(pixalFilters).forEach(([trait, value]) => {
          if (value && value !== '') {
            filtered = filtered.filter(nft => {
              const traitValue = nft.data?.[trait] as string
              return traitValue && traitValue.toLowerCase().includes(value.toLowerCase())
            })
          }
        })
        break
      case 'waxrock': {
        // Fix WaxRock detection - check for Pixals with valid WaxRock traits
        filtered = filtered.filter(nft => {
          const waxRock = nft.data?.WaxRock as string
          return nft.collection === 'pixeljourney' && 
                 nft.schema === 'pixals' && 
                 waxRock && 
                 waxRock !== 'none'
        })
        break
      }
      case 'ingredients':
        filtered = filtered.filter(nft => nft.collection === 'pixeljourney' && nft.schema === 'ingredients')
        break
      default:
        break
    }
    
    return filtered
  }

  const getPaginatedNFTs = () => {
    const filtered = getFilteredNFTs()
    const startIndex = (currentPage - 1) * itemsPerPage
    return filtered.slice(startIndex, startIndex + itemsPerPage)
  }

  const getTotalPages = () => {
    const filtered = getFilteredNFTs()
    return Math.ceil(filtered.length / itemsPerPage)
  }

  const handleNFTClick = (nft: NFTAsset) => {
    setSelectedNFT(nft)
    setIsModalOpen(true)
  }

  const fetchCollectionAvatars = async (collections: string[]) => {
    const avatars: Record<string, string> = {}
    
    for (const collection of collections) {
      try {
        const response = await fetch(`https://aa.dapplica.io/atomicassets/v1/collections/${collection}`)
        const data = await response.json()
        
        if (data.success && data.data?.img) {
          const img = data.data.img
          avatars[collection] = img.startsWith('http') ? img : `https://ipfs.io/ipfs/${img}`
        }
      } catch (error) {
        log.error(`Error fetching avatar for collection ${collection}:`, 'UserProfileLookup.fetchCollectionAvatars', error as Error)
      }
    }
    
    setCollectionAvatars(avatars)
  }



  const handleCloseModal = () => {
    setIsModalOpen(false)
    setSelectedNFT(null)
  }

  const handlePageChange = (page: number) => {
    setCurrentPage(page)
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }

  if (loading) {
    return (
      <div className="user-profile-lookup">
        <div className="profile-container">
          <div className="loading-state">
            <div className="loading-spinner"></div>
            <p>Loading user profile...</p>
          </div>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="user-profile-lookup">
        <div className="profile-container">
          <div className="error-state">
            <div className="error-icon">⚠️</div>
            <h2>Profile Not Found</h2>
            <p>{error}</p>
            <div className="error-actions">
              <button onClick={() => navigate(-1)} className="btn-secondary">
                Go Back
              </button>
              <button onClick={loadUserProfile} className="btn-primary">
                Try Again
              </button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  if (!profileData || !userStats) {
    return (
      <div className="user-profile-lookup">
        <div className="profile-container">
          <div className="no-data">
            <p>No profile data available</p>
          </div>
        </div>
      </div>
    )
  }

  const filteredNFTs = getFilteredNFTs()
  const paginatedNFTs = getPaginatedNFTs()
  const totalPages = getTotalPages()

  return (
    <div className="user-profile-lookup">
      <div className="profile-container">
        {/* Profile Header */}
        <div className="profile-header">
          <div className="profile-avatar">
            <div className="avatar-placeholder">
              {account?.charAt(0).toUpperCase()}
            </div>
          </div>
          <div className="profile-info">
            <h1>{account}</h1>
            <div className="profile-badges">
              {profileData.isVerified && (
                <span className="badge verified">✓ Verified</span>
              )}
              {userBadges.map((badge) => (
                <span key={badge.id} className={`badge ${badge.id}`} title={badge.description}>
                  {badge.icon} {badge.name}
                </span>
              ))}
            </div>
          </div>
          <div className="profile-stats-quick">
            <div className="stat-item">
              <span className="stat-value">{userStats.totalAssets}</span>
              <span className="stat-label">NFTs Owned</span>
            </div>
            <div className="stat-item">
              <span className="stat-value">{userStats.uniqueCollections}</span>
              <span className="stat-label">Collections</span>
            </div>
            <div className="stat-item">
              <span className="stat-value">{userStats.rareAssets}</span>
              <span className="stat-label">Rare Assets</span>
            </div>
          </div>
        </div>

        {/* WAX Account Information */}
        {accountInfo && (
          <div className="account-info-section">
            <h3>Account Information</h3>
            <div className="account-info-grid">
              <div className="user-info-card">
                <div className="info-icon">👤</div>
                <div className="info-details">
                  <h4>Account Name</h4>
                  <p>{accountInfo.account_name}</p>
                </div>
              </div>
              <div className="user-info-card">
                <div className="info-icon">📅</div>
                <div className="info-details">
                  <h4>Created</h4>
                  <p>{new Date(accountInfo.created).toLocaleDateString()}</p>
                </div>
              </div>
              <div className="user-info-card">
                <div className="info-icon">🔑</div>
                <div className="info-details">
                  <h4>Permissions</h4>
                  <p>{accountInfo.permissions?.length || 0} keys</p>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* WAX Balance & Tokens */}
        <div className="balance-section">
          <h3>Balances</h3>
          <div className="balance-grid">
            <div className="balance-card primary">
              <div className="balance-icon">💰</div>
              <div className="balance-details">
                <h4>WAX Balance</h4>
                <p className="balance-value">{waxBalance}</p>
              </div>
            </div>
            {tokenBalances.map((token, index) => (
              <div key={index} className="balance-card">
                <div className="balance-icon">🪙</div>
                <div className="balance-details">
                   <h4>{token.symbol}</h4>
                   <p className="balance-value">{token.balance}</p>
                   <p className="balance-usd">{token.value}</p>
                 </div>
              </div>
            ))}
          </div>
        </div>

        {/* Resources */}
        {resourceData && (
          <div className="resources-section">
            <h3>Resources</h3>
            <div className="resources-grid">
              <div className="resource-card">
                <div className="resource-header">
                  <span className="resource-icon">⚡</span>
                  <h4>CPU</h4>
                </div>
                <div className="resource-bar">
                  <div 
                    className="resource-fill" 
                    style={{ width: `${(resourceData.cpu.used / resourceData.cpu.max) * 100}%` }}
                  ></div>
                </div>
                <div className="resource-stats">
                  <span>{resourceData.cpu.used.toLocaleString()} / {resourceData.cpu.max.toLocaleString()}</span>
                  <span>{((resourceData.cpu.used / resourceData.cpu.max) * 100).toFixed(1)}%</span>
                </div>
              </div>
              <div className="resource-card">
                <div className="resource-header">
                  <span className="resource-icon">🌐</span>
                  <h4>NET</h4>
                </div>
                <div className="resource-bar">
                  <div 
                    className="resource-fill" 
                    style={{ width: `${(resourceData.net.used / resourceData.net.max) * 100}%` }}
                  ></div>
                </div>
                <div className="resource-stats">
                  <span>{resourceData.net.used.toLocaleString()} / {resourceData.net.max.toLocaleString()}</span>
                  <span>{((resourceData.net.used / resourceData.net.max) * 100).toFixed(1)}%</span>
                </div>
              </div>
              <div className="resource-card">
                <div className="resource-header">
                  <span className="resource-icon">💾</span>
                  <h4>RAM</h4>
                </div>
                <div className="resource-bar">
                  <div 
                    className="resource-fill" 
                    style={{ width: `${(resourceData.ram.used / resourceData.ram.max) * 100}%` }}
                  ></div>
                </div>
                <div className="resource-stats">
                  <span>{(resourceData.ram.used / 1024).toFixed(2)} KB / {(resourceData.ram.max / 1024).toFixed(2)} KB</span>
                  <span>{((resourceData.ram.used / resourceData.ram.max) * 100).toFixed(1)}%</span>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Stats Cards */}
        <div className="stats-grid">
          <div className="stat-card">
            <div className="stat-icon">🎨</div>
            <div className="stat-details">
              <h3>Total Assets</h3>
              <p className="stat-value">{userStats.totalAssets}</p>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon">📚</div>
            <div className="stat-details">
              <h3>Collections</h3>
              <p className="stat-value">{userStats.uniqueCollections}</p>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon">💎</div>
            <div className="stat-details">
              <h3>Estimated Value</h3>
              <p className="stat-value">{userStats.totalValue}</p>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon">⭐</div>
            <div className="stat-details">
              <h3>Rare Assets</h3>
              <p className="stat-value">{userStats.rareAssets}</p>
            </div>
          </div>
        </div>

        {/* Collections List */}
        <div className="collections-section">
          <h3>Collections ({profileData.collections.length})</h3>
          <div className="collections-list">
            {profileData.collections.map((collection, index) => (
              <div key={index} className="collection-item" onClick={() => navigate(`/collections/${collection}`)}>
                <div className="user-collection-avatar">
                  {collectionAvatars[collection] ? (
                    <img 
                      src={collectionAvatars[collection]} 
                      alt={`${collection} avatar`}
                      onError={(e) => {
                        e.currentTarget.style.display = 'none';
                        (e.currentTarget.nextElementSibling as HTMLElement)!.style.display = 'flex';
                      }}
                    />
                  ) : null}
                  <div className="fallback-avatar" style={{ display: collectionAvatars[collection] ? 'none' : 'flex' }}>
                    {collection.charAt(0).toUpperCase()}
                  </div>
                </div>
                <span className="collection-name">{collection}</span>
                <span className="collection-count">
                  {profileData.nftsOwned.filter(nft => nft.collection === collection).length}
                </span>
              </div>
            ))}
          </div>
        </div>

        {/* NFT Inventory */}
        <div className="inventory-section">
          <div className="inventory-header">
            <h3>NFT Inventory ({filteredNFTs.length})</h3>
            <div className="filter-tabs">
              <button 
                className={`filter-tab ${filter === 'all' ? 'active' : ''}`}
                onClick={() => { setFilter('all'); setCurrentPage(1) }}
              >
                All ({profileData.nftsOwned.length})
              </button>
              <button 
                className={`filter-tab ${filter === 'pixals' ? 'active' : ''}`}
                onClick={() => { setFilter('pixals'); setCurrentPage(1) }}
              >
                Pixals ({profileData.nftsOwned.filter(nft => nft.collection === 'pixeljourney' && nft.schema === 'pixals').length})
              </button>
              <button 
                className={`filter-tab ${filter === 'waxrock' ? 'active' : ''}`}
                onClick={() => { setFilter('waxrock'); setCurrentPage(1) }}
              >
                WaxRock ({profileData.nftsOwned.filter(nft => {
                  const waxRock = nft.data?.WaxRock as string
                  return nft.collection === 'pixeljourney' && 
                         nft.schema === 'pixals' && 
                         waxRock && 
                         waxRock !== 'none'
                }).length})
              </button>
              <button 
                className={`filter-tab ${filter === 'ingredients' ? 'active' : ''}`}
                onClick={() => { setFilter('ingredients'); setCurrentPage(1) }}
              >
                Ingredients ({profileData.nftsOwned.filter(nft => nft.collection === 'pixeljourney' && nft.schema === 'ingredients').length})
              </button>
            </div>
            
            {/* Pixal Metadata Filters */}
            {filter === 'pixals' && (
              <div className="pixal-filters">
                <h4>Filter by Traits</h4>
                <div className="filters-grid">
                  {Object.entries(pixalFilters).map(([trait, value]) => (
                    <div key={trait} className="filter-group">
                      <label>{trait}</label>
                      <select
                        value={value}
                        onChange={(e) => {
                          setPixalFilters(prev => ({
                            ...prev,
                            [trait]: e.target.value
                          }))
                          setCurrentPage(1)
                        }}
                      >
                        <option value="">All {trait}</option>
                        {availableTraits[trait]?.map(traitValue => (
                          <option key={traitValue} value={traitValue}>
                            {traitValue}
                          </option>
                        ))}
                      </select>
                    </div>
                  ))}
                </div>
                <button 
                  className="clear-filters-btn"
                  onClick={() => {
                    setPixalFilters({
                      Body: '',
                      Eyes: '',
                      Head: '',
                      Mouth: '',
                      WaxRock: '',
                      PixelPet: '',
                      Background: '',
                      'Hats and Hair': '',
                      'Glasses and Masks': '',
                      'Special Accessories': ''
                    })
                    setCurrentPage(1)
                  }}
                >
                  Clear All Filters
                </button>
              </div>
            )}
          </div>

          {paginatedNFTs.length === 0 ? (
            <div className="empty-inventory">
              <div className="empty-icon">📦</div>
              <h3>No NFTs Found</h3>
              <p>This user doesn't have any NFTs in the selected category.</p>
            </div>
          ) : (
            <>
              <div className="nft-grid">
                {paginatedNFTs.map((nft) => (
                  <div key={nft.asset_id} className="nft-card" onClick={() => handleNFTClick(nft)}>
                    <div className="nft-image">
                      {nft.video ? (
                        <video 
                          src={nft.video} 
                          className="nft-media"
                          muted 
                          loop
                          onMouseEnter={(e) => e.currentTarget.play()}
                          onMouseLeave={(e) => e.currentTarget.pause()}
                          onError={(e) => {
                            // Fallback to image if video fails
                            const img = document.createElement('img')
                            img.src = nft.image
                            img.className = 'nft-media'
                            img.alt = nft.name
                            img.loading = 'lazy'
                            e.currentTarget.parentNode?.replaceChild(img, e.currentTarget)
                          }}
                        />
                      ) : (
                        <img src={nft.image} alt={nft.name} className="nft-media" loading="lazy" />
                      )}
                    </div>
                    <div className="nft-info">
                      <h4 className="nft-name">{nft.name}</h4>
                      <p className="nft-collection">{nft.collection}</p>
                      <div className="nft-schema-template">
                        <span className="nft-schema">{nft.schema}</span>
                        {nft.template_id && (
                          <span className="nft-template">T#{nft.template_id}</span>
                        )}
                      </div>
                      <div className="nft-details">
                        <span className="nft-mint">#{nft.template_mint}</span>
                        {nft.max_supply && parseInt(nft.max_supply) > 0 && (
                          <span className="nft-supply">/{nft.max_supply}</span>
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              {/* Pagination */}
              {totalPages > 1 && (
                <div className="pagination">
                  <button 
                    className="pagination-btn" 
                    onClick={() => handlePageChange(currentPage - 1)}
                    disabled={currentPage === 1}
                  >
                    Previous
                  </button>
                  
                  <div className="pagination-numbers">
                    {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                      let pageNum
                      if (totalPages <= 5) {
                        pageNum = i + 1
                      } else if (currentPage <= 3) {
                        pageNum = i + 1
                      } else if (currentPage >= totalPages - 2) {
                        pageNum = totalPages - 4 + i
                      } else {
                        pageNum = currentPage - 2 + i
                      }
                      
                      return (
                        <button
                          key={pageNum}
                          className={`pagination-number ${currentPage === pageNum ? 'active' : ''}`}
                          onClick={() => handlePageChange(pageNum)}
                        >
                          {pageNum}
                        </button>
                      )
                    })}
                  </div>
                  
                  <button 
                    className="pagination-btn" 
                    onClick={() => handlePageChange(currentPage + 1)}
                    disabled={currentPage === totalPages}
                  >
                    Next
                  </button>
                </div>
              )}
            </>
          )}
        </div>
      </div>

      {/* NFT Details Modal */}
      {selectedNFT && (
        <NFTDetailsModal
          nft={selectedNFT}
          isOpen={isModalOpen}
          onClose={handleCloseModal}
        />
      )}
    </div>
  )
}

export default UserProfileLookup