import React, { Component, type ReactNode } from 'react'
import { logger } from '../services/loggingService'
import ErrorMessage from './ErrorMessage'
import '../css/error-message.css'

interface Props {
  children: ReactNode
  fallback?: ReactNode
  onError?: (error: Error, errorInfo: React.ErrorInfo) => void
}

interface State {
  hasError: boolean
  error?: Error
  errorInfo?: React.ErrorInfo
  retryCount: number
}

class ErrorBoundary extends Component<Props, State> {
  constructor(props: Props) {
    super(props)
    this.state = { hasError: false, retryCount: 0 }
  }

  static getDerivedStateFromError(error: Error): Partial<State> {
    // Update state so the next render will show the fallback UI
    return { hasError: true, error }
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    logger.error('ErrorBoundary caught an error', 'ErrorBoundary', error, {
      componentStack: errorInfo.componentStack,
      errorBoundary: this.constructor.name
    })
    
    // Call custom error handler if provided
    if (this.props.onError) {
      this.props.onError(error, errorInfo)
    }
    
    this.setState({
      error,
      errorInfo
    })
    
    // Report error to monitoring service (if available)
    this.reportError(error, errorInfo)
  }

  private reportError = async (error: Error, errorInfo: React.ErrorInfo) => {
    try {
      const errorReport = {
        message: error.message,
        stack: error.stack,
        componentStack: errorInfo.componentStack,
        timestamp: new Date().toISOString(),
        userAgent: navigator.userAgent,
        url: window.location.href
      }
      
      logger.warn('Error Report generated', 'ErrorBoundary', errorReport)
      
      // Error reporting is now handled by the logging service
      // The logger will automatically report errors in production
    } catch (reportingError) {
      logger.error('Failed to report error', 'ErrorBoundary', reportingError as Error)
    }
  }

  handleRetry = () => {
    const newRetryCount = this.state.retryCount + 1
    
    // Prevent infinite retry loops
    if (newRetryCount > 3) {
      logger.warn('Maximum retry attempts reached', 'ErrorBoundary')
      return
    }
    
    this.setState({ 
      hasError: false, 
      error: undefined, 
      errorInfo: undefined,
      retryCount: newRetryCount
    })
  }

  render() {
    if (this.state.hasError) {
      // Custom fallback UI
      if (this.props.fallback) {
        return this.props.fallback
      }

      // Default error UI
      const isMaxRetries = this.state.retryCount >= 3
      
      return (
        <ErrorMessage
          title="Something went wrong"
          message={isMaxRetries 
            ? "Multiple errors occurred. Please refresh the page or contact support if the problem persists."
            : "An unexpected error occurred. Please try again or refresh the page."
          }
          onRetry={isMaxRetries ? undefined : this.handleRetry}
          showRetry={!isMaxRetries}
        />
      )
    }

    return this.props.children
  }
}

export default ErrorBoundary