import React, { useState, useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { log } from '../services/loggingService'
import '../css/transaction-details.css'

interface ActionTrace {
  act?: {
    name: string
    account: string
    authorization?: Array<{
      actor: string
      permission: string
    }>
    data?: Record<string, unknown>
  }
  receipt?: {
    receiver: string
    act_digest: string
    global_sequence: number
    recv_sequence: number
    auth_sequence: Array<[string, number]>
    code_sequence: number
    abi_sequence: number
  }
  context_free?: boolean
  elapsed?: number
  console?: string
  trx_id?: string
  block_num?: number
  block_time?: string
  producer_block_id?: string
  account_ram_deltas?: Array<{
    account: string
    delta: number
  }>
  except?: unknown
  error_code?: number
  inline_traces?: ActionTrace[]
}

interface TransactionReceipt {
  status: string
  cpu_usage_us?: number
  max_net_usage_words?: number
  net_usage_words?: number
}

interface TransactionDetails {
  transaction_id: string
  block_num: number
  block_time: string
  action_name: string
  actor: string
  receiver: string
  asset_id?: string
  asset_name?: string
  collection_name?: string
  price?: string
  from?: string
  to?: string
  memo?: string
  status: 'success' | 'failed'
  cpu_usage_us: number
  net_usage_words: number
  traces?: ActionTrace[]
}

interface BlockInfo {
  block_num: number
  block_id: string
  timestamp: string
  producer: string
  transaction_count: number
}

const TransactionDetails: React.FC = () => {
  const { transactionId } = useParams<{ transactionId: string }>()
  const navigate = useNavigate()
  const [transaction, setTransaction] = useState<TransactionDetails | null>(null)
  const [blockInfo, setBlockInfo] = useState<BlockInfo | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [activeTab, setActiveTab] = useState<'overview' | 'traces' | 'raw'>('overview')

  useEffect(() => {
    if (transactionId) {
      fetchTransactionDetails(transactionId)
    }
  }, [transactionId])

  const fetchTransactionDetails = async (txId: string) => {
    try {
      setLoading(true)
      setError(null)
      
      // Fetch transaction details from WAX blockchain
      const response = await fetch(`https://wax.eosrio.io/v1/history/get_transaction`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          id: txId
        })
      })
      
      if (!response.ok) {
        throw new Error(`Failed to fetch transaction: ${response.status}`)
      }
      
      const data = await response.json()
      
      if (!data.id) {
        throw new Error('Transaction not found')
      }
      
      // Parse transaction data
      const parsedTransaction: TransactionDetails = {
        transaction_id: data.id,
        block_num: data.block_num,
        block_time: data.block_time,
        action_name: data.trx?.transaction?.actions?.[0]?.name || 'unknown',
        actor: data.trx?.transaction?.actions?.[0]?.authorization?.[0]?.actor || 'unknown',
        receiver: data.trx?.transaction?.actions?.[0]?.account || 'unknown',
        status: data.receipt?.status === 'executed' ? 'success' : 'failed',
        cpu_usage_us: (data.receipt as TransactionReceipt)?.cpu_usage_us || 0,
        net_usage_words: (data.receipt as TransactionReceipt)?.max_net_usage_words || (data.receipt as TransactionReceipt)?.net_usage_words || 0,
        traces: data.traces || []
      }
      
      // Extract additional info from action data if available
      const actionData = data.trx?.transaction?.actions?.[0]?.data
      if (actionData) {
        if (actionData.asset_ids) {
          parsedTransaction.asset_id = actionData.asset_ids[0]
        }
        if (actionData.from) {
          parsedTransaction.from = actionData.from
        }
        if (actionData.to) {
          parsedTransaction.to = actionData.to
        }
        if (actionData.memo) {
          parsedTransaction.memo = actionData.memo
        }
        if (actionData.listing_price_to_assert) {
          parsedTransaction.price = `${parseFloat(actionData.listing_price_to_assert.split(' ')[0])} WAX`
        }
      }
      
      setTransaction(parsedTransaction)
      
      // Fetch block information
      const blockResponse = await fetch(`https://wax.eosrio.io/v1/chain/get_block`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          block_num_or_id: data.block_num
        })
      })
      
      if (blockResponse.ok) {
        const blockData = await blockResponse.json()
        setBlockInfo({
          block_num: blockData.block_num,
          block_id: blockData.id,
          timestamp: blockData.timestamp,
          producer: blockData.producer,
          transaction_count: blockData.transactions?.length || 0
        })
      }
      
    } catch (err) {
      log.error('Error fetching transaction details', 'TransactionDetails.fetchTransactionDetails', err as Error)
      setError(err instanceof Error ? err.message : 'Failed to load transaction details')
    } finally {
      setLoading(false)
    }
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString + 'Z').toLocaleString()
  }

  const handleUserClick = (account: string) => {
    navigate(`/user-profile-lookup?user=${encodeURIComponent(account)}`)
  }

  const handleAssetClick = (assetId: string) => {
    navigate(`/assets/${assetId}`)
  }

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text)
  }

  if (loading) {
    return (
      <div className="page-content">
        <div className="transaction-details-container">
          <div className="loading-state">
            <div className="loading-spinner"></div>
            <p>Loading transaction details...</p>
          </div>
        </div>
      </div>
    )
  }

  if (error || !transaction) {
    return (
      <div className="page-content">
        <div className="transaction-details-container">
          <div className="error-state">
            <h2>Transaction Not Found</h2>
            <p>{error || 'The requested transaction could not be found.'}</p>
            <button onClick={() => navigate(-1)} className="btn btn-primary">
              Go Back
            </button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="page-content">
      <div className="transaction-details-container">
        {/* Header */}
        <div className="transaction-header">
          <button onClick={() => navigate(-1)} className="back-button">
            ← Back
          </button>
          <div className="transaction-title">
            <h1>Transaction Details</h1>
            <div className="transaction-id">
              <span>ID: {transaction.transaction_id}</span>
              <button 
                onClick={() => copyToClipboard(transaction.transaction_id)}
                className="copy-button"
                title="Copy transaction ID"
              >
                📋
              </button>
            </div>
          </div>
          <div className={`transaction-status ${transaction.status}`}>
            {transaction.status === 'success' ? '✅' : '❌'} {transaction.status.toUpperCase()}
          </div>
        </div>

        {/* Tab Navigation */}
        <div className="tab-navigation">
          <button 
            className={`tab ${activeTab === 'overview' ? 'active' : ''}`}
            onClick={() => setActiveTab('overview')}
          >
            Overview
          </button>
          <button 
            className={`tab ${activeTab === 'traces' ? 'active' : ''}`}
            onClick={() => setActiveTab('traces')}
          >
            Action Traces
          </button>
          <button 
            className={`tab ${activeTab === 'raw' ? 'active' : ''}`}
            onClick={() => setActiveTab('raw')}
          >
            Raw Data
          </button>
        </div>

        {/* Tab Content */}
        <div className="tab-content">
          {activeTab === 'overview' && (
            <div className="overview-tab">
              {/* Basic Information */}
              <div className="info-section">
                <h3>Basic Information</h3>
                <div className="info-grid">
                  <div className="info-item">
                    <label>Block Number:</label>
                    <span>{transaction.block_num.toLocaleString()}</span>
                  </div>
                  <div className="info-item">
                    <label>Timestamp:</label>
                    <span>{formatDate(transaction.block_time)}</span>
                  </div>
                  <div className="info-item">
                    <label>Action:</label>
                    <span className="action-name">{transaction.action_name}</span>
                  </div>
                  <div className="info-item">
                    <label>Contract:</label>
                    <span className="contract-name">{transaction.receiver}</span>
                  </div>
                </div>
              </div>

              {/* Transaction Parties */}
              <div className="info-section">
                <h3>Transaction Parties</h3>
                <div className="info-grid">
                  <div className="info-item">
                    <label>Actor:</label>
                    <span 
                      className="clickable-account"
                      onClick={() => handleUserClick(transaction.actor)}
                    >
                      {transaction.actor}
                    </span>
                  </div>
                  {transaction.from && transaction.from !== transaction.actor && (
                    <div className="info-item">
                      <label>From:</label>
                      <span 
                        className="clickable-account"
                        onClick={() => handleUserClick(transaction.from!)}
                      >
                        {transaction.from}
                      </span>
                    </div>
                  )}
                  {transaction.to && (
                    <div className="info-item">
                      <label>To:</label>
                      <span 
                        className="clickable-account"
                        onClick={() => handleUserClick(transaction.to!)}
                      >
                        {transaction.to}
                      </span>
                    </div>
                  )}
                </div>
              </div>

              {/* Asset Information */}
              {(transaction.asset_id || transaction.price) && (
                <div className="info-section">
                  <h3>Asset Information</h3>
                  <div className="info-grid">
                    {transaction.asset_id && (
                      <div className="info-item">
                        <label>Asset ID:</label>
                        <span 
                          className="clickable-asset"
                          onClick={() => handleAssetClick(transaction.asset_id!)}
                        >
                          {transaction.asset_id}
                        </span>
                      </div>
                    )}
                    {transaction.asset_name && (
                      <div className="info-item">
                        <label>Asset Name:</label>
                        <span>{transaction.asset_name}</span>
                      </div>
                    )}
                    {transaction.collection_name && (
                      <div className="info-item">
                        <label>Collection:</label>
                        <span>
                          {typeof transaction.collection_name === 'string'
                            ? transaction.collection_name
                            : (transaction.collection_name as { collection_name?: string; name?: string })?.collection_name ||
                              (transaction.collection_name as { collection_name?: string; name?: string })?.name ||
                              'Unknown'}
                        </span>
                      </div>
                    )}
                    {transaction.price && (
                      <div className="info-item">
                        <label>Price:</label>
                        <span className="price">{transaction.price}</span>
                      </div>
                    )}
                  </div>
                </div>
              )}

              {/* Resource Usage */}
              <div className="info-section">
                <h3>Resource Usage</h3>
                <div className="info-grid">
                  <div className="info-item">
                    <label>CPU Usage:</label>
                    <span>{transaction.cpu_usage_us.toLocaleString()} μs</span>
                  </div>
                  <div className="info-item">
                    <label>NET Usage:</label>
                    <span>{transaction.net_usage_words.toLocaleString()} words</span>
                  </div>
                </div>
              </div>

              {/* Block Information */}
              {blockInfo && (
                <div className="info-section">
                  <h3>Block Information</h3>
                  <div className="info-grid">
                    <div className="info-item">
                      <label>Block ID:</label>
                      <span className="block-id">{blockInfo.block_id}</span>
                    </div>
                    <div className="info-item">
                      <label>Producer:</label>
                      <span>{blockInfo.producer}</span>
                    </div>
                    <div className="info-item">
                      <label>Transactions in Block:</label>
                      <span>{blockInfo.transaction_count}</span>
                    </div>
                  </div>
                </div>
              )}

              {/* Memo */}
              {transaction.memo && (
                <div className="info-section">
                  <h3>Memo</h3>
                  <div className="memo-content">
                    {transaction.memo}
                  </div>
                </div>
              )}
            </div>
          )}

          {activeTab === 'traces' && (
            <div className="traces-tab">
              <h3>Action Traces</h3>
              {transaction.traces && transaction.traces.length > 0 ? (
                <div className="traces-list">
                  {transaction.traces.map((trace, index) => (
                    <div key={index} className="trace-item">
                      <div className="trace-header">
                        <span className="trace-index">#{index + 1}</span>
                        <span className="trace-action">{trace.act?.name || 'unknown'}</span>
                        <span className="trace-account">{trace.act?.account || 'unknown'}</span>
                      </div>
                      <pre className="trace-data">
                        {JSON.stringify(trace, null, 2)}
                      </pre>
                    </div>
                  ))}
                </div>
              ) : (
                <p>No action traces available for this transaction.</p>
              )}
            </div>
          )}

          {activeTab === 'raw' && (
            <div className="raw-tab">
              <h3>Raw Transaction Data</h3>
              <pre className="raw-data">
                {JSON.stringify(transaction, null, 2)}
              </pre>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default TransactionDetails