// Known WaxRock types from Pixal PFP template 781515
// These are the actual gem types that appear in the 'WaxRock' attribute
// 96% of Pixal PFPs have WaxRock: "none", 4% have one of these rare gems
export const WAXROCK_TYPES = [
  'Citrine',
  'Tourmaline', 
  'Emerald',
  'Garnet',
  'Peridot',
  'Jade',
  'Sapphire',
  'Turquoise',
  'Pigeon Blood Ruby',
  'Blue Rock',
  'Black Rock',
  'Rock'
] as const

export type WaxRockType = typeof WAXROCK_TYPES[number]

