import React, { useState, useEffect, useCallback } from 'react'
import { apiService } from '../services/api'
import { log } from '../services/loggingService'
import MediaRenderer from './MediaRenderer'
import '../css/rwax-live-transactions.css'

interface RWAXTransaction {
  transfer_id: string
  sender_name: string
  recipient_name: string
  assets: Array<{
    asset_id?: string
    name?: string
    data?: {
      img?: string
      video?: string
      [key: string]: string | number | boolean | undefined
    }
    template?: {
      template_id: string
      max_supply: string
      immutable_data?: Record<string, string | number | boolean>
    }
    collection?: {
      collection_name: string
      name: string
      img?: string
    }
    schema?: {
      schema_name: string
    }
    template_mint?: string | number
  }>
  memo?: string
  created_at_time: string
  txid: string
  type: 'tokenization' | 'redemption'
}

interface RWAXLiveTransactionsProps {
  limit?: number
  refreshInterval?: number
}

const RWAXLiveTransactions: React.FC<RWAXLiveTransactionsProps> = ({ 
  limit = 12, 
  refreshInterval = 30000 
}) => {
  const [transactions, setTransactions] = useState<RWAXTransaction[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [lastUpdate, setLastUpdate] = useState<Date | null>(null)
  const [page, setPage] = useState(1)
  const [hasMore, setHasMore] = useState(true)
  const [loadingMore, setLoadingMore] = useState(false)

  const fetchTransactions = useCallback(async (pageNum: number = 1, append: boolean = false) => {
    try {
      if (!append) {
        setError(null)
        setLoading(true)
      } else {
        setLoadingMore(true)
      }
      
      const response = await apiService.getRWAXTransfers({
        limit,
        page: pageNum,
        order: 'desc'
      })
      
      if (response.success) {
        const newTransactions = response.data
        
        if (append) {
          setTransactions(prev => [...prev, ...newTransactions])
        } else {
          setTransactions(newTransactions)
        }
        
        // Check if there are more transactions to load
        setHasMore(newTransactions.length === limit)
        setLastUpdate(new Date())
      } else {
        throw new Error('Failed to fetch RWAX transactions')
      }
      
    } catch (err) {
      log.error('Error fetching RWAX transactions', 'RWAXLiveTransactions', err as Error)
      if (!append) {
        setError('Failed to load RWAX transactions')
      }
    } finally {
      setLoading(false)
      setLoadingMore(false)
    }
  }, [limit])

  const loadMore = async () => {
    if (!loadingMore && hasMore) {
      const nextPage = page + 1
      setPage(nextPage)
      await fetchTransactions(nextPage, true)
    }
  }

  useEffect(() => {
    fetchTransactions()
    
    // Set up auto-refresh
    const interval = setInterval(() => {
      fetchTransactions(1, false)
      setPage(1)
    }, refreshInterval)
    
    return () => clearInterval(interval)
  }, [limit, refreshInterval, fetchTransactions])

  const formatTime = (timestamp: string) => {
    try {
      const date = new Date(timestamp)
      
      // Check if date is valid
      if (isNaN(date.getTime())) {
        return 'Unknown'
      }
      
      const now = new Date()
      const diffMs = now.getTime() - date.getTime()
      const diffMins = Math.floor(diffMs / 60000)
      
      // Handle negative differences (future dates)
      if (diffMins < 0) return 'Just now'
      if (diffMins < 1) return 'Just now'
      if (diffMins < 60) return `${diffMins}m ago`
      if (diffMins < 1440) return `${Math.floor(diffMins / 60)}h ago`
      return `${Math.floor(diffMins / 1440)}d ago`
    } catch (error) {
      return 'Unknown'
    }
  }

  const getTransactionIcon = (type: 'tokenization' | 'redemption') => {
    return type === 'tokenization' ? '🔄' : '🎁'
  }

  const getTransactionLabel = (type: 'tokenization' | 'redemption') => {
    return type === 'tokenization' ? 'Tokenized' : 'Redeemed'
  }

  const processImageUrl = (imageUrl?: string): string => {
    if (!imageUrl) return '/placeholder-nft.png'
    
    // Handle IPFS URLs
    if (imageUrl.startsWith('Qm') || imageUrl.startsWith('baf')) {
      return `https://ipfs.io/ipfs/${imageUrl}`
    }
    
    // Handle relative URLs
    if (imageUrl.startsWith('/')) {
      return `https://ipfs.io/ipfs${imageUrl}`
    }
    
    return imageUrl
  }

  if (loading && transactions.length === 0) {
    return (
      <div className="rwax-live-transactions">
        <div className="rwax-live-transactions-header">
          <h3>Live RWAX Transactions</h3>
          <div className="loading-spinner"></div>
        </div>
        <div className="transactions-loading">
          <p>Loading RWAX transactions...</p>
        </div>
      </div>
    )
  }

  if (error && transactions.length === 0) {
    return (
      <div className="rwax-live-transactions">
        <div className="rwax-live-transactions-header">
          <h3>Live RWAX Transactions</h3>
          <button onClick={() => fetchTransactions()} className="refresh-button">
            🔄
          </button>
        </div>
        <div className="transactions-error">
          <p>{error}</p>
          <button onClick={() => fetchTransactions()} className="retry-button">
            Try Again
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="rwax-live-transactions">
      <div className="rwax-live-transactions-header">
        <h3>Live RWAX Transactions</h3>
        <div className="header-actions">
          {lastUpdate && (
            <span className="last-update">
              Updated {formatTime(lastUpdate.toISOString())}
            </span>
          )}
          <button 
            onClick={() => {
              fetchTransactions(1, false)
              setPage(1)
            }} 
            className="refresh-button"
            disabled={loading}
          >
            {loading ? '⏳' : '🔄'}
          </button>
        </div>
      </div>
      
      <div className="rwax-transactions-grid">
        {transactions.length === 0 ? (
          <div className="no-transactions">
            <p>No recent RWAX transactions found</p>
          </div>
        ) : (
          transactions.map((tx, index) => (
            <div key={`${tx.txid}-${tx.transfer_id}-${index}`} className="rwax-transaction-card">
              <div className="transaction-header">
                <div className="transaction-type">
                  <span className="transaction-icon">
                    {getTransactionIcon(tx.type)}
                  </span>
                  <span className={`transaction-label ${tx.type}`}>
                    {getTransactionLabel(tx.type)}
                  </span>
                </div>
                <span className="transaction-time">
                  {formatTime(tx.created_at_time)}
                </span>
              </div>
              
              <div className="transaction-assets">
                {tx.assets.slice(0, 1).map((asset) => (
                  <div key={asset.asset_id} className="asset-preview">
                    <MediaRenderer 
                      imageUrl={processImageUrl(asset.data?.img)}
                      alt={asset.name || 'Unknown Asset'}
                      className="asset-image"
                    />
                    <div className="asset-info">
                      <h4 className="asset-name">{asset.name || 'Unknown Asset'}</h4>
                      <p className="asset-mint">#{asset.template_mint || asset.asset_id || 'N/A'}</p>
                    </div>
                  </div>
                ))}
                {tx.assets.length > 1 && (
                  <div className="additional-assets">
                    +{tx.assets.length - 1} more
                  </div>
                )}
              </div>
              
              <div className="transaction-details">
                <div className="transaction-accounts">
                  <span className="account from">
                    From: {tx.sender_name.length > 8 ? `${tx.sender_name.slice(0, 6)}...` : tx.sender_name}
                  </span>
                  <span className="account to">
                    To: {tx.recipient_name.length > 8 ? `${tx.recipient_name.slice(0, 6)}...` : tx.recipient_name}
                  </span>
                </div>
                
                <div className="transaction-actions">
                  <button 
                    className="view-tx-button"
                    onClick={() => window.open(`https://waxblock.io/transaction/${tx.txid}`, '_blank')}
                    title="View on WAX Block Explorer"
                  >
                    🔗
                  </button>
                </div>
              </div>
            </div>
          ))
        )}
      </div>
      
      {hasMore && (
        <div className="load-more-section">
          <button 
            onClick={loadMore}
            className="load-more-button"
            disabled={loadingMore}
          >
            {loadingMore ? (
              <>
                <div className="loading-spinner small"></div>
                Loading more...
              </>
            ) : (
              'Load More Transactions'
            )}
          </button>
        </div>
      )}
    </div>
  )
}

export default RWAXLiveTransactions