import type { TransactionAction, TransactionType } from './transactionService'

/**
 * Transaction Action Builders
 * Provides type-safe builders for common transaction types
 */

// Marketplace Purchase Actions
export const buildPurchaseActions = (
  buyer: string,
  saleId: string,
  price: string,
  currency: string = 'WAX',
  marketContract: string = 'atomicmarket'
): TransactionAction[] => {
  const actions: TransactionAction[] = []

  // Transfer payment to marketplace
  const tokenContract = currency === 'WAX' ? 'eosio.token' : 'token.rwax'
  actions.push({
    account: tokenContract,
    name: 'transfer',
    authorization: [{ actor: buyer, permission: 'active' }],
    data: {
      from: buyer,
      to: marketContract,
      quantity: `${price} ${currency}`,
      memo: 'deposit'
    }
  })

  // Purchase the NFT
  actions.push({
    account: marketContract,
    name: 'purchasesale',
    authorization: [{ actor: buyer, permission: 'active' }],
    data: {
      buyer,
      sale_id: saleId,
      intended_delphi_median: 0,
      taker_marketplace: 'market.pxj'
    }
  })

  return actions
}

// NFT Purchase Actions
export const buildNFTPurchaseActions = (
  buyer: string,
  seller: string,
  assetId: string,
  price: string,
  currency: string = 'WAX'
): TransactionAction[] => {
  const actions: TransactionAction[] = []

  // Transfer payment to seller
  if (currency === 'WAX') {
    actions.push({
      account: 'eosio.token',
      name: 'transfer',
      authorization: [{ actor: buyer, permission: 'active' }],
      data: {
        from: buyer,
        to: seller,
        quantity: `${price} WAX`,
        memo: `Purchase NFT ${assetId}`
      }
    })
  }

  // Transfer NFT to buyer
  actions.push({
    account: 'atomicassets',
    name: 'transfer',
    authorization: [{ actor: seller, permission: 'active' }],
    data: {
      from: seller,
      to: buyer,
      asset_ids: [assetId],
      memo: `Sale to ${buyer}`
    }
  })

  return actions
}

// NFT Transfer Actions
export const buildNFTTransferActions = (
  from: string,
  to: string,
  assetIds: string[],
  memo: string = ''
): TransactionAction[] => {
  return [{
    account: 'atomicassets',
    name: 'transfer',
    authorization: [{ actor: from, permission: 'active' }],
    data: {
      from,
      to,
      asset_ids: assetIds,
      memo
    }
  }]
}

// Token Transfer Actions
export const buildTokenTransferActions = (
  from: string,
  to: string,
  amount: string,
  currency: string = 'WAX',
  memo: string = ''
): TransactionAction[] => {
  let tokenContract: string
  
  switch (currency) {
    case 'WAX':
      tokenContract = 'eosio.token'
      break
    case 'RWAX':
      tokenContract = 'token.rwax'
      break
    case 'PXJ':
      tokenContract = 'pixeljourney'
      break
    default:
      tokenContract = 'eosio.token' // Default to WAX contract
  }
  
  return [{
    account: tokenContract,
    name: 'transfer',
    authorization: [{ actor: from, permission: 'active' }],
    data: {
      from,
      to,
      quantity: `${amount} ${currency}`,
      memo
    }
  }]
}

// NFT Listing Actions (AtomicMarket)
export const buildNFTListingActions = (
  seller: string,
  assetId: string,
  price: string,
  currency: string = 'WAX',
  marketContract: string = 'atomicmarket'
): TransactionAction[] => {
  return [
    // Announce sale
    {
      account: marketContract,
      name: 'announcesale',
      authorization: [{ actor: seller, permission: 'active' }],
      data: {
        seller,
        asset_ids: [assetId],
        listing_price_per_asset: `${price} ${currency}`,
        settlement_symbol: `4,${currency}`,
        maker_marketplace: 'pxmarket'
      }
    }
  ]
}

// Cancel NFT Listing Actions
export const buildCancelListingActions = (
  seller: string,
  saleId: string,
  marketContract: string = 'atomicmarket'
): TransactionAction[] => {
  return [{
    account: marketContract,
    name: 'cancelsale',
    authorization: [{ actor: seller, permission: 'active' }],
    data: {
      sale_id: saleId
    }
  }]
}

// Crafting Actions
export const buildCraftingActions = (
  crafter: string,
  schemaName: string,
  templateId: string,
  ingredientAssets: string[],
  craftingContract: string = 'atomicassets'
): TransactionAction[] => {
  return [
    // Burn ingredient assets
    {
      account: craftingContract,
      name: 'burnasset',
      authorization: [{ actor: crafter, permission: 'active' }],
      data: {
        asset_owner: crafter,
        asset_ids: ingredientAssets
      }
    },
    // Mint new asset
    {
      account: craftingContract,
      name: 'mintasset',
      authorization: [{ actor: craftingContract, permission: 'active' }],
      data: {
        authorized_minter: craftingContract,
        collection_name: 'pixeljourney',
        schema_name: schemaName,
        template_id: templateId,
        new_asset_owner: crafter,
        immutable_data: [],
        mutable_data: [],
        tokens_to_back: []
      }
    }
  ]
}

// Blend Actions (NeftyBlocks)
export const buildBlendActions = (
  blender: string,
  blendId: string,
  ingredientAssets: string[],
  blendContract: string = 'blend.nefty'
): TransactionAction[] => {
  return [
    // Transfer assets to blend contract
    {
      account: 'atomicassets',
      name: 'transfer',
      authorization: [{ actor: blender, permission: 'active' }],
      data: {
        from: blender,
        to: blendContract,
        asset_ids: ingredientAssets,
        memo: `blend:${blendId}`
      }
    },
    // Execute blend
    {
      account: blendContract,
      name: 'claimblend',
      authorization: [{ actor: blender, permission: 'active' }],
      data: {
        claimer: blender,
        blend_id: blendId,
        asset_ids: ingredientAssets
      }
    }
  ]
}

// Burn Actions
export const buildBurnActions = (
  owner: string,
  assetIds: string[]
): TransactionAction[] => {
  return [{
    account: 'atomicassets',
    name: 'burnasset',
    authorization: [{ actor: owner, permission: 'active' }],
    data: {
      asset_owner: owner,
      asset_ids: assetIds
    }
  }]
}

// Unpack Actions
export const buildUnpackActions = (
  unpacker: string,
  packAssetId: string,
  unpackContract: string = 'atomicpacks'
): TransactionAction[] => {
  return [
    // Transfer pack to unpack contract
    {
      account: 'atomicassets',
      name: 'transfer',
      authorization: [{ actor: unpacker, permission: 'active' }],
      data: {
        from: unpacker,
        to: unpackContract,
        asset_ids: [packAssetId],
        memo: 'unpack'
      }
    },
    // Claim unpacked assets
    {
      account: unpackContract,
      name: 'claimunpack',
      authorization: [{ actor: unpacker, permission: 'active' }],
      data: {
        pack_asset_id: packAssetId,
        unpack_to: unpacker
      }
    }
  ]
}

// Auction Bid Actions
export const buildAuctionBidActions = (
  bidder: string,
  auctionId: string,
  bidAmount: string,
  currency: string = 'WAX',
  auctionContract: string = 'atomicmarket'
): TransactionAction[] => {
  const actions: TransactionAction[] = []

  // Transfer bid amount to auction contract
  const tokenContract = currency === 'WAX' ? 'eosio.token' : 'token.rwax'
  actions.push({
    account: tokenContract,
    name: 'transfer',
    authorization: [{ actor: bidder, permission: 'active' }],
    data: {
      from: bidder,
      to: auctionContract,
      quantity: `${bidAmount} ${currency}`,
      memo: `auction bid ${auctionId}`
    }
  })

  // Place bid
  actions.push({
    account: auctionContract,
    name: 'auctionbid',
    authorization: [{ actor: bidder, permission: 'active' }],
    data: {
      bidder,
      auction_id: auctionId,
      bid: `${bidAmount} ${currency}`,
      taker_marketplace: 'pxmarket'
    }
  })

  return actions
}

// Tokenization Actions (RWAX)
export const buildTokenizationActions = (
  tokenizer: string,
  waxAmount: string,
  rwaxContract: string = 'token.rwax'
): TransactionAction[] => {
  return [
    // Transfer WAX to RWAX contract
    {
      account: 'eosio.token',
      name: 'transfer',
      authorization: [{ actor: tokenizer, permission: 'active' }],
      data: {
        from: tokenizer,
        to: rwaxContract,
        quantity: `${waxAmount} WAX`,
        memo: 'tokenize'
      }
    }
  ]
}

// Batch Purchase Actions
export const buildBatchPurchaseActions = (
  buyer: string,
  purchases: Array<{
    seller: string
    assetId: string
    price: string
    currency?: string
  }>
): TransactionAction[] => {
  const actions: TransactionAction[] = []
  
  // Group by currency and seller for efficient transfers
  const transfersBySellerAndCurrency = new Map<string, Map<string, string>>()
  
  purchases.forEach(({ seller, price, currency = 'WAX' }) => {
    if (!transfersBySellerAndCurrency.has(seller)) {
      transfersBySellerAndCurrency.set(seller, new Map())
    }
    
    const sellerTransfers = transfersBySellerAndCurrency.get(seller)!
    const currentAmount = sellerTransfers.get(currency) || '0'
    const newAmount = (parseFloat(currentAmount) + parseFloat(price)).toFixed(8)
    sellerTransfers.set(currency, newAmount)
  })
  
  // Add payment transfers
  transfersBySellerAndCurrency.forEach((currencies, seller) => {
    currencies.forEach((amount, currency) => {
      const tokenContract = currency === 'WAX' ? 'eosio.token' : 'token.rwax'
      actions.push({
        account: tokenContract,
        name: 'transfer',
        authorization: [{ actor: buyer, permission: 'active' }],
        data: {
          from: buyer,
          to: seller,
          quantity: `${amount} ${currency}`,
          memo: 'Batch NFT purchase'
        }
      })
    })
  })
  
  // Add NFT transfers
  const transfersBySeller = new Map<string, string[]>()
  purchases.forEach(({ seller, assetId }) => {
    if (!transfersBySeller.has(seller)) {
      transfersBySeller.set(seller, [])
    }
    transfersBySeller.get(seller)!.push(assetId)
  })
  
  transfersBySeller.forEach((assetIds, seller) => {
    actions.push({
      account: 'atomicassets',
      name: 'transfer',
      authorization: [{ actor: seller, permission: 'active' }],
      data: {
        from: seller,
        to: buyer,
        asset_ids: assetIds,
        memo: `Batch sale to ${buyer}`
      }
    })
  })
  
  return actions
}

/**
 * Transaction Builder Factory
 * Provides a unified interface for building different transaction types
 */
export class TransactionBuilder {
  static nftPurchase(buyer: string, seller: string, assetId: string, price: string, currency?: string) {
    return {
      type: 'nft_purchase' as TransactionType,
      actions: buildNFTPurchaseActions(buyer, seller, assetId, price, currency)
    }
  }
  
  static nftTransfer(from: string, to: string, assetIds: string[], memo?: string) {
    return {
      type: 'nft_transfer' as TransactionType,
      actions: buildNFTTransferActions(from, to, assetIds, memo)
    }
  }
  
  static tokenTransfer(from: string, to: string, amount: string, currency?: string, memo?: string) {
    return {
      type: 'token_transfer' as TransactionType,
      actions: buildTokenTransferActions(from, to, amount, currency, memo)
    }
  }
  
  static nftListing(seller: string, assetId: string, price: string, currency?: string) {
    return {
      type: 'nft_listing' as TransactionType,
      actions: buildNFTListingActions(seller, assetId, price, currency)
    }
  }
  
  static cancelListing(seller: string, saleId: string) {
    return {
      type: 'nft_cancel_listing' as TransactionType,
      actions: buildCancelListingActions(seller, saleId)
    }
  }
  
  static crafting(crafter: string, schemaName: string, templateId: string, ingredientAssets: string[]) {
    return {
      type: 'crafting' as TransactionType,
      actions: buildCraftingActions(crafter, schemaName, templateId, ingredientAssets)
    }
  }
  
  static blend(blender: string, blendId: string, ingredientAssets: string[]) {
    return {
      type: 'blend' as TransactionType,
      actions: buildBlendActions(blender, blendId, ingredientAssets)
    }
  }
  
  static burn(owner: string, assetIds: string[]) {
    return {
      type: 'burn' as TransactionType,
      actions: buildBurnActions(owner, assetIds)
    }
  }
  
  static unpack(unpacker: string, packAssetId: string) {
    return {
      type: 'unpack' as TransactionType,
      actions: buildUnpackActions(unpacker, packAssetId)
    }
  }
  
  static auctionBid(bidder: string, auctionId: string, bidAmount: string, currency?: string) {
    return {
      type: 'auction_bid' as TransactionType,
      actions: buildAuctionBidActions(bidder, auctionId, bidAmount, currency)
    }
  }
  
  static tokenization(tokenizer: string, waxAmount: string) {
    return {
      type: 'tokenization' as TransactionType,
      actions: buildTokenizationActions(tokenizer, waxAmount)
    }
  }
  
  static batchPurchase(buyer: string, purchases: Array<{ seller: string; assetId: string; price: string; currency?: string }>) {
    return {
      type: 'bulk_operation' as TransactionType,
      actions: buildBatchPurchaseActions(buyer, purchases)
    }
  }
}

export default TransactionBuilder