// Component to handle navigation state based on current route
const AppContent = () => {
  const location = useLocation()
  const [currentPage, setCurrentPage] = useState('landing')
  
  // Navigation state tracking for route changes

  // Update current page based on route - first one should remain /landing and not just landing
  React.useEffect(() => {
    const path = location.pathname
    if (path === '' || path === '/' || path === '/market-alphatest/' || path === '/market-alphatest') {
      setCurrentPage('/landing')
    } else if (path === '/marketplace' || path === '/market-alphatest/marketplace') {
      setCurrentPage('marketplace')
    } else if (path === '/rewards') {
      setCurrentPage('rewards')
    } else if (path === '/inventory') {
      setCurrentPage('inventory')
    } else if (path === '/packs') {
      setCurrentPage('packs')
    } else if (path.startsWith('/collections')) {
      setCurrentPage('collections')
    } else if (path === '/activity' || path === '/stats') {
      setCurrentPage('activity')
    } else if (path === '/staking') {
      setCurrentPage('staking')
    } else if (path === '/profile') {
      setCurrentPage('profile')
    } else if (path === '/watchlist') {
      setCurrentPage('watchlist')
    } else if (path === '/auctions') {
      setCurrentPage('auctions')
    } else if (path === '/learn') {
      setCurrentPage('learn')
    } else if (path === '/journeymap') {
      setCurrentPage('journeymap')
    } else if (path === '/pxcrafting') {
      setCurrentPage('pxcrafting')
    }
  }, [location.pathname])

  // Scroll to top when route changes
  React.useEffect(() => {
    window.scrollTo(0, 0)
  }, [location.pathname])

  const handlePageChange = (page: string) => {
    setCurrentPage(page)
    // Navigate programmatically if needed
    if (page === 'landing') {
      window.history.pushState(null, '', '/')
    } else if (page === 'marketplace') {
      window.history.pushState(null, '', '/marketplace')
    } else {
      window.history.pushState(null, '', `/${page}`)
    }
  }

  return (
    <div className="App">
      <Navigation currentPage={currentPage} onPageChange={handlePageChange} />
      <main id="main-content" role="main" aria-label="Main content" className="main-content">
        <Routes>
          <Route path="/" element={<APIErrorBoundary><LandingPage /></APIErrorBoundary>} />
          <Route path="/marketplace" element={<APIErrorBoundary><Marketplace /></APIErrorBoundary>} />
          <Route path="/rewards" element={<APIErrorBoundary><Rewards onPageChange={handlePageChange} /></APIErrorBoundary>} />
          <Route path="/inventory" element={<APIErrorBoundary><Inventory /></APIErrorBoundary>} />
          <Route path="/packs" element={<APIErrorBoundary><Packs /></APIErrorBoundary>} />
          <Route path="/collections" element={<APIErrorBoundary><Collections /></APIErrorBoundary>} />
          <Route path="/collections/:collectionName" element={<APIErrorBoundary><CollectionDetail /></APIErrorBoundary>} />
          <Route path="/collections/:collectionName/templates/:templateId" element={<APIErrorBoundary><TemplateDetail /></APIErrorBoundary>} />
          <Route path="/assets/:assetId" element={<APIErrorBoundary><AssetDetail /></APIErrorBoundary>} />
                <Route path="/listings/:saleId" element={<APIErrorBoundary><ListingDetail /></APIErrorBoundary>} />
                <Route path="/transactions/:transactionId" element={<APIErrorBoundary><TransactionDetails /></APIErrorBoundary>} />
                <Route path="/activity" element={<APIErrorBoundary><ActivityStats /></APIErrorBoundary>} />
          <Route path="/stats" element={<APIErrorBoundary><ActivityStats /></APIErrorBoundary>} />
          <Route path="/profile" element={<APIErrorBoundary><UserProfile /></APIErrorBoundary>} />
          <Route path="/profile/:account" element={<APIErrorBoundary><UserProfileLookup /></APIErrorBoundary>} />

          <Route path="/user-profile-lookup" element={<APIErrorBoundary><UserProfileLookup /></APIErrorBoundary>} />
          <Route path="/watchlist" element={<APIErrorBoundary><Watchlist /></APIErrorBoundary>} />
          <Route path="/auctions" element={<APIErrorBoundary><Auctions /></APIErrorBoundary>} />
          <Route path="/auctions/:id" element={<APIErrorBoundary><AuctionDetail /></APIErrorBoundary>} />
          <Route path="/learn" element={<APIErrorBoundary><Learn /></APIErrorBoundary>} />
          <Route path="/journeymap" element={<APIErrorBoundary><JourneyMap /></APIErrorBoundary>} />
          <Route path="/staking" element={<APIErrorBoundary><Staking /></APIErrorBoundary>} />
          <Route path="/rwax" element={<APIErrorBoundary><RWax /></APIErrorBoundary>} />
              <Route path="/pxcrafting" element={<APIErrorBoundary><PxCrafting /></APIErrorBoundary>} />
              <Route path="/pxswap" element={<APIErrorBoundary><PxSwap /></APIErrorBoundary>} />
        </Routes>
      </main>
      <Footer />
    </div>
  )
}

function App() {
  // Initialize monitoring and push notifications on app start
  React.useEffect(() => {
    // Initialize monitoring
    monitoringService.trackEvent('app_initialized', {
      timestamp: Date.now(),
      userAgent: navigator.userAgent,
      viewport: {
        width: window.innerWidth,
        height: window.innerHeight
      }
    })

    // Initialize accessibility service
    accessibilityService.initialize()
      .then(() => {
        log.info('Accessibility service ready', 'App')
        monitoringService.trackEvent('accessibility_service_ready', {
          preferences: accessibilityService.getPreferences()
        })
      })
      .catch((error) => {
        log.error('Failed to initialize accessibility service', 'App', error)
        monitoringService.reportError(error, 'accessibility_init_error')
      })

    // Initialize push notification service
    pushNotificationService.initialize().then((success) => {
      if (success) {
        log.info('Push notification service initialized successfully', 'App')
        monitoringService.trackEvent('push_service_ready', {
          supported: pushNotificationService.getPermissionState().supported
        })
      } else {
        log.warn('Push notification service initialization failed', 'App')
      }
    }).catch((error) => {
      log.error('Push notification service initialization error', 'App', error)
      monitoringService.reportError(error, 'push_service_init_error')
    })

    // Preload marketplace configuration
    MarketplaceConfigService.preloadConfiguration().then(() => {
      log.info('Marketplace configuration preloaded successfully', 'App')
      monitoringService.trackEvent('marketplace_config_preloaded', {
        timestamp: Date.now()
      })
    }).catch((error) => {
      log.warn('Failed to preload marketplace configuration', 'App', { error })
      monitoringService.reportError(error, 'marketplace_config_preload_error')
    })
  }, [])

  return (
    <ErrorBoundary>
      <WalletProvider>
        <DisplaySettingsProvider>
          <NotificationProvider>
            <Router basename="/market-alphatest">
              <AppContent />
            </Router>
            <WatchlistAlertChecker />
            <PriceAlertManager />
            <NotificationContainer />
            <ConfirmDialog />
            <ChatWidget />
            <PWAInstallBanner />
            <OfflineIndicator />
            <TransactionNotifications />
          </NotificationProvider>
        </DisplaySettingsProvider>
      </WalletProvider>
    </ErrorBoundary>
  )
}

export default App
