import React, { useState, useEffect, useCallback, useMemo } from 'react'
import { useWallet } from '../hooks/useWallet'
import { apiService } from '../services/api'
import { NeftyBlendService } from '../services/neftyBlendService'
import NFTDetailsModal from './NFTDetailsModal'
import UnpackModal from './UnpackModal'
import MediaRenderer from './MediaRenderer'
import { getAssetImageUrl, getAssetVideoUrl } from '../utils/mediaUtils'
import { useDisplaySettings } from '../hooks/useDisplaySettings'
import { useNotifications } from '../hooks/useNotifications'
import '../css/inventory-page.css'
import '../css/packs-page.css'

export interface PackNFT {
  asset_id: string
  name: string
  img: string
  video?: string
  collection: {
    name: string
    collection_name: string
  }
  schema: {
    schema_name: string
  }
  template: {
    template_id: string
    max_supply: string
  }
  data: Record<string, string | number | boolean | null | undefined>
  mint_number?: number
}

const Packs: React.FC = () => {
  const { settings } = useDisplaySettings()
  const { showSuccess, showError } = useNotifications()
  
  const [packs, setPacks] = useState<PackNFT[]>([])
  const [loading, setLoading] = useState(false)
  const [selectedNft, setSelectedNft] = useState<PackNFT | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [selectedItems, setSelectedItems] = useState<Set<string>>(new Set())
  const [isMultiSelectMode, setIsMultiSelectMode] = useState(false)
  const [showUnpackModal, setShowUnpackModal] = useState(false)
  const [searchTerm, setSearchTerm] = useState('')
  const [debouncedSearchTerm, setDebouncedSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [hasMoreNFTs, setHasMoreNFTs] = useState(false)
  const [apiPage, setApiPage] = useState(1)

  const { isConnected, account, login, session } = useWallet()

  const itemsPerPage = 20

  // Debounce search term
  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedSearchTerm(searchTerm)
    }, 300)

    return () => clearTimeout(timer)
  }, [searchTerm])

  const loadPacks = useCallback(async (page = 1, append = false) => {
    if (!account) return
    
    if (page === 1) {
      setLoading(true)
    }
    
    try {
      const response = await apiService.getUserInventoryPaginated(account, page, 100)
      
      // Convert API response to PackNFT format and filter for packs only
      const allNFTs: PackNFT[] = response.assets.map(nft => ({
        asset_id: nft.asset_id,
        name: nft.name,
        img: nft.image,
        video: nft.video || (nft.traits?.video as string) || undefined,
        collection: {
          name: nft.collection,
          collection_name: nft.collection
        },
        schema: {
          schema_name: nft.schema
        },
        template: {
          template_id: nft.template_id?.toString() || '0',
          max_supply: (nft.max_supply || 0).toString()
        },
        data: nft.traits || {},
        mint_number: nft.template_mint
      }))
      
      // Filter only pack NFTs
      const packNFTs = allNFTs.filter(nft => NeftyBlendService.isPackNFT(nft))
      
      if (append) {
        setPacks(prev => [...prev, ...packNFTs])
      } else {
        setPacks(packNFTs)
        setCurrentPage(1)
      }
      
      setHasMoreNFTs(response.assets.length === 100)
      setApiPage(page)
    } catch (error) {
      console.error('Error loading packs:', error)
      showError('Failed to load pack inventory')
    } finally {
      setLoading(false)
    }
  }, [account, showError])

  useEffect(() => {
    if (isConnected && account) {
      loadPacks(1, false)
    } else {
      setPacks([])
    }
  }, [isConnected, account, loadPacks])

  // Filter packs based on search term
  const filteredPacks = useMemo(() => {
    if (!debouncedSearchTerm) return packs
    
    const searchLower = debouncedSearchTerm.toLowerCase()
    return packs.filter(pack => 
      pack.name.toLowerCase().includes(searchLower) ||
      pack.collection.collection_name.toLowerCase().includes(searchLower) ||
      pack.schema.schema_name.toLowerCase().includes(searchLower)
    )
  }, [packs, debouncedSearchTerm])

  // Paginate filtered packs
  const paginatedPacks = useMemo(() => {
    const startIndex = (currentPage - 1) * itemsPerPage
    const endIndex = startIndex + itemsPerPage
    return filteredPacks.slice(startIndex, endIndex)
  }, [filteredPacks, currentPage, itemsPerPage])

  const totalPages = Math.ceil(filteredPacks.length / itemsPerPage)
  const filteredCount = filteredPacks.length

  const handleNFTClick = (pack: PackNFT) => {
    if (isMultiSelectMode) {
      const newSelected = new Set(selectedItems)
      if (newSelected.has(pack.asset_id)) {
        newSelected.delete(pack.asset_id)
      } else {
        newSelected.add(pack.asset_id)
      }
      setSelectedItems(newSelected)
    } else {
      setSelectedNft(pack)
      setIsModalOpen(true)
    }
  }

  const handleBulkUnpack = () => {
    if (selectedItems.size === 0) return
    setShowUnpackModal(true)
  }

  const handleLoadMore = () => {
    if (hasMoreNFTs) {
      loadPacks(apiPage + 1, true)
    }
  }

  const handlePageChange = (page: number) => {
    setCurrentPage(page)
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }

  const toggleMultiSelect = () => {
    setIsMultiSelectMode(!isMultiSelectMode)
    setSelectedItems(new Set())
  }

  if (!isConnected) {
    return (
      <div className="page-content">
        <div className="page-header">
          <h1>Pack Inventory</h1>
          <p>Manage and unpack your NFT packs</p>
        </div>
        <div className="connect-wallet-prompt">
          <div className="connect-card">
            <h3>Connect Your Wallet</h3>
            <p>Connect your WAX wallet to view your pack inventory</p>
            <button className="connect-btn" onClick={login}>
              Connect Wallet
            </button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="page-content packs-page">
      <div className="page-header">
        <h1>Pack Inventory</h1>
        <p>Manage and unpack your NFT packs, chests, boxes, and containers</p>
      </div>

      {/* Search and Controls */}
      <div className="inventory-controls">
        <div className="search-container">
          <input
            type="text"
            placeholder="Search packs..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
        </div>
        
        <div className="control-buttons">
          <button 
            className={`multi-select-btn ${isMultiSelectMode ? 'active' : ''}`}
            onClick={toggleMultiSelect}
          >
            {isMultiSelectMode ? '✓ Multi-Select' : 'Multi-Select'}
          </button>
          
          {isMultiSelectMode && selectedItems.size > 0 && (
            <button className="bulk-btn unpack-btn" onClick={handleBulkUnpack}>
              📦 Unpack ({selectedItems.size})
            </button>
          )}
        </div>
      </div>

      {/* Loading State */}
      {loading && (
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading your packs...</p>
        </div>
      )}

      {/* Pack Grid */}
      {!loading && (
        <>
          <div className="inventory-page-stats">
            <p>{filteredCount} packs found</p>
          </div>
          
          {packs.length > 0 ? (
            <>
              <div className="inventory-page-grid">
                {paginatedPacks.map((pack) => (
                  <div
                    key={pack.asset_id}
                    className={`nft-card ${
                      isMultiSelectMode && selectedItems.has(pack.asset_id) ? 'selected' : ''
                    }`}
                    onClick={() => handleNFTClick(pack)}
                  >
                    {isMultiSelectMode && (
                      <div className="selection-checkbox">
                        <input
                          type="checkbox"
                          checked={selectedItems.has(pack.asset_id)}
                          onChange={() => {}}
                        />
                      </div>
                    )}
                    
                    <div className="nft-image-container">
                      <MediaRenderer
                          imageUrl={getAssetImageUrl({ img: pack.img })}
                          videoUrl={pack.video ? getAssetVideoUrl({ video: pack.video }) : undefined}
                          alt={pack.name}
                          className="w-full h-full object-cover"
                          controls={settings.showVideoControls}
                          autoPlay={settings.autoPlayVideos}
                          muted={settings.muteVideos}
                        />
                      
                      <div className="pack-indicator">
                        📦 Pack
                      </div>
                    </div>
                    
                    <div className="nft-info">
                      <h3 className="nft-name">{pack.name}</h3>
                      <p className="nft-collection">{typeof pack.collection === 'string' ? pack.collection : pack.collection?.collection_name || pack.collection?.name || 'Unknown'}</p>
                      <p className="nft-schema">{pack.schema.schema_name}</p>
                      {pack.mint_number && (
                        <p className="nft-mint">Mint #{pack.mint_number}</p>
                      )}
                    </div>
                  </div>
                ))}
              </div>

              {/* Load More Button */}
              {hasMoreNFTs && (
                <div className="load-more-container">
                  <button className="load-more-btn" onClick={handleLoadMore}>
                    Load More Packs
                  </button>
                </div>
              )}

              {/* Pagination */}
              {totalPages > 1 && (
                <div className="pagination-container">
                  <div className="pagination">
                    <button
                      className="pagination-btn"
                      onClick={() => handlePageChange(currentPage - 1)}
                      disabled={currentPage === 1}
                    >
                      Previous
                    </button>
                    
                    {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                      const page = i + 1
                      return (
                        <button
                          key={page}
                          className={`pagination-btn ${currentPage === page ? 'active' : ''}`}
                          onClick={() => handlePageChange(page)}
                        >
                          {page}
                        </button>
                      )
                    })}
                    
                    <button
                      className="pagination-btn"
                      onClick={() => handlePageChange(currentPage + 1)}
                      disabled={currentPage === totalPages}
                    >
                      Next
                    </button>
                  </div>
                  
                  <div className="pagination-info">
                    Page {currentPage} of {totalPages}
                  </div>
                </div>
              )}
            </>
          ) : (
            <div className="empty-state">
              <div className="empty-icon">📦</div>
              <h3>No Packs Found</h3>
              <p>You don't have any pack NFTs in your inventory yet.</p>
              <p>Packs, chests, boxes, and containers will appear here when you acquire them.</p>
            </div>
          )}
        </>
      )}

      {/* NFT Details Modal */}
      {selectedNft && (
        <NFTDetailsModal
          nft={{
            asset_id: selectedNft.asset_id,
            name: selectedNft.name,
            image: selectedNft.img,
            collection: selectedNft.collection.collection_name,
            schema: selectedNft.schema.schema_name,
            template_mint: selectedNft.mint_number,
            traits: selectedNft.data,
            price: null,
            sale_id: null,
            seller: null
          }}
          isOpen={isModalOpen}
          onClose={() => {
            setIsModalOpen(false)
            setSelectedNft(null)
          }}
        />
      )}

      {/* Unpack Modal */}
      <UnpackModal 
        selectedNFTs={packs.filter(pack => selectedItems.has(pack.asset_id))}
        isOpen={showUnpackModal}
        onClose={() => {
          setShowUnpackModal(false)
        }}
        userAccount={account || ''}
        walletSession={session}
        onUnpackComplete={() => {
          setSelectedItems(new Set())
          setIsMultiSelectMode(false)
          loadPacks(1, false)
          showSuccess('Packs unpacked successfully!')
        }}
      />
    </div>
  )
}

export default Packs