/**
 * HTML sanitization utilities to prevent XSS attacks
 */

// Allowed HTML tags for safe rendering
const ALLOWED_TAGS = ['strong', 'em', 'b', 'i', 'u', 'br', 'p', 'span'];

// Allowed attributes for HTML tags
const ALLOWED_ATTRIBUTES: Record<string, string[]> = {
  'span': ['class'],
  'p': ['class'],
};

/**
 * Sanitizes HTML content by removing potentially dangerous elements and attributes
 * @param html - The HTML string to sanitize
 * @returns Sanitized HTML string
 */
export function sanitizeHtml(html: string): string {
  // Create a temporary DOM element to parse the HTML
  const tempDiv = document.createElement('div');
  tempDiv.innerHTML = html;

  // Recursively sanitize all elements
  sanitizeElement(tempDiv);

  return tempDiv.innerHTML;
}

/**
 * Recursively sanitizes a DOM element and its children
 * @param element - The DOM element to sanitize
 */
function sanitizeElement(element: Element): void {
  const children = Array.from(element.children);
  
  for (const child of children) {
    const tagName = child.tagName.toLowerCase();
    
    // Remove disallowed tags
    if (!ALLOWED_TAGS.includes(tagName)) {
      // Replace with text content to preserve the content but remove the tag
      const textNode = document.createTextNode(child.textContent || '');
      child.parentNode?.replaceChild(textNode, child);
      continue;
    }
    
    // Remove disallowed attributes
    const allowedAttrs = ALLOWED_ATTRIBUTES[tagName] || [];
    const attributes = Array.from(child.attributes);
    
    for (const attr of attributes) {
      if (!allowedAttrs.includes(attr.name)) {
        child.removeAttribute(attr.name);
      }
    }
    
    // Recursively sanitize children
    sanitizeElement(child);
  }
}

/**
 * Escapes HTML special characters to prevent XSS
 * @param text - The text to escape
 * @returns Escaped text
 */
export function escapeHtml(text: string): string {
  const div = document.createElement('div');
  div.textContent = text;
  return div.innerHTML;
}

/**
 * Safely formats markdown-like text with basic formatting
 * @param content - The content to format
 * @returns Sanitized HTML string
 */
export function formatSafeMarkdown(content: string): string {
  // First escape any existing HTML
  const escaped = escapeHtml(content);
  
  // Apply basic markdown formatting
  const formatted = escaped
    .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>') // Bold
    .replace(/\*(.*?)\*/g, '<em>$1</em>') // Italic
    .replace(/\n/g, '<br>'); // Line breaks
  
  // Sanitize the result
  return sanitizeHtml(formatted);
}