import React, { useState, useEffect, useMemo, useCallback } from 'react'
import { useWallet } from '../hooks/useWallet'
import type { InventoryNFT } from '../types/inventory'
import { useNotifications } from '../hooks/useNotifications'
import { apiService } from '../services/api'
import { formatWAXPrice, formatNumber } from '../utils/formatUtils'
import { log } from '../services/loggingService'
import { WharfKitService } from '../services/wharfkit'

import '../css/modal.css'

interface BulkListingModalProps {
  nfts: InventoryNFT[]
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
}



interface PriceSuggestion {
  floorPrice: number
  averagePrice: number
  suggestedPrice: number
  confidence: 'high' | 'medium' | 'low'
  sampleSize: number
}

const BulkListingModal: React.FC<BulkListingModalProps> = ({ nfts, isOpen, onClose, onSuccess }) => {
  const [listingPrices, setListingPrices] = useState<Record<string, string>>({})
  const [bulkPrice, setBulkPrice] = useState('')
  const [isListing, setIsListing] = useState(false)
  const [listingProgress, setListingProgress] = useState(0)
  const [priceSuggestions, setPriceSuggestions] = useState<Record<string, PriceSuggestion>>({})
  const [loadingSuggestions, setLoadingSuggestions] = useState(false)
  const [showSuggestions, setShowSuggestions] = useState(true)
  const [sortBy, setSortBy] = useState<string>('name_asc')
  const { session } = useWallet()
  const { showSuccess, showError } = useNotifications()

  const waxCurrency = {
    symbol: 'WAX',
    precision: 8
  }

  const handlePriceChange = (assetId: string, price: string) => {
    setListingPrices(prev => ({
      ...prev,
      [assetId]: price
    }))
  }

  // Fetch price suggestions for NFTs with enhanced error handling and fallbacks
  const fetchPriceSuggestions = useCallback(async () => {
    if (!nfts.length || !isOpen) return
    
    setLoadingSuggestions(true)
    const suggestions: Record<string, PriceSuggestion> = {}
    const maxRetries = 3
    
    const fetchWithRetry = async (url: string, retries: number = maxRetries): Promise<unknown> => {
      try {
        const controller = new AbortController()
        const timeoutId = setTimeout(() => controller.abort(), 10000) // 10 second timeout
        
        const response = await fetch(url, {
          signal: controller.signal,
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        })
        
        clearTimeout(timeoutId)
        
        if (!response.ok) {
          throw new Error(`HTTP ${response.status}: ${response.statusText}`)
        }
        
        return await response.json()
      } catch (error) {
        if (retries > 0 && error instanceof Error && !error.name.includes('AbortError')) {
          log.warn(`Retrying API call (${maxRetries - retries + 1}/${maxRetries})`, 'BulkListingModal.fetchWithRetry', { error: error.message, url })
          await new Promise(resolve => setTimeout(resolve, 1000 * (maxRetries - retries + 1))) // Exponential backoff
          return fetchWithRetry(url, retries - 1)
        }
        throw error
      }
    }
    
    try {
      // Group NFTs by collection for batch analysis
      const collectionGroups = nfts.reduce((groups, nft) => {
        const collection = nft.collection.collection_name
        if (!groups[collection]) groups[collection] = []
        groups[collection].push(nft)
        return groups
      }, {} as Record<string, InventoryNFT[]>)
      
      const collectionEntries = Object.entries(collectionGroups)
      let successfulCollections = 0
      
      // Analyze each collection with enhanced error handling
      for (const [collection, collectionNFTs] of collectionEntries) {
        try {
          // Primary API: AtomicMarket sales data
          const salesUrl = `${apiService.atomicMarketAPI}/atomicmarket/v1/sales?state=3&collection_name=${collection}&limit=100&order=desc&sort=updated`
          let salesData = null
          
          try {
            salesData = await fetchWithRetry(salesUrl)
          } catch (primaryError) {
            log.error(`Primary API failed for ${collection}, trying fallback:`, 'BulkListingModal.fetchPriceSuggestions', primaryError as Error)
            
            // Fallback API: Try with different parameters
            try {
              const fallbackUrl = `${apiService.atomicMarketAPI}/atomicmarket/v1/sales?collection_name=${collection}&state=3&limit=50&order=desc`
              salesData = await fetchWithRetry(fallbackUrl, 1)
            } catch (fallbackError) {
              log.error(`Fallback API also failed for ${collection}:`, 'BulkListingModal.fetchPriceSuggestions', fallbackError as Error)
              // Apply default pricing strategy for this collection
               applyDefaultPricing(collectionNFTs, suggestions)
               continue
            }
          }
          
          // Type guard for API response
          const isValidApiResponse = (data: unknown): data is { success: boolean; data: any[] } => {
            return typeof data === 'object' && data !== null && 
                   'success' in data && 'data' in data && 
                   Array.isArray((data as any).data)
          }
          
          if (isValidApiResponse(salesData) && salesData.success && salesData.data.length > 0) {
            const sales = salesData.data
            const prices = sales
              .filter((sale: {price?: {amount?: string}}) => sale.price?.amount)
              .map((sale: {price: {amount: string, token_precision?: number}}) => {
                const amount = parseFloat(sale.price.amount)
                const precision = sale.price.token_precision || 8
                return amount / Math.pow(10, precision)
              })
              .filter((price: number) => price > 0 && price < 10000) // Filter out outliers
            
            if (prices.length > 0) {
              // Enhanced price calculation with outlier removal
              const sortedPrices = prices.sort((a: number, b: number) => a - b)
              const q1Index = Math.floor(sortedPrices.length * 0.25)
              const q3Index = Math.floor(sortedPrices.length * 0.75)
              const iqr = sortedPrices[q3Index] - sortedPrices[q1Index]
              const lowerBound = sortedPrices[q1Index] - 1.5 * iqr
              const upperBound = sortedPrices[q3Index] + 1.5 * iqr
              
              const filteredPrices = sortedPrices.filter((price: number) => price >= lowerBound && price <= upperBound)
              
              if (filteredPrices.length > 0) {
                const floorPrice = Math.min(...filteredPrices)
                const averagePrice = filteredPrices.reduce((sum: number, price: number) => sum + price, 0) / filteredPrices.length
                const medianPrice = filteredPrices[Math.floor(filteredPrices.length / 2)]
                
                // Smart suggested price calculation
                let suggestedPrice: number
                if (filteredPrices.length >= 10) {
                  // Use median-based pricing for better stability
                  suggestedPrice = Math.max(floorPrice * 1.03, medianPrice * 0.98)
                } else {
                  // Conservative pricing for limited data
                  suggestedPrice = Math.max(floorPrice * 1.05, averagePrice * 0.95)
                }
                
                let confidence: 'high' | 'medium' | 'low'
                if (filteredPrices.length >= 20) {
                  confidence = 'high'
                } else if (filteredPrices.length >= 10) {
                  confidence = 'medium'
                } else {
                  confidence = 'low'
                }
                
                // Apply suggestions to all NFTs in this collection
                collectionNFTs.forEach((nft: InventoryNFT) => {
                  suggestions[nft.asset_id] = {
                    floorPrice,
                    averagePrice,
                    suggestedPrice,
                    confidence,
                    sampleSize: filteredPrices.length
                  }
                })
                
                successfulCollections++
              } else {
                 // Apply default pricing if no valid prices after filtering
                 applyDefaultPricing(collectionNFTs, suggestions)
               }
             } else {
               // Apply default pricing if no price data
               applyDefaultPricing(collectionNFTs, suggestions)
             }
           } else {
             // Apply default pricing if API returns no data
             applyDefaultPricing(collectionNFTs, suggestions)
          }
        } catch (error) {
          log.error(`Failed to process collection ${collection}:`, 'BulkListingModal.fetchPriceSuggestions', error as Error)
          // Apply default pricing for failed collections
           applyDefaultPricing(collectionNFTs, suggestions)
        }
        
        // Add delay between requests to avoid rate limiting
        if (collectionEntries.indexOf([collection, collectionNFTs]) < collectionEntries.length - 1) {
          await new Promise(resolve => setTimeout(resolve, 500))
        }
      }
      
      // Show user feedback about data quality
      if (successfulCollections === 0) {
        showError('Price Analysis', 'Unable to fetch current market data. Using default pricing suggestions.')
      } else if (successfulCollections < collectionEntries.length) {
        log.warn(`Price data available for ${successfulCollections}/${collectionEntries.length} collections`)
      }
      
      setPriceSuggestions(suggestions)
    } catch (error) {
      log.error('Error fetching price suggestions:', 'BulkListingModal.fetchPriceSuggestions', error as Error)
      showError('Price Analysis Failed', 'Unable to analyze market prices. Please set prices manually.')
      // Apply default pricing for all NFTs as final fallback
      nfts.forEach(nft => {
        suggestions[nft.asset_id] = {
          floorPrice: 1.0,
          averagePrice: 2.0,
          suggestedPrice: 1.5,
          confidence: 'low',
          sampleSize: 0
        }
      })
      setPriceSuggestions(suggestions)
    } finally {
      setLoadingSuggestions(false)
    }
  }, [nfts, isOpen, showError])
  
  // Helper method to apply default pricing strategy
  const applyDefaultPricing = (collectionNFTs: InventoryNFT[], suggestions: Record<string, PriceSuggestion>) => {
    // Default pricing based on collection popularity and rarity
    const defaultFloor = 1.0
    const defaultAverage = 2.0
    const defaultSuggested = 1.5
    
    collectionNFTs.forEach((nft: InventoryNFT) => {
      suggestions[nft.asset_id] = {
        floorPrice: defaultFloor,
        averagePrice: defaultAverage,
        suggestedPrice: defaultSuggested,
        confidence: 'low',
        sampleSize: 0
      }
    })
  }
  
  useEffect(() => {
    if (isOpen && nfts.length > 0) {
      fetchPriceSuggestions()
    }
  }, [isOpen, nfts, fetchPriceSuggestions])

  // Sort NFTs for display
  const sortedNfts = useMemo(() => {
    const sorted = [...nfts]
    
    switch (sortBy) {
      case 'name_asc':
        return sorted.sort((a, b) => (a.name || '').localeCompare(b.name || ''))
      case 'name_desc':
        return sorted.sort((a, b) => (b.name || '').localeCompare(a.name || ''))
      case 'collection_asc':
        return sorted.sort((a, b) => a.collection.collection_name.localeCompare(b.collection.collection_name))
      case 'collection_desc':
        return sorted.sort((a, b) => b.collection.collection_name.localeCompare(a.collection.collection_name))
      case 'mint_asc':
        return sorted.sort((a, b) => (a.mint_number || 0) - (b.mint_number || 0))
      case 'mint_desc':
        return sorted.sort((a, b) => (b.mint_number || 0) - (a.mint_number || 0))
      case 'price_asc':
        return sorted.sort((a, b) => {
          const priceA = parseFloat(listingPrices[a.asset_id] || '0')
          const priceB = parseFloat(listingPrices[b.asset_id] || '0')
          return priceA - priceB
        })
      case 'price_desc':
        return sorted.sort((a, b) => {
          const priceA = parseFloat(listingPrices[a.asset_id] || '0')
          const priceB = parseFloat(listingPrices[b.asset_id] || '0')
          return priceB - priceA
        })
      default:
        return sorted
    }
  }, [nfts, sortBy, listingPrices])

  const applyBulkPrice = () => {
    if (!bulkPrice) return
    
    const newPrices: Record<string, string> = {}
    nfts.forEach(nft => {
      newPrices[nft.asset_id] = bulkPrice
    })
    setListingPrices(newPrices)
  }


  
  const applySuggestedPrices = () => {
    const newPrices: Record<string, string> = {}
    nfts.forEach(nft => {
      const suggestion = priceSuggestions[nft.asset_id]
      if (suggestion) {
        newPrices[nft.asset_id] = formatNumber(suggestion.suggestedPrice, 4)
      }
    })
    setListingPrices(newPrices)
  }
  
  const applyFloorPrices = () => {
    const newPrices: Record<string, string> = {}
    nfts.forEach(nft => {
      const suggestion = priceSuggestions[nft.asset_id]
      if (suggestion) {
        newPrices[nft.asset_id] = formatNumber(suggestion.floorPrice * 0.99, 4) // 1% below floor
      }
    })
    setListingPrices(newPrices)
  }

  // Enhanced smart pricing with proper useEffect
  useEffect(() => {
    if (showSuggestions && nfts.length > 0) {
      fetchPriceSuggestions()
    }
  }, [showSuggestions, nfts, fetchPriceSuggestions])









  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!session || nfts.length === 0) {
      showError('Validation Error', 'Please connect your wallet and select NFTs to list')
      return
    }

    // Validate all prices
    const invalidPrices = nfts.filter(nft => {
      const price = listingPrices[nft.asset_id]
      return !price || isNaN(parseFloat(price)) || parseFloat(price) <= 0
    })

    if (invalidPrices.length > 0) {
      showError(
        'Invalid Prices',
        `Please set valid prices for all NFTs. ${invalidPrices.length} NFT(s) have invalid prices.`
      )
      return
    }

    setIsListing(true)
    setListingProgress(0)

    try {
      // Create listing actions for all NFTs
      const actions = nfts.map(nft => {
        const priceFloat = parseFloat(listingPrices[nft.asset_id])
        const formattedPrice = priceFloat.toFixed(waxCurrency.precision)
        const priceString = `${formattedPrice} ${waxCurrency.symbol}`

        return {
          account: 'atomicmarket',
          name: 'announcesale',
          authorization: [{
            actor: WharfKitService.safeGetActor(session),
            permission: WharfKitService.safeGetPermission(session)
          }],
          data: {
            seller: WharfKitService.safeGetActor(session),
            asset_ids: [nft.asset_id],
            listing_price: priceString,
            settlement_symbol: `${waxCurrency.precision},${waxCurrency.symbol}`,
            maker_marketplace: 'market.pxj'
          }
        }
      })

      // Execute listings in batches to avoid transaction size limits
      const batchSize = 5 // List up to 5 NFTs per transaction
      const batches = []
      
      for (let i = 0; i < actions.length; i += batchSize) {
        batches.push(actions.slice(i, i + batchSize))
      }

      let completedBatches = 0
      
      for (const batch of batches) {
        const result = await session.transact({ actions: batch })
        
        if (result) {
          completedBatches++
          setListingProgress((completedBatches / batches.length) * 100)
        }
      }
      
      showSuccess(
        'Listing Successful',
        `Successfully listed ${nfts.length} NFT(s) on the marketplace!`
      )
      onSuccess?.()
      onClose()
      setListingPrices({})
      setBulkPrice('')
      
    } catch (error) {
      log.error('Bulk listing failed:', 'BulkListingModal.handleSubmit', error as Error)
      showError(
        'Listing Failed',
        error instanceof Error ? error.message : 'Unknown error occurred'
      )
    } finally {
      setIsListing(false)
      setListingProgress(0)
    }
  }

  const getTotalValue = () => {
    return nfts.reduce((total, nft) => {
      const price = parseFloat(listingPrices[nft.asset_id] || '0')
      return total + (isNaN(price) ? 0 : price)
    }, 0)
  }

  if (!isOpen) return null

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content bulk-listing-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Bulk List NFTs</h2>
          <button className="modal-close" onClick={onClose}>
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </button>
        </div>

        <div className="modal-body">
          <div className="listing-summary">
            <h3>Listing Summary</h3>
            <p>You are about to list <strong>{nfts.length} NFT{nfts.length !== 1 ? 's' : ''}</strong></p>
            <p className="total-value">Total Value: <strong>{formatWAXPrice(getTotalValue())}</strong></p>
          </div>

          <form onSubmit={handleSubmit} className="listing-form">
            {/* Smart Pricing Section */}
            <div className="smart-pricing-section">
              <div className="pricing-header">
                <h4>Smart Pricing Tools</h4>
                <button 
                  type="button" 
                  className="toggle-suggestions"
                  onClick={() => setShowSuggestions(!showSuggestions)}
                >
                  {showSuggestions ? '📊 Hide Analysis' : '📊 Show Analysis'}
                </button>
              </div>
              
              {showSuggestions && (
                <div className="price-analysis">
                  {loadingSuggestions ? (
                    <div className="loading-suggestions">
                      <div className="spinner"></div>
                      <span>Analyzing market data...</span>
                    </div>
                  ) : (
                    <div className="suggestion-controls">
                      <button 
                        type="button" 
                        className="btn-suggestion suggested"
                        onClick={applySuggestedPrices}
                        disabled={isListing || Object.keys(priceSuggestions).length === 0}
                        title="Apply AI-recommended prices based on market analysis"
                      >
                        🎯 Apply Suggested Prices
                      </button>
                      <button 
                        type="button" 
                        className="btn-suggestion floor"
                        onClick={applyFloorPrices}
                        disabled={isListing || Object.keys(priceSuggestions).length === 0}
                        title="Price 1% below current floor prices for quick sales"
                      >
                        ⚡ Quick Sale (Floor -1%)
                      </button>
                    </div>
                  )}
                </div>
              )}
              
              {/* Manual Bulk Price Setting */}
              <div className="bulk-price-controls">
                <h5>Manual Bulk Pricing</h5>
                <div className="bulk-input-group">
                  <input
                    type="number"
                    value={bulkPrice}
                    onChange={(e) => setBulkPrice(e.target.value)}
                    placeholder="Set same price for all"
                    step="0.0001"
                    min="0"
                    disabled={isListing}
                  />
                  <button 
                    type="button" 
                    className="btn-secondary"
                    onClick={applyBulkPrice}
                    disabled={!bulkPrice || isListing}
                  >
                    Apply to All
                  </button>
                </div>
              </div>
            </div>

            {/* Individual NFT Pricing */}
            <div className="nft-pricing-list">
              <div className="pricing-header">
                <h4>Individual Pricing</h4>
                {nfts.length > 1 && (
                  <div className="sort-container">
                    <label htmlFor="nft-sort">Sort by:</label>
                    <select 
                      id="nft-sort"
                      className="sort-select"
                      value={sortBy}
                      onChange={(e) => setSortBy(e.target.value)}
                    >
                      <option value="name_asc">Name (A-Z)</option>
                      <option value="name_desc">Name (Z-A)</option>
                      <option value="collection_asc">Collection (A-Z)</option>
                      <option value="collection_desc">Collection (Z-A)</option>
                      <option value="mint_asc">Mint # (Low-High)</option>
                      <option value="mint_desc">Mint # (High-Low)</option>
                      <option value="price_asc">Price (Low-High)</option>
                      <option value="price_desc">Price (High-Low)</option>
                    </select>
                  </div>
                )}
              </div>
              <div className="bulk-listing-pricing-list-container">
                {sortedNfts.map((nft) => {
                  const suggestion = priceSuggestions[nft.asset_id]
                  return (
                    <div key={nft.asset_id} className="bulk-listing-nft-pricing-item">
                      <div className="bulk-listing-nft-info">
                        <div className="bulk-listing-nft-image">
                          <img 
                            src={nft.img?.startsWith('http') ? nft.img : `https://ipfs.io/ipfs/${nft.img}`}
                            alt={nft.name}
                            onError={(e) => {
                              const target = e.target as HTMLImageElement
                              target.src = 'https://pixeljourney.xyz/img/toppixals/10.webp'
                            }}
                          />
                        </div>
                        <div className="bulk-listing-nft-details">
                          <span className="bulk-listing-nft-name">{nft.name}</span>
                          <span className="bulk-listing-nft-collection">{typeof nft.collection === 'string' ? nft.collection : nft.collection?.collection_name || nft.collection?.name || 'Unknown'}</span>
                          {nft.mint_number && (
                            <span className="bulk-listing-nft-mint">#{nft.mint_number}</span>
                          )}
                          {suggestion && (
                            <div className="bulk-listing-price-suggestions">
                              <div className="bulk-listing-suggestion-row">
                                <span className="bulk-listing-suggestion-label">Floor:</span>
                                <span className="bulk-listing-suggestion-value">{formatWAXPrice(suggestion.floorPrice)}</span>
                              </div>
                              <div className="bulk-listing-suggestion-row">
                                <span className="bulk-listing-suggestion-label">Avg:</span>
                                <span className="bulk-listing-suggestion-value">{formatWAXPrice(suggestion.averagePrice)}</span>
                              </div>
                              <div className="bulk-listing-suggestion-row">
                                <span className="bulk-listing-suggestion-label">Suggested:</span>
                                <span className={`bulk-listing-suggestion-value confidence-${suggestion.confidence}`}>
                                  {formatWAXPrice(suggestion.suggestedPrice)}
                                </span>
                                <span className="bulk-listing-confidence-badge">{suggestion.confidence}</span>
                              </div>
                              <div className="bulk-listing-sample-size">Based on {suggestion.sampleSize} recent sales</div>
                            </div>
                          )}
                        </div>
                      </div>
                      <div className="bulk-listing-price-input-section">
                        <div className="bulk-listing-price-input">
                          <input
                            type="number"
                            value={listingPrices[nft.asset_id] || ''}
                            onChange={(e) => handlePriceChange(nft.asset_id, e.target.value)}
                            placeholder="Price in WAX"
                            step="0.0001"
                            min="0"
                            required
                            disabled={isListing}
                          />
                          <span className="currency-label">WAX</span>
                        </div>
                        {suggestion && (
                          <div className="bulk-listing-quick-price-buttons">
                            <button
                              type="button"
                              className="bulk-listing-quick-price-btn suggested"
                              onClick={() => handlePriceChange(nft.asset_id, formatNumber(suggestion.suggestedPrice, 4))}
                              disabled={isListing}
                              title="Use suggested price"
                            >
                              💡
                            </button>
                            <button
                              type="button"
                              className="bulk-listing-quick-price-btn floor"
                              onClick={() => handlePriceChange(nft.asset_id, formatNumber(suggestion.floorPrice * 0.99, 4))}
                              disabled={isListing}
                              title="Use floor price -1%"
                            >
                              ⚡
                            </button>
                          </div>
                        )}
                      </div>
                    </div>
                  )
                })}
              </div>
            </div>

            {isListing && (
              <div className="listing-progress">
                <div className="progress-bar">
                  <div 
                    className="progress-fill" 
                    style={{ width: `${listingProgress}%` }}
                  ></div>
                </div>
                <p>Listing NFTs... {Math.round(listingProgress)}% complete</p>
              </div>
            )}

            <div className="modal-actions">
              <button 
                type="button" 
                className="btn-secondary" 
                onClick={onClose}
                disabled={isListing}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn-primary"
                disabled={isListing || getTotalValue() === 0}
              >
                {isListing ? 'Listing...' : `List ${nfts.length} NFT${nfts.length !== 1 ? 's' : ''}`}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default BulkListingModal