import React, { useState } from 'react'
import { NeftyBlendService } from '../services/neftyBlendService'
import { useNotifications } from '../hooks/useNotifications'
import type { Session } from '@wharfkit/session'
import '../css/modal.css'
import '../css/unpack-modal.css'

interface PackNFT {
  asset_id: string
  name: string
  img?: string
  mint_number?: number
  collection: {
    name: string
    collection_name: string
  }
  schema: {
    schema_name: string
  }
  template: {
    template_id: string
    max_supply: string
  }
  data: Record<string, string | number | boolean | null | undefined>
}

interface UnpackResult {
  nft: PackNFT
  success: boolean
  error?: string
  transaction?: unknown
}

interface UnpackModalProps {
  isOpen: boolean
  onClose: () => void
  selectedNFTs: PackNFT[]
  userAccount: string
  walletSession: Session | null
  onUnpackComplete: () => void
}

const UnpackModal: React.FC<UnpackModalProps> = ({
  isOpen,
  onClose,
  selectedNFTs,
  userAccount,
  walletSession,
  onUnpackComplete
}) => {
  const [isUnpacking, setIsUnpacking] = useState(false)
  const { showError, showSuccess } = useNotifications()

  // Filter selected NFTs to only show packs
  const packNFTs = selectedNFTs.filter(nft => NeftyBlendService.isPackNFT(nft))

  const handleUnpack = async () => {
    if (!userAccount || !walletSession) {
      showError('Wallet not connected', 'Connection Required')
      return
    }

    if (packNFTs.length === 0) {
      showError('No pack NFTs selected', 'No Packs Selected')
      return
    }

    setIsUnpacking(true)
    const results: UnpackResult[] = []

    try {
      for (const nft of packNFTs) {
        const packType = NeftyBlendService.getPackType(nft)
        let transaction
        
        if (packType === 'pixel_ingredient') {
          transaction = await NeftyBlendService.unpackPixelIngredientPack(nft.asset_id, userAccount)
        } else {
          transaction = await NeftyBlendService.unpackGenericPack(nft.asset_id, userAccount)
        }
        
        const result = await NeftyBlendService.executeUnpack(transaction, walletSession)
        results.push({ nft, transaction: result, success: true })
      }
      
      // Results processed successfully
      showSuccess('Packs unpacked successfully!', 'Unpack Complete')
      onUnpackComplete()
    } catch (error) {
      console.error('Error unpacking NFTs:', error)
      showError('Failed to unpack NFTs. Please try again.', 'Unpack Failed')
    } finally {
      setIsUnpacking(false)
    }
  }

  if (!isOpen) return null

  const canUnpack = packNFTs.length > 0

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content unpack-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Unpack NFT</h2>
          <button className="modal-close" onClick={onClose}>
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </button>
        </div>

        <div className="modal-body">
          <div className="unpack-nft-info">
            <h3>Selected Pack NFTs ({packNFTs.length})</h3>
            {packNFTs.map((nft) => (
              <div key={nft.asset_id} className="nft-item">
                <div className="nft-image">
                  <img src={nft.img} alt={nft.name} />
                </div>
                <div className="nft-details">
                  <h4>{nft.name}</h4>
                  <p className="collection-name">{typeof nft.collection === 'string' ? nft.collection : nft.collection?.collection_name || nft.collection?.name || 'Unknown'}</p>
                  <p className="schema-name">{nft.schema.schema_name}</p>
                  {nft.mint_number && (
                    <p className="mint-number">Mint #{nft.mint_number}</p>
                  )}
                </div>
              </div>
            ))}
          </div>

          {canUnpack ? (
            <div className="unpack-info">
              <div className="info-box">
                <h4>📦 Pack Contents</h4>
                <div className="pack-contents">
                  <p>These packs contain random rewards that will be revealed upon unpacking.</p>
                  <p className="warning">⚠️ Unpacking will burn these packs permanently!</p>
                </div>
              </div>

              <button 
                className="unpack-btn"
                onClick={handleUnpack}
                disabled={isUnpacking || !walletSession}
              >
                {isUnpacking ? (
                  <>
                    <div className="loading-spinner small"></div>
                    Unpacking...
                  </>
                ) : (
                  `📦 Unpack ${packNFTs.length} Pack${packNFTs.length > 1 ? 's' : ''}`
                )}
              </button>
            </div>
          ) : (
            <div className="cannot-unpack">
              <div className="info-box error">
                <h4>❌ Cannot Unpack</h4>
                <p>No pack NFTs selected for unpacking.</p>
                <p>Only packs, chests, boxes, and containers can be unpacked.</p>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default UnpackModal