import React, { useState, useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { apiService } from '../services/api'
import type { NFTAsset } from '../services/api'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'

import { log } from '../services/loggingService'
import NFTDetailsModal from './NFTDetailsModal'
import SendOfferModal from './SendOfferModal'
import SocialShare from './SocialShare'
import ImageZoom from './ImageZoom'
import '../css/listing-detail.css'
import '../css/rwax.css'

interface AssetImageData {
  data?: {
    img?: string
    image?: string
  }
  template?: {
    immutable_data?: {
      img?: string
      image?: string
    }
  }
}

const ListingDetail: React.FC = () => {
  const { saleId } = useParams<{ saleId: string }>()
  const navigate = useNavigate()
  const [listing, setListing] = useState<NFTAsset | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [usdPrice, setUsdPrice] = useState<string>('')
  const [isDetailsModalOpen, setIsDetailsModalOpen] = useState(false)
  const [isOfferModalOpen, setIsOfferModalOpen] = useState(false)
  const [isShareModalOpen, setIsShareModalOpen] = useState(false)
  const [showImageZoom, setShowImageZoom] = useState(false)
  const [saleState, setSaleState] = useState<number>(0)

  useEffect(() => {
    if (saleId) {
      loadListingData()
    }
  }, [saleId])

  const loadListingData = async () => {
    if (!saleId) return
    
    try {
      setLoading(true)
      setError(null)
      
      // Use apiService for better error handling and fallback support
      const url = `${apiService.atomicMarketAPI}/atomicmarket/v1/sales/${saleId}`
      const response = await apiService.fetchWithRetry(url)
      const data = await response.json()
      
      if (data.success && data.data) {
        const sale = data.data
        
        // Set sale state
        setSaleState(sale.state)
        
        // Convert to NFTAsset format
        const nftAsset: NFTAsset = {
          sale_id: sale.sale_id,
          asset_id: sale.assets[0]?.asset_id || '',
          name: sale.assets[0]?.name || `${sale.assets[0]?.collection?.collection_name || 'Unknown'} #${sale.assets[0]?.asset_id}`,
          image: getAssetImage(sale.assets[0]),
          price: `${(parseFloat(sale.price.amount) / Math.pow(10, sale.price.token_precision)).toFixed(4)} ${sale.price.token_symbol}`,
          seller: sale.seller,
          collection: sale.assets[0]?.collection?.collection_name || 'Unknown',
          schema: sale.assets[0]?.schema?.schema_name || 'Unknown',
          template_mint: parseInt(sale.assets[0]?.template?.template_id || '0') || 0,
          max_supply: sale.assets[0]?.template?.max_supply || '0',
          traits: Object.fromEntries(
            Object.entries(sale.assets[0]?.data || {}).map(([key, value]) => [
              key, 
              value === null || value === undefined ? '' : 
              typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean' ? value : String(value)
            ])
          )
        }
        
        setListing(nftAsset)
        
        // Get USD price
        const waxPrice = parseFloat(sale.price.amount) / Math.pow(10, sale.price.token_precision)
        const usdValue = await apiService.getUSDPrice(waxPrice)
        setUsdPrice(usdValue)
      } else {
        setError('Listing not found')
      }
    } catch (err) {
      console.error('Error loading listing:', err)
      setError(`Failed to load listing data: ${err instanceof Error ? err.message : 'Unknown error'}`)
    } finally {
      setLoading(false)
    }
  }

  const getAssetImage = (asset: AssetImageData | null): string => {
    if (!asset) return ''
    
    // Try different image sources
    if (asset.data?.img) {
      return asset.data.img.startsWith('http') ? asset.data.img : `https://ipfs.io/ipfs/${asset.data.img}`
    }
    if (asset.data?.image) {
      return asset.data.image.startsWith('http') ? asset.data.image : `https://ipfs.io/ipfs/${asset.data.image}`
    }
    if (asset.template?.immutable_data?.img) {
      return asset.template.immutable_data.img.startsWith('http') ? asset.template.immutable_data.img : `https://ipfs.io/ipfs/${asset.template.immutable_data.img}`
    }
    if (asset.template?.immutable_data?.image) {
      return asset.template.immutable_data.image.startsWith('http') ? asset.template.immutable_data.image : `https://ipfs.io/ipfs/${asset.template.immutable_data.image}`
    }
    
    return 'https://pixeljourney.xyz/img/toppixals/10.webp'
  }

  const { isConnected, purchaseNFT, login } = useWallet()
  const { showSuccess, showError, showWarning } = useNotifications()
  // Transaction tracking is now handled directly by WharfKit

  const handlePurchase = async () => {
    if (!listing) return
    
    try {
      // Check if user is logged in
      if (!isConnected) {
        showWarning('Please connect your wallet to purchase this NFT')
        try {
          await login()
        } catch (error) {
          log.error('Login failed', 'ListingDetail', error as Error)
          return
        }
      }

      // Validate listing data
      if (!listing.sale_id || !listing.price) {
        showError('This NFT is not available for purchase')
        return
      }

      // Extract price value (remove ' WAX' suffix if present)
      const priceValue = listing.price.replace(' WAX', '')
      
      log.info('Purchasing NFT', 'ListingDetail.handlePurchase', { saleId: listing.sale_id, price: priceValue })
      
      // Execute purchase transaction directly with WharfKit
      await purchaseNFT(listing.sale_id, priceValue)
      
      showSuccess(`Successfully purchased ${listing.name || 'NFT'}!`)
      
      // Navigate back to marketplace or refresh listing
      navigate('/marketplace')
      
    } catch (error) {
      log.error('Purchase failed', 'ListingDetail', error as Error)
      const errorMessage = error instanceof Error ? error.message : 'Unknown error occurred'
      showError(`Purchase failed: ${errorMessage}`)
    }
  }

  const handleSellerClick = () => {
    if (!listing || !listing.seller) return
    navigate(`/user-profile-lookup?user=${encodeURIComponent(listing.seller)}`)
  }

  const handleCollectionClick = () => {
    if (!listing) return
    navigate(`/collections/${listing.collection}`)
  }

  const handleAssetClick = () => {
    if (!listing || !listing.asset_id) return
    navigate(`/assets/${listing.asset_id}`)
  }

  if (loading) {
    return (
      <div className="listing-detail-container">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading listing...</p>
        </div>
      </div>
    )
  }

  if (error || !listing) {
    return (
      <div className="listing-detail-container">
        <div className="error-container">
          <h2>Listing Not Found</h2>
          <p>{error || 'The requested listing could not be found.'}</p>
          <button onClick={() => navigate('/marketplace')} className="btn-primary">
            Back to Marketplace
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="listing-detail-container">
      <div className="listing-detail-header">
        <button onClick={() => navigate(-1)} className="back-button">
          ← Back
        </button>
        <h1>Listing Details</h1>
      </div>

      <div className="listing-detail-content">
        <div className="listing-image-section">
          <div className="listing-image">
            {listing.image.includes('.mp4') || listing.image.includes('.webm') ? (
              <video 
                src={listing.image} 
                controls 
                muted 
                loop
                className="listing-media"
              />
            ) : (
              <>
                <img 
                  src={listing.image} 
                  alt={listing.name}
                  className="listing-media"
                  onError={(e) => {
                    const target = e.target as HTMLImageElement
                    target.src = 'https://pixeljourney.xyz/img/toppixals/10.webp'
                  }}
                />
                <div 
                  className="zoom-overlay"
                  onClick={() => setShowImageZoom(true)}
                >
                  <svg viewBox="0 0 24 24" fill="currentColor">
                    <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
                    <path d="M12 10h-2v2H9v-2H7V9h2V7h1v2h2v1z"/>
                  </svg>
                </div>
              </>
            )}
          </div>
        </div>

        <div className="listing-info-section">
          <div className="listing-header">
            <h2>{listing.name}</h2>
            <div className="listing-actions">
              <button 
                onClick={() => setIsDetailsModalOpen(true)}
                className="listing-detail-nav-btn"
              >
                📋 View Details
              </button>
              <button 
                onClick={() => setIsOfferModalOpen(true)}
                className="listing-detail-nav-btn"
              >
                <svg viewBox="0 -8 72 72" fill="currentColor" style={{width: '16px', height: '16px', marginRight: '8px'}}>
                  <path d="M64,12.78v17s-3.63.71-4.38.81-3.08.85-4.78-.78C52.22,27.25,42.93,18,42.93,18a3.54,3.54,0,0,0-4.18-.21c-2.36,1.24-5.87,3.07-7.33,3.78a3.37,3.37,0,0,1-5.06-2.64,3.44,3.44,0,0,1,2.1-3c3.33-2,10.36-6,13.29-7.52,1.78-1,3.06-1,5.51,1C50.27,12,53,14.27,53,14.27a2.75,2.75,0,0,0,2.26.43C58.63,14,64,12.78,64,12.78ZM27,41.5a3,3,0,0,0-3.55-4.09,3.07,3.07,0,0,0-.64-3,3.13,3.13,0,0,0-3-.75,3.07,3.07,0,0,0-.65-3,3.38,3.38,0,0,0-4.72.13c-1.38,1.32-2.27,3.72-1,5.14s2.64.55,3.72.3c-.3,1.07-1.2,2.07-.09,3.47s2.64.55,3.72.3c-.3,1.07-1.16,2.16-.1,3.46s2.84.61,4,.25c-.45,1.15-1.41,2.39-.18,3.79s4.08.75,5.47-.58a3.32,3.32,0,0,0,.3-4.68A3.18,3.18,0,0,0,27,41.5Zm25.35-8.82L41.62,22a3.53,3.53,0,0,0-3.77-.68c-1.5.66-3.43,1.56-4.89,2.24a8.15,8.15,0,0,1-3.29,1.1,5.59,5.59,0,0,1-3-10.34C29,12.73,34.09,10,34.09,10a6.46,6.46,0,0,0-5-2C25.67,8,18.51,12.7,18.51,12.7a5.61,5.61,0,0,1-4.93.13L8,10.89v19.4s1.59.46,3,1a6.33,6.33,0,0,1,1.56-2.47,6.17,6.17,0,0,1,8.48-.06,5.4,5.4,0,0,1,1.34,2.37,5.49,5.49,0,0,1,2.29,1.4A5.4,5.4,0,0,1,26,34.94a5.47,5.47,0,0,1,3.71,4,5.38,5.38,0,0,1,2.39,1.43,5.65,5.65,0,0,1,1.48,4.89a0,0,0,0,1,0,0s.8.9,1.29,1.39a2.46,2.46,0,0,0,3.48-3.48s2,2.48,4.28,1c2-1.4,1.69-3.06.74-4a3.19,3.19,0,0,0,4.77.13,2.45,2.45,0,0,0,.13-3.3s1.33,1.81,4,.12c1.89-1.6,1-3.43,0-4.39Z"/>
                </svg>
                Make Offer
              </button>
              <button 
                onClick={() => setIsShareModalOpen(true)}
                className="listing-detail-nav-btn"
              >
                🔗 Share
              </button>
            </div>
          </div>

          <div className="listing-price-section">
            <div className="price-display">
              <div className="price-main">{listing.price}</div>
              <div className="price-usd">{usdPrice}</div>
              <div className="sale-status-display">
                <span className={`status-badge ${saleState === 1 ? 'status-active' : saleState === 3 ? 'status-sold' : 'status-canceled'}`}>
                  {saleState === 1 ? '🔄 Available' : saleState === 3 ? '✅ Sold' : '❌ Canceled'}
                </span>
              </div>
            </div>
            {saleState === 1 ? (
              <button onClick={handlePurchase} className="listing-detail-nav-btn listing-detail-primary-btn purchase-btn">
                🛒 Buy Now
              </button>
            ) : (
              <button disabled className="listing-detail-nav-btn listing-detail-primary-btn purchase-btn">
                {saleState === 3 ? '✅ Sold' : '❌ Not Available'}
              </button>
            )}
          </div>

          <div className="listing-details">
            <div className="detail-item">
              <span className="detail-label">Collection:</span>
              <span className="detail-value link" onClick={handleCollectionClick}>
                {listing.collection}
              </span>
            </div>
            <div className="detail-item">
              <span className="detail-label">Schema:</span>
              <span className="detail-value">{listing.schema}</span>
            </div>
            <div className="detail-item">
              <span className="detail-label">Asset ID:</span>
              <span className="detail-value link" onClick={handleAssetClick}>
                {listing.asset_id}
              </span>
            </div>
            <div className="detail-item">
              <span className="detail-label">Seller:</span>
              <span className="detail-value link" onClick={handleSellerClick}>
                {listing.seller}
              </span>
            </div>
            <div className="detail-item">
              <span className="detail-label">Sale ID:</span>
              <span className="detail-value">{listing.sale_id}</span>
            </div>
            {listing.template_mint && listing.template_mint > 0 && (
              <div className="detail-item">
                <span className="detail-label">Mint:</span>
                <span className="detail-value">
                  {listing.template_mint} / {listing.max_supply === '0' ? '∞' : listing.max_supply}
                </span>
              </div>
            )}
          </div>

          {listing.traits && Object.keys(listing.traits).length > 0 && (
            <div className="listing-attributes">
              <h3>Attributes</h3>
              <div className="attributes-grid">
                {Object.entries(listing.traits).map(([key, value]) => (
                  <div key={key} className="attribute-item">
                    <span className="attribute-name">{key}</span>
                    <span className="attribute-value">{value}</span>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Modals */}
      <NFTDetailsModal 
        nft={listing}
        isOpen={isDetailsModalOpen}
        onClose={() => setIsDetailsModalOpen(false)}
      />
      
      <SendOfferModal 
        nft={listing}
        isOpen={isOfferModalOpen}
        onClose={() => setIsOfferModalOpen(false)}
      />
      
      <SocialShare 
        nft={listing}
        isOpen={isShareModalOpen}
        onClose={() => setIsShareModalOpen(false)}
      />
      
      <ImageZoom 
         isOpen={showImageZoom}
         onClose={() => setShowImageZoom(false)}
         imageUrl={listing.image}
         imageName={listing.name}
       />
    </div>
  )
}

export default ListingDetail