import React, { useState, useEffect } from 'react'
import { apiService } from '../services/api'
import { useNotifications } from '../hooks/useNotifications'
import type { NFTAsset } from '../services/api'
import '../css/nft-comparison.css'

interface NFTComparisonProps {
  isOpen: boolean
  onClose: () => void
  initialNFTs?: NFTAsset[]
}

const NFTComparison: React.FC<NFTComparisonProps> = ({ isOpen, onClose, initialNFTs = [] }) => {
  const [selectedNFTs, setSelectedNFTs] = useState<NFTAsset[]>(initialNFTs)
  const [searchTerm, setSearchTerm] = useState('')
  const [searchResults, setSearchResults] = useState<NFTAsset[]>([])
  const [isSearching, setIsSearching] = useState(false)
  const { showWarning } = useNotifications()
  const [comparisonMetrics, setComparisonMetrics] = useState({
    showPrice: true,
    showRarity: true,
    showAttributes: true,
    showCollection: true,
    showSeller: true
  })

  useEffect(() => {
    if (initialNFTs.length > 0) {
      setSelectedNFTs(initialNFTs)
    }
  }, [initialNFTs])

  const searchNFTs = async (query: string) => {
    if (!query.trim()) {
      setSearchResults([])
      return
    }

    setIsSearching(true)
    try {
      // Use existing API methods to search for NFTs
      const [pixelJourney, friedFactory, cryptoMuffins] = await Promise.all([
        apiService.getWaxRockPixals(20),
        apiService.getFriedFactoryNFTs(20),
        apiService.getCryptoMuffinsNFTs(20)
      ])
      
      const allNFTs = [...pixelJourney, ...friedFactory, ...cryptoMuffins]
      const filtered = allNFTs.filter(nft => 
        nft.name.toLowerCase().includes(query.toLowerCase()) ||
        nft.collection.toLowerCase().includes(query.toLowerCase())
      )
      
      setSearchResults(filtered.slice(0, 10)) // Limit to 10 results
    } catch (error) {
      console.error('Error searching NFTs:', error)
      setSearchResults([])
    } finally {
      setIsSearching(false)
    }
  }

  const addNFTToComparison = (nft: NFTAsset) => {
    if (selectedNFTs.length >= 4) {
      showWarning('You can compare up to 4 NFTs at once', 'Comparison Limit')
      return
    }
    
    if (selectedNFTs.find(n => n.asset_id === nft.asset_id)) {
      showWarning('This NFT is already in the comparison', 'Duplicate NFT')
      return
    }

    setSelectedNFTs([...selectedNFTs, nft])
    setSearchTerm('')
    setSearchResults([])
  }

  const removeNFTFromComparison = (assetId: string) => {
    setSelectedNFTs(selectedNFTs.filter(nft => nft.asset_id !== assetId))
  }

  const toggleMetric = (metric: keyof typeof comparisonMetrics) => {
    setComparisonMetrics(prev => ({
      ...prev,
      [metric]: !prev[metric]
    }))
  }

  const getRarityColor = (rarity?: string) => {
    if (!rarity) return '#666'
    switch (rarity.toLowerCase()) {
      case 'common': return '#9CA3AF'
      case 'rare': return '#3B82F6'
      case 'epic': return '#8B5CF6'
      case 'legendary': return '#F59E0B'
      case 'mythic': return '#EF4444'
      default: return '#666'
    }
  }

  const getAttributeValue = (nft: NFTAsset, attributeName: string) => {
    if (!nft.traits) return 'N/A'
    const attr = nft.traits[attributeName]
    return attr || 'N/A'
  }

  const getAllAttributes = () => {
    const allAttributes = new Set<string>()
    selectedNFTs.forEach(nft => {
      if (nft.traits) {
        Object.keys(nft.traits).forEach(key => {
          if (key !== 'rarity') {
            allAttributes.add(key)
          }
        })
      }
    })
    return Array.from(allAttributes)
  }

  if (!isOpen) return null

  return (
    <div className="nft-comparison-overlay">
      <div className="nft-comparison-modal">
        <div className="comparison-header">
          <h2>NFT Comparison Tool</h2>
          <button className="close-btn" onClick={onClose}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        <div className="comparison-content">
          {/* Search Section */}
          <div className="search-section">
            <div className="search-input-container">
              <input
                type="text"
                placeholder="Search NFTs to add to comparison..."
                value={searchTerm}
                onChange={(e) => {
                  setSearchTerm(e.target.value)
                  searchNFTs(e.target.value)
                }}
                className="search-input"
              />
              {isSearching && <div className="search-loading">Searching...</div>}
            </div>
            
            {searchResults.length > 0 && (
              <div className="search-results">
                {searchResults.map(nft => (
                  <div key={nft.asset_id} className="search-result-item" onClick={() => addNFTToComparison(nft)}>
                    <img src={nft.image} alt={nft.name} className="result-image" />
                    <div className="result-info">
                      <div className="result-name">{nft.name}</div>
                      <div className="result-collection">{nft.collection}</div>
                      <div className="result-price">{nft.price}</div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>

          {/* Comparison Metrics Toggle */}
          <div className="metrics-toggle">
            <h3>Show Comparison Metrics:</h3>
            <div className="toggle-buttons">
              {Object.entries(comparisonMetrics).map(([key, value]) => (
                <button
                  key={key}
                  className={`toggle-btn ${value ? 'active' : ''}`}
                  onClick={() => toggleMetric(key as keyof typeof comparisonMetrics)}
                >
                  {key.replace('show', '').replace(/([A-Z])/g, ' $1').trim()}
                </button>
              ))}
            </div>
          </div>

          {/* Comparison Table */}
          {selectedNFTs.length > 0 ? (
            <div className="comparison-table">
              <div className="comparison-grid">
                {/* Header Row */}
                <div className="comparison-row header-row">
                  <div className="metric-label">NFT</div>
                  {selectedNFTs.map(nft => (
                    <div key={nft.asset_id} className="nft-column">
                      <div className="nft-header">
                        <img src={nft.image} alt={nft.name} className="nft-image" />
                        <div className="nft-info">
                          <div className="nft-name">{nft.name}</div>
                          <div className="nft-id">#{nft.asset_id}</div>
                        </div>
                        <button 
                          className="remove-nft-btn"
                          onClick={() => removeNFTFromComparison(nft.asset_id)}
                          title="Remove from comparison"
                        >
                          ×
                        </button>
                      </div>
                    </div>
                  ))}
                </div>

                {/* Price Row */}
                {comparisonMetrics.showPrice && (
                  <div className="comparison-row">
                    <div className="metric-label">Price</div>
                    {selectedNFTs.map(nft => (
                      <div key={nft.asset_id} className="metric-value price">
                        {nft.price}
                      </div>
                    ))}
                  </div>
                )}

                {/* Rarity Row */}
                {comparisonMetrics.showRarity && (
                  <div className="comparison-row">
                    <div className="metric-label">Rarity</div>
                    {selectedNFTs.map(nft => (
                      <div key={nft.asset_id} className="metric-value rarity">
                        <span 
                          className="rarity-badge"
                          style={{ backgroundColor: getRarityColor(String(nft.traits?.rarity || 'Unknown')) }}
                        >
                          {nft.traits?.rarity || 'Unknown'}
                        </span>
                      </div>
                    ))}
                  </div>
                )}

                {/* Collection Row */}
                {comparisonMetrics.showCollection && (
                  <div className="comparison-row">
                    <div className="metric-label">Collection</div>
                    {selectedNFTs.map(nft => (
                      <div key={nft.asset_id} className="metric-value">
                        {nft.collection}
                      </div>
                    ))}
                  </div>
                )}

                {/* Seller Row */}
                {comparisonMetrics.showSeller && (
                  <div className="comparison-row">
                    <div className="metric-label">Seller</div>
                    {selectedNFTs.map(nft => (
                      <div key={nft.asset_id} className="metric-value">
                        {nft.seller}
                      </div>
                    ))}
                  </div>
                )}

                {/* Attributes Rows */}
                {comparisonMetrics.showAttributes && getAllAttributes().map(attributeName => (
                  <div key={attributeName} className="comparison-row">
                    <div className="metric-label">{attributeName}</div>
                    {selectedNFTs.map(nft => (
                      <div key={nft.asset_id} className="metric-value">
                        {getAttributeValue(nft, attributeName)}
                      </div>
                    ))}
                  </div>
                ))}
              </div>
            </div>
          ) : (
            <div className="empty-comparison">
              <div className="empty-icon">📊</div>
              <h3>No NFTs to Compare</h3>
              <p>Search and add NFTs above to start comparing their attributes, prices, and more.</p>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default NFTComparison