import React from 'react'
import { API_ENDPOINTS } from '../config/marketplaceConfig'

interface MediaRendererProps {
  imageUrl?: string
  videoUrl?: string | null
  alt: string
  className?: string
  controls?: boolean
  muted?: boolean
  loop?: boolean
  autoPlay?: boolean
  loading?: 'lazy' | 'eager'
  fallbackSrc?: string
  enableAnimations?: boolean
}

const MediaRenderer: React.FC<MediaRendererProps> = React.memo(({
  imageUrl,
  videoUrl,
  alt,
  className = '',
  controls = true,
  muted = true,
  loop = false,
  autoPlay = false,
  loading = 'lazy',
  enableAnimations = true
}) => {
  // Helper function to process URLs (handle IPFS, etc.)
  const processUrl = (url: string | undefined | null): string | null => {
    if (!url) return null
    if (typeof url !== 'string') return null
    if (url.startsWith('http')) return url
    if (url.startsWith('Qm')) return `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${url}`
    return url
  }

  const processedVideoUrl = processUrl(videoUrl)
  const processedImageUrl = processUrl(imageUrl)
  
  // Create a placeholder for NFTs without images
  const getPlaceholderSrc = () => {
    // Create a simple SVG placeholder
    const svg = `<svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
      <rect width="200" height="200" fill="#f3f4f6"/>
      <text x="100" y="90" text-anchor="middle" font-family="Arial" font-size="14" fill="#6b7280">NFT</text>
      <text x="100" y="110" text-anchor="middle" font-family="Arial" font-size="12" fill="#9ca3af">No Image</text>
      <text x="100" y="130" text-anchor="middle" font-family="Arial" font-size="10" fill="#d1d5db">Available</text>
    </svg>`
    return `data:image/svg+xml;base64,${btoa(svg)}`
  }
  
  const finalImageUrl = processedImageUrl || getPlaceholderSrc()

  // If we have a video URL and animations are enabled, render video with image as poster
  if (processedVideoUrl && enableAnimations) {
    return (
      <video 
        className={className}
        controls={controls}
        muted={muted}
        loop={loop}
        autoPlay={autoPlay}
        poster={processedImageUrl ? processedImageUrl : undefined}
        onError={(e) => {
          // If video fails to load, replace with image
          const target = e.target as HTMLVideoElement
          const img = document.createElement('img')
          img.src = finalImageUrl
          img.alt = alt
          img.className = className
          img.loading = loading
          target.parentNode?.replaceChild(img, target)
        }}
      >
        <source src={processedVideoUrl} type="video/mp4" />
        <source src={processedVideoUrl} type="video/webm" />
        Your browser does not support the video tag.
      </video>
    )
  }
  
  // If animations are disabled but we have a video, show just the image
  if (processedVideoUrl && !enableAnimations) {
    return (
      <img 
        src={finalImageUrl}
        alt={alt}
        className={className}
        loading={loading}
        onError={(e) => {
          // If image fails to load, use placeholder
          const target = e.target as HTMLImageElement
          const placeholder = getPlaceholderSrc()
          if (target.src !== placeholder) {
            target.src = placeholder
          }
        }}
      />
    )
  }

  // Otherwise render image
  return (
    <img 
      src={finalImageUrl}
      alt={alt}
      className={className}
      loading={loading}
      onError={(e) => {
        // If image fails to load, use placeholder
        const target = e.target as HTMLImageElement
        const placeholder = getPlaceholderSrc()
        if (target.src !== placeholder) {
          target.src = placeholder
        }
      }}
    />
  )
})

MediaRenderer.displayName = 'MediaRenderer'

export default MediaRenderer