import React, { useState, useEffect } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import { stakingService, type BatchTransactionResult } from '../services/stakingService'
import { NFTImage } from './FallbackImage'
import { log } from '../services/loggingService'
import '../css/modal.css'

interface NFTAsset {
  asset_id: string
  template_id: string
  name: string
  image: string
  data: Record<string, unknown>
}

interface StakeDataRow {
  user: string
  staked_assets: string[]
}

interface NFTStakingModalProps {
  isOpen: boolean
  onClose: () => void
  templateId: string
  templateName: string
  action: 'stake' | 'unstake'
  onSuccess?: () => void
}

const NFTStakingModal: React.FC<NFTStakingModalProps> = ({ 
  isOpen, 
  onClose, 
  templateId,
  templateName,
  action, 
  onSuccess 
}) => {
  const [assets, setAssets] = useState<NFTAsset[]>([])
  const [selectedAssets, setSelectedAssets] = useState<string[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [isProcessing, setIsProcessing] = useState(false)
  const [batchProgress, setBatchProgress] = useState<{ current: number; total: number } | null>(null)
  const [transactionResults, setTransactionResults] = useState<BatchTransactionResult | null>(null)
  const { session, account } = useWallet()
  const { addNotification, showSuccess, showError } = useNotifications()

  useEffect(() => {
    if (isOpen && account) {
      fetchAssets()
    }
  }, [isOpen, account, templateId])

  const fetchAssets = async () => {
    if (!account) return
    
    setIsLoading(true)
    try {
      if (action === 'stake') {
        // Fetch unstaked assets from AtomicAssets API
        const { apiService } = await import('../services/api')
        const endpoint = `${apiService.atomicAssetsAPI}/atomicassets/v1/assets?owner=${account}&template_id=${templateId}&page=1&limit=100&order=desc&sort=asset_id`
        const response = await apiService.fetchWithRetry(endpoint)
        const data = await response.json()
        setAssets(data.data || [])
      } else {
        // For unstaking, we need to fetch staked assets from contract tables
        const { apiService } = await import('../services/api')
        const contractResponse = await apiService.waxRpcCall('/v1/chain/get_table_rows', {
          json: true,
          code: 'farms.waxdao',
          scope: 'farms.waxdao',
          table: 'stakers',
          lower_bound: account,
          upper_bound: account,
          limit: 1
        })
        
        const contractData = contractResponse as { rows: StakeDataRow[] }
        const userStakeData = contractData.rows.find((row: StakeDataRow) => row.user === account)
        const stakedAssetIds = userStakeData?.staked_assets || []
        
        // Fetch asset details from AtomicAssets API
        if (stakedAssetIds.length === 0) {
          setAssets([])
        } else {
          const assetsResponse = await apiService.fetchWithRetry(`${apiService.atomicAssetsAPI}/atomicassets/v1/assets?ids=${stakedAssetIds.join(',')}&page=1&limit=1000`)
          if (!assetsResponse.ok) {
            throw new Error('Failed to fetch asset details')
          }
          
          const assetsData = await assetsResponse.json()
          setAssets(assetsData.data || [])
        }
      }
    } catch (error) {
      log.error('Failed to fetch staking assets', 'NFTStakingModal.fetchAssets', error as Error, { account })
      addNotification({ type: 'error', message: 'Failed to load assets' })
    } finally {
      setIsLoading(false)
    }
  }

  const handleAssetToggle = (assetId: string) => {
    setSelectedAssets(prev => 
      prev.includes(assetId) 
        ? prev.filter(id => id !== assetId)
        : [...prev, assetId]
    )
  }

  const handleSelectAll = () => {
    if (selectedAssets.length === assets.length) {
      setSelectedAssets([])
    } else {
      setSelectedAssets(assets.map(asset => asset.asset_id))
    }
  }

  const handleSubmit = async () => {
    if (!session || selectedAssets.length === 0) return

    setIsProcessing(true)
    setBatchProgress({ current: 0, total: selectedAssets.length })
    setTransactionResults(null)
    
    try {
      let result: BatchTransactionResult
      
      if (action === 'stake') {
        result = await stakingService.stakeNFTsBatch(
          session,
          selectedAssets,
          'pixeljourney'
        )
      } else {
        result = await stakingService.unstakeNFTsBatch(
          session,
          selectedAssets
        )
      }
      
      setTransactionResults(result)
      
      if (result.success) {
        showSuccess(
          'Transaction Successful',
          `Successfully ${action}d ${result.successful_transactions.length} ${templateName} NFT${result.successful_transactions.length > 1 ? 's' : ''}`
        )
        
        // Close modal after successful transaction
        setTimeout(() => {
          onSuccess?.()
          onClose()
        }, 2000)
      } else {
        const successCount = result.successful_transactions.length
        const failCount = result.failed_transactions.length
        
        if (successCount > 0) {
          showSuccess(
            'Partial Success',
            `${successCount} NFTs ${action}d successfully, ${failCount} failed`
          )
        } else {
          showError(
            'Transaction Failed',
            `Failed to ${action} NFTs. Please try again.`
          )
        }
      }
      
    } catch (error: unknown) {
      log.error(`NFT ${action} operation failed`, `NFTStakingModal.handleBatchAction`, error as Error, { action, selectedAssetsCount: selectedAssets.length })
      const errorMessage = error instanceof Error ? error.message : `Failed to ${action} NFTs`
      showError(
        'Transaction Error',
        errorMessage
      )
    } finally {
      setIsProcessing(false)
      setBatchProgress(null)
    }
  }

  if (!isOpen) return null

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content nft-staking-modal" onClick={e => e.stopPropagation()}>
        <div className="modal-header">
          <h2>{action === 'stake' ? 'Stake' : 'Unstake'} {templateName} NFTs</h2>
          <button className="modal-close" onClick={onClose}>×</button>
        </div>
        
        <div className="modal-body">
          {isLoading ? (
            <div className="loading-state">
              <div className="loading-spinner"></div>
              <p>Loading your {templateName} NFTs...</p>
            </div>
          ) : isProcessing ? (
            <div className="processing-state">
              <div className="loading-spinner"></div>
              <p>{action === 'stake' ? 'Staking' : 'Unstaking'} your NFTs...</p>
              {batchProgress && (
                <div className="batch-progress">
                  <div className="progress-bar">
                    <div 
                      className="progress-fill"
                      style={{ width: `${(batchProgress.current / batchProgress.total) * 100}%` }}
                    ></div>
                  </div>
                  <p>Processing {batchProgress.current} of {batchProgress.total} NFTs</p>
                </div>
              )}
              {transactionResults && (
                <div className="transaction-results">
                  <div className="results-summary">
                    <div className="success-count">
                      ✅ {transactionResults.successful_transactions.length} successful
                    </div>
                    {transactionResults.failed_transactions.length > 0 && (
                      <div className="error-count">
                        ❌ {transactionResults.failed_transactions.length} failed
                      </div>
                    )}
                  </div>
                  {transactionResults.failed_transactions.length > 0 && (
                    <div className="failed-transactions">
                      <h4>Failed Transactions:</h4>
                      {transactionResults.failed_transactions.slice(0, 5).map(failure => (
                        <div key={failure.asset_id} className="failure-item">
                          <span>#{failure.asset_id}</span>
                          <span className="error-message">{failure.error}</span>
                        </div>
                      ))}
                      {transactionResults.failed_transactions.length > 5 && (
                        <p>... and {transactionResults.failed_transactions.length - 5} more</p>
                      )}
                    </div>
                  )}
                </div>
              )}
            </div>
          ) : (
            <>
              <div className="selection-controls">
                <button 
                  className="btn btn-secondary"
                  onClick={handleSelectAll}
                >
                  {selectedAssets.length === assets.length ? 'Deselect All' : 'Select All'}
                </button>
                <span className="selection-count">
                  {selectedAssets.length} of {assets.length} selected
                </span>
              </div>
              
              <div className="asset-grid">
                {assets.length === 0 ? (
                  <div className="no-assets">
                    <p>No {templateName} NFTs available to {action}</p>
                  </div>
                ) : (
                  assets.map(asset => (
                    <div 
                      key={asset.asset_id}
                      className={`asset-item ${selectedAssets.includes(asset.asset_id) ? 'selected' : ''}`}
                      onClick={() => handleAssetToggle(asset.asset_id)}
                    >
                      <NFTImage 
                        src={asset.image || `https://ipfs.io/ipfs/${asset.data?.img || 'placeholder'}`} 
                        alt={asset.name}
                      />
                      <div className="asset-id">#{asset.asset_id}</div>
                      {selectedAssets.includes(asset.asset_id) && (
                        <div className="selection-indicator">✓</div>
                      )}
                    </div>
                  ))
                )}
              </div>
            </>
          )}
        </div>
        
        <div className="modal-footer">
          <button className="btn btn-secondary" onClick={onClose}>
            Cancel
          </button>
          <button 
            className="btn btn-primary"
            onClick={handleSubmit}
            disabled={selectedAssets.length === 0 || isProcessing}
          >
            {isProcessing ? (
              <>
                <div className="loading-spinner"></div>
                {action === 'stake' ? 'Staking...' : 'Unstaking...'}
              </>
            ) : (
              `${action === 'stake' ? 'Stake' : 'Unstake'} ${selectedAssets.length} NFT${selectedAssets.length !== 1 ? 's' : ''}`
            )}
          </button>
        </div>
      </div>
    </div>
  )
}

export default NFTStakingModal