// Image service for handling fallback images and image loading
import { logger } from './loggingService'
import { loadIPFSImageWithRateLimit } from './ipfsRateLimiter'

export interface ImageConfig {
  width?: number
  height?: number
  backgroundColor?: string
  textColor?: string
  text?: string
  type?: 'nft' | 'collection' | 'avatar' | 'token' | 'generic'
}

export interface ImageLoadResult {
  success: boolean
  src: string
  fallbackUsed: boolean
  error?: Error
}

class ImageService {
  private imageCache = new Map<string, string>()
  private loadingPromises = new Map<string, Promise<ImageLoadResult>>()
  private readonly MAX_CACHE_SIZE = 500
  
  /**
   * Generate SVG fallback image
   */
  generateFallbackSVG(config: ImageConfig = {}): string {
    const {
      width = 150,
      height = 150,
      backgroundColor = '#f3f4f6',
      textColor = '#6b7280',
      text,
      type = 'generic'
    } = config
    
    const displayText = text || this.getDefaultText(type)
    const fontSize = Math.min(width, height) * 0.15
    
    const svg = `
      <svg width="${width}" height="${height}" xmlns="http://www.w3.org/2000/svg">
        <rect width="100%" height="100%" fill="${backgroundColor}"/>
        <g transform="translate(${width/2}, ${height/2})">
          ${this.getIconForType(type, Math.min(width, height) * 0.3, textColor)}
          <text 
            x="0" 
            y="${fontSize * 1.5}" 
            font-family="system-ui, -apple-system, sans-serif" 
            font-size="${fontSize}" 
            font-weight="500" 
            fill="${textColor}" 
            text-anchor="middle" 
            dominant-baseline="middle"
          >
            ${displayText}
          </text>
        </g>
      </svg>
    `
    
    return `data:image/svg+xml;base64,${btoa(svg)}`
  }
  
  /**
   * Get default text for image type
   */
  private getDefaultText(type: string): string {
    switch (type) {
      case 'nft': return 'NFT'
      case 'collection': return 'Collection'
      case 'avatar': return 'Avatar'
      case 'token': return 'Token'
      default: return 'Image'
    }
  }
  
  /**
   * Get SVG icon for image type
   */
  private getIconForType(type: string, size: number, color: string): string {
    const iconSize = size
    const strokeWidth = Math.max(1, iconSize * 0.08)
    
    switch (type) {
      case 'nft':
        return `
          <rect 
            x="${-iconSize/2}" 
            y="${-iconSize/2}" 
            width="${iconSize}" 
            height="${iconSize}" 
            rx="${iconSize * 0.1}" 
            fill="none" 
            stroke="${color}" 
            stroke-width="${strokeWidth}"
          />
          <circle 
            cx="0" 
            cy="${-iconSize * 0.15}" 
            r="${iconSize * 0.15}" 
            fill="${color}"
          />
        `
      case 'collection':
        return `
          <rect 
            x="${-iconSize/2}" 
            y="${-iconSize/2}" 
            width="${iconSize * 0.7}" 
            height="${iconSize * 0.7}" 
            rx="${iconSize * 0.05}" 
            fill="none" 
            stroke="${color}" 
            stroke-width="${strokeWidth}"
          />
          <rect 
            x="${-iconSize/2 + iconSize * 0.15}" 
            y="${-iconSize/2 + iconSize * 0.15}" 
            width="${iconSize * 0.7}" 
            height="${iconSize * 0.7}" 
            rx="${iconSize * 0.05}" 
            fill="none" 
            stroke="${color}" 
            stroke-width="${strokeWidth}"
          />
        `
      case 'avatar':
        return `
          <circle 
            cx="0" 
            cy="${-iconSize * 0.1}" 
            r="${iconSize * 0.25}" 
            fill="none" 
            stroke="${color}" 
            stroke-width="${strokeWidth}"
          />
          <path 
            d="M ${-iconSize * 0.35} ${iconSize * 0.3} Q 0 ${iconSize * 0.1} ${iconSize * 0.35} ${iconSize * 0.3}" 
            fill="none" 
            stroke="${color}" 
            stroke-width="${strokeWidth}"
          />
        `
      case 'token':
        return `
          <circle 
            cx="0" 
            cy="0" 
            r="${iconSize * 0.4}" 
            fill="none" 
            stroke="${color}" 
            stroke-width="${strokeWidth}"
          />
          <text 
            x="0" 
            y="${iconSize * 0.1}" 
            font-family="system-ui, -apple-system, sans-serif" 
            font-size="${iconSize * 0.3}" 
            font-weight="bold" 
            fill="${color}" 
            text-anchor="middle" 
            dominant-baseline="middle"
          >
            $
          </text>
        `
      default:
        return `
          <rect 
            x="${-iconSize/2}" 
            y="${-iconSize/2}" 
            width="${iconSize}" 
            height="${iconSize}" 
            rx="${iconSize * 0.1}" 
            fill="none" 
            stroke="${color}" 
            stroke-width="${strokeWidth}"
          />
          <path 
            d="M ${-iconSize * 0.2} ${-iconSize * 0.1} L ${iconSize * 0.2} ${-iconSize * 0.1} L 0 ${iconSize * 0.2} Z" 
            fill="${color}"
          />
        `
    }
  }
  
  /**
   * Load image with fallback support
   */
  async loadImage(src: string, config: ImageConfig = {}): Promise<ImageLoadResult> {
    if (!src) {
      return {
        success: false,
        src: this.generateFallbackSVG(config),
        fallbackUsed: true
      }
    }
    
    // Check cache first
    const cached = this.imageCache.get(src)
    if (cached) {
      return {
        success: true,
        src: cached,
        fallbackUsed: false
      }
    }
    
    // Check if already loading
    const existingPromise = this.loadingPromises.get(src)
    if (existingPromise) {
      return existingPromise
    }
    
    // Create loading promise
    const loadingPromise = this.performImageLoad(src, config)
    this.loadingPromises.set(src, loadingPromise)
    
    try {
      const result = await loadingPromise
      return result
    } finally {
      this.loadingPromises.delete(src)
    }
  }
  
  /**
   * Perform actual image loading
   */
  private async performImageLoad(src: string, config: ImageConfig): Promise<ImageLoadResult> {
    // Check if this is an IPFS URL that needs rate limiting
    if (this.isIPFSUrl(src)) {
      try {
        const rateLimitedUrl = await loadIPFSImageWithRateLimit(src)
        this.addToCache(src, rateLimitedUrl)
        return {
          success: true,
          src: rateLimitedUrl,
          fallbackUsed: false
        }
      } catch (error) {
        logger.warn(`IPFS rate-limited load failed: ${src}`, 'ImageService', { error })
        return {
          success: false,
          src: this.generateFallbackSVG(config),
          fallbackUsed: true,
          error: error instanceof Error ? error : new Error('IPFS load failed')
        }
      }
    }

    // For non-IPFS URLs, use standard loading
    return new Promise((resolve) => {
      const img = new Image()
      const timeout = setTimeout(() => {
        img.onload = null
        img.onerror = null
        resolve({
          success: false,
          src: this.generateFallbackSVG(config),
          fallbackUsed: true,
          error: new Error('Image load timeout')
        })
      }, 10000) // 10 second timeout
      
      img.onload = () => {
        clearTimeout(timeout)
        this.addToCache(src, src)
        resolve({
          success: true,
          src,
          fallbackUsed: false
        })
      }
      
      img.onerror = (error) => {
        clearTimeout(timeout)
        const fallbackSrc = this.generateFallbackSVG(config)
        logger.warn(`Failed to load image: ${src}`, 'ImageService', { error })
        resolve({
          success: false,
          src: fallbackSrc,
          fallbackUsed: true,
          error: error instanceof Error ? error : new Error('Image load failed')
        })
      }
      
      img.src = src
    })
  }
  
  /**
   * Add image to cache
   */
  private addToCache(key: string, value: string): void {
    // Implement LRU cache behavior
    if (this.imageCache.size >= this.MAX_CACHE_SIZE) {
      const firstKey = this.imageCache.keys().next().value
      if (firstKey) {
        this.imageCache.delete(firstKey)
      }
    }
    
    this.imageCache.set(key, value)
  }
  
  /**
   * Preload multiple images
   */
  async preloadImages(urls: string[], config: ImageConfig = {}): Promise<ImageLoadResult[]> {
    const promises = urls.map(url => this.loadImage(url, config))
    return Promise.all(promises)
  }
  
  /**
   * Get optimized image URL with fallback
   */
  getOptimizedImageUrl(src: string, config: ImageConfig = {}): string {
    if (!src) {
      return this.generateFallbackSVG(config)
    }
    
    // Check if it's already a data URL or SVG
    if (src.startsWith('data:') || src.startsWith('blob:')) {
      return src
    }
    
    // For IPFS URLs, use gateway optimization
    if (src.includes('ipfs.io') || src.includes('ipfs://')) {
      return this.optimizeIPFSUrl(src)
    }
    
    return src
  }
  
  /**
   * Check if URL is an IPFS URL
   */
  private isIPFSUrl(src: string): boolean {
    return src.includes('/ipfs/') || src.startsWith('ipfs://') || src.includes('gateway.pinata.cloud')
  }

  /**
   * Optimize IPFS URLs for better loading
   */
  private optimizeIPFSUrl(src: string): string {
    // For IPFS URLs, we'll let the rate limiter handle gateway selection
    // This method is now mainly for non-rate-limited scenarios
    if (this.isIPFSUrl(src)) {
      return src // Let rate limiter handle optimization
    }
    
    return src
  }
  
  /**
   * Clear image cache
   */
  clearCache(): void {
    this.imageCache.clear()
    this.loadingPromises.clear()
  }
  
  /**
   * Get cache statistics
   */
  getCacheStats(): { size: number; maxSize: number; hitRate: number } {
    return {
      size: this.imageCache.size,
      maxSize: this.MAX_CACHE_SIZE,
      hitRate: 0 // Would need to track hits/misses for accurate calculation
    }
  }
}

// Export singleton instance
export const imageService = new ImageService()

// Export utility functions
export const createFallbackImage = (config: ImageConfig = {}) => {
  return imageService.generateFallbackSVG(config)
}

export const loadImageWithFallback = (src: string, config: ImageConfig = {}) => {
  return imageService.loadImage(src, config)
}