import React, { useEffect, useRef, useCallback } from 'react'
import { useNotifications } from '../hooks/useNotifications'
import '../css/notifications.css'

const NotificationContainer: React.FC = () => {
  const { notifications, removeNotification, soundEnabled } = useNotifications()
  const audioContextRef = useRef<AudioContext | null>(null)
  const soundsRef = useRef<{ [key: string]: AudioBuffer }>({})

  const createTone = (audioContext: AudioContext, frequency: number, duration: number, type: OscillatorType): AudioBuffer => {
    const sampleRate = audioContext.sampleRate
    const frameCount = sampleRate * duration
    const buffer = audioContext.createBuffer(1, frameCount, sampleRate)
    const channelData = buffer.getChannelData(0)
    
    for (let i = 0; i < frameCount; i++) {
      const t = i / sampleRate
      let sample = 0
      
      switch (type) {
        case 'sine':
          sample = Math.sin(2 * Math.PI * frequency * t)
          break
        case 'square':
          sample = Math.sign(Math.sin(2 * Math.PI * frequency * t))
          break
        case 'triangle':
          sample = (2 / Math.PI) * Math.asin(Math.sin(2 * Math.PI * frequency * t))
          break
      }
      
      // Apply envelope for smooth sound
      const envelope = Math.exp(-t * 5) // Exponential decay
      channelData[i] = sample * envelope * 0.1 // Low volume
    }
    
    return buffer
  }

  const playNotificationSound = useCallback((type: string) => {
    if (!soundEnabled || !audioContextRef.current || !soundsRef.current[type]) return
    
    try {
      const source = audioContextRef.current.createBufferSource()
      source.buffer = soundsRef.current[type]
      source.connect(audioContextRef.current.destination)
      source.start()
    } catch (error) {
      console.warn('Failed to play notification sound:', error)
    }
  }, [soundEnabled])

  // Initialize audio context and load sounds
  useEffect(() => {
    const initAudio = async () => {
      try {
        audioContextRef.current = new (window.AudioContext || (window as { webkitAudioContext?: typeof AudioContext }).webkitAudioContext)()
        
        // Create sound buffers for different notification types
        const sounds = {
          success: createTone(audioContextRef.current, 800, 0.1, 'sine'),
          error: createTone(audioContextRef.current, 400, 0.2, 'square'),
          warning: createTone(audioContextRef.current, 600, 0.15, 'triangle'),
          info: createTone(audioContextRef.current, 500, 0.1, 'sine')
        }
        
        soundsRef.current = sounds
      } catch (error) {
        console.warn('Audio context initialization failed:', error)
      }
    }
    
    initAudio()
    
    return () => {
      if (audioContextRef.current) {
        audioContextRef.current.close()
      }
    }
  }, [])
  
  // Play sound when new notification appears
  useEffect(() => {
    if (notifications.length > 0) {
      const latestNotification = notifications[notifications.length - 1]
      playNotificationSound(latestNotification.type)
    }
  }, [notifications.length, notifications, playNotificationSound])

  if (notifications.length === 0) {
    return null
  }
  
  const handleRemoveNotification = (id: string) => {
    // Add removing class for exit animation
    const notificationElement = document.querySelector(`[data-notification-id="${id}"]`)
    if (notificationElement) {
      notificationElement.classList.add('removing')
      setTimeout(() => {
        removeNotification(id)
      }, 300) // Match animation duration
    } else {
      removeNotification(id)
    }
  }

  return (
    <div className="notification-container">
      {notifications.map((notification) => (
        <div
          key={notification.id}
          className={`notification notification-${notification.type}`}
          data-notification-id={notification.id}
        >
          <div className="notification-content">
            <div className="notification-header">
              {notification.title && (
                <h4 className="notification-title">{notification.title}</h4>
              )}
              <button
                className="notification-close"
                onClick={() => handleRemoveNotification(notification.id)}
                aria-label="Close notification"
              >
                <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                  <line x1="18" y1="6" x2="6" y2="18"></line>
                  <line x1="6" y1="6" x2="18" y2="18"></line>
                </svg>
              </button>
            </div>
            <div className="notification-body">
              <div className="notification-icon">
                {notification.type === 'success' && (
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                    <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14"></path>
                    <polyline points="22,4 12,14.01 9,11.01"></polyline>
                  </svg>
                )}
                {notification.type === 'error' && (
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                    <circle cx="12" cy="12" r="10"></circle>
                    <line x1="15" y1="9" x2="9" y2="15"></line>
                    <line x1="9" y1="9" x2="15" y2="15"></line>
                  </svg>
                )}
                {notification.type === 'warning' && (
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                    <path d="m21.73 18-8-14a2 2 0 0 0-3.48 0l-8 14A2 2 0 0 0 4 21h16a2 2 0 0 0 1.73-3Z"></path>
                    <line x1="12" y1="9" x2="12" y2="13"></line>
                    <line x1="12" y1="17" x2="12.01" y2="17"></line>
                  </svg>
                )}
                {notification.type === 'info' && (
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                    <circle cx="12" cy="12" r="10"></circle>
                    <path d="M12 16v-4"></path>
                    <path d="M12 8h.01"></path>
                  </svg>
                )}
              </div>
              <div className="notification-message">
                <p>{notification.message}</p>
                {notification.action && (
                  <button
                    className="notification-action"
                    onClick={notification.action.onClick}
                  >
                    {notification.action.label}
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  )
}

export default NotificationContainer