import { useState, useEffect } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import type { NFTAsset, AssetData } from '../services/api'
import MediaRenderer from './MediaRenderer'
import { useDisplaySettings } from '../hooks/useDisplaySettings'
import { API_ENDPOINTS } from '../config/marketplaceConfig'
import '../css/modal.css'
import '../css/send-offer-modal.css'

interface SendOfferModalProps {
  isOpen: boolean
  onClose: () => void
  nft: NFTAsset | null
}

interface UserNFT {
  asset_id: string
  name: string
  image: string
  video?: string | null
  collection: string | { name: string; collection_name: string }
  template_mint?: number
  max_supply?: string
}

export default function SendOfferModal({ isOpen, onClose, nft }: SendOfferModalProps) {
  const { session, isConnected, account } = useWallet()
  const { settings } = useDisplaySettings()
  const { showSuccess, showError } = useNotifications()
  const [offerType, setOfferType] = useState<'wax' | 'nft'>('wax')
  const [waxAmount, setWaxAmount] = useState('')
  const [customMemo, setCustomMemo] = useState('')
  const [userNFTs, setUserNFTs] = useState<UserNFT[]>([])
  const [selectedNFTs, setSelectedNFTs] = useState<string[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [loadingInventory, setLoadingInventory] = useState(false)
  const [isPixalHolder, setIsPixalHolder] = useState(false)

  // Check if user is a Pixal PFP holder
  useEffect(() => {
    const checkPixalHolder = async () => {
      if (!account) return
      
      try {
        // Check if user owns any Pixal PFP NFTs
        const response = await fetch(`https://aa.dapplica.io/atomicassets/v1/assets?owner=${account}&collection_name=pixeljourney&limit=1`)
        const data = await response.json()
        setIsPixalHolder(data.data && data.data.length > 0)
      } catch (error) {
        console.error('Error checking Pixal holder status:', error)
      }
    }

    if (isOpen && isConnected) {
      checkPixalHolder()
    }
  }, [isOpen, isConnected, account])

  // Load user's NFTs for trade offers
  useEffect(() => {
    const loadUserNFTs = async () => {
      if (!account || offerType !== 'nft') return
      
      setLoadingInventory(true)
      try {
        const response = await fetch(`https://aa.dapplica.io/atomicassets/v1/assets?owner=${account}&limit=100`)
        const data = await response.json()
        
        const formattedNFTs: UserNFT[] = data.data?.map((asset: AssetData) => {
          const rawImage = asset.data?.img || asset.data?.image
          const rawVideo = asset.data?.video
          let imageUrl = 'https://pixeljourney.xyz/img/toppixals/10.webp'
          let videoUrl: string | null = null
          
          if (rawVideo && typeof rawVideo === 'string') {
            if (rawVideo.startsWith('http')) {
              videoUrl = rawVideo
            } else {
              videoUrl = `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${rawVideo}`
            }
          }
          
          if (rawImage && typeof rawImage === 'string') {
            if (rawImage.startsWith('http')) {
              imageUrl = rawImage
            } else {
              // Handle IPFS hash
              imageUrl = `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${rawImage}`
            }
          }
          
          return {
            asset_id: asset.asset_id || '',
            name: asset.name || `${asset.collection?.collection_name || 'Unknown'} #${asset.asset_id || 'N/A'}`,
            image: imageUrl,
            video: videoUrl,
            collection: asset.collection?.collection_name || 'Unknown',
            template_mint: undefined,
            max_supply: asset.template?.max_supply || undefined
          }
        }) || []
        
        setUserNFTs(formattedNFTs)
      } catch (error) {
        console.error('Error loading user NFTs:', error)
      } finally {
        setLoadingInventory(false)
      }
    }

    if (isOpen && offerType === 'nft') {
      loadUserNFTs()
    }
  }, [isOpen, offerType, account])

  const handleNFTSelection = (assetId: string) => {
    setSelectedNFTs(prev => 
      prev.includes(assetId) 
        ? prev.filter(id => id !== assetId)
        : [...prev, assetId]
    )
  }

  const handleSendWaxOffer = async () => {
    if (!account || !nft || !waxAmount || !session) return
    
    setIsLoading(true)
    try {
      const memo = customMemo || (isPixalHolder ? 'Trade Offer sent via PxMarket - market.pixeljourney.xyz' : 'Trade Offer sent via PxMarket - market.pixeljourney.xyz')
      
      // Create WAX buy offer transaction
      const actions = [
        {
          account: 'eosio.token',
          name: 'transfer',
          authorization: [{
            actor: account,
            permission: 'active'
          }],
          data: {
            from: account,
            to: 'atomicmarket',
            quantity: `${parseFloat(waxAmount).toFixed(8)} WAX`,
            memo: 'deposit'
          }
        },
        {
          account: 'atomicmarket',
          name: 'createbuyo',
          authorization: [{
            actor: account,
            permission: 'active'
          }],
          data: {
            buyer: account,
            recipient: nft.owner || nft.seller,
            price: `${parseFloat(waxAmount).toFixed(8)} WAX`,
            asset_ids: [nft.asset_id],
            memo: memo,
            maker_marketplace: 'market.pxj'
          }
        }
      ]

      // Execute transaction through wallet
      await session.transact({ actions })

      showSuccess('WAX offer sent successfully!')
      onClose()
    } catch (error) {
      console.error('Error sending WAX offer:', error)
      showError('Failed to send WAX offer. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const handleSendNFTOffer = async () => {
    if (!account || !nft || selectedNFTs.length === 0 || !session) return
    
    setIsLoading(true)
    try {
      const memo = customMemo || (isPixalHolder ? 'NFT Trade Offer sent via PxMarket - market.pixeljourney.xyz' : 'NFT Trade Offer sent via PxMarket - market.pixeljourney.xyz')
      
      // Create NFT trade offer transaction
      const actions = [{
        account: 'atomicassets',
        name: 'createoffer',
        authorization: [{
          actor: account,
          permission: 'active'
        }],
        data: {
          sender: account,
          recipient: nft.owner || nft.seller,
          sender_asset_ids: selectedNFTs,
          recipient_asset_ids: [nft.asset_id],
          memo: memo
        }
      }]

      // Execute transaction through wallet
      await session.transact({ actions })

      showSuccess('NFT trade offer sent successfully!')
      onClose()
    } catch (error) {
      console.error('Error sending NFT offer:', error)
      showError('Failed to send NFT offer. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const handleSubmit = () => {
    if (offerType === 'wax') {
      handleSendWaxOffer()
    } else {
      handleSendNFTOffer()
    }
  }

  if (!isOpen || !nft) return null

  return (
    <div className="modal-backdrop" onClick={onClose}>
      <div className="modal-content send-offer-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>Send Offer for {nft.name}</h3>
          <button className="modal-close" onClick={onClose}>
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </button>
        </div>

        <div className="modal-body">
          {/* NFT Preview */}
          <div className="target-nft-preview">
            <MediaRenderer
               imageUrl={nft.image}
               alt={nft.name}
               enableAnimations={settings.enableAnimatedNFTs}
             />
            <div className="nft-info">
              <h4>{nft.name}</h4>
              <p>Collection: {typeof nft.collection === 'string' ? nft.collection : (nft.collection as { collection_name?: string; name?: string })?.collection_name || (nft.collection as { name?: string })?.name || 'Unknown'}</p>
              {nft.price && <p>Listed Price: {nft.price} WAX</p>}
            </div>
          </div>

          {/* Offer Type Selection */}
          <div className="offer-type-selection">
            <h4>Choose Offer Type</h4>
            <div className="offer-type-buttons">
              <button 
                className={`offer-type-btn ${offerType === 'wax' ? 'active' : ''}`}
                onClick={() => setOfferType('wax')}
              >
                <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                  <circle cx="12" cy="12" r="10"></circle>
                  <path d="M12 6v6l4 2"></path>
                </svg>
                WAX Offer
              </button>
              <button 
                className={`offer-type-btn ${offerType === 'nft' ? 'active' : ''}`}
                onClick={() => setOfferType('nft')}
              >
                <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                  <rect x="3" y="3" width="18" height="18" rx="2" ry="2"></rect>
                  <circle cx="8.5" cy="8.5" r="1.5"></circle>
                  <polyline points="21,15 16,10 5,21"></polyline>
                </svg>
                NFT Trade
              </button>
            </div>
          </div>

          {/* WAX Offer Form */}
          {offerType === 'wax' && (
            <div className="wax-offer-form">
              <div className="form-group">
                <label htmlFor="waxAmount">WAX Amount</label>
                <input
                  id="waxAmount"
                  type="number"
                  step="0.0001"
                  min="0"
                  value={waxAmount}
                  onChange={(e) => setWaxAmount(e.target.value)}
                  placeholder="Enter WAX amount"
                  className="form-input"
                />
              </div>
            </div>
          )}

          {/* NFT Trade Form */}
          {offerType === 'nft' && (
            <div className="nft-trade-form">
              <h4>Select NFTs to Trade</h4>
              {loadingInventory ? (
                <div className="loading-inventory">
                  <div className="spinner"></div>
                  <p>Loading your NFTs...</p>
                </div>
              ) : (
                <div className="user-nfts-grid">
                  {userNFTs.map((userNft) => (
                    <div 
                      key={userNft.asset_id}
                      className={`user-nft-item ${selectedNFTs.includes(userNft.asset_id) ? 'selected' : ''}`}
                      onClick={() => handleNFTSelection(userNft.asset_id)}
                    >
                      <MediaRenderer
                         imageUrl={userNft.image}
                         videoUrl={userNft.video}
                         alt={userNft.name}
                         enableAnimations={settings.enableAnimatedNFTs}
                       />
                      <div className="nft-details">
                        <p className="nft-name">{userNft.name}</p>
                        <p className="nft-collection">{typeof userNft.collection === 'string' ? userNft.collection : userNft.collection?.collection_name || userNft.collection?.name || 'Unknown'}</p>
                      </div>
                      {selectedNFTs.includes(userNft.asset_id) && (
                        <div className="selection-indicator">
                          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                            <polyline points="20,6 9,17 4,12"></polyline>
                          </svg>
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}

          {/* Custom Memo (for Pixal holders) */}
          {isPixalHolder && (
            <div className="custom-memo-section">
              <div className="pixal-holder-badge">
                <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                  <polygon points="12,2 15.09,8.26 22,9 17,14.14 18.18,21.02 12,17.77 5.82,21.02 7,14.14 2,9 8.91,8.26"></polygon>
                </svg>
                Pixal PFP Holder
              </div>
              <div className="form-group">
                <label htmlFor="customMemo">Custom Memo (Optional)</label>
                <textarea
                  id="customMemo"
                  value={customMemo}
                  onChange={(e) => setCustomMemo(e.target.value)}
                  placeholder="Add a custom message to your offer..."
                  className="form-textarea"
                  rows={3}
                />
              </div>
            </div>
          )}
        </div>

        <div className="modal-footer">
          <button className="btn-secondary" onClick={onClose}>
            Cancel
          </button>
          <button 
            className="btn-primary"
            onClick={handleSubmit}
            disabled={isLoading || !isConnected || 
              (offerType === 'wax' && !waxAmount) || 
              (offerType === 'nft' && selectedNFTs.length === 0)
            }
          >
            {isLoading ? (
              <>
                <div className="spinner-small"></div>
                Sending...
              </>
            ) : (
              `Send ${offerType === 'wax' ? 'WAX' : 'NFT'} Offer`
            )}
          </button>
        </div>
      </div>
    </div>
  )
}