import type { NFTAsset, SaleData } from './api'
import { priceService } from './priceService'
import { ErrorMessageService } from './errorMessageService'
import { storageService } from './storageService'

export interface CartItem {
  nft: NFTAsset
  addedAt: number
  isAvailable?: boolean
  sale_id: string | null
  selected?: boolean
}

export interface CartTotals {
  totalWAX: string
  totalUSD: string
  itemCount: number
}

class CartService {
  private storageKey = 'pixal-marketplace-cart'
  private cart: CartItem[] = []
  private listeners: Array<(cart: CartItem[]) => void> = []

  constructor() {
    this.loadCart()
  }

  // Load cart from storage with proper error handling
  private loadCart(): void {
    try {
      this.cart = storageService.getJSON(this.storageKey, [])
    } catch (error) {
      ErrorMessageService.logError('CartService.loadCart', error)
      this.cart = []
    }
  }

  // Save cart to storage with proper error handling
  private saveCart(): void {
    try {
      const success = storageService.setJSON(this.storageKey, this.cart)
      if (!success) {
        ErrorMessageService.logError('CartService.saveCart', new Error('Failed to save cart to storage'), { 
          storageKey: this.storageKey, 
          cartSize: this.cart.length 
        })
      }
      this.notifyListeners()
    } catch (error) {
      ErrorMessageService.logError('CartService.saveCart', error, { storageKey: this.storageKey, cartSize: this.cart.length })
    }
  }

  // Add listener for cart changes
  addListener(listener: (cart: CartItem[]) => void): void {
    this.listeners.push(listener)
  }

  // Remove listener
  removeListener(listener: (cart: CartItem[]) => void): void {
    this.listeners = this.listeners.filter(l => l !== listener)
  }

  // Notify all listeners of cart changes
  private notifyListeners(): void {
    this.listeners.forEach(listener => listener([...this.cart]))
  }

  // Add item to cart
  addToCart(nft: NFTAsset): boolean {
    // Check if item already exists in cart
    const existingIndex = this.cart.findIndex(item => item.nft.asset_id === nft.asset_id)
    
    if (existingIndex !== -1) {
      // Item already in cart, don't add duplicate
      return false
    }

    const cartItem: CartItem = {
      nft,
      addedAt: Date.now(),
      isAvailable: true,
      sale_id: nft.sale_id
    }

    this.cart.push(cartItem)
    this.saveCart()
    return true
  }

  // Remove item from cart
  removeFromCart(assetId: string): boolean {
    const initialLength = this.cart.length
    this.cart = this.cart.filter(item => item.nft.asset_id !== assetId)
    
    if (this.cart.length !== initialLength) {
      this.saveCart()
      return true
    }
    return false
  }

  // Check if item is in cart
  isInCart(assetId: string): boolean {
    return this.cart.some(item => item.nft.asset_id === assetId)
  }

  // Get cart items
  getCart(): CartItem[] {
    return [...this.cart]
  }

  // Get cart count
  getCartCount(): number {
    return this.cart.length
  }

  // Clear entire cart
  clearCart(): void {
    this.cart = []
    this.saveCart()
  }

  // Remove unavailable items from cart
  cleanupUnavailableItems(): void {
    const initialLength = this.cart.length
    this.cart = this.cart.filter(item => item.isAvailable !== false)
    
    if (this.cart.length !== initialLength) {
      this.saveCart()
    }
  }

  // Calculate cart totals
  async calculateTotals(): Promise<CartTotals> {
    const availableItems = this.cart.filter(item => item.isAvailable !== false)
    
    let totalWAXValue = 0
    
    // Sum up all WAX prices
    availableItems.forEach(item => {
      if (item.nft.price) {
        const waxAmount = parseFloat(item.nft.price.replace(' WAX', ''))
        if (!isNaN(waxAmount)) {
          totalWAXValue += waxAmount
        }
      }
    })

    const totalWAX = `${totalWAXValue.toFixed(4)} WAX`
    const totalUSD = await priceService.convertWaxToUSDAsync(totalWAX)

    return {
      totalWAX,
      totalUSD,
      itemCount: availableItems.length
    }
  }

  // Check availability of cart items using AtomicAssets API with batching
  async checkAvailability(): Promise<void> {
    if (this.cart.length === 0) return
    
    const { apiService } = await import('./api')
    
    try {
      // Batch check all sales at once instead of individual requests
      const saleIds = this.cart.map(item => item.sale_id).filter(Boolean)
      if (saleIds.length === 0) return
      
      const url = `${apiService.atomicMarketAPI}/atomicmarket/v1/sales?ids=${saleIds.join(',')}&state=1&limit=${saleIds.length}`
      const response = await apiService.fetchWithRetry(url)
      const data = await response.json()
      
      // Create a set of active sale IDs for quick lookup
      const activeSaleIds = new Set(
        data.success && data.data ? data.data.map((sale: SaleData) => sale.sale_id) : []
      )
      
      // Update availability for all items
      for (const item of this.cart) {
        item.isAvailable = item.sale_id ? activeSaleIds.has(item.sale_id) : false
      }
      
    } catch (error) {
      ErrorMessageService.logError('CartService.checkAvailability', error, { cartSize: this.cart.length })
      // If batch check fails, mark all items as potentially unavailable
      for (const item of this.cart) {
        item.isAvailable = false
      }
    }
    
    this.saveCart()
  }

  // Toggle item selection (for multi-select functionality)
  toggleSelection(assetId: string): void {
    const item = this.cart.find(item => item.nft.asset_id === assetId)
    if (item) {
      item.selected = !item.selected
      this.saveCart()
    }
  }

  // Select all available items
  selectAll(): void {
    this.cart.forEach(item => {
      if (item.isAvailable !== false) {
        item.selected = true
      }
    })
    this.saveCart()
  }

  // Deselect all items
  deselectAll(): void {
    this.cart.forEach(item => {
      item.selected = false
    })
    this.saveCart()
  }

  // Get selected items
  getSelectedItems(): CartItem[] {
    return this.cart.filter(item => item.selected === true)
  }

  // Get selected item count
  getSelectedCount(): number {
    return this.cart.filter(item => item.selected === true).length
  }

  // Remove selected items from cart
  removeSelectedItems(): void {
    this.cart = this.cart.filter(item => item.selected !== true)
    this.saveCart()
  }

  // Check if item is selected
  isSelected(assetId: string): boolean {
    const item = this.cart.find(item => item.nft.asset_id === assetId)
    return item?.selected === true
  }
}

// Export singleton instance
export const cartService = new CartService()