import React, { useState } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import { log } from '../services/loggingService'
import { WharfKitService } from '../services/wharfkit'
import '../css/modal.css'
import '../css/token-balances.css'

interface TokenBalance {
  contract: string
  symbol: string
  balance: string
  value: string
}

interface TokenTransferModalProps {
  token: TokenBalance | null
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
}

const TokenTransferModal: React.FC<TokenTransferModalProps> = ({ token, isOpen, onClose, onSuccess }) => {
  const [recipient, setRecipient] = useState('')
  const [amount, setAmount] = useState('')
  const [memo, setMemo] = useState('')
  const [isTransferring, setIsTransferring] = useState(false)
  const [recipients, setRecipients] = useState<string[]>([])
  const [isBulkTransfer, setIsBulkTransfer] = useState(false)
  const { session, transferTokens } = useWallet()
  const { showSuccess, showError } = useNotifications()

  const validateWaxAccount = (account: string): boolean => {
    // WAX account names can be 1-12 characters, lowercase letters, numbers 1-5, and dots
    const waxAccountRegex = /^[a-z1-5.]{1,12}$/
    return waxAccountRegex.test(account) && account.length >= 1 && account.length <= 12
  }

  const validateAmount = (value: string): boolean => {
    const numValue = parseFloat(value)
    const maxBalance = parseFloat(token?.balance || '0')
    return numValue > 0 && numValue <= maxBalance
  }

  const parseRecipients = (recipientString: string): string[] => {
    return recipientString
      .split(',')
      .map(r => r.trim().toLowerCase())
      .filter(r => r.length > 0)
  }

  const handleRecipientChange = (value: string) => {
    setRecipient(value.toLowerCase())
    const parsedRecipients = parseRecipients(value)
    setRecipients(parsedRecipients)
    setIsBulkTransfer(parsedRecipients.length > 1)
  }

  const handleMaxClick = () => {
    if (token) {
      // Extract just the numeric part from the balance string
      const balanceAmount = token.balance.split(' ')[0]
      setAmount(balanceAmount)
    }
  }

  const getAmountPerRecipient = (): number => {
    if (!amount || recipients.length === 0) return 0
    return parseFloat(amount) / recipients.length
  }

  const getTokenPrecision = (balance: string): number => {
    // Extract precision from the balance string (e.g., "100.12345678 TOKEN" -> 8)
    const parts = balance.split(' ')
    if (parts.length > 0) {
      const decimalPart = parts[0].split('.')[1]
      return decimalPart ? decimalPart.length : 0
    }
    return 8 // Default to 8 decimals
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!token || !session || !recipient.trim() || !amount.trim()) {
      showError('Validation Error', 'Please fill in all required fields')
      return
    }

    // Validate all recipients
    const invalidRecipients = recipients.filter(r => !validateWaxAccount(r))
    if (invalidRecipients.length > 0) {
      showError(
        'Invalid Account Names', 
        `Invalid WAX account names: ${invalidRecipients.join(', ')}. Account names must be 12 characters, lowercase letters and numbers 1-5.`
      )
      return
    }

    if (!validateAmount(amount)) {
      showError(
        'Invalid Amount',
        'Please enter a valid amount that does not exceed your balance'
      )
      return
    }

    setIsTransferring(true)

    try {
      const precision = getTokenPrecision(token.balance)
      const actions = []

      if (isBulkTransfer && recipients.length > 1) {
        // Bulk transfer - divide amount equally among recipients
        const amountPerRecipient = getAmountPerRecipient()
        const formattedAmountPerRecipient = amountPerRecipient.toFixed(precision) + ' ' + token.symbol
        
        for (const recipientAccount of recipients) {
          actions.push({
            account: token.contract,
            name: 'transfer',
            authorization: [{
              actor: session.actor,
              permission: session.permission
            }],
            data: {
              from: WharfKitService.safeGetActor(session),
              to: recipientAccount,
              quantity: formattedAmountPerRecipient,
              memo: memo.trim() || 'Bulk transfer via PxMarket - market.pixeljourney.xyz'
            }
          })
        }
      } else {
        // Single transfer
        const formattedAmount = parseFloat(amount).toFixed(precision) + ' ' + token.symbol
        
        actions.push({
          account: token.contract,
          name: 'transfer',
          authorization: [{
            actor: WharfKitService.safeGetActor(session),
            permission: WharfKitService.safeGetPermission(session)
          }],
          data: {
            from: WharfKitService.safeGetActor(session),
            to: recipients[0] || recipient.trim(),
            quantity: formattedAmount,
            memo: memo.trim() || 'Transferred via PxMarket - market.pixeljourney.xyz'
          }
        })
      }

      if (isBulkTransfer) {
        // Handle bulk transfers - execute each transfer individually
        let successCount = 0
        for (const recipientAccount of recipients) {
          try {
            if (!transferTokens) {
              log.error('transferTokens service not available', 'TokenTransferModal')
              continue
            }
            const result = await transferTokens(
              recipientAccount,
              getAmountPerRecipient().toString(),
              token.symbol,
              memo.trim() || 'Bulk transfer via PxMarket - market.pixeljourney.xyz'
            )
            if (result) successCount++
          } catch (error) {
            log.error(`Transfer to ${recipientAccount} failed`, 'TokenTransferModal', error as Error)
          }
        }
        
        if (successCount > 0) {
          const successMessage = successCount === recipients.length
            ? `${amount} ${token.symbol} transferred to all ${recipients.length} recipients successfully!`
            : `${amount} ${token.symbol} transferred to ${successCount}/${recipients.length} recipients successfully!`
          showSuccess('Bulk Transfer Complete', successMessage)
        }
      } else {
        // Single transfer using centralized service
        if (!transferTokens) {
          showError('Transfer Error', 'Transfer service not available')
          return
        }
        const result = await transferTokens(
          recipients[0] || recipient.trim(),
          amount,
          token.symbol,
          memo.trim() || 'Transferred via PxMarket - market.pixeljourney.xyz'
        )
        
        if (result) {
          showSuccess('Transfer Successful', `${amount} ${token.symbol} transferred successfully!`)
        }
      }
      
      // Reset form and close modal on success
      onSuccess?.()
      onClose()
      setRecipient('')
      setAmount('')
      setMemo('')
      setRecipients([])
      setIsBulkTransfer(false)
    } catch (error) {
      log.error('Token transfer failed', 'TokenTransferModal', error as Error)
      showError(
        'Transfer Failed',
        error instanceof Error ? error.message : 'Unknown error occurred'
      )
    } finally {
      setIsTransferring(false)
    }
  }

  const handleClose = () => {
    if (!isTransferring) {
      setRecipient('')
      setAmount('')
      setMemo('')
      setRecipients([])
      setIsBulkTransfer(false)
      onClose()
    }
  }

  if (!isOpen || !token) return null

  return (
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content token-transfer-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Transfer {token.symbol}</h2>
          <button 
            className="modal-close" 
            onClick={handleClose}
            disabled={isTransferring}
          >
            ×
          </button>
        </div>

        <div className="modal-body">
          <div className="token-preview">
            <div className="token-info">
              <div className="token-icon">🪙</div>
              <div className="token-details">
                <h3>{token.symbol}</h3>
                <p className="token-contract">{token.contract}</p>
                <p className="token-balance">Available: {token.balance} {token.symbol}</p>
                {token.value !== 'N/A' && token.value !== '0.00' && (
                  <p className="token-usd-value">≈ ${token.value} USD</p>
                )}
              </div>
            </div>
          </div>

          <form onSubmit={handleSubmit} className="modal-form token-transfer-form">
            <div className="form-group">
              <label htmlFor="recipient">
                {isBulkTransfer ? 'Recipients (comma-separated) *' : 'Recipient WAX Account *'}
              </label>
              <input
                type="text"
                id="recipient"
                value={recipient}
                onChange={(e) => handleRecipientChange(e.target.value)}
                placeholder={isBulkTransfer ? "e.g. pixeljourney,market.pxj,council.pxj" : "e.g. pixeljourney"}
                required
                disabled={isTransferring}
                className="recipient-input"
              />
              <small className="input-help">
                {isBulkTransfer 
                  ? `Enter WAX account names separated by commas (${recipients.length} recipients detected)`
                  : 'Enter a valid WAX account name (12 characters, lowercase letters and numbers 1-5)'
                }
              </small>
              {isBulkTransfer && recipients.length > 1 && (
                <div className="recipients-preview">
                  <strong>Recipients:</strong>
                  <ul>
                    {recipients.map((rec, index) => (
                      <li key={index} className={validateWaxAccount(rec) ? 'valid' : 'invalid'}>
                        {rec} {!validateWaxAccount(rec) && '❌'}
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>

            <div className="form-group">
              <label htmlFor="amount">Amount *</label>
              <div className="amount-input-wrapper">
                <input
                  type="number"
                  id="amount"
                  value={amount}
                  onChange={(e) => setAmount(e.target.value)}
                  placeholder="0.00000000"
                  step="0.00000001"
                  min="0"
                  max={token.balance.split(' ')[0]}
                  required
                  disabled={isTransferring}
                  className="amount-input"
                />
                <button
                  type="button"
                  className="max-btn"
                  onClick={handleMaxClick}
                  disabled={isTransferring}
                >
                  MAX
                </button>
              </div>
              {isBulkTransfer && recipients.length > 1 && amount && (
                <small className="distribution-info">
                  Each recipient will receive: {getAmountPerRecipient().toFixed(getTokenPrecision(token.balance))} {token.symbol}
                </small>
              )}
            </div>

            <div className="form-group">
              <label htmlFor="memo">Memo (Optional)</label>
              <input
                type="text"
                id="memo"
                value={memo}
                onChange={(e) => setMemo(e.target.value)}
                placeholder="Optional transfer message"
                maxLength={256}
                disabled={isTransferring}
                className="memo-input"
              />
            </div>

            <div className="transfer-warning">
              <div className="warning-icon">⚠️</div>
              <div className="warning-text">
                <strong>Warning:</strong> This action cannot be undone. Make sure you trust the recipient and have entered the correct account name and amount.
              </div>
            </div>

            <div className="modal-actions">
              <button 
                type="button" 
                className="btn-secondary"
                onClick={handleClose}
                disabled={isTransferring}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn-primary transfer-btn"
                disabled={
                  isTransferring || 
                  !recipient.trim() || 
                  !amount.trim() || 
                  !validateWaxAccount(recipient.trim()) ||
                  !validateAmount(amount)
                }
              >
                {isTransferring ? (
                  <>
                    <div className="spinner"></div>
                    {isBulkTransfer ? 'Bulk Transferring...' : 'Transferring...'}
                  </>
                ) : (
                  isBulkTransfer ? `Bulk Transfer ${token.symbol}` : `Transfer ${token.symbol}`
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default TokenTransferModal