import React, { useState, useEffect, useCallback } from 'react'
import { useParams, Link } from 'react-router-dom'
import { apiService } from '../services/api'
import type { NFTAsset } from '../services/api'
import { getAssetImageUrl, getAssetVideoUrl, createIPFSUrl } from '../utils/mediaUtils'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'

import { log } from '../services/loggingService'
import '../css/asset-detail.css'

interface SchemaFormat {
  name: string
  type: string
}



interface BackedToken {
  token_contract: string
  token_symbol: string
  token_precision: number
  amount: string
}

interface Asset {
  asset_id: string
  owner?: string
  name?: string
  schema: {
    schema_name: string
    format?: SchemaFormat[]
  }
  collection: {
    collection_name: string
    name?: string
    img?: string
  }
  template: {
    template_id: string
    max_supply: string
    issued_supply: string
    is_transferable: boolean
    is_burnable: boolean
    immutable_data: Record<string, unknown>
    created_at_time: string
    created_at_block: string
  }
  backed_tokens: BackedToken[]
  burned_by_account?: string
  burned_at_block?: string
  burned_at_time?: string
  updated_at_block: string
  updated_at_time: string
  transferred_at_block: string
  transferred_at_time: string
  minted_at_block: string
  minted_at_time: string
  is_transferable: boolean
  is_burnable: boolean
  data: Record<string, unknown>
}

const AssetDetail: React.FC = () => {
  const { assetId } = useParams<{ assetId: string }>()
  const [asset, setAsset] = useState<Asset | null>(null)
  const [loading, setLoading] = useState(false)
  const [listing, setListing] = useState<NFTAsset | null>(null)
  const [listingLoading, setListingLoading] = useState(false)
  const [usdPrice, setUsdPrice] = useState<string>('')
  
  const { isConnected, purchaseNFT, login } = useWallet()
  const { showSuccess, showError, showWarning } = useNotifications()
  // Transaction tracking is now handled directly by WharfKit


  const loadAssetData = useCallback(async () => {
    if (!assetId) return
    
    setLoading(true)
    try {
      const assetData = await apiService.getAssetDetails(assetId)
      setAsset(assetData as unknown as Asset)
    } catch (error) {
      log.error('Failed to load asset data', 'AssetDetail.loadAssetData', error as Error, { assetId })
    } finally {
      setLoading(false)
    }
  }, [assetId])

  const checkListingStatus = useCallback(async () => {
    if (!assetId) return
    
    setListingLoading(true)
    try {
      // Search for the asset in marketplace listings
      const marketplaceData = await apiService.getMarketplaceNFTs({
        page: 1,
        limit: 1,
        asset_id: assetId
      })
      
      if (marketplaceData.data && marketplaceData.data.length > 0) {
        const saleData = marketplaceData.data[0]
        // Convert simplified marketplace data to NFTAsset format
        const nftAsset: NFTAsset = {
          sale_id: saleData.sale_id,
          asset_id: saleData.asset_id,
          name: asset?.name || `Asset #${saleData.asset_id}`,
          image: String(asset?.data?.img || asset?.template?.immutable_data?.img || ''),
          price: saleData.price,
          seller: saleData.seller,
          collection: asset?.collection?.collection_name || 'Unknown',
          schema: asset?.schema?.schema_name || 'Unknown',
          template_mint: undefined,
          max_supply: asset?.template?.max_supply || undefined,
          traits: asset?.data ? Object.fromEntries(
            Object.entries(asset.data).filter(([, value]) => value !== undefined && value !== null)
          ) as Record<string, string | number | boolean> : {}
        }
        setListing(nftAsset)
        
        // Get USD price if available
        if (nftAsset.price) {
          try {
            const priceValue = parseFloat(nftAsset.price.replace(' WAX', ''))
            const waxPriceResponse = await fetch('https://api.coingecko.com/api/v3/simple/price?ids=wax&vs_currencies=usd')
            const waxPriceData = await waxPriceResponse.json()
            const waxToUsd = waxPriceData.wax?.usd || 0
            const usdValue = (priceValue * waxToUsd).toFixed(2)
            setUsdPrice(`$${usdValue} USD`)
          } catch (error) {
            log.warn('Failed to fetch USD price for asset', 'AssetDetail.checkListingStatus', { assetId, error: (error as Error).message })
          }
        }
      } else {
        setListing(null)
        setUsdPrice('')
      }
    } catch (error) {
      log.error('Failed to check listing status', 'AssetDetail.checkListingStatus', error as Error, { assetId })
      setListing(null)
    } finally {
      setListingLoading(false)
    }
  }, [assetId])

  useEffect(() => {
    if (assetId) {
      loadAssetData()
      checkListingStatus()
    }
  }, [assetId, loadAssetData, checkListingStatus])

  const handlePurchase = async () => {
    if (!listing) return
    
    try {
      // Check if user is logged in
      if (!isConnected) {
        showWarning('Please connect your wallet to purchase this NFT')
        try {
          await login()
        } catch (error) {
          log.error('Wallet login failed during purchase', 'AssetDetail.handlePurchase', error as Error, { assetId, saleId: listing.sale_id })
          return
        }
      }

      // Validate listing data
      if (!listing.sale_id || !listing.price) {
        showError('This NFT is not available for purchase')
        return
      }

      // Extract price value (remove ' WAX' suffix if present)
      const priceValue = listing.price.replace(' WAX', '')
      
      log.info('Initiating NFT purchase', 'AssetDetail.handlePurchase', { saleId: listing.sale_id, price: priceValue, assetId })
      
      // Execute purchase transaction directly with WharfKit
      await purchaseNFT(listing.sale_id, priceValue)
      
      showSuccess(`Successfully purchased ${listing.name || 'NFT'}!`)
      
      // Refresh listing status
      checkListingStatus()
      
    } catch (error) {
      log.error('NFT purchase failed', 'AssetDetail.handlePurchase', error as Error, { assetId, saleId: listing?.sale_id, price: listing?.price })
      const errorMessage = error instanceof Error ? error.message : 'Unknown error occurred'
      showError(`Purchase failed: ${errorMessage}`)
    }
  }

  const getImageUrl = (imgPath: string): string => {
    if (!imgPath) return 'https://pixeljourney.xyz/img/toppixals/10.webp'
    if (imgPath.startsWith('http')) return imgPath
    return createIPFSUrl(imgPath)
  }

  const getAssetImage = (): string => {
    if (!asset) return 'https://pixeljourney.xyz/img/toppixals/10.webp'
    
    return getAssetImageUrl(asset) ?? 'https://pixeljourney.xyz/img/toppixals/10.webp'
  }

  const getAssetVideo = (): string | null => {
    if (!asset) return null
    
    return getAssetVideoUrl(asset)
  }

  const formatDate = (timestamp: string): string => {
    return new Date(parseInt(timestamp) * 1000).toLocaleString()
  }

  const renderAttributeValue = (value: string | number | boolean | null | undefined): React.ReactNode => {
    if (value === null || value === undefined) {
      return <span className="null-value">null</span>
    }
    
    if (typeof value === 'boolean') {
      return <span className="boolean-value">{value.toString()}</span>
    }
    
    if (typeof value === 'number') {
      return <span className="number-value">{value.toLocaleString()}</span>
    }
    
    if (typeof value === 'object') {
      return (
        <pre className="object-value">
          {JSON.stringify(value, null, 2)}
        </pre>
      )
    }
    
    // Check if it's an IPFS hash or URL for images
    const strValue = String(value)
    if (strValue.match(/\.(jpg|jpeg|png|gif|webp)$/i) || strValue.startsWith('Qm')) {
      return (
        <div className="image-attribute">
          <img 
            src={getImageUrl(strValue)} 
            alt="Attribute image"
            className="attribute-image"
            onError={(e) => {
              e.currentTarget.style.display = 'none'
            }}
          />
          <span className="image-hash">{strValue}</span>
        </div>
      )
    }
    
    return <span className="string-value">{strValue}</span>
  }

  const getAllAttributes = (): Record<string, string | number | boolean | null | undefined> => {
    if (!asset) return {}
    
    return {
       ...asset.template?.immutable_data,
       ...asset.data
     } as Record<string, string | number | boolean | null | undefined>
  }

  if (loading) {
    return (
      <div className="asset-detail-container">
        <div className="loading-state">
          <div className="spinner"></div>
          <p>Loading asset details...</p>
        </div>
      </div>
    )
  }

  if (!asset) {
    return (
      <div className="asset-detail-container">
        <div className="error-state">
          <h2>Asset Not Found</h2>
          <p>The asset "{assetId}" could not be found.</p>
          <Link to="/collections" className="back-link">← Back to Collections</Link>
        </div>
      </div>
    )
  }

  const videoUrl = getAssetVideo()
  const allAttributes = getAllAttributes()

  return (
    <div className="asset-detail-container">
      <div className="asset-content">
        {/* Navigation */}
        <div className="breadcrumb">
          <Link to="/" className="breadcrumb-link">Home</Link>
          <span className="breadcrumb-separator">›</span>
          <Link to="/collections" className="breadcrumb-link">Collections</Link>
          <span className="breadcrumb-separator">›</span>
          <Link to={`/collections/${asset.collection.collection_name}`} className="breadcrumb-link">
            {asset.collection.name || asset.collection.collection_name}
          </Link>
          <span className="breadcrumb-separator">›</span>
          <span className="breadcrumb-current">{asset.name || `Asset #${assetId}`}</span>
        </div>

      {/* Asset Header */}
      <div className="asset-header">
        <div className="asset-media-container">
          {videoUrl ? (
            <video 
              src={videoUrl}
              className="asset-video"
              controls
              poster={getAssetImage()}
            >
              Your browser does not support the video tag.
            </video>
          ) : (
            <img 
              src={getAssetImage()} 
              alt={asset.name || `Asset #${assetId}`}
              className="asset-image"
              onError={() => {/* Image error handling removed */}}
            />
          )}
        </div>
        
        <div className="asset-info">
          <h1>{asset.name || `Asset #${assetId}`}</h1>
          <p className="asset-id">Asset ID: {asset.asset_id}</p>
          
          <div className="asset-links">
            <p className="asset-collection">
              Collection: 
              <Link to={`/collections/${asset.collection.collection_name}`}>
                {asset.collection.name || asset.collection.collection_name}
              </Link>
            </p>
            <p className="asset-template">
              Template: 
              <Link to={`/collections/${asset.collection.collection_name}/templates/${asset.template.template_id}`}>
                {(asset.template?.immutable_data?.name as string) || `#${asset.template.template_id}`}
              </Link>
            </p>
            <p className="asset-schema">Schema: {asset.schema.schema_name}</p>
          </div>
          
          <div className="asset-ownership">
            <div className="owner-info">
              <span className="owner-label">Owner:</span>
              <span className="owner-address">{asset.owner}</span>
            </div>
            <div className="author-info">
              <span className="author-label">Collection Author:</span>
              <span className="author-address">{asset.owner || 'Unknown'}</span>
            </div>
          </div>
          
          {/* Listing Status */}
          {listingLoading ? (
            <div className="listing-status loading">
              <span>Checking listing status...</span>
            </div>
          ) : listing ? (
            <div className="listing-status available">
              <div className="listing-info">
                <h3>Available for Sale</h3>
                <div className="price-display">
                  <span className="price-wax">{listing.price}</span>
                  {usdPrice && <span className="price-usd">{usdPrice}</span>}
                </div>
              </div>
              <button 
                className="buy-now-btn"
                onClick={handlePurchase}
                disabled={!isConnected}
              >
                {isConnected ? 'Buy Now' : 'Connect Wallet to Buy'}
              </button>
            </div>
          ) : (
            <div className="listing-status not-listed">
              <span>Not currently listed for sale</span>
            </div>
          )}
          
          <div className="asset-timestamps">
            <div className="timestamp">
              <span className="timestamp-label">Minted:</span>
              <span className="timestamp-value">{formatDate(asset.minted_at_time)}</span>
            </div>
            <div className="timestamp">
              <span className="timestamp-label">Last Transfer:</span>
              <span className="timestamp-value">{formatDate(asset.transferred_at_time)}</span>
            </div>
            <div className="timestamp">
              <span className="timestamp-label">Last Update:</span>
              <span className="timestamp-value">{formatDate(asset.updated_at_time)}</span>
            </div>
          </div>
          
          {asset.burned_by_account && (
            <div className="burn-info">
              <span className="burn-label">🔥 Burned by:</span>
              <span className="burn-account">{asset.burned_by_account}</span>
              <span className="burn-time">{formatDate(asset.burned_at_time!)}</span>
            </div>
          )}
        </div>
      </div>

      {/* Template Info */}
      <div className="template-info-section">
        <h2>Template Information</h2>
        <div className="template-stats">
          <div className="template-stat">
            <span className="stat-label">Template ID:</span>
            <span className="stat-value">{asset.template.template_id}</span>
          </div>
          <div className="template-stat">
            <span className="stat-label">Max Supply:</span>
            <span className="stat-value">
              {asset.template.max_supply === '0' ? 'Unlimited' : asset.template.max_supply}
            </span>
          </div>
          <div className="template-stat">
            <span className="stat-label">Issued Supply:</span>
            <span className="stat-value">{asset.template.issued_supply}</span>
          </div>
        </div>
      </div>

      {/* Asset Attributes */}
      {Object.keys(allAttributes).length > 0 && (
        <div className="asset-attributes">
          <h2>Attributes</h2>
          <div className="attributes-grid">
            {Object.entries(allAttributes).map(([key, value]) => (
              <div key={key} className="attribute-item">
                <span className="attribute-name">{key}</span>
                <div className="attribute-value">
                  {renderAttributeValue(value)}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Schema Format */}
      <div className="schema-format">
        <h2>Schema Format</h2>
        <div className="format-grid">
          {asset.schema.format?.map((field, index) => (
            <div key={index} className="format-item">
              <span className="format-name">{field.name}</span>
              <span className="format-type">{field.type}</span>
            </div>
          ))}
        </div>
      </div>

      {/* Backed Tokens */}
      {asset.backed_tokens && asset.backed_tokens.length > 0 && (
        <div className="backed-tokens">
          <h2>Backed Tokens</h2>
          <div className="tokens-list">
            {asset.backed_tokens.map((token, index) => (
              <div key={index} className="token-item">
                <pre>{JSON.stringify(token, null, 2)}</pre>
              </div>
            ))}
          </div>
        </div>
      )}
      </div>
    </div>
  )
}

export default AssetDetail