import React, { useState } from 'react'
import { useWallet } from '../hooks/useWallet'
import type { InventoryNFT } from '../types/inventory'
import { useNotifications } from '../hooks/useNotifications'
import { log } from '../services/loggingService'
import '../css/modal.css'

interface TransferModalProps {
  nft: InventoryNFT | null
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
}

const TransferModal: React.FC<TransferModalProps> = ({ nft, isOpen, onClose, onSuccess }) => {
  const [recipient, setRecipient] = useState('')
  const [memo, setMemo] = useState('')
  const [isTransferring, setIsTransferring] = useState(false)
  const { session, transferNFT } = useWallet()
  const { showSuccess, showError } = useNotifications()

  const validateWaxAccount = (account: string): boolean => {
    // WAX account names must be 12 characters, lowercase letters and numbers 1-5
    const waxAccountRegex = /^[a-z1-5]{12}$/
    return waxAccountRegex.test(account)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!nft || !session || !recipient.trim()) {
      showError('Validation Error', 'Please fill in all required fields')
      return
    }

    if (!validateWaxAccount(recipient.trim())) {
      showError(
        'Invalid Account Name', 
        'Please enter a valid WAX account name (12 characters, lowercase letters and numbers 1-5)'
      )
      return
    }

    setIsTransferring(true)

    try {
      if (!transferNFT) {
        throw new Error('NFT transfer service is not available')
      }
      // Use centralized NFT transfer service
      const result = await transferNFT(
        nft.asset_id,
        recipient.trim()
      )
      
      if (result) {
        showSuccess('Transfer Successful', 'NFT transferred successfully!')
        onSuccess?.()
        onClose()
        setRecipient('')
        setMemo('')
      }
    } catch (error) {
      log.error('Transfer failed', 'TransferModal', error as Error)
      showError(
        'Transfer Failed',
        error instanceof Error ? error.message : 'Unknown error occurred'
      )
    } finally {
      setIsTransferring(false)
    }
  }

  const handleClose = () => {
    if (!isTransferring) {
      setRecipient('')
      setMemo('')
      onClose()
    }
  }

  if (!isOpen || !nft) return null

  return (
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content transfer-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Transfer NFT</h2>
          <button 
            className="modal-close" 
            onClick={handleClose}
            disabled={isTransferring}
          >
            ×
          </button>
        </div>

        <div className="modal-body">
          <div className="nft-preview">
            <div className="nft-image">
              {nft.video ? (
                <video 
                  src={nft.video.startsWith('http') ? nft.video : `https://ipfs.io/ipfs/${nft.video}`}
                  muted 
                  loop
                  autoPlay
                />
              ) : nft.img ? (
                <img 
                  src={nft.img.startsWith('http') ? nft.img : `https://ipfs.io/ipfs/${nft.img}`}
                  alt={nft.name}
                />
              ) : (
                <div className="no-image">No Image</div>
              )}
            </div>
            <div className="nft-info">
              <h3>{nft.name}</h3>
              <p className="nft-collection">{typeof nft.collection === 'string' ? nft.collection : nft.collection?.name || nft.collection?.collection_name || 'Unknown'}</p>
              <p className="nft-schema">{nft.schema.schema_name}</p>
              {nft.mint_number && (
                <p className="nft-mint">Mint #{nft.mint_number}</p>
              )}
            </div>
          </div>

          <form onSubmit={handleSubmit} className="modal-form transfer-form">
            <div className="form-group">
              <label htmlFor="recipient">Recipient WAX Account *</label>
              <input
                type="text"
                id="recipient"
                value={recipient}
                onChange={(e) => setRecipient(e.target.value.toLowerCase())}
                placeholder="e.g. pixeljourney"
                maxLength={12}
                required
                disabled={isTransferring}
                className="recipient-input"
              />
              <small className="input-help">
                Enter a valid WAX account name (12 characters, lowercase letters and numbers 1-5)
              </small>
            </div>

            <div className="form-group">
              <label htmlFor="memo">Memo (Optional)</label>
              <input
                type="text"
                id="memo"
                value={memo}
                onChange={(e) => setMemo(e.target.value)}
                placeholder="Optional transfer message"
                maxLength={256}
                disabled={isTransferring}
                className="memo-input"
              />
            </div>

            <div className="transfer-warning">
              <div className="warning-icon">⚠️</div>
              <div className="warning-text">
                <strong>Warning:</strong> This action cannot be undone. Make sure you trust the recipient and have entered the correct account name.
              </div>
            </div>

            <div className="modal-actions">
              <button 
                type="button" 
                className="btn-secondary"
                onClick={handleClose}
                disabled={isTransferring}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn-primary transfer-btn"
                disabled={isTransferring || !recipient.trim() || !validateWaxAccount(recipient.trim())}
              >
                {isTransferring ? (
                  <>
                    <div className="spinner"></div>
                    Transferring...
                  </>
                ) : (
                  'Transfer NFT'
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default TransferModal