import React, { useState, useEffect, useRef } from 'react'
import { useWallet } from '../contexts/WalletContext'
import { apiService } from '../services/api'
import { alcorService } from '../services/alcorService'
import { log } from '../services/loggingService'
import '../css/token-dropdown.css'

export interface TokenOption {
  symbol: string
  contract: string
  amount: string
  decimals: number
  icon?: string
  logo?: string
  priority?: number
}

interface TokenDropdownProps {
  selectedToken: string
  onTokenSelect: (token: string) => void
  availableTokens?: string[]
  className?: string
  filterByAvailable?: boolean // If true, only show tokens from availableTokens
}

const TokenDropdown: React.FC<TokenDropdownProps> = ({
  selectedToken,
  onTokenSelect,
  availableTokens = ['WAX', 'WPIXAL'],
  className = '',
  filterByAvailable = false
}) => {
  const { account } = useWallet()
  const [isOpen, setIsOpen] = useState(false)
  const [tokens, setTokens] = useState<TokenOption[]>([])
  const [alcorTokens, setAlcorTokens] = useState<any[]>([])
  const [loading, setLoading] = useState(false)
  const dropdownRef = useRef<HTMLDivElement>(null)

  // Load available tokens from Alcor
  const loadAlcorTokens = async () => {
    try {
      const tokens = await alcorService.getAllTokens()
      setAlcorTokens(tokens)
      return tokens
    } catch (error) {
      log.error('Failed to load Alcor tokens', 'TokenDropdown.loadAlcorTokens', error as Error)
      // Fallback to common token configurations
      const fallbackTokens = [
        { symbol: 'WAX', contract: 'eosio.token', precision: 8, logo: null },
        { symbol: 'WPIXAL', contract: 'pixeljourney', precision: 8, logo: null },
        { symbol: 'PIXAL', contract: 'pixeljourney', precision: 8, logo: null },
        { symbol: 'LSWAX', contract: 'token.defi', precision: 8, logo: null },
        { symbol: 'BRWL', contract: 'brawlertoken', precision: 4, logo: null },
        { symbol: 'TLM', contract: 'alien.worlds', precision: 4, logo: null },
        { symbol: 'WOMBAT', contract: 'wombattoken', precision: 4, logo: null }
      ]
      setAlcorTokens(fallbackTokens)
      return fallbackTokens
    }
  }

  // Favored tokens that should appear first and have priority
  const favoredTokenConfigs: Record<string, { contract: string; decimals: number; icon: string; priority: number }> = {
    'PXJ': { contract: 'pixeljourney', decimals: 8, icon: '🎯', priority: 1 },
    'WPIXAL': { contract: 'pixeljourney', decimals: 8, icon: '🎮', priority: 2 },
    'WAX': { contract: 'eosio.token', decimals: 8, icon: '🪙', priority: 3 },
    'KING': { contract: 'alpha.waxfun', decimals: 8, icon: '👑', priority: 4 },
    'WAXUSDC': { contract: 'eth.token', decimals: 8, icon: '💵', priority: 5 },
    'WAXWBTC': { contract: 'eth.token', decimals: 8, icon: '₿', priority: 6 },
    'WUF': { contract: 'wuffi', decimals: 4, icon: '🐕', priority: 7 },
    'LSWAX': { contract: 'token.defi', decimals: 8, icon: '💎', priority: 8 },
    'BRWL': { contract: 'brawlertoken', decimals: 4, icon: '⚔️', priority: 9 },
    'TLM': { contract: 'alien.worlds', decimals: 4, icon: '👽', priority: 10 },
    'WOMBAT': { contract: 'wombattoken', decimals: 4, icon: '🐨', priority: 11 }
  }

  // Get token config from favored tokens first, then Alcor tokens
  const getTokenConfig = (symbol: string) => {
    // Check favored tokens first
    const favoredToken = favoredTokenConfigs[symbol]
    if (favoredToken) {
      return {
        contract: favoredToken.contract,
        decimals: favoredToken.decimals,
        icon: favoredToken.icon,
        logo: null,
        priority: favoredToken.priority
      }
    }
    
    // Then check Alcor tokens
    const alcorToken = alcorTokens.find(t => t.symbol === symbol)
    if (alcorToken) {
      return {
        contract: alcorToken.contract,
        decimals: alcorToken.precision,
        icon: getTokenIcon(symbol),
        logo: alcorToken.logo,
        priority: 999 // Lower priority for non-favored tokens
      }
    }
    
    // Final fallback
    return { contract: 'eosio.token', decimals: 8, icon: '🪙', logo: null, priority: 1000 }
  }

  // Get token icon (emoji fallback)
  const getTokenIcon = (symbol: string): string => {
    const iconMap: Record<string, string> = {
      'WAX': '🪙',
      'WPIXAL': '🎮',
      'PIXAL': '🎯',
      'LSWAX': '💎',
      'BRWL': '⚔️',
      'TLM': '👽',
      'WOMBAT': '🐨'
    }
    return iconMap[symbol] || '🪙'
  }

  // Load token balances from Light API
  const loadTokenBalances = async () => {
    // Ensure Alcor tokens are loaded first
    if (alcorTokens.length === 0) {
      await loadAlcorTokens()
    }

    if (!account) {
      // Set default tokens with 0 balance when not connected
      const tokensToShow = filterByAvailable ? availableTokens : alcorTokens.map(t => t.symbol)
      const defaultTokens = tokensToShow.map(symbol => {
        const config = getTokenConfig(symbol)
        return {
          symbol,
          contract: config.contract,
          amount: '0.0000',
          decimals: config.decimals,
          icon: config.icon,
          logo: config.logo
        }
      })
      setTokens(defaultTokens)
      return
    }

    setLoading(true)
    try {
      const lightApiResponse = await apiService.getLightApiBalances(account)
      
      if (lightApiResponse && lightApiResponse.balances) {
        const tokenOptions: TokenOption[] = []
        
        // Determine which tokens to process
        const tokensToProcess = filterByAvailable ? availableTokens : alcorTokens.map(t => t.symbol)
        
        // Process tokens and match with Light API data
        for (const symbol of tokensToProcess) {
          const config = getTokenConfig(symbol)
          if (!config) continue
          
          const balance = lightApiResponse.balances.find(
            b => b.currency === symbol && b.contract === config.contract
          )
          
          tokenOptions.push({
            symbol,
            contract: config.contract,
            amount: balance ? balance.amount : '0.0000',
            decimals: balance ? parseInt(balance.decimals) : config.decimals,
            icon: config.icon,
            logo: config.logo,
            priority: config.priority || 999
          })
        }
        
        // Sort tokens by priority (lower numbers first)
        tokenOptions.sort((a, b) => (a.priority || 999) - (b.priority || 999))
        
        // If not filtering by available tokens, add any additional tokens from Light API
         if (!filterByAvailable) {
           for (const balance of lightApiResponse.balances) {
             if (!tokensToProcess.includes(balance.currency)) {
               const config = getTokenConfig(balance.currency)
               if (config) {
                 tokenOptions.push({
                   symbol: balance.currency,
                   contract: balance.contract,
                   amount: balance.amount,
                   decimals: parseInt(balance.decimals),
                   icon: config.icon,
                   logo: config.logo,
                   priority: config.priority || 999
                 })
               }
             }
           }
           // Sort again after adding additional tokens
           tokenOptions.sort((a, b) => (a.priority || 999) - (b.priority || 999))
         }
         
         setTokens(tokenOptions)
      } else {
        // Fallback to default tokens with 0 balance
        const tokensToShow = filterByAvailable ? availableTokens : alcorTokens.map(t => t.symbol)
        const defaultTokens = tokensToShow.map(symbol => {
          const config = getTokenConfig(symbol)
          return {
            symbol,
            contract: config.contract,
            amount: '0.0000',
            decimals: config.decimals,
            icon: config.icon,
            logo: config.logo,
            priority: config.priority || 999
          }
        })
        
        // Sort fallback tokens by priority
        defaultTokens.sort((a, b) => (a.priority || 999) - (b.priority || 999))
        
        setTokens(defaultTokens)
      }
    } catch (error) {
      log.error('Failed to load token balances', 'TokenDropdown.loadTokenBalances', error as Error)
      
      // Fallback to default tokens with 0 balance
      const tokensToShow = filterByAvailable ? availableTokens : alcorTokens.map(t => t.symbol)
      const defaultTokens = tokensToShow.map(symbol => {
        const config = getTokenConfig(symbol)
        return {
          symbol,
          contract: config.contract,
          amount: '0.0000',
          decimals: config.decimals,
          icon: config.icon,
          logo: config.logo,
          priority: config.priority || 999
        }
      })
      
      // Sort fallback tokens by priority
      defaultTokens.sort((a, b) => (a.priority || 999) - (b.priority || 999))
      
      setTokens(defaultTokens)
    } finally {
      setLoading(false)
    }
  }

  // Load Alcor tokens on component mount
  useEffect(() => {
    loadAlcorTokens()
  }, [])

  useEffect(() => {
    loadTokenBalances()
  }, [account, availableTokens, alcorTokens, filterByAvailable])

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setIsOpen(false)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  const handleTokenSelect = (token: TokenOption) => {
    onTokenSelect(token.symbol)
    setIsOpen(false)
  }

  const selectedTokenData = tokens.find(t => t.symbol === selectedToken)
  const selectedTokenConfig = getTokenConfig(selectedToken)

  return (
    <div className={`token-dropdown ${className}`} ref={dropdownRef}>
      <button 
        className="token-button"
        onClick={() => setIsOpen(!isOpen)}
        type="button"
      >
        <span className="token-icon">
          {selectedTokenData?.logo ? (
            <img src={selectedTokenData.logo} alt={selectedToken} className="token-logo" />
          ) : (
            selectedTokenData?.icon || selectedTokenConfig?.icon || '🪙'
          )}
        </span>
        <span className="token-symbol">{selectedToken}</span>
        <span className={`dropdown-arrow ${isOpen ? 'open' : ''}`}>▼</span>
      </button>
      
      {isOpen && (
        <div className="token-dropdown-menu">
          {loading ? (
            <div className="token-dropdown-loading">
              <span>Loading tokens...</span>
            </div>
          ) : (
            <>
              {tokens.map((token) => (
                <button
                  key={`${token.contract}-${token.symbol}`}
                  className={`token-option ${token.symbol === selectedToken ? 'selected' : ''}`}
                  onClick={() => handleTokenSelect(token)}
                  type="button"
                >
                  <div className="token-option-left">
                    <span className="token-option-icon">
                      {token.logo ? (
                        <img src={token.logo} alt={token.symbol} className="token-logo" />
                      ) : (
                        token.icon
                      )}
                    </span>
                    <div className="token-option-info">
                      <span className="token-option-symbol">{token.symbol}</span>
                      <span className="token-option-contract">{token.contract}</span>
                    </div>
                  </div>
                  <div className="token-option-balance">
                    {parseFloat(token.amount).toFixed(4)}
                  </div>
                </button>
              ))}
              {tokens.length === 0 && (
                <div className="token-dropdown-empty">
                  <span>No tokens available</span>
                </div>
              )}
            </>
          )}
        </div>
      )}
    </div>
  )
}

export default TokenDropdown