import React, { useState } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import type { InventoryNFT } from '../types/inventory'
import MediaRenderer from './MediaRenderer'
import { getAssetImageUrl, getAssetVideoUrl } from '../utils/mediaUtils'
import { log } from '../services/loggingService'
import { WharfKitService } from '../services/wharfkit'
import '../css/modal.css'

interface ListingModalProps {
  nft: InventoryNFT | null
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
  bundleNfts?: InventoryNFT[] // Optional bundle NFTs
}

const ListingModal: React.FC<ListingModalProps> = ({ nft, isOpen, onClose, onSuccess, bundleNfts = [] }) => {
  const [price, setPrice] = useState('')
  const [isBundle, setIsBundle] = useState(false)
  const [bundleName, setBundleName] = useState('')
  const [isListing, setIsListing] = useState(false)
  const { session } = useWallet()
  const { showSuccess, showError } = useNotifications()

  // AtomicMarket only supports WAX listings
  const waxCurrency = { symbol: 'WAX', contract: 'eosio.token', precision: 8 }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!nft || !session || !price) {
      showError('Please fill in all required fields')
      return
    }

    if (isBundle && !bundleName.trim()) {
      showError('Please enter a bundle name')
      return
    }

    const priceFloat = parseFloat(price)
    if (isNaN(priceFloat) || priceFloat <= 0) {
      showError('Please enter a valid price')
      return
    }

    setIsListing(true)

    try {
      const selectedCurrency = waxCurrency

      // Format price with proper precision
      const formattedPrice = priceFloat.toFixed(selectedCurrency.precision)
      const priceString = `${formattedPrice} ${selectedCurrency.symbol}`

      // Determine asset IDs for listing
      const assetIds = isBundle && bundleNfts.length > 0 
        ? [nft.asset_id, ...bundleNfts.map(bundleNft => bundleNft.asset_id)]
        : [nft.asset_id]

      // AtomicMarket listing action
      const action = {
        account: 'atomicmarket',
        name: 'announcesale',
        authorization: [{
          actor: WharfKitService.safeGetActor(session),
          permission: WharfKitService.safeGetPermission(session)
        }],
        data: {
          seller: WharfKitService.safeGetActor(session),
          asset_ids: assetIds,
          listing_price: priceString,
          settlement_symbol: `${selectedCurrency.precision},${selectedCurrency.symbol}`,
          maker_marketplace: 'market.pxj'
        }
      }

      const result = await session.transact({ actions: [action] })
      
      if (result) {
        const listingType = isBundle ? 'Bundle' : 'NFT'
        showSuccess(`${listingType} listed successfully!`)
        onSuccess?.()
        onClose()
        setPrice('')
      }
    } catch (error) {
      log.error('Listing failed', 'ListingModal', error instanceof Error ? error : new Error(String(error)))
      showError(`Listing failed: ${error instanceof Error ? error.message : 'Unknown error'}`)
    } finally {
      setIsListing(false)
    }
  }

  const handleClose = () => {
    if (!isListing) {
      setPrice('')
      setIsBundle(false)
      setBundleName('')
      onClose()
    }
  }

  if (!isOpen || !nft) return null

  return (
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content listing-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>List NFT for Sale</h2>
          <button 
            className="modal-close" 
            onClick={handleClose}
            disabled={isListing}
          >
            ×
          </button>
        </div>

        <div className="modal-body">
          <div className="nft-preview enhanced-preview">
            <div className="nft-image">
              <MediaRenderer
                imageUrl={getAssetImageUrl(nft)}
                videoUrl={getAssetVideoUrl(nft)}
                alt={nft.name || `Asset #${nft.asset_id}`}
                className="nft-media"
              />
            </div>
            <div className="nft-info">
              <h3>{nft.name}</h3>
              <p className="nft-collection">{typeof nft.collection === 'string' ? nft.collection : nft.collection?.name || nft.collection?.collection_name || 'Unknown'}</p>
              <p className="nft-schema">{nft.schema.schema_name}</p>
              {nft.mint_number && (
                <p className="nft-mint">Mint #{nft.mint_number}</p>
              )}
              {isBundle && bundleNfts.length > 0 && (
                <p className="bundle-info">+ {bundleNfts.length} additional NFT{bundleNfts.length > 1 ? 's' : ''}</p>
              )}
            </div>
          </div>

          {/* Bundle Creation Section */}
          {bundleNfts.length > 0 && (
            <div className="bundle-section">
              <div className="bundle-toggle">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={isBundle}
                    onChange={(e) => setIsBundle(e.target.checked)}
                    disabled={isListing}
                  />
                  <span className="checkmark"></span>
                  Create Bundle with {bundleNfts.length} additional NFT{bundleNfts.length > 1 ? 's' : ''}
                </label>
              </div>
              
              {isBundle && (
                <div className="bundle-details">
                  <div className="form-group">
                    <label htmlFor="bundleName">Bundle Name *</label>
                    <input
                      type="text"
                      id="bundleName"
                      value={bundleName}
                      onChange={(e) => setBundleName(e.target.value)}
                      placeholder="Enter bundle name"
                      disabled={isListing}
                      className="bundle-name-input"
                    />
                  </div>
                  
                  <div className="bundle-preview">
                    <h4>Bundle Contents:</h4>
                    <div className="bundle-nfts">
                      <div className="bundle-nft-item main-nft">
                        <MediaRenderer imageUrl={getAssetImageUrl(nft)} videoUrl={getAssetVideoUrl(nft)} alt={nft.name || `Asset #${nft.asset_id}`} className="bundle-nft-image" />
                        <span>{nft.name}</span>
                      </div>
                      {bundleNfts.slice(0, 3).map((bundleNft) => (
                        <div key={bundleNft.asset_id} className="bundle-nft-item">
                          <MediaRenderer imageUrl={getAssetImageUrl(bundleNft)} videoUrl={getAssetVideoUrl(bundleNft)} alt={bundleNft.name || `Asset #${bundleNft.asset_id}`} className="bundle-nft-image" />
                          <span>{bundleNft.name}</span>
                        </div>
                      ))}
                      {bundleNfts.length > 3 && (
                        <div className="bundle-more">+{bundleNfts.length - 3} more</div>
                      )}
                    </div>
                  </div>
                </div>
              )}
            </div>
          )}

          <form onSubmit={handleSubmit} className="modal-form listing-form">
            <div className="form-group">
              <label htmlFor="price">Price *</label>
              <div className="price-input-group">
                <input
                  type="number"
                  id="price"
                  value={price}
                  onChange={(e) => setPrice(e.target.value)}
                  placeholder="0.00"
                  step="0.0001"
                  min="0"
                  required
                  disabled={isListing}
                  className="price-input"
                />
                <div className="currency-display">
                  <span className="currency-symbol">{waxCurrency.symbol}</span>
                  <span className="currency-note">(AtomicMarket only supports WAX)</span>
                </div>
              </div>
            </div>

            <div className="listing-info">
              <p className="info-text">
                <strong>Marketplace:</strong> AtomicMarket
              </p>
              <p className="info-text">
                <strong>Marketplace Fee:</strong> 2%
              </p>
              <p className="info-text">
                <strong>Collection Fee:</strong> Varies by collection
              </p>
            </div>

            <div className="modal-actions">
              <button 
                type="button" 
                className="btn-secondary"
                onClick={handleClose}
                disabled={isListing}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn-primary"
                disabled={isListing || !price}
              >
                {isListing ? (
                  <>
                    <div className="spinner"></div>
                    Listing...
                  </>
                ) : (
                  'List for Sale'
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default ListingModal