// IPFS utility functions for consistent URL handling across the app
import { API_ENDPOINTS } from '../config/marketplaceConfig'

/**
 * Convert various IPFS URL formats to a standardized format
 * @param imageUrl - The image URL or hash
 * @returns Standardized IPFS URL
 */
export function normalizeIPFSUrl(imageUrl: string | undefined | null): string {
  if (!imageUrl) {
    return ''
  }

  // If it's already a full HTTP URL, return as-is
  if (imageUrl.startsWith('http://') || imageUrl.startsWith('https://')) {
    return imageUrl
  }

  // If it's a data URL or blob URL, return as-is
  if (imageUrl.startsWith('data:') || imageUrl.startsWith('blob:')) {
    return imageUrl
  }

  // Handle ipfs:// protocol
  if (imageUrl.startsWith('ipfs://')) {
    const hash = imageUrl.replace('ipfs://', '')
    return getIPFSGatewayUrl(hash)
  }

  // Handle /ipfs/ path
  if (imageUrl.includes('/ipfs/')) {
    const hash = imageUrl.split('/ipfs/')[1]
    return getIPFSGatewayUrl(hash)
  }

  // If it looks like an IPFS hash (starts with Qm and is long enough)
  if (imageUrl.startsWith('Qm') && imageUrl.length > 40) {
    return getIPFSGatewayUrl(imageUrl)
  }

  // If it's just a hash without protocol, assume it's IPFS
  if (imageUrl.length > 40 && /^[a-zA-Z0-9]+$/.test(imageUrl)) {
    return getIPFSGatewayUrl(imageUrl)
  }

  // Default: assume it's a relative path and use IPFS gateway
  return getIPFSGatewayUrl(imageUrl)
}

/**
 * Get a random IPFS gateway URL for load balancing
 * @param hash - The IPFS hash
 * @returns Full IPFS gateway URL
 */
export function getIPFSGatewayUrl(hash: string): string {
  const gateways = API_ENDPOINTS.IPFS.GATEWAYS
  const randomGateway = gateways[Math.floor(Math.random() * gateways.length)]
  
  // Clean the hash (remove any leading slashes or ipfs/ prefix)
  const cleanHash = hash.replace(/^\/?(ipfs\/)?/, '')
  
  return `${randomGateway}${cleanHash}`
}

/**
 * Check if a URL is an IPFS URL
 * @param url - The URL to check
 * @returns True if it's an IPFS URL
 */
export function isIPFSUrl(url: string | undefined | null): boolean {
  if (!url) return false
  
  return (
    url.includes('/ipfs/') ||
    url.startsWith('ipfs://') ||
    url.includes('gateway.pinata.cloud') ||
    url.includes('ipfs.io') ||
    url.includes('dweb.link')
  )
}

/**
 * Extract IPFS hash from various URL formats
 * @param url - The URL containing an IPFS hash
 * @returns The extracted hash or null if not found
 */
export function extractIPFSHash(url: string): string | null {
  if (!url) return null

  // Handle ipfs:// protocol
  if (url.startsWith('ipfs://')) {
    return url.replace('ipfs://', '')
  }

  // Handle /ipfs/ in URL
  const ipfsMatch = url.match(/\/ipfs\/([a-zA-Z0-9]+)/)
  if (ipfsMatch) {
    return ipfsMatch[1]
  }

  // If it looks like a hash itself
  if (url.startsWith('Qm') && url.length > 40) {
    return url
  }

  return null
}

/**
 * Get fallback image URL for failed IPFS loads
 * @returns Fallback image URL
 */
export function getFallbackImageUrl(): string {
  // Use a reliable fallback image from IPFS
  return 'https://ipfs.io/ipfs/QmQKA9fxWBAsoDnMRZj1v1vsjwZeWZpHmnoEyujmeMi2rp'
}

/**
 * Create an image element with IPFS fallback handling
 * @param src - The image source URL
 * @param onLoad - Callback when image loads successfully
 * @param onError - Callback when image fails to load
 * @returns HTMLImageElement with fallback handling
 */
export function createImageWithFallback(
  src: string,
  onLoad?: (event: Event) => void,
  onError?: (event: Event) => void
): HTMLImageElement {
  const img = new Image()
  
  let hasTriedFallback = false
  
  img.onerror = (event) => {
    if (!hasTriedFallback && isIPFSUrl(src)) {
      hasTriedFallback = true
      // Try with a different gateway
      const hash = extractIPFSHash(src)
      if (hash) {
        img.src = getIPFSGatewayUrl(hash)
        return
      }
    }
    
    // If all else fails, use fallback image
    if (!hasTriedFallback) {
      hasTriedFallback = true
      img.src = getFallbackImageUrl()
      return
    }
    
    // Call the original error handler
    if (onError) {
      onError(event as Event)
    }
  }
  
  img.onload = onLoad || null
  img.src = normalizeIPFSUrl(src)
  
  return img
}

/**
 * Preload an IPFS image with fallback handling
 * @param src - The image source URL
 * @returns Promise that resolves with the working URL
 */
export function preloadIPFSImage(src: string): Promise<string> {
  return new Promise((resolve, reject) => {
    const img = createImageWithFallback(
      src,
      () => resolve(img.src),
      () => reject(new Error(`Failed to load image: ${src}`))
    )
  })
}