import React, { useState, useEffect, useRef, useCallback } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import { chatService, type ChatMessage, type ChatCostInfo } from '../services/chatService'
import { formatSafeMarkdown } from '../utils/sanitize'
import { WharfKitService } from '../services/wharfkit'
import type { NFTAsset } from '../services/api'
import '../css/listing-chat-modal.css'

interface ListingChatModalProps {
  isOpen: boolean
  onClose: () => void
  nft: NFTAsset
}

export const ListingChatModal: React.FC<ListingChatModalProps> = ({ isOpen, onClose, nft }) => {
  const { account, session } = useWallet()
  const { showSuccess, showError } = useNotifications()
  const messagesEndRef = useRef<HTMLDivElement>(null)
  
  const [messages, setMessages] = useState<ChatMessage[]>([])
  const [newMessage, setNewMessage] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const [costInfo, setCostInfo] = useState<ChatCostInfo | null>(null)
  const [showPriceInfo, setShowPriceInfo] = useState(false)
  
  // Create listing-specific tag
  const listingTag = `listing:${nft.sale_id || nft.asset_id}`
  
  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }, [])
  
  const loadMessages = useCallback(async (refresh = false) => {
    if (!refresh && messages.length > 0) return
    
    setIsLoading(true)
    try {
      // Load messages with listing-specific filter
      const listingMessages = await chatService.getMessages({
        collection: listingTag,
        hideSpam: true
      }, 50)
      
      setMessages(listingMessages)
    } catch (error) {
      console.error('Error loading listing messages:', error)
      showError('Failed to load messages')
    } finally {
      setIsLoading(false)
    }
  }, [listingTag, messages.length, showError])
  
  const updateCostInfo = useCallback(async () => {
    if (!account || !newMessage.trim()) {
      setCostInfo(null)
      return
    }
    
    try {
      const cost = await chatService.calculateMessageCost(newMessage, account)
      setCostInfo(cost)
    } catch (error) {
      console.error('Error calculating cost:', error)
    }
  }, [account, newMessage])
  
  const sendMessage = async () => {
    if (!account || !newMessage.trim() || !session) {
      showError('Please connect your wallet to send messages')
      return
    }
    
    if (!nft.seller) {
      showError('Seller information not available')
      return
    }
    
    if (nft.seller === WharfKitService.safeGetActor(session)) {
      showError('You cannot message yourself')
      return
    }
    
    setIsSending(true)
    
    try {
      // Format message with listing tag and seller reference
      const messageWithTags = `${listingTag} :${nft.seller}: ${newMessage.trim()}`
      
      const txId = await chatService.sendMessage(
        messageWithTags,
        account
      )
      
      showSuccess('Message sent!', `Transaction: ${txId.substring(0, 8)}...`)
      setNewMessage('')
      setCostInfo(null)
      
      // Refresh messages after a short delay
      setTimeout(() => {
        loadMessages(true)
      }, 2000)
      
    } catch (error) {
      console.error('Error sending message:', error)
      
      // Check if this might be a successful transaction with unexpected response format
      const errorMessage = error instanceof Error ? error.message : 'Unknown error'
      if (errorMessage.includes('transaction ID not found')) {
        showError(
          'Message Status Unclear', 
          'Your message may have been sent successfully. Please check your transaction history or refresh the chat to confirm.'
        )
      } else {
        showError('Failed to send message', errorMessage)
      }
    } finally {
      setIsSending(false)
    }
  }
  
  // Load messages when modal opens
  useEffect(() => {
    if (isOpen) {
      loadMessages()
    }
  }, [isOpen, loadMessages])
  
  // Update cost info when message changes
  useEffect(() => {
    const debounceTimer = setTimeout(updateCostInfo, 300)
    return () => clearTimeout(debounceTimer)
  }, [updateCostInfo])
  
  // Scroll to bottom when new messages arrive
  useEffect(() => {
    scrollToBottom()
  }, [messages, scrollToBottom])
  
  // Handle escape key to close modal
  useEffect(() => {
    const handleEscape = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        onClose()
      }
    }
    
    if (isOpen) {
      document.addEventListener('keydown', handleEscape)
      return () => document.removeEventListener('keydown', handleEscape)
    }
  }, [isOpen, onClose])
  
  if (!isOpen) return null
  
  return (
    <div className="listing-chat-modal-backdrop" onClick={onClose}>
      <div className="listing-chat-modal" onClick={(e) => e.stopPropagation()}>
        {/* Header */}
        <div className="listing-chat-header">
          <div className="listing-chat-header-left">
            <div className="listing-info">
              <img 
                src={nft.image || '/placeholder-nft.png'} 
                alt={nft.name}
                className="listing-nft-image"
              />
              <div className="listing-details">
                <h3 className="listing-title">{nft.name}</h3>
                <p className="listing-seller">Seller: {nft.seller}</p>
                {nft.price && (
                  <p className="listing-price">{nft.price}</p>
                )}
              </div>
            </div>
          </div>
          <div className="listing-chat-header-right">
            <button 
              className="chat_btn chat_btn--icon"
              onClick={() => loadMessages(true)}
              disabled={isLoading}
              title="Refresh messages"
            >
              <span className={`chat_refresh-icon ${isLoading ? 'chat_refresh-icon--spinning' : ''}`}>🔄</span>
            </button>
            <button 
              className="chat_btn chat_btn--icon"
              onClick={onClose}
              title="Close chat"
            >
              ✕
            </button>
          </div>
        </div>
        
        {/* Messages */}
        <div className="listing-chat-messages">
          {isLoading && messages.length === 0 ? (
            <div className="chat_loading">Loading messages...</div>
          ) : messages.length === 0 ? (
            <div className="chat_empty">
              <p>No messages yet for this listing.</p>
              <p>Be the first to start the conversation!</p>
            </div>
          ) : (
            messages.map((message) => (
              <div key={message.id} className={`chat_message chat_message--${message.messageType || 'normal'}`}>
                <div className="chat_message-header">
                  <div className="chat_message-avatar">
                    <img 
                      src={`https://wax.atomichub.io/v1/accounts/${message.sender}/avatar`}
                      alt={message.sender}
                      className="chat_avatar"
                      onError={(e) => {
                        const target = e.target as HTMLImageElement
                        target.src = '/default-avatar.png'
                      }}
                    />
                  </div>
                  <div className="chat_message-info">
                    <span className="chat_sender">{message.sender}</span>
                    <span className="chat_timestamp">
                      {new Date(message.timestamp).toLocaleString()}
                    </span>
                    {message.pxjAmount && (
                      <span className="chat_amount">{message.pxjAmount}</span>
                    )}
                  </div>
                  <div className="chat_message-actions">
                    {message.transactionId && (
                      <a 
                        href={`https://waxblock.io/transaction/${message.transactionId}`}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="chat_tx-link"
                        title="View transaction"
                      >
                        🔗
                      </a>
                    )}
                  </div>
                </div>
                <div 
                  className="chat_message-content"
                  dangerouslySetInnerHTML={{ __html: formatSafeMarkdown(message.message) }}
                />
              </div>
            ))
          )}
          <div ref={messagesEndRef} />
        </div>
        
        {/* Input Section */}
        <div className="listing-chat-input-section">
          {costInfo && (
            <div className="chat_cost-info">
              <div className="chat_cost-display">
                <span>Message Cost: <span className="chat_cost-amount">{(costInfo.totalCost / 10000).toFixed(4)} PXJ</span></span>
                <button 
                  className="chat_info-btn"
                  onClick={() => setShowPriceInfo(!showPriceInfo)}
                  title="View price breakdown"
                >
                  ℹ️
                </button>
              </div>
              {costInfo.hasPixalPFP && (
                <span className="chat_discount">Pixal PFP Discount: {costInfo.discount}%</span>
              )}
              <span className="chat_char-count">
                {newMessage.length}/240 chars
              </span>
              {showPriceInfo && (
                <div className="chat_price-tooltip">
                  <div className="chat_price-content">
                    <h4>Price Breakdown</h4>
                    <div className="price-factor">
                      <span>Base Cost:</span>
                      <span>{(costInfo.baseCost / 10000).toFixed(4)} PXJ</span>
                    </div>
                    <div className="price-factor">
                      <span>Character Cost ({newMessage.length} chars):</span>
                      <span>{((costInfo.totalCost - costInfo.baseCost) / 10000).toFixed(4)} PXJ</span>
                    </div>
                    {costInfo.hasPixalPFP && (
                      <div className="price-factor discount">
                        <span>Pixal PFP Discount ({costInfo.discount}%):</span>
                        <span>-{((costInfo.baseCost + (costInfo.totalCost - costInfo.baseCost)) * (costInfo.discount / 100) / 10000).toFixed(4)} PXJ</span>
                      </div>
                    )}
                    <div className="price-factor total">
                      <span>Total:</span>
                      <span>{(costInfo.totalCost / 10000).toFixed(4)} PXJ</span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          )}
          
          <div className="listing-chat-input-wrapper">
            <textarea
              className="listing-chat-input"
              value={newMessage}
              onChange={(e) => setNewMessage(e.target.value)}
              placeholder={`Send a message about ${nft.name}...`}
              maxLength={240}
              rows={3}
              onKeyDown={(e) => {
                if (e.key === 'Enter' && !e.shiftKey) {
                  e.preventDefault()
                  sendMessage()
                }
              }}
            />
            <button 
              className="listing-chat-send-btn"
              onClick={sendMessage}
              disabled={!newMessage.trim() || isSending || !account}
            >
              {isSending ? '⏳' : '📤'}
            </button>
          </div>
          
          {!account && (
            <div className="listing-chat-connect-notice">
              Please connect your wallet to send messages
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default ListingChatModal