import { apiService } from './api'
import { wharfKitService } from './wharfkit'
import { ErrorMessageService } from './errorMessageService'
import { log } from './loggingService'
import { API_ENDPOINTS } from '../config/marketplaceConfig'

export interface ChatMessage {
  id: string
  sender: string
  message: string
  timestamp: string
  pxjAmount: string
  transactionId: string
  isSponsored: boolean
  collectionTag?: string
  messageType: 'normal' | 'sponsored' | 'premium'
  characterCount: number
}

export interface ChatCostInfo {
  baseCost: number
  characterCost: number
  totalCost: number
  hasPixalPFP: boolean
  discount: number
  isSponsored?: boolean
}

export interface ChatFilters {
  collection?: string
  minPxjAmount?: number
  maxPxjAmount?: number
  hideSpam?: boolean
  showOnlySponsored?: boolean
}

export interface ModerationSettings {
  blacklistedAccounts: string[]
  bannedWords: string[]
  trustedDomains: string[]
  maxMessageLength: number
  minTimeBetweenMessages: number // seconds
}

class ChatService {
  private readonly CHAT_ACCOUNT = 'chat.pxj'
  private readonly PXJ_CONTRACT = 'pixeljourney'
  private readonly PXJ_SYMBOL = 'PXJ'
  
  // Pricing constants
  private readonly BASE_COST_PIXAL_HOLDER = 100 // 100 PXJ for Pixal holders
  private readonly BASE_COST_NON_HOLDER = 10000 // 10,000 PXJ for non-holders
  private readonly SPONSORED_COST_PIXAL_HOLDER = 10000 // 1000 PXJ for Pixal holders (10k PXJ)
  private readonly SPONSORED_COST_NON_HOLDER = 25000 // 25000 PXJ for non-holders (25k PXJ)
  
  // Spam prevention
  private readonly MIN_MESSAGE_COST = 100 // 100 PXJ minimum (aligned with holder base cost)

  
  private messageCache: Map<string, ChatMessage[]> = new Map()
  private userRateLimit: Map<string, number[]> = new Map()
  private blacklistedAccounts: Set<string> = new Set()
  // Removed duplicate bannedWords - now using moderationSettings
  
  private moderationSettings: ModerationSettings = {
    blacklistedAccounts: [],
    bannedWords: ['spam', 'scam', 'hack', 'phishing'],
    trustedDomains: ['wax.io', 'atomichub.io', 'wax.bloks.io', 'pixeljourney.io'],
    maxMessageLength: 240,
    minTimeBetweenMessages: 5
  }
  
  private userLastMessageTime: Map<string, number> = new Map()
  
  /**
   * Calculate the cost to send a message based on length and user privileges
   */
  async calculateMessageCost(message: string, userAccount: string, isSponsored: boolean = false): Promise<ChatCostInfo> {
    const characterCount = message.length
    const hasPixalPFP = await this.checkPixalPFPOwnership(userAccount)
    
    // Handle sponsored messages with fixed pricing
    if (isSponsored) {
      const sponsoredCost = hasPixalPFP ? this.SPONSORED_COST_PIXAL_HOLDER : this.SPONSORED_COST_NON_HOLDER
      const discount = hasPixalPFP ? 60 : 0 // 60% discount for Pixal PFP holders (from 25k to 10k PXJ)
      
      return {
        baseCost: sponsoredCost,
        characterCost: 0,
        totalCost: sponsoredCost,
        hasPixalPFP,
        discount,
        isSponsored: true
      }
    }
    
    const baseCost = hasPixalPFP ? this.BASE_COST_PIXAL_HOLDER : this.BASE_COST_NON_HOLDER
    
    // Improved scaling: cost increases more significantly with character count
    let characterMultiplier = 1
    if (characterCount > 50) {
      characterMultiplier = 1 + (characterCount - 50) / 50 // Increases by 100% per 50 chars
    }
    
    const characterCost = Math.floor(baseCost * (characterMultiplier - 1))
    const totalCost = Math.max(this.MIN_MESSAGE_COST, baseCost + characterCost)
    
    // Cap the maximum cost for holders at 500 PXJ (50000 units)
    const maxCostForHolders = hasPixalPFP ? 50000 : totalCost
    const finalCost = hasPixalPFP ? Math.min(totalCost, maxCostForHolders) : totalCost
    
    const discount = hasPixalPFP ? 99 : 0 // 99% discount for Pixal PFP holders (from 10k to 100 PXJ)
    
    return {
      baseCost,
      characterCost,
      totalCost: finalCost,
      hasPixalPFP,
      discount,
      isSponsored: false
    }
  }
  
  /**
   * Check if user owns a Pixal PFP NFT (template 781515)
   */
  private async checkPixalPFPOwnership(userAccount: string): Promise<boolean> {
    try {
      const assets = await apiService.getUserInventory(userAccount)
      if (!assets || assets.length === 0) {
        return false
      }
      
      // Check for Pixal PFP template 781515
      const hasPixalPFP = assets.some(asset => 
        asset.template_id === '781515'
      )
      
      return hasPixalPFP
    } catch (error) {
      ErrorMessageService.logError('ChatService.checkPixalPFPOwnership', error, { userAccount })
      return false
    }
  }
  
  /**
   * Send a chat message by transferring PXJ with memo
   */
  async sendMessage(
    message: string, 
    userAccount: string, 
    collectionTag?: string,
    isSponsored: boolean = false
  ): Promise<string> {
    // Enhanced validation and moderation
    this.validateMessageForSending(message, userAccount)
    
    const costInfo = await this.calculateMessageCost(message, userAccount)
    let finalCost = costInfo.totalCost
    let memo = message
    
    // Handle sponsored messages
    if (isSponsored) {
      const hasPixalPFP = await this.checkPixalPFPOwnership(userAccount)
      finalCost = hasPixalPFP ? this.SPONSORED_COST_PIXAL_HOLDER : this.SPONSORED_COST_NON_HOLDER
      memo = `::sponsored:: ${message}`
    }
    
    // Add collection tag if specified
    if (collectionTag) {
      memo = `::col:${collectionTag}:: ${memo}`
    }
    
    // Update last message time for rate limiting
    this.userLastMessageTime.set(userAccount, Date.now())
    
    // Prepare transfer action
    const transferAction = {
      account: this.PXJ_CONTRACT,
      name: 'transfer',
      authorization: [{
        actor: userAccount,
        permission: 'active'
      }],
      data: {
        from: userAccount,
        to: this.CHAT_ACCOUNT,
        quantity: `${(finalCost).toFixed(8)} ${this.PXJ_SYMBOL}`,
        memo: memo
      }
    }
    
    try {
      const result = await wharfKitService.transact([transferAction]) as any
      
      // Extract transaction ID from various possible response formats
      let transactionId: string | null = null
      
      if (result) {
        // Try different possible properties where transaction ID might be stored
        transactionId = result.transaction_id || 
                       result.id || 
                       result.processed?.id || 
                       result.processed?.transaction_id ||
                       (result.response && result.response.transaction_id) ||
                       (result.transaction && result.transaction.id)
      }
      
      if (transactionId) {
        // Update rate limit
        this.updateRateLimit(userAccount)
        
        // Clear cache to force refresh
        this.messageCache.clear()
        
        return transactionId
      } else {
        // Log the actual result structure for debugging
        log.warn('Unexpected transaction result format', 'ChatService.sendMessage', { result })
        throw new Error('Transaction completed but transaction ID not found in response')
      }
    } catch (error) {
      // Only show error if it's actually a transaction failure, not a parsing issue
      if (error instanceof Error && error.message.includes('transaction ID not found')) {
        // This might still be a successful transaction, just with unexpected response format
        log.warn('Transaction may have succeeded but response format was unexpected', 'ChatService.sendMessage', { error })
      }
      ErrorMessageService.logError('ChatService.sendMessage', error, { messageLength: message.length })
      throw new Error(`Failed to send message: ${error instanceof Error ? error.message : 'Unknown error'}`)
    }
  }
  
  /**
   * Fetch chat messages from blockchain
   */
  async getMessages(
    filters: ChatFilters = {},
    limit: number = 50,
    offset: number = 0
  ): Promise<ChatMessage[]> {
    const cacheKey = JSON.stringify({ filters, limit, offset })
    
    // Check cache first
    if (this.messageCache.has(cacheKey)) {
      return this.messageCache.get(cacheKey)!
    }
    
    try {
      // Fetch transfers to chat account
      const transfers = await apiService.getAccountTransfers(
        this.CHAT_ACCOUNT,
        this.PXJ_CONTRACT,
        this.PXJ_SYMBOL,
        limit,
        offset
      )
      
      if (!transfers || transfers.length === 0) {
        return []
      }
      
      const messages: ChatMessage[] = []
      
      for (const transfer of transfers) {
        const message = this.parseTransferToMessage(transfer)
        if (message && this.applyFilters(message, filters) && !this.isMessageBlockedByModeration(message)) {
          messages.push(message)
        }
      }
      
      // Sort by timestamp (newest first)
      messages.sort((a, b) => new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime())
      
      // Cache the results
      this.messageCache.set(cacheKey, messages)
      
      return messages
    } catch (error) {
      ErrorMessageService.logError('ChatService.loadMessages', error)
      return []
    }
  }
  
  /**
   * Parse a blockchain transfer into a chat message
   */
  private parseTransferToMessage(transfer: {
    memo?: string
    from: string
    quantity: string
    timestamp: string
    transaction_id: string
  }): ChatMessage | null {
    try {
      const memo = String(transfer.memo || '')
      const sender = transfer.from
      const amount = transfer.quantity
      const timestamp = transfer.timestamp
      const transactionId = transfer.transaction_id
      
      // Skip if sender is blacklisted
      if (this.blacklistedAccounts.has(sender)) {
        return null
      }
      
      // Parse memo for special tags
      let message = memo
      let isSponsored = false
      let collectionTag: string | undefined
      let messageType: 'normal' | 'sponsored' | 'premium' = 'normal'
      
      // Check for sponsored tag
      if (memo.startsWith('::sponsored::')) {
        // Validate that the PXJ amount meets the minimum for sponsored messages
        const pxjAmount = parseFloat(amount.split(' ')[0])
        const minSponsoredAmount = 1000 // 1000 PXJ minimum (10k PXJ in base units)
        
        if (pxjAmount >= minSponsoredAmount) {
          isSponsored = true
          messageType = 'sponsored'
          message = memo.replace('::sponsored::', '').trim()
        } else {
          // If insufficient PXJ for sponsored, treat as normal message but remove the sponsored tag
          message = memo.replace('::sponsored::', '').trim()
        }
      }
      
      // Check for collection tag
      const collectionMatch = memo.match(/::col:([^:]+)::/)
      if (collectionMatch) {
        collectionTag = collectionMatch[1]
        message = message.replace(collectionMatch[0], '').trim()
      }
      
      // Determine message type based on PXJ amount
      const pxjAmount = parseFloat(amount.split(' ')[0])
      if (pxjAmount >= 10) { // 10+ PXJ = premium message
        messageType = 'premium'
      }
      
      return {
        id: `${transactionId}_${sender}`,
        sender,
        message,
        timestamp,
        pxjAmount: amount,
        transactionId,
        isSponsored,
        collectionTag,
        messageType,
        characterCount: message.length
      }
    } catch (error) {
      const errorObj = error instanceof Error ? error : new Error('Failed to parse transfer to message')
      ErrorMessageService.logError('ChatService.parseTransferToMessage', errorObj, {
        transfer: transfer?.transaction_id || 'unknown'
      })
      return null
    }
  }
  
  /**
   * Apply filters to messages
   */
  private applyFilters(message: ChatMessage, filters: ChatFilters): boolean {
    // Collection filter
    if (filters.collection && message.collectionTag !== filters.collection) {
      return false
    }
    
    // Minimum PXJ amount filter
    if (filters.minPxjAmount) {
      const amount = parseFloat(message.pxjAmount.split(' ')[0])
      if (amount < filters.minPxjAmount) {
        return false
      }
    }
    
    // Maximum PXJ amount filter
    if (filters.maxPxjAmount) {
      const amount = parseFloat(message.pxjAmount.split(' ')[0])
      if (amount > filters.maxPxjAmount) {
        return false
      }
    }
    
    // Hide spam filter
    if (filters.hideSpam && this.isSpamMessage(message)) {
      return false
    }
    
    // Show only sponsored filter
    if (filters.showOnlySponsored && !message.isSponsored) {
      return false
    }
    
    return true
  }
  
  /**
   * Check if message is spam
   */
  private isSpamMessage(message: ChatMessage): boolean {
    const text = message.message.toLowerCase()
    return this.moderationSettings.bannedWords.some((word: string) => text.includes(word))
  }
  

  
  /**
   * Update rate limit for user
   */
  private updateRateLimit(userAccount: string): void {
    const now = Date.now()
    const userMessages = this.userRateLimit.get(userAccount) || []
    userMessages.push(now)
    this.userRateLimit.set(userAccount, userMessages)
  }
  

  
  /**
   * Add account to blacklist
   */
  blacklistAccount(account: string): void {
    this.blacklistedAccounts.add(account)
  }
  
  /**
   * Remove account from blacklist
   */
  unblacklistAccount(account: string): void {
    this.blacklistedAccounts.delete(account)
  }
  
  /**
   * Clear message cache
   */
  clearCache(): void {
    this.messageCache.clear()
  }
  
  /**
   * Get user's avatar URL (Wax profile or Pixal PFP)
   */
  async getUserAvatar(userAccount: string): Promise<string> {
    try {
      // First try to get Pixal PFP
      const inventory = await apiService.getUserInventory(userAccount)
      const pixalAssets = inventory.filter(asset => asset.collection === 'pixeljourney' && asset.schema === 'Pixal')
      if (pixalAssets && pixalAssets.length > 0) {
        const asset = pixalAssets[0]
        if (asset.traits && asset.traits.img && typeof asset.traits.img === 'string') {
          return `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${asset.traits.img.startsWith('Qm') ? asset.traits.img : asset.traits.img}`
        }
      }
      
      // Fallback to default avatar
      return `${API_ENDPOINTS.AVATAR.DICEBEAR}?seed=${userAccount}`
    } catch (error) {
      const errorObj = error instanceof Error ? error : new Error('Failed to get user avatar')
      ErrorMessageService.logError('ChatService.getUserAvatar', errorObj, {
        userAccount
      })
      return `${API_ENDPOINTS.AVATAR.DICEBEAR}?seed=${userAccount}`
    }
  }
  
  /**
   * Enhanced message validation for sending
   */
  private validateMessageForSending(message: string, userAccount: string): void {
    // Check if account is blacklisted
    if (this.moderationSettings.blacklistedAccounts.includes(userAccount)) {
      throw new Error('Account is blacklisted from sending messages.')
    }
    
    // Check message length
    if (message.length > this.moderationSettings.maxMessageLength) {
      throw new Error(`Message too long. Maximum ${this.moderationSettings.maxMessageLength} characters allowed.`)
    }
    
    // Check for banned words
    const lowerMessage = message.toLowerCase()
    for (const bannedWord of this.moderationSettings.bannedWords) {
      if (lowerMessage.includes(bannedWord.toLowerCase())) {
        throw new Error('Message contains prohibited content.')
      }
    }
    
    // Check time between messages
    const lastMessageTime = this.userLastMessageTime.get(userAccount)
    if (lastMessageTime) {
      const timeDiff = (Date.now() - lastMessageTime) / 1000
      if (timeDiff < this.moderationSettings.minTimeBetweenMessages) {
        throw new Error(`Please wait ${this.moderationSettings.minTimeBetweenMessages - Math.floor(timeDiff)} more seconds before sending another message.`)
      }
    }
    
    // Verify links against trusted domains
    this.verifyLinksInMessage(message)
  }
  
  /**
   * Check if message is blocked by moderation settings
   */
  private isMessageBlockedByModeration(message: ChatMessage): boolean {
    // Check if sender is blacklisted
    if (this.moderationSettings.blacklistedAccounts.includes(message.sender)) {
      return true
    }
    
    // Check for banned words
    const lowerMessage = message.message.toLowerCase()
    for (const bannedWord of this.moderationSettings.bannedWords) {
      if (lowerMessage.includes(bannedWord.toLowerCase())) {
        return true
      }
    }
    
    return false
  }
  
  /**
   * Verify links in message against trusted domains
   */
  private verifyLinksInMessage(message: string): void {
    const urlRegex = /(https?:\/\/[^\s]+)/g
    const urls = message.match(urlRegex)
    
    if (urls) {
      for (const url of urls) {
        try {
          const urlObj = new URL(url)
          const domain = urlObj.hostname.toLowerCase()
          
          // Check if domain is in trusted list
          const isTrusted = this.moderationSettings.trustedDomains.some(trustedDomain => 
            domain === trustedDomain.toLowerCase() || domain.endsWith('.' + trustedDomain.toLowerCase())
          )
          
          if (!isTrusted) {
            throw new Error(`Links to ${domain} are not allowed. Only trusted domains are permitted.`)
          }
        } catch (error) {
          if (error instanceof Error && error.message.includes('not allowed')) {
            throw error
          }
          throw new Error('Invalid URL detected in message.')
        }
      }
    }
  }
  
  /**
   * Update moderation settings
   */
  updateModerationSettings(settings: Partial<ModerationSettings>): void {
    this.moderationSettings = { ...this.moderationSettings, ...settings }
  }
  
  /**
   * Get current moderation settings
   */
  getModerationSettings(): ModerationSettings {
    return { ...this.moderationSettings }
  }
}

export const chatService = new ChatService()
export default chatService