import React, { useState, useEffect, useCallback } from 'react'
import { createPortal } from 'react-dom'
import { useWallet } from '../contexts/WalletContext'
import { useNotifications } from '../hooks/useNotifications'
import { rwaxService, type UserAsset, type FeeToken } from '../services/rwaxService'
import MediaRenderer from './MediaRenderer'
import { log } from '../services/loggingService'

interface TokenizeFromInventoryModalProps {
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
}

const TokenizeFromInventoryModal: React.FC<TokenizeFromInventoryModalProps> = ({
  isOpen,
  onClose,
  onSuccess
}) => {
  const { account } = useWallet()
  const { showSuccess, showError } = useNotifications()
  
  const [userAssets, setUserAssets] = useState<UserAsset[]>([])
  const [feeTokens, setFeeTokens] = useState<FeeToken[]>([])
  const [selectedAssets, setSelectedAssets] = useState<Set<string>>(new Set())
  const [selectedFeeToken, setSelectedFeeToken] = useState<FeeToken | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [totalAssets, setTotalAssets] = useState(0)
  const assetsPerPage = 12

  // Load user's Pixal assets
  const loadUserAssets = useCallback(async (page: number = 1) => {
    if (!account) return
    
    setLoading(true)
    setError('')
    
    try {
      const offset = (page - 1) * assetsPerPage
      const assets = await rwaxService.getUserPixalAssets(account, assetsPerPage, offset)
      setUserAssets(assets)
      setTotalAssets(assets.length) // In a real implementation, you'd get total count from API
    } catch (error) {
      log.error('Failed to load user Pixal assets', 'TokenizeFromInventoryModal.loadUserAssets', error as Error, { account, page })
      setError('Failed to load your Pixal assets. Please try again.')
      showError('Failed to load assets')
    } finally {
      setLoading(false)
    }
  }, [account, assetsPerPage, showError])

  // Load fee tokens
  const loadFeeTokens = useCallback(async () => {
    try {
      const tokens = await rwaxService.getFeeTokens()
      setFeeTokens(tokens)
    } catch (error) {
      log.error('Failed to load fee tokens', 'TokenizeFromInventoryModal.loadFeeTokens', error as Error)
      showError('Failed to load fee tokens')
    }
  }, [showError])

  // Load data when modal opens
  useEffect(() => {
    if (isOpen && account) {
      loadUserAssets(currentPage)
      loadFeeTokens()
    }
  }, [isOpen, account, currentPage, loadUserAssets, loadFeeTokens])

  // Handle asset selection
  const toggleAssetSelection = (assetId: string) => {
    const newSelection = new Set(selectedAssets)
    if (newSelection.has(assetId)) {
      newSelection.delete(assetId)
    } else {
      newSelection.add(assetId)
    }
    setSelectedAssets(newSelection)
  }

  // Select all assets on current page
  const selectAllAssets = () => {
    const allAssetIds = new Set(userAssets.map(asset => asset.assetId.toString()))
    setSelectedAssets(allAssetIds)
  }

  // Clear all selections
  const clearAllSelections = () => {
    setSelectedAssets(new Set())
  }

  // Execute tokenization
  const executeTokenization = async () => {
    if (!account || selectedAssets.size === 0 || !selectedFeeToken) {
      showError('Please select assets and a fee token')
      return
    }

    setLoading(true)
    try {
      const assetIds = Array.from(selectedAssets)
      const transaction = await rwaxService.executeTokenization(assetIds, selectedFeeToken)
      
      showSuccess(`Tokenization transaction prepared for ${assetIds.length} asset(s)`)
      log.info('Tokenization transaction prepared', 'TokenizeFromInventoryModal.executeTokenization', { assetIds, feeToken: selectedFeeToken.symbol, transaction })
      
      // Reset selections and close modal
      setSelectedAssets(new Set())
      setSelectedFeeToken(null)
      onClose()
      
      // Call success callback
      if (onSuccess) {
        onSuccess()
      }
    } catch (error) {
      log.error('Tokenization execution failed', 'TokenizeFromInventoryModal.executeTokenization', error as Error, { assetIds: Array.from(selectedAssets), feeToken: selectedFeeToken?.symbol })
      showError('Tokenization failed. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  // Handle page change
  const handlePageChange = (page: number) => {
    setCurrentPage(page)
    setSelectedAssets(new Set()) // Clear selections when changing pages
  }

  if (!isOpen) return null

  const totalPages = Math.ceil(totalAssets / assetsPerPage)
  const selectedCount = selectedAssets.size

  return createPortal(
    <div className="modal-overlay" onClick={onClose}>
      <div className="tokenize-inventory-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Tokenize from Inventory</h2>
          <button className="close-button" onClick={onClose}>×</button>
        </div>
        
        <div className="modal-body">
          {error && (
            <div className="error-message">
              {error}
            </div>
          )}
          
          {/* Selection Controls */}
          <div className="selection-controls">
            <div className="selection-info">
              <span>{selectedCount} asset{selectedCount !== 1 ? 's' : ''} selected</span>
            </div>
            <div className="selection-actions">
              <button 
                className="select-all-btn"
                onClick={selectAllAssets}
                disabled={loading}
              >
                Select All on Page
              </button>
              <button 
                className="clear-all-btn"
                onClick={clearAllSelections}
                disabled={loading || selectedCount === 0}
              >
                Clear All
              </button>
            </div>
          </div>

          {/* Assets Grid */}
          <div className="assets-section">
            <h3>Your Pixal Assets</h3>
            {loading ? (
              <div className="loading-spinner">
                <div className="spinner"></div>
                <p>Loading your assets...</p>
              </div>
            ) : (
              <div className="assets-grid">
                {userAssets.map((asset) => (
                  <div 
                    key={asset.assetId}
                    className={`asset-card ${selectedAssets.has(asset.assetId.toString()) ? 'selected' : ''}`}
                    onClick={() => toggleAssetSelection(asset.assetId.toString())}
                  >
                    <div className="asset-image-container">
                      <MediaRenderer 
                        imageUrl={asset.image}
                        alt={asset.name}
                        className="asset-image"
                      />
                      {selectedAssets.has(asset.assetId.toString()) && (
                        <div className="selection-indicator">
                          <svg viewBox="0 0 24 24" fill="currentColor">
                            <path d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z"/>
                          </svg>
                        </div>
                      )}
                    </div>
                    <div className="asset-info">
                      <h4 className="asset-name">{asset.name}</h4>
                      <p className="asset-id">#{asset.assetId.toString()}</p>
                    </div>
                  </div>
                ))}
              </div>
            )}
            
            {/* Pagination */}
            {totalPages > 1 && (
              <div className="pagination">
                <button 
                  className="pagination-btn"
                  onClick={() => handlePageChange(currentPage - 1)}
                  disabled={currentPage === 1 || loading}
                >
                  Previous
                </button>
                <span className="pagination-info">
                  Page {currentPage} of {totalPages}
                </span>
                <button 
                  className="pagination-btn"
                  onClick={() => handlePageChange(currentPage + 1)}
                  disabled={currentPage === totalPages || loading}
                >
                  Next
                </button>
              </div>
            )}
          </div>

          {/* Fee Token Selection */}
          {selectedCount > 0 && (
            <div className="fee-selection">
              <h3>Select Fee Token</h3>
              <div className="fee-tokens-grid">
                {feeTokens.map((token, index) => (
                  <div 
                    key={index}
                    className={`fee-option ${selectedFeeToken === token ? 'selected' : ''}`}
                    onClick={() => setSelectedFeeToken(token)}
                  >
                    <div className="fee-info">
                      <span className="fee-amount">{token.fee}</span>
                      <span className="fee-contract">({token.contract})</span>
                    </div>
                    {token.wax_value && token.symbol !== 'WAX' && (
                      <div className="wax-equivalent">
                        ≈ {parseFloat(token.wax_value).toFixed(4)} WAX
                      </div>
                    )}
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
        
        <div className="modal-footer">
          <button className="cancel-button" onClick={onClose} disabled={loading}>
            Cancel
          </button>
          <button 
            className="tokenize-button"
            onClick={executeTokenization}
            disabled={loading || selectedCount === 0 || !selectedFeeToken}
          >
            {loading ? 'Processing...' : `Tokenize ${selectedCount} Asset${selectedCount !== 1 ? 's' : ''}`}
          </button>
        </div>
      </div>
    </div>,
    document.body
  )
}

export default TokenizeFromInventoryModal