import React, { useState } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import type { InventoryNFT } from '../types/inventory'
import { log } from '../services/loggingService'
import { MarketplaceConfigService } from '../config/marketplaceConfig'
import { WharfKitService } from '../services/wharfkit'
import '../css/modal.css'

interface BurnModalProps {
  nft: InventoryNFT | null
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
}

const BurnModal: React.FC<BurnModalProps> = ({ nft, isOpen, onClose, onSuccess }) => {
  const [confirmText, setConfirmText] = useState('')
  const [isBurning, setIsBurning] = useState(false)
  const [step, setStep] = useState<'warning' | 'confirm'>('warning')
  const { session } = useWallet()
  const { showError, showSuccess } = useNotifications()

  const requiredConfirmText = 'BURN'

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!nft || !session) {
      showError('Session error. Please try again.', 'Connection Error')
      return
    }

    // Check if this is a WaxRock Pixal PFP - block burning if it is
    if (MarketplaceConfigService.isPixalPFP(nft.template.template_id)) {
      const waxRock = nft.data?.WaxRock as string
      if (waxRock && MarketplaceConfigService.isValidWaxRock(waxRock)) {
        showError(
          `This is a rare WaxRock Pixal PFP (${waxRock}) and cannot be burned. WaxRock Pixals are protected from burning and recycling on our platform.`,
          'Protected NFT',
          8000
        )
        return
      }
    }

    if (confirmText.toUpperCase() !== requiredConfirmText) {
      showError(`Please type "${requiredConfirmText}" to confirm`, 'Confirmation Required')
      return
    }

    setIsBurning(true)

    try {
      // AtomicAssets burn action
      const action = {
        account: 'atomicassets',
        name: 'burnasset',
        authorization: [{
          actor: WharfKitService.safeGetActor(session),
          permission: WharfKitService.safeGetPermission(session)
        }],
        data: {
          asset_owner: WharfKitService.safeGetActor(session),
          asset_id: nft.asset_id
        }
      }

      const result = await session.transact({ actions: [action] })
      
      if (result) {
        showSuccess('NFT burned successfully!', 'Burn Complete')
        onSuccess?.()
        onClose()
        resetModal()
      }
    } catch (error) {
      log.error('Burn failed', 'BurnModal', error as Error)
      showError(`Burn failed: ${error instanceof Error ? error.message : 'Unknown error'}`, 'Burn Failed')
    } finally {
      setIsBurning(false)
    }
  }

  const resetModal = () => {
    setConfirmText('')
    setStep('warning')
  }

  const handleClose = () => {
    if (!isBurning) {
      resetModal()
      onClose()
    }
  }

  const handleProceed = () => {
    setStep('confirm')
  }

  if (!isOpen || !nft) return null

  return (
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content burn-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>🔥 Burn NFT</h2>
          <button 
            className="modal-close" 
            onClick={handleClose}
            disabled={isBurning}
          >
            ×
          </button>
        </div>

        <div className="modal-body">
          <div className="nft-preview">
            <div className="nft-image">
              {nft.video ? (
                <video 
                  src={nft.video.startsWith('http') ? nft.video : `https://ipfs.io/ipfs/${nft.video}`}
                  muted 
                  loop
                  autoPlay
                />
              ) : nft.img ? (
                <img 
                  src={nft.img.startsWith('http') ? nft.img : `https://ipfs.io/ipfs/${nft.img}`}
                  alt={nft.name}
                />
              ) : (
                <div className="no-image">No Image</div>
              )}
            </div>
            <div className="nft-info">
              <h3>{nft.name}</h3>
              <p className="nft-collection">{typeof nft.collection === 'string' ? nft.collection : nft.collection?.name || nft.collection?.collection_name || 'Unknown'}</p>
              <p className="nft-schema">{nft.schema.schema_name}</p>
              {nft.mint_number && (
                <p className="nft-mint">Mint #{nft.mint_number}</p>
              )}
            </div>
          </div>

          {step === 'warning' && (
            <div className="burn-warning-step">
              <div className="danger-warning">
                <div className="warning-icon">🚨</div>
                <div className="warning-content">
                  <h3>PERMANENT ACTION WARNING</h3>
                  <ul className="warning-list">
                    <li>This action will <strong>permanently destroy</strong> your NFT</li>
                    <li>The NFT will be <strong>completely removed</strong> from the blockchain</li>
                    <li>This action <strong>cannot be undone or reversed</strong></li>
                    <li>You will <strong>not receive any compensation</strong></li>
                    <li>The NFT will <strong>disappear forever</strong></li>
                  </ul>
                </div>
              </div>

              <div className="burn-reasons">
                <h4>Common reasons for burning NFTs:</h4>
                <ul>
                  <li>Reducing supply to increase rarity</li>
                  <li>Participating in burn-to-earn mechanics</li>
                  <li>Collection cleanup or rebranding</li>
                  <li>Accidental duplicate mints</li>
                </ul>
              </div>

              <div className="modal-actions">
                <button 
                  type="button" 
                  className="btn-secondary"
                  onClick={handleClose}
                >
                  Cancel
                </button>
                <button 
                  type="button" 
                  className="btn-danger"
                  onClick={handleProceed}
                >
                  I Understand, Proceed
                </button>
              </div>
            </div>
          )}

          {step === 'confirm' && (
            <form onSubmit={handleSubmit} className="modal-form burn-confirm-step">
              <div className="final-warning">
                <div className="warning-icon">⚠️</div>
                <p>
                  <strong>FINAL WARNING:</strong> You are about to permanently destroy this NFT. 
                  This action cannot be undone.
                </p>
              </div>

              <div className="form-group">
                <label htmlFor="confirmText">
                  Type <strong>"{requiredConfirmText}"</strong> to confirm:
                </label>
                <input
                  type="text"
                  id="confirmText"
                  value={confirmText}
                  onChange={(e) => setConfirmText(e.target.value)}
                  placeholder={`Type "${requiredConfirmText}" here`}
                  required
                  disabled={isBurning}
                  className="confirm-input"
                  autoComplete="off"
                />
              </div>

              <div className="modal-actions">
                <button 
                  type="button" 
                  className="btn-secondary"
                  onClick={() => setStep('warning')}
                  disabled={isBurning}
                >
                  Back
                </button>
                <button 
                  type="submit" 
                  className="btn-danger burn-btn"
                  disabled={isBurning || confirmText.toUpperCase() !== requiredConfirmText}
                >
                  {isBurning ? (
                    <>
                      <div className="spinner"></div>
                      Burning...
                    </>
                  ) : (
                    '🔥 Burn NFT Forever'
                  )}
                </button>
              </div>
            </form>
          )}
        </div>
      </div>
    </div>
  )
}

export default BurnModal