// Centralized error message service for consistent error handling

export interface ErrorMessageConfig {
  title: string
  message: string
  action?: string
}

export class ErrorMessageService {
  // Standard error messages for common scenarios
  static readonly MESSAGES = {
    // Network and API errors
    NETWORK_ERROR: {
      title: 'Connection Error',
      message: 'Unable to connect to the server. Please check your internet connection and try again.',
      action: 'Retry'
    },
    TIMEOUT_ERROR: {
      title: 'Request Timeout',
      message: 'The request took too long to complete. Please try again.',
      action: 'Retry'
    },
    API_ERROR: {
      title: 'Service Unavailable',
      message: 'The service is temporarily unavailable. Please try again in a moment.',
      action: 'Retry'
    },
    LOAD_FAILED: {
      title: 'Loading Failed',
      message: 'Failed to load data. Please refresh the page or try again later.',
      action: 'Refresh'
    },
    
    // Authentication and wallet errors
    WALLET_CONNECTION_FAILED: {
      title: 'Wallet Connection Failed',
      message: 'Unable to connect to your wallet. Please ensure your wallet is unlocked and try again.',
      action: 'Retry'
    },
    WALLET_DISCONNECTED: {
      title: 'Wallet Disconnected',
      message: 'Your wallet has been disconnected. Please reconnect to continue.',
      action: 'Connect Wallet'
    },
    INSUFFICIENT_BALANCE: {
      title: 'Insufficient Balance',
      message: 'You do not have enough balance to complete this transaction.',
      action: 'Add Funds'
    },
    
    // Transaction errors
    TRANSACTION_FAILED: {
      title: 'Transaction Failed',
      message: 'The transaction could not be completed. Please try again.',
      action: 'Retry'
    },
    TRANSACTION_REJECTED: {
      title: 'Transaction Rejected',
      message: 'The transaction was rejected. Please try again if this was unintentional.',
      action: 'Retry'
    },
    
    // Data validation errors
    INVALID_INPUT: {
      title: 'Invalid Input',
      message: 'Please check your input and try again.',
      action: 'Correct Input'
    },
    REQUIRED_FIELD: {
      title: 'Required Field Missing',
      message: 'Please fill in all required fields.',
      action: 'Complete Form'
    },
    
    // NFT and marketplace specific errors
    NFT_NOT_FOUND: {
      title: 'NFT Not Found',
      message: 'The requested NFT could not be found or may no longer be available.',
      action: 'Go Back'
    },
    COLLECTION_NOT_FOUND: {
      title: 'Collection Not Found',
      message: 'The requested collection could not be found or may not exist.',
      action: 'Browse Collections'
    },
    PURCHASE_FAILED: {
      title: 'Purchase Failed',
      message: 'Unable to complete the purchase. The item may no longer be available.',
      action: 'Refresh Listings'
    },
    LISTING_FAILED: {
      title: 'Listing Failed',
      message: 'Unable to list your NFT. Please check your wallet and try again.',
      action: 'Retry'
    },
    
    // Generic fallback
    UNKNOWN_ERROR: {
      title: 'Something Went Wrong',
      message: 'An unexpected error occurred. Please try again or contact support if the problem persists.',
      action: 'Retry'
    }
  } as const
  
  /**
   * Get a standard error message configuration
   */
  static getMessage(key: keyof typeof ErrorMessageService.MESSAGES): ErrorMessageConfig {
    return ErrorMessageService.MESSAGES[key]
  }
  
  /**
   * Create a custom error message with consistent formatting
   */
  static createMessage(title: string, message: string, action?: string): ErrorMessageConfig {
    return { title, message, action }
  }
  
  /**
   * Format an error object into a user-friendly message
   */
  static formatError(error: unknown, fallbackMessage?: ErrorMessageConfig): ErrorMessageConfig {
    if (error instanceof Error) {
      // Check for specific error patterns
      const message = error.message.toLowerCase()
      const errorName = error.name.toLowerCase()
      
      // Network and connection errors
      if (message.includes('network') || message.includes('fetch') || message.includes('connection')) {
        return ErrorMessageService.MESSAGES.NETWORK_ERROR
      }
      
      // Timeout errors
      if (message.includes('timeout') || message.includes('aborted') || errorName.includes('timeout')) {
        return ErrorMessageService.MESSAGES.TIMEOUT_ERROR
      }
      
      // Transaction errors
      if (message.includes('rejected') || message.includes('cancelled')) {
        return ErrorMessageService.MESSAGES.TRANSACTION_REJECTED
      }
      
      // Balance errors
      if (message.includes('insufficient')) {
        return ErrorMessageService.MESSAGES.INSUFFICIENT_BALANCE
      }
      
      // Not found errors
      if (message.includes('not found')) {
        return ErrorMessageService.MESSAGES.NFT_NOT_FOUND
      }
      
      // Chunk loading errors (code splitting)
      if (errorName === 'chunkloaderror') {
        return ErrorMessageService.MESSAGES.LOAD_FAILED
      }
      
      // API and HTTP errors
      if (message.includes('api') || message.includes('http') || message.includes('server')) {
        return ErrorMessageService.MESSAGES.API_ERROR
      }
      
      // Return custom message with error details for development
      if (import.meta.env?.DEV || window.location.hostname === 'localhost') {
        return ErrorMessageService.createMessage(
          `Error: ${error.name}`,
          error.message,
          'Retry'
        )
      }
      
      // Generic error message for production
      return ErrorMessageService.createMessage(
        'Error',
        'An error occurred while processing your request.',
        'Retry'
      )
    }
    
    return fallbackMessage || ErrorMessageService.MESSAGES.UNKNOWN_ERROR
  }
  
  /**
   * Log error consistently across the application
   * @deprecated Use log.error from loggingService instead
   */
  static logError(context: string, error: unknown, additionalInfo?: Record<string, unknown>): void {
    // Use dynamic import to avoid circular dependencies
    import('./loggingService').then(({ log }) => {
      const errorInstance = error instanceof Error ? error : new Error(String(error))
      log.error(`Error in ${context}`, context, errorInstance, additionalInfo)
    }).catch(() => {
      // Fallback to console if logging service is not available
      console.error(`Error in ${context}:`, error, additionalInfo)
    })
  }
}

// Export commonly used error messages for convenience
export const ERROR_MESSAGES = ErrorMessageService.MESSAGES
export const formatError = ErrorMessageService.formatError
export const logError = ErrorMessageService.logError