/**
 * Utility functions for formatting numbers, prices, and decimals
 */

/**
 * Formats a number with dynamic decimal places
 * - Shows up to 8 decimal places for precision
 * - Removes trailing zeros
 * - Handles very small numbers appropriately
 * @param value - The number to format
 * @param maxDecimals - Maximum decimal places (default: 8)
 * @param minDecimals - Minimum decimal places (default: 0)
 * @returns Formatted number string
 */
export function formatDynamicDecimals(
  value: number | string,
  maxDecimals: number = 8,
  minDecimals: number = 0
): string {
  const num = typeof value === 'string' ? parseFloat(value) : value
  
  if (isNaN(num)) return '0'
  
  // Handle zero
  if (num === 0) return minDecimals > 0 ? '0.' + '0'.repeat(minDecimals) : '0'
  
  // For very small numbers, use scientific notation if needed
  if (Math.abs(num) < Math.pow(10, -maxDecimals)) {
    return num.toExponential(2)
  }
  
  // Format with maximum decimals, then remove trailing zeros
  let formatted = num.toFixed(maxDecimals)
  
  // Remove trailing zeros after decimal point
  if (formatted.includes('.')) {
    formatted = formatted.replace(/\.?0+$/, '')
  }
  
  // Ensure minimum decimal places
  if (minDecimals > 0) {
    const decimalIndex = formatted.indexOf('.')
    if (decimalIndex === -1) {
      formatted += '.' + '0'.repeat(minDecimals)
    } else {
      const currentDecimals = formatted.length - decimalIndex - 1
      if (currentDecimals < minDecimals) {
        formatted += '0'.repeat(minDecimals - currentDecimals)
      }
    }
  }
  
  return formatted
}

/**
 * Formats a WAX price with appropriate decimal precision
 * @param price - Price in WAX (string or number)
 * @param showCurrency - Whether to append ' WAX' (default: true)
 * @returns Formatted price string
 */
export function formatWaxPrice(price: string | number, showCurrency: boolean = true): string {
  const numPrice = typeof price === 'string' ? parseFloat(price.replace(' WAX', '')) : price
  
  if (isNaN(numPrice)) return showCurrency ? '0 WAX' : '0'
  
  let formatted: string
  
  if (numPrice >= 1000) {
    // For large amounts, show 2-4 decimals max
    formatted = formatDynamicDecimals(numPrice, 4, 0)
  } else if (numPrice >= 1) {
    // For amounts >= 1, show up to 4 decimals
    formatted = formatDynamicDecimals(numPrice, 4, 0)
  } else if (numPrice >= 0.01) {
    // For amounts >= 0.01, show up to 6 decimals
    formatted = formatDynamicDecimals(numPrice, 6, 0)
  } else {
    // For very small amounts, show up to 8 decimals
    formatted = formatDynamicDecimals(numPrice, 8, 0)
  }
  
  return showCurrency ? `${formatted} WAX` : formatted
}

/**
 * Formats a number with thousands separators
 * @param value - The number to format
 * @param decimals - Number of decimal places (optional)
 * @returns Formatted number with commas
 */
export function formatNumberWithCommas(value: number | string, decimals?: number): string {
  const num = typeof value === 'string' ? parseFloat(value) : value
  
  if (isNaN(num)) return '0'
  
  const options: Intl.NumberFormatOptions = {
    minimumFractionDigits: 0,
    maximumFractionDigits: decimals ?? 8
  }
  
  return num.toLocaleString('en-US', options)
}

/**
 * Formats a percentage with appropriate decimal places
 * @param value - The percentage value (0-100)
 * @param maxDecimals - Maximum decimal places (default: 2)
 * @returns Formatted percentage string
 */
export function formatPercentage(value: number, maxDecimals: number = 2): string {
  if (isNaN(value)) return '0%'
  
  const formatted = formatDynamicDecimals(value, maxDecimals, 0)
  return `${formatted}%`
}

/**
 * Formats a large number with appropriate units (K, M, B)
 * @param value - The number to format
 * @param decimals - Number of decimal places (default: 1)
 * @returns Formatted number with unit
 */
export function formatLargeNumber(value: number, decimals: number = 1): string {
  if (isNaN(value)) return '0'
  
  const absValue = Math.abs(value)
  const sign = value < 0 ? '-' : ''
  
  if (absValue >= 1e9) {
    return `${sign}${formatDynamicDecimals(absValue / 1e9, decimals, 0)}B`
  } else if (absValue >= 1e6) {
    return `${sign}${formatDynamicDecimals(absValue / 1e6, decimals, 0)}M`
  } else if (absValue >= 1e3) {
    return `${sign}${formatDynamicDecimals(absValue / 1e3, decimals, 0)}K`
  } else {
    return `${sign}${formatDynamicDecimals(absValue, decimals, 0)}`
  }
}

/**
 * Parses a price string and extracts the numeric value
 * @param priceString - Price string (e.g., "1.2345 WAX")
 * @returns Numeric price value
 */
export function parsePriceString(priceString: string): number {
  if (!priceString) return 0
  
  // Remove currency symbols and extra whitespace
  const cleaned = priceString.replace(/[^0-9.-]/g, '')
  const parsed = parseFloat(cleaned)
  
  return isNaN(parsed) ? 0 : parsed
}

/**
 * Validates if a string represents a valid decimal number
 * @param value - String to validate
 * @param maxDecimals - Maximum allowed decimal places
 * @returns True if valid decimal
 */
export function isValidDecimal(value: string, maxDecimals: number = 8): boolean {
  if (!value || value.trim() === '') return false
  
  const regex = new RegExp(`^\\d+(\\.\\d{1,${maxDecimals}})?$`)
  return regex.test(value.trim())
}

/**
 * Rounds a number to specified decimal places without trailing zeros
 * @param value - Number to round
 * @param decimals - Decimal places
 * @returns Rounded number
 */
export function roundToDecimals(value: number, decimals: number): number {
  const factor = Math.pow(10, decimals)
  return Math.round(value * factor) / factor
}

// Aliases for consistent naming
export const formatWAXPrice = formatWaxPrice
export const formatNumber = formatDynamicDecimals