import React, { useState } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import { wharfKitService, WharfKitService } from '../services/wharfkit'
import { apiService } from '../services/api'
import { log } from '../services/loggingService'
import '../css/collection-tier-modal.css'

interface TierOption {
  id: string
  name: string
  cost: number
  costFormatted: string
  icon: string
  benefits: string[]
  description: string
}

interface CollectionTierModalProps {
  isOpen: boolean
  onClose: () => void
}

const TIER_OPTIONS: TierOption[] = [
  {
    id: 'bronze',
    name: 'Bronze',
    cost: 5000000,
    costFormatted: '5M PXJ',
    icon: '🥉',
    description: 'Perfect for new collections getting started',
    benefits: [
      'Basic marketplace listing',
      'Collection verification badge',
      'Community Discord access',
      'Monthly featured spot rotation'
    ]
  },
  {
    id: 'silver',
    name: 'Silver',
    cost: 15000000,
    costFormatted: '15M PXJ',
    icon: '🥈',
    description: 'Enhanced visibility and analytics access',
    benefits: [
      'All Bronze benefits',
      'Priority marketplace placement',
      'Weekly newsletter mentions',
      'Custom collection banner',
      'Analytics dashboard access'
    ]
  },
  {
    id: 'gold',
    name: 'Gold',
    cost: 30000000,
    costFormatted: '30M PXJ',
    icon: '🥇',
    description: 'Premium promotion and exclusive events',
    benefits: [
      'All Silver benefits',
      'Homepage featured carousel',
      'Social media promotion',
      'Exclusive events participation',
      'Direct community manager contact'
    ]
  },
  {
    id: 'platinum',
    name: 'Platinum',
    cost: 50000000,
    costFormatted: '50M PXJ',
    icon: '💎',
    description: 'Ultimate tier with maximum benefits',
    benefits: [
      'All Gold benefits',
      'Permanent homepage presence',
      'Dedicated marketing campaigns',
      'WaxRock Council consideration',
      'Platform governance participation',
      'White-label marketplace options'
    ]
  }
]

const CollectionTierModal: React.FC<CollectionTierModalProps> = ({ isOpen, onClose }) => {
  const [selectedTier, setSelectedTier] = useState<string>('')
  const [collectionName, setCollectionName] = useState('')
  const [isProcessing, setIsProcessing] = useState(false)
  const [userBalance, setUserBalance] = useState<number | null>(null)
  const [balanceLoading, setBalanceLoading] = useState(false)
  
  const { session, isConnected } = useWallet()
  const { showSuccess, showError } = useNotifications()

  // Fetch user's PXJ balance when modal opens
  React.useEffect(() => {
    if (isOpen && isConnected && session) {
      fetchUserBalance()
    }
  }, [isOpen, isConnected, session])

  const fetchUserBalance = async () => {
    if (!session) return
    
    setBalanceLoading(true)
    try {
      const pxjBalance = await apiService.getWaxCurrencyBalance(
        WharfKitService.safeGetActor(session), 
        'token.pxj', 
        'PXJ'
      )
      const balance = pxjBalance.length > 0 ? parseFloat(pxjBalance[0].balance.split(' ')[0]) : 0
      setUserBalance(balance)
    } catch (error) {
      log.error('Failed to fetch PXJ balance', 'CollectionTierModal', error as Error)
      setUserBalance(0)
    } finally {
      setBalanceLoading(false)
    }
  }

  const handleTierPayment = async () => {
    if (!selectedTier || !collectionName.trim()) {
      showError('Please select a tier and enter a collection name')
      return
    }

    if (!isConnected || !session) {
      showError('Please connect your wallet to proceed with payment')
      return
    }

    const tier = TIER_OPTIONS.find(t => t.id === selectedTier)
    if (!tier) {
      showError('Invalid tier selected')
      return
    }

    if (userBalance !== null && userBalance < tier.cost) {
      showError(`Insufficient PXJ balance. You need at least ${tier.costFormatted} to upgrade to ${tier.name} tier.`)
      return
    }

    setIsProcessing(true)
    
    try {
      const memo = `upgrade-collection:${collectionName.toUpperCase()}:${tier.id.toUpperCase()}`
      
      const transferAction = {
        account: 'token.pxj',
        name: 'transfer',
        authorization: [{
          actor: WharfKitService.safeGetActor(session),
          permission: 'active'
        }],
        data: {
          from: WharfKitService.safeGetActor(session),
          to: 'market.pxj',
          quantity: `${tier.cost.toFixed(8)} PXJ`,
          memo: memo
        }
      }

      await wharfKitService.transact([transferAction])
      
      showSuccess(`Successfully upgraded ${collectionName} to ${tier.name} tier! Your upgrade will be processed by our team.`)
      onClose()
      setSelectedTier('')
      setCollectionName('')
      
    } catch (error) {
      log.error('Tier upgrade payment failed', 'CollectionTierModal', error instanceof Error ? error : new Error(String(error)))
      showError(`Payment failed: ${error instanceof Error ? error.message : 'Unknown error'}`)
    } finally {
      setIsProcessing(false)
    }
  }

  const selectedTierData = TIER_OPTIONS.find(t => t.id === selectedTier)
  const canAfford = (tier: TierOption) => userBalance !== null && userBalance >= tier.cost

  if (!isOpen) return null

  return (
    <div className="modal-backdrop" onClick={onClose}>
      <div className="modal-content collection-tier-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2 className="modal-title">Upgrade Collection Tier</h2>
          <button className="modal-close" onClick={onClose}>
            <svg viewBox="0 0 24 24" fill="currentColor">
              <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/>
            </svg>
          </button>
        </div>

        <div className="modal-body">
          {/* Balance Display */}
          <div className="balance-section">
            <div className="balance-info">
              <span className="balance-label">Your PXJ Balance:</span>
              <span className="collection-balance-amount">
                {balanceLoading ? (
                  <span className="loading-text">Loading...</span>
                ) : (
                  <span className={userBalance !== null && userBalance > 0 ? 'balance-positive' : 'balance-zero'}>
                    {userBalance !== null ? `${userBalance.toLocaleString()} PXJ` : 'Unable to load'}
                  </span>
                )}
              </span>
            </div>
          </div>

          {/* Collection Name Input */}
          <div className="form-group">
            <label htmlFor="collectionName">Collection Name:</label>
            <input
              type="text"
              id="collectionName"
              value={collectionName}
              onChange={(e) => setCollectionName(e.target.value)}
              placeholder="Enter your collection name"
              className="collection-input"
              disabled={isProcessing}
            />
          </div>

          {/* Tier Selection */}
          <div className="tier-selection">
            <h3>Select Tier:</h3>
            <div className="tier-grid">
              {TIER_OPTIONS.map((tier) => {
                const affordable = canAfford(tier)
                const isSelected = selectedTier === tier.id
                
                return (
                  <div
                    key={tier.id}
                    className={`tier-card ${
                      isSelected ? 'selected' : ''
                    } ${
                      !affordable ? 'unaffordable' : ''
                    }`}
                    onClick={() => !isProcessing && setSelectedTier(tier.id)}
                  >
                    <div className="tier-header">
                      <span className="tier-icon">{tier.icon}</span>
                      <div className="tier-info">
                        <h4 className="tier-name">{tier.name}</h4>
                        <div className="tier-cost">{tier.costFormatted}</div>
                      </div>
                      {!affordable && (
                        <div className="insufficient-badge">Insufficient Balance</div>
                      )}
                    </div>
                    <p className="tier-description">{tier.description}</p>
                    <div className="tier-benefits">
                      <h5>Benefits:</h5>
                      <ul>
                        {tier.benefits.map((benefit, index) => (
                          <li key={index}>{benefit}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                )
              })}
            </div>
          </div>

          {/* Selected Tier Summary */}
          {selectedTierData && (
            <div className="selected-tier-summary">
              <h4>Upgrade Summary</h4>
              <div className="summary-content">
                <div className="summary-item">
                  <span>Collection:</span>
                  <span>{collectionName || 'Not specified'}</span>
                </div>
                <div className="summary-item">
                  <span>Tier:</span>
                  <span>{selectedTierData.icon} {selectedTierData.name}</span>
                </div>
                <div className="summary-item">
                  <span>Cost:</span>
                  <span>{selectedTierData.costFormatted}</span>
                </div>
                <div className="summary-item">
                  <span>Remaining Balance:</span>
                  <span>
                    {userBalance !== null 
                      ? `${(userBalance - selectedTierData.cost).toLocaleString()} PXJ`
                      : 'Unknown'
                    }
                  </span>
                </div>
              </div>
            </div>
          )}
        </div>

        <div className="modal-footer">
          <button 
            className="btn-secondary"
            onClick={onClose}
            disabled={isProcessing}
          >
            Cancel
          </button>
          <button 
            className="btn-primary"
            onClick={handleTierPayment}
            disabled={
              isProcessing || 
              !selectedTier || 
              !collectionName.trim() || 
              !isConnected ||
              (selectedTierData && userBalance !== null && userBalance < selectedTierData.cost)
            }
          >
            {isProcessing ? (
              <>
                <span className="loading-spinner"></span>
                Processing...
              </>
            ) : (
              `Upgrade to ${selectedTierData?.name || 'Selected Tier'}`
            )}
          </button>
        </div>
      </div>
    </div>
  )
}

export default CollectionTierModal