import React, { useState, useEffect, useRef, useCallback } from 'react'
import '../css/advanced-search.css'

interface SearchSuggestion {
  text: string
  type: 'name' | 'collection' | 'schema' | 'history'
  count?: number
}

interface AdvancedSearchProps {
  searchTerm: string
  onSearchChange: (term: string) => void
  suggestions?: SearchSuggestion[]
  placeholder?: string
  showFilters?: boolean
  onFilterChange?: (filters: SearchFilters) => void
  className?: string
}

interface SearchFilters {
  priceRange: { min: string; max: string }
  rarities: string[]
  collections: string[]
  schemas: string[]
  sortBy: string
}

const SEARCH_HISTORY_KEY = 'pixeljourney_search_history'
const MAX_HISTORY_ITEMS = 10

const AdvancedSearch: React.FC<AdvancedSearchProps> = ({
  searchTerm,
  onSearchChange,
  suggestions = [],
  placeholder = "Search NFTs, collections, schemas...",
  showFilters = false,
  onFilterChange,
  className = ''
}) => {
  const [showSuggestions, setShowSuggestions] = useState(false)
  const [searchHistory, setSearchHistory] = useState<string[]>([])
  const [filters, setFilters] = useState<SearchFilters>({
    priceRange: { min: '', max: '' },
    rarities: [],
    collections: [],
    schemas: [],
    sortBy: 'relevance'
  })
  const [showAdvancedFilters, setShowAdvancedFilters] = useState(false)
  
  const searchInputRef = useRef<HTMLInputElement>(null)
  const suggestionsRef = useRef<HTMLDivElement>(null)
  const [selectedSuggestionIndex, setSelectedSuggestionIndex] = useState(-1)

  // Load search history from localStorage
  useEffect(() => {
    const savedHistory = localStorage.getItem(SEARCH_HISTORY_KEY)
    if (savedHistory) {
      try {
        setSearchHistory(JSON.parse(savedHistory))
      } catch (error) {
        console.error('Failed to parse search history:', error)
      }
    }
  }, [])

  // Save search to history
  const saveToHistory = useCallback((term: string) => {
    if (!term.trim() || term.length < 2) return
    
    const newHistory = [term, ...searchHistory.filter(item => item !== term)]
      .slice(0, MAX_HISTORY_ITEMS)
    
    setSearchHistory(newHistory)
    localStorage.setItem(SEARCH_HISTORY_KEY, JSON.stringify(newHistory))
  }, [searchHistory])

  // Clear search history
  const clearHistory = useCallback(() => {
    setSearchHistory([])
    localStorage.removeItem(SEARCH_HISTORY_KEY)
  }, [])

  // Handle search input change
  const handleSearchChange = useCallback((value: string) => {
    onSearchChange(value)
    setSelectedSuggestionIndex(-1)
    
    if (value.length >= 2) {
      setShowSuggestions(true)
    } else {
      setShowSuggestions(false)
    }
  }, [onSearchChange])

  // Handle search submit
  const handleSearchSubmit = useCallback(() => {
    if (searchTerm.trim()) {
      saveToHistory(searchTerm.trim())
      setShowSuggestions(false)
      searchInputRef.current?.blur()
    }
  }, [searchTerm, saveToHistory])

  // Handle suggestion click
  const handleSuggestionClick = useCallback((suggestion: SearchSuggestion) => {
    handleSearchChange(suggestion.text)
    saveToHistory(suggestion.text)
    setShowSuggestions(false)
  }, [handleSearchChange, saveToHistory])

  // Handle keyboard navigation
  const handleKeyDown = useCallback((e: React.KeyboardEvent) => {
    if (!showSuggestions) return

    const allSuggestions = [
      ...searchHistory.map(item => ({ text: item, type: 'history' as const })),
      ...suggestions
    ]

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault()
        setSelectedSuggestionIndex(prev => 
          prev < allSuggestions.length - 1 ? prev + 1 : prev
        )
        break
      case 'ArrowUp':
        e.preventDefault()
        setSelectedSuggestionIndex(prev => prev > -1 ? prev - 1 : -1)
        break
      case 'Enter':
        e.preventDefault()
        if (selectedSuggestionIndex >= 0 && allSuggestions[selectedSuggestionIndex]) {
          handleSuggestionClick(allSuggestions[selectedSuggestionIndex])
        } else {
          handleSearchSubmit()
        }
        break
      case 'Escape':
        setShowSuggestions(false)
        setSelectedSuggestionIndex(-1)
        searchInputRef.current?.blur()
        break
    }
  }, [showSuggestions, searchHistory, suggestions, selectedSuggestionIndex, handleSuggestionClick, handleSearchSubmit])

  // Handle filter changes
  const handleFilterChange = useCallback((newFilters: Partial<SearchFilters>) => {
    const updatedFilters = { ...filters, ...newFilters }
    setFilters(updatedFilters)
    onFilterChange?.(updatedFilters)
  }, [filters, onFilterChange])

  // Close suggestions when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (suggestionsRef.current && !suggestionsRef.current.contains(event.target as Node) &&
          searchInputRef.current && !searchInputRef.current.contains(event.target as Node)) {
        setShowSuggestions(false)
        setSelectedSuggestionIndex(-1)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  const allSuggestions = [
    ...searchHistory.map(item => ({ text: item, type: 'history' as const })),
    ...suggestions
  ]

  return (
    <div className={`advanced-search ${className}`}>
      <div className="search-container">
        <div className="search-input-wrapper">
          <svg className="search-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="m21 21-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
          </svg>
          
          <input
            ref={searchInputRef}
            type="text"
            value={searchTerm}
            onChange={(e) => handleSearchChange(e.target.value)}
            onKeyDown={handleKeyDown}
            onFocus={() => {
              if (searchTerm.length >= 2 || searchHistory.length > 0) {
                setShowSuggestions(true)
              }
            }}
            placeholder={placeholder}
            className="search-input"
            aria-label="Search for NFTs, collections, or schemas"
            aria-describedby={showSuggestions ? 'search-suggestions' : undefined}
            aria-expanded={showSuggestions}
            aria-autocomplete="list"
            role="combobox"
          />
          
          {searchTerm && (
            <button
              className="clear-search"
              onClick={() => {
                handleSearchChange('')
                setShowSuggestions(false)
              }}
              title="Clear search"
              aria-label="Clear search input"
              type="button"
            >
              <span aria-hidden="true">✕</span>
            </button>
          )}
          
          {showFilters && (
            <button
              className={`filter-toggle ${showAdvancedFilters ? 'active' : ''}`}
              onClick={() => setShowAdvancedFilters(!showAdvancedFilters)}
              title="Advanced filters"
              aria-label={showAdvancedFilters ? 'Hide advanced filters' : 'Show advanced filters'}
              aria-expanded={showAdvancedFilters}
              type="button"
            >
              <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" aria-hidden="true">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2.586a1 1 0 01-.293.707l-6.414 6.414a1 1 0 00-.293.707V17l-4 4v-6.586a1 1 0 00-.293-.707L3.293 7.293A1 1 0 013 6.586V4z" />
              </svg>
            </button>
          )}
        </div>

        {/* Search Suggestions */}
        {showSuggestions && allSuggestions.length > 0 && (
          <div 
            ref={suggestionsRef} 
            className="search-suggestions"
            id="search-suggestions"
            role="listbox"
            aria-label="Search suggestions"
          >
            {searchHistory.length > 0 && (
              <div className="suggestions-section">
                <div className="suggestions-header">
                  <span id="recent-searches-label">Recent searches</span>
                  <button 
                    onClick={clearHistory} 
                    className="clear-history"
                    aria-label="Clear search history"
                    type="button"
                  >
                    Clear
                  </button>
                </div>
                <div role="group" aria-labelledby="recent-searches-label">
                  {searchHistory.map((item, index) => (
                    <div
                      key={`history-${index}`}
                      className={`suggestion-item history ${selectedSuggestionIndex === index ? 'selected' : ''}`}
                      onClick={() => handleSuggestionClick({ text: item, type: 'history' })}
                      role="option"
                      aria-selected={selectedSuggestionIndex === index}
                      tabIndex={-1}
                    >
                      <svg className="suggestion-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24" aria-hidden="true">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                      </svg>
                      <span>{item}</span>
                    </div>
                  ))}
                </div>
              </div>
            )}
            
            {suggestions.length > 0 && (
              <div className="suggestions-section">
                {searchHistory.length > 0 && <div className="suggestions-divider" />}
                <div className="suggestions-header">
                  <span>Suggestions</span>
                </div>
                {suggestions.map((suggestion, index) => {
                  const actualIndex = searchHistory.length + index
                  return (
                    <div
                      key={`suggestion-${index}`}
                      className={`suggestion-item ${suggestion.type} ${selectedSuggestionIndex === actualIndex ? 'selected' : ''}`}
                      onClick={() => handleSuggestionClick(suggestion)}
                    >
                      <svg className="suggestion-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        {suggestion.type === 'name' && (
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 7h.01M7 3h5c.512 0 1.024.195 1.414.586l7 7a2 2 0 010 2.828l-7 7a2 2 0 01-2.828 0l-7-7A1.994 1.994 0 013 12V7a4 4 0 014-4z" />
                        )}
                        {suggestion.type === 'collection' && (
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
                        )}
                        {suggestion.type === 'schema' && (
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                        )}
                      </svg>
                      <span>{suggestion.text}</span>
                      {suggestion.count && (
                        <span className="suggestion-count">{suggestion.count}</span>
                      )}
                    </div>
                  )
                })}
              </div>
            )}
          </div>
        )}
      </div>

      {/* Advanced Filters */}
      {showFilters && showAdvancedFilters && (
        <div className="advanced-filters">
          <div className="filters-grid">
            {/* Price Range */}
            <div className="filter-group">
              <label className="filter-label">Price Range (WAX)</label>
              <div className="price-range">
                <input
                  type="number"
                  placeholder="Min"
                  value={filters.priceRange.min}
                  onChange={(e) => handleFilterChange({
                    priceRange: { ...filters.priceRange, min: e.target.value }
                  })}
                  className="price-input"
                />
                <span className="price-separator">to</span>
                <input
                  type="number"
                  placeholder="Max"
                  value={filters.priceRange.max}
                  onChange={(e) => handleFilterChange({
                    priceRange: { ...filters.priceRange, max: e.target.value }
                  })}
                  className="price-input"
                />
              </div>
            </div>

            {/* Sort By */}
            <div className="filter-group">
              <label className="filter-label">Sort By</label>
              <select
                value={filters.sortBy}
                onChange={(e) => handleFilterChange({ sortBy: e.target.value })}
                className="filter-select"
              >
                <option value="relevance">Relevance</option>
                <option value="price_asc">Price: Low to High</option>
                <option value="price_desc">Price: High to Low</option>
                <option value="name_asc">Name: A to Z</option>
                <option value="name_desc">Name: Z to A</option>
                <option value="newest">Newest First</option>
                <option value="oldest">Oldest First</option>
              </select>
            </div>
          </div>

          <div className="filter-actions">
            <button
              onClick={() => {
                const resetFilters = {
                  priceRange: { min: '', max: '' },
                  rarities: [],
                  collections: [],
                  schemas: [],
                  sortBy: 'relevance'
                }
                setFilters(resetFilters)
                onFilterChange?.(resetFilters)
              }}
              className="filter-reset"
            >
              Reset Filters
            </button>
          </div>
        </div>
      )}
    </div>
  )
}

export default AdvancedSearch
export type { SearchSuggestion, SearchFilters }