import React, { useState, useEffect } from 'react'
import type { NFTAsset } from '../services/api'
import { priceService } from '../services/priceService'
import { watchlistService } from '../services/watchlistService'

import { log } from '../services/loggingService'
import '../css/watchlist.css'

interface WatchlistItem {
  nft: NFTAsset
  targetPrice?: number
  alertType: 'below' | 'above' | 'any'
  addedAt: number
  lastPrice: number
  priceChange?: number
}

const Watchlist: React.FC = () => {
  const [watchlistItems, setWatchlistItems] = useState<WatchlistItem[]>([])
  const [loading, setLoading] = useState(true)
  const [usdPrices, setUsdPrices] = useState<Record<string, string>>({})
  const [showAddAlert, setShowAddAlert] = useState<string | null>(null)
  const [alertPrice, setAlertPrice] = useState('')
  const [alertType, setAlertType] = useState<'below' | 'above' | 'any'>('below')

  useEffect(() => {
    loadWatchlist()
  }, [])

  useEffect(() => {
    const loadUsdPrices = async () => {
      const prices: Record<string, string> = {}
      for (const item of watchlistItems) {
        if (item.nft.price) {
          prices[item.nft.asset_id] = await priceService.convertWaxToUSDAsync(item.nft.price)
        }
      }
      setUsdPrices(prices)
    }
    
    if (watchlistItems.length > 0) {
      loadUsdPrices()
    }
  }, [watchlistItems])

  const loadWatchlist = async () => {
    try {
      setLoading(true)
      const items = await watchlistService.getWatchlist()
      setWatchlistItems(items)
    } catch (error) {
      log.error('Error loading watchlist', 'Watchlist', error as Error)
    } finally {
      setLoading(false)
    }
  }

  const removeFromWatchlist = async (assetId: string) => {
    try {
      await watchlistService.removeFromWatchlist(assetId)
      setWatchlistItems(prev => prev.filter(item => item.nft.asset_id !== assetId))
    } catch (error) {
      log.error('Error removing from watchlist', 'Watchlist', error as Error)
    }
  }

  const addPriceAlert = async (assetId: string) => {
    if (!alertPrice) return
    
    try {
      await watchlistService.setPriceAlert(assetId, {
        targetPrice: parseFloat(alertPrice),
        alertType,
        enabled: true
      })
      
      // Update local state
      setWatchlistItems(prev => prev.map(item => 
        item.nft.asset_id === assetId 
          ? { ...item, targetPrice: parseFloat(alertPrice), alertType }
          : item
      ))
      
      setShowAddAlert(null)
      setAlertPrice('')
    } catch (error) {
      log.error('Error adding price alert', 'Watchlist', error as Error)
    }
  }

  const removePriceAlert = async (assetId: string) => {
    try {
      await watchlistService.removePriceAlert(assetId)
      setWatchlistItems(prev => prev.map(item => 
        item.nft.asset_id === assetId 
          ? { ...item, targetPrice: undefined, alertType: 'any' }
          : item
      ))
    } catch (error) {
      log.error('Error removing price alert', 'Watchlist', error as Error)
    }
  }

  const getPriceChangeColor = (change?: number) => {
    if (!change) return 'var(--text-secondary)'
    return change > 0 ? 'var(--success-color)' : 'var(--error-color)'
  }

  const getPriceChangeIcon = (change?: number) => {
    if (!change) return ''
    return change > 0 ? '↗' : '↘'
  }

  if (loading) {
    return (
      <div className="watchlist-container">
        <div className="watchlist-loading">
          <div className="loading-spinner"></div>
          <p>Loading your watchlist...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="watchlist-container">
      <div className="watchlist-header">
        <h1>My Watchlist</h1>
        <p className="watchlist-subtitle">
          Track your favorite NFTs and get notified when prices change
        </p>
      </div>

      {watchlistItems.length === 0 ? (
        <div className="watchlist-empty">
          <div className="empty-icon">👁️</div>
          <h3>Your watchlist is empty</h3>
          <p>Add NFTs to your watchlist from the marketplace to track their prices</p>
          <a href="/marketplace" className="btn-primary">
            Browse Marketplace
          </a>
        </div>
      ) : (
        <div className="watchlist-grid">
          {watchlistItems.map((item) => (
            <div key={item.nft.asset_id} className="watchlist-card">
              <div className="watchlist-card-image">
                <img 
                  src={item.nft.image} 
                  alt={item.nft.name}
                  onError={(e) => {
                    const target = e.target as HTMLImageElement
                    target.src = 'https://pixeljourney.xyz/img/toppixals/10.webp'
                  }}
                />
                {item.targetPrice && (
                  <div className="alert-badge">
                    <span className="alert-icon">🔔</span>
                  </div>
                )}
              </div>
              
              <div className="watchlist-card-content">
                <h3 className="nft-name">{item.nft.name}</h3>
                <p className="nft-collection">{item.nft.collection}</p>
                
                <div className="price-info">
                  <div className="current-price">
                    <span className="price-label">Current Price:</span>
                    <div className="price-values">
                      <span className="wax-price">{item.nft.price} WAX</span>
                      {usdPrices[item.nft.asset_id] && (
                        <span className="usd-price">${usdPrices[item.nft.asset_id]}</span>
                      )}
                    </div>
                  </div>
                  
                  {item.priceChange !== undefined && (
                    <div className="price-change" style={{ color: getPriceChangeColor(item.priceChange) }}>
                      <span className="change-icon">{getPriceChangeIcon(item.priceChange)}</span>
                      <span>{Math.abs(item.priceChange).toFixed(2)}%</span>
                    </div>
                  )}
                </div>
                
                {item.targetPrice && (
                  <div className="price-alert-info">
                    <span className="alert-label">Alert when price is {item.alertType}:</span>
                    <span className="alert-price">{item.targetPrice} WAX</span>
                  </div>
                )}
                
                <div className="watchlist-actions">
                  {item.targetPrice ? (
                    <button 
                      className="btn-secondary btn-sm"
                      onClick={() => removePriceAlert(item.nft.asset_id)}
                    >
                      Remove Alert
                    </button>
                  ) : (
                    <button 
                      className="btn-primary btn-sm"
                      onClick={() => setShowAddAlert(item.nft.asset_id)}
                    >
                      Add Price Alert
                    </button>
                  )}
                  
                  <button 
                    className="btn-danger btn-sm"
                    onClick={() => removeFromWatchlist(item.nft.asset_id)}
                  >
                    Remove
                  </button>
                </div>
              </div>
              
              {showAddAlert === item.nft.asset_id && (
                <div className="price-alert-form">
                  <h4>Set Price Alert</h4>
                  <div className="form-group">
                    <label>Alert Type:</label>
                    <select 
                      value={alertType} 
                      onChange={(e) => setAlertType(e.target.value as 'below' | 'above' | 'any')}
                    >
                      <option value="below">Price drops below</option>
                      <option value="above">Price rises above</option>
                      <option value="any">Any price change</option>
                    </select>
                  </div>
                  
                  {alertType !== 'any' && (
                    <div className="form-group">
                      <label>Target Price (WAX):</label>
                      <input 
                        type="number" 
                        step="0.01" 
                        value={alertPrice}
                        onChange={(e) => setAlertPrice(e.target.value)}
                        placeholder="Enter price in WAX"
                      />
                    </div>
                  )}
                  
                  <div className="form-actions">
                    <button 
                      className="btn-primary btn-sm"
                      onClick={() => addPriceAlert(item.nft.asset_id)}
                      disabled={alertType !== 'any' && !alertPrice}
                    >
                      Set Alert
                    </button>
                    <button 
                      className="btn-secondary btn-sm"
                      onClick={() => setShowAddAlert(null)}
                    >
                      Cancel
                    </button>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  )
}

export default Watchlist