// Currency conversion service for converting USD to various currencies
import { ErrorMessageService } from './errorMessageService'
import { API_ENDPOINTS } from '../config/marketplaceConfig'

export interface ExchangeRates {
  [currency: string]: number
}

export interface CryptoRates {
  bitcoin: { usd: number }
  ethereum: { usd: number }
}

class CurrencyService {
  private exchangeRates: ExchangeRates = {}
  private cryptoRates: CryptoRates = { bitcoin: { usd: 0 }, ethereum: { usd: 0 } }
  private lastFetchTime: number = 0
  private readonly CACHE_DURATION = 10 * 60 * 1000 // 10 minutes
  private readonly EXCHANGE_API = API_ENDPOINTS.EXCHANGE_RATE_API
  private readonly CRYPTO_API = API_ENDPOINTS.COINGECKO_API

  // Fallback exchange rates (approximate)
  private readonly FALLBACK_RATES: ExchangeRates = {
    USD: 1,
    EUR: 0.85,
    DKK: 6.30,
    AED: 3.67,
    INR: 83.12,
    RUB: 74.50,
    CNY: 7.10,
    JPY: 110.00
  }

  private readonly FALLBACK_CRYPTO: CryptoRates = {
    bitcoin: { usd: 115000 },
    ethereum: { usd: 4000 }
  }

  async getExchangeRates(): Promise<ExchangeRates> {
    const now = Date.now()
    
    // Return cached rates if still valid
    if (Object.keys(this.exchangeRates).length > 0 && (now - this.lastFetchTime) < this.CACHE_DURATION) {
      return this.exchangeRates
    }

    try {
      // Fetch fiat currency rates
      const response = await fetch(this.EXCHANGE_API, {
        method: 'GET',
        headers: {
          'Accept': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`)
      }

      const data = await response.json()
      
      // Extract the rates we need
      this.exchangeRates = {
        USD: 1,
        EUR: data.rates?.EUR || this.FALLBACK_RATES.EUR,
        DKK: data.rates?.DKK || this.FALLBACK_RATES.DKK,
        AED: data.rates?.AED || this.FALLBACK_RATES.AED,
        INR: data.rates?.INR || this.FALLBACK_RATES.INR,
        RUB: data.rates?.RUB || this.FALLBACK_RATES.RUB,
        CNY: data.rates?.CNY || this.FALLBACK_RATES.CNY,
        JPY: data.rates?.JPY || this.FALLBACK_RATES.JPY
      }

      // Fetch crypto rates
      try {
        const cryptoResponse = await fetch(this.CRYPTO_API, {
          method: 'GET',
          headers: {
            'Accept': 'application/json'
          }
        })

        if (cryptoResponse.ok) {
          const cryptoData = await cryptoResponse.json()
          this.cryptoRates = {
            bitcoin: { usd: cryptoData.bitcoin?.usd || this.FALLBACK_CRYPTO.bitcoin.usd },
            ethereum: { usd: cryptoData.ethereum?.usd || this.FALLBACK_CRYPTO.ethereum.usd }
          }
        }
      } catch (cryptoError) {
        ErrorMessageService.logError('CurrencyService.getCryptoRates', cryptoError)
        this.cryptoRates = this.FALLBACK_CRYPTO
      }

      this.lastFetchTime = now
      return this.exchangeRates
    } catch (error) {
      ErrorMessageService.logError('CurrencyService.getExchangeRates', error, { api: this.EXCHANGE_API })
      
      // Use fallback rates
      this.exchangeRates = this.FALLBACK_RATES
      this.cryptoRates = this.FALLBACK_CRYPTO
      this.lastFetchTime = now
      
      return this.exchangeRates
    }
  }

  async convertUSDToCurrency(usdAmount: number, targetCurrency: string): Promise<string> {
    if (usdAmount <= 0) return '0.00'
    
    // Handle crypto currencies
    if (targetCurrency === 'BTC') {
      await this.getExchangeRates() // This also fetches crypto rates
      const btcPrice = this.cryptoRates.bitcoin.usd
      const btcAmount = usdAmount / btcPrice
      return btcAmount >= 0.001 ? btcAmount.toFixed(6) : btcAmount.toExponential(2)
    }
    
    if (targetCurrency === 'ETH') {
      await this.getExchangeRates() // This also fetches crypto rates
      const ethPrice = this.cryptoRates.ethereum.usd
      const ethAmount = usdAmount / ethPrice
      return ethAmount >= 0.001 ? ethAmount.toFixed(6) : ethAmount.toExponential(2)
    }

    // Handle fiat currencies
    const rates = await this.getExchangeRates()
    const rate = rates[targetCurrency]
    
    if (!rate) {
      return '0.00'
    }

    const convertedAmount = usdAmount * rate
    
    // Format based on currency
    if (targetCurrency === 'JPY') {
      return Math.round(convertedAmount).toString() // JPY doesn't use decimals
    }
    
    return convertedAmount.toFixed(2)
  }

  async convertWaxToCurrency(waxAmount: string, targetCurrency: string): Promise<string> {
    try {
      // First convert WAX to USD
      const { priceService } = await import('./priceService')
      const usdString = await priceService.convertWaxToUSDAsync(waxAmount)
      
      // Extract USD amount from string (remove $ and commas)
      const usdAmount = parseFloat(usdString.replace(/[$,]/g, ''))
      
      if (targetCurrency === 'USD') {
        return usdString
      }
      
      // Convert USD to target currency
      const convertedAmount = await this.convertUSDToCurrency(usdAmount, targetCurrency)
      
      // Format with currency symbol
      return this.formatCurrencyAmount(convertedAmount, targetCurrency)
    } catch (error) {
      ErrorMessageService.logError('CurrencyService.convertWaxToCurrency', error, { waxAmount, targetCurrency })
      return '0.00'
    }
  }

  formatCurrencyAmount(amount: string, currency: string): string {
    switch (currency) {
      case 'USD':
        return `$${amount}`
      case 'EUR':
        return `€${amount}`
      case 'DKK':
        return `${amount} kr`
      case 'AED':
        return `${amount} د.إ`
      case 'INR':
        return `₹${amount}`
      case 'RUB':
        return `${amount} ₽`
      case 'CNY':
        return `¥${amount}`
      case 'JPY':
        return `¥${amount}`
      case 'BTC':
        return `₿${amount}`
      case 'ETH':
        return `Ξ${amount}`
      default:
        return `${amount} ${currency}`
    }
  }

  getCurrencySymbol(currency: string): string {
    const symbols: { [key: string]: string } = {
      USD: '$',
      EUR: '€',
      DKK: 'kr',
      AED: 'د.إ',
      INR: '₹',
      RUB: '₽',
      CNY: '¥',
      JPY: '¥',
      BTC: '₿',
      ETH: 'Ξ'
    }
    return symbols[currency] || currency
  }
}

export const currencyService = new CurrencyService()
export default currencyService