import { SessionKit, Session } from '@wharfkit/session'
import { WebRenderer } from '@wharfkit/web-renderer'
import { WalletPluginAnchor } from '@wharfkit/wallet-plugin-anchor'
import { WalletPluginCloudWallet } from '@wharfkit/wallet-plugin-cloudwallet'
import { WalletPluginWombat } from '@wharfkit/wallet-plugin-wombat'
import { TransactPluginAutoCorrect } from '@wharfkit/transact-plugin-autocorrect'
import { APIClient } from '@wharfkit/antelope'
// Removed unused TransactPluginResourceProvider import
import { ErrorMessageService } from './errorMessageService'
import { logger } from './loggingService'
import { API_ENDPOINTS } from '../config/marketplaceConfig'
import { transactionService, type TransactionType } from './transactionService'
import { buildPurchaseActions, buildTokenTransferActions } from './transactionBuilders'
import type { WalletContextType } from '../contexts/WalletContextDefinition'

interface TransactionAction {
  account: string
  name: string
  authorization: Array<{
    actor: string
    permission: string
  }>
  data: Record<string, unknown>
}

interface CraftingData {
  recipe: string
  quantity: number
  ingredientAssets: Array<{ templateId: number; assetIds: string[] }>
  pxjCost: number
}

class WharfKitService {
  private sessionKit: SessionKit | null = null
  private currentSession: Session | null = null
  private listeners: ((context: WalletContextType) => void)[] = []

  constructor() {
    this.initializeSessionKit()
    this.restoreSession()
  }

  private initializeSessionKit() {
    try {
      const config = {
        appName: 'PxMarket2',
        chains: [{
          id: '1064487b3cd1a897ce03ae5b6a865651747e2e152090f99c1d19d44e01aea5a4',
          url: API_ENDPOINTS.WAX_CHAIN.GREYMASS
        }],
        ui: new WebRenderer(),
        walletPlugins: [
          new WalletPluginAnchor(),
          new WalletPluginCloudWallet(),
          new WalletPluginWombat()
        ]
      }

      const options = {
        transactPlugins: [
          new TransactPluginAutoCorrect()
        ]
      }

      this.sessionKit = new SessionKit(config, options)
      // WharfKit SessionKit initialized successfully
    } catch (error) {
      ErrorMessageService.logError('WharfKitService.initialize', error)
    }
  }

  private async restoreSession() {
    if (!this.sessionKit) return

    try {
      const restored = await this.sessionKit.restore()
      if (restored) {
        this.currentSession = restored
        // Session restored for user: this.currentSession.actor
        this.notifyListeners()
      }
    } catch (error) {
      ErrorMessageService.logError('WharfKitService.restoreSession', error)
    }
  }

  async login(): Promise<Session | null> {
    if (!this.sessionKit) {
      ErrorMessageService.logError('WharfKitService.login', new Error('SessionKit not initialized'))
      return null
    }

    try {
      const response = await this.sessionKit.login()
      if (response.session) {
        this.currentSession = response.session
        // Login successful for user: this.currentSession.actor
        this.notifyListeners()
        return this.currentSession
      }
    } catch (error) {
      ErrorMessageService.logError('WharfKitService.login', error)
      throw error
    }

    return null
  }

  async logout(): Promise<void> {
    if (!this.sessionKit || !this.currentSession) return

    try {
      await this.sessionKit.logout(this.currentSession)
      this.currentSession = null
      // Logout successful
      this.notifyListeners()
    } catch (error) {
      ErrorMessageService.logError('WharfKitService.logout', error)
      throw error
    }
  }

  isLoggedIn(): boolean {
    return this.currentSession !== null
  }

  getSession(): Session | null {
    return this.currentSession
  }

  getActor(): string | null {
    return this.currentSession?.actor?.toString() || null
  }

  getAccountName(): string | null {
    return this.currentSession?.actor?.toString() || null
  }

  getPermission(): string {
    return this.currentSession?.permission?.toString() || 'active'
  }

  // Safe session property access helpers
  static safeGetActor(session: Session | null): string {
    return session?.actor?.toString() || ''
  }

  static safeGetPermission(session: Session | null): string {
    return session?.permission?.toString() || 'active'
  }

  async transact(actions: TransactionAction[]): Promise<unknown> {
    if (!this.currentSession) {
      throw new Error('No active session. Please login first.')
    }

    try {
      logger.debug('Sending transaction with actions', 'WHARFKIT', { actionsCount: actions.length })
      const result = await this.currentSession.transact({
        actions
      }, {
        broadcast: true,
        expireSeconds: 120
      })
      logger.info('Transaction successful', 'WHARFKIT', { result })
      return result
    } catch (error) {
      ErrorMessageService.logError('WharfKitService.transact', error, { actions })
      throw error
    }
  }

  // Purchase NFT transaction
  async purchaseNFT(saleId: string, price: string): Promise<unknown> {
    if (!this.currentSession) {
      throw new Error('Please login to purchase NFTs')
    }

    const actions = [
      {
        account: 'eosio.token',
        name: 'transfer',
        authorization: [{
          actor: this.currentSession?.actor?.toString() || '',
          permission: 'active'
        }],
        data: {
          from: this.currentSession?.actor?.toString() || '',
          to: 'atomicmarket',
          quantity: `${parseFloat(price).toFixed(8)} WAX`,
          memo: `deposit`
        }
      },
      {
        account: 'atomicmarket',
        name: 'purchasesale',
        authorization: [{
          actor: this.currentSession?.actor?.toString() || '',
          permission: 'active'
        }],
        data: {
          buyer: this.currentSession?.actor?.toString() || '',
          sale_id: saleId,
          intended_delphi_median: 0,
          taker_marketplace: 'market.pxj'
        }
      }
    ]

    logger.debug('Crafting transaction actions', 'WHARFKIT', { actionsCount: actions.length })
    return this.transact(actions)
  }

  // Batch purchase multiple NFTs in a single transaction
  async batchPurchaseNFTs(purchases: Array<{saleId: string, price: string}>): Promise<unknown> {
    if (!this.currentSession) {
      throw new Error('Please login to purchase NFTs')
    }

    if (purchases.length === 0) {
      throw new Error('No items to purchase')
    }

    const actions: TransactionAction[] = []

    // Calculate total price for the batch
    const totalPrice = purchases.reduce((sum, purchase) => {
      return sum + parseFloat(purchase.price)
    }, 0)

    // Add single transfer action for the total amount
    actions.push({
      account: 'eosio.token',
      name: 'transfer',
      authorization: [{
        actor: this.currentSession.actor.toString(),
        permission: 'active'
      }],
      data: {
        from: this.currentSession.actor.toString(),
        to: 'atomicmarket',
        quantity: `${totalPrice.toFixed(8)} WAX`,
        memo: `deposit`
      }
    })

    // Add purchase actions for each NFT
    purchases.forEach(purchase => {
      actions.push({
        account: 'atomicmarket',
        name: 'purchasesale',
        authorization: [{
          actor: this.currentSession?.actor.toString() || '',
          permission: 'active'
        }],
        data: {
          buyer: this.currentSession?.actor.toString() || '',
          sale_id: purchase.saleId,
          intended_delphi_median: 0,
          taker_marketplace: 'market.pxj'
        }
      })
    })

    return this.transact(actions)
  }

  // Transfer tokens (PXJ, WAX, etc.)
  async transferTokens(to: string, amount: string, currency: string = 'PXJ', memo: string = ''): Promise<unknown> {
    if (!this.currentSession) {
      throw new Error('Please login to transfer tokens')
    }

    const actions = buildTokenTransferActions(
      this.currentSession?.actor?.toString() || '',
      to,
      amount,
      currency,
      memo
    )

    logger.debug('Token transfer transaction actions', 'WHARFKIT', { 
      actionsCount: actions.length,
      to,
      amount,
      currency,
      memo
    })
    
    return this.transact(actions)
  }

  // Bulk transfer multiple NFTs to a single recipient
  async bulkTransferNFT(recipient: string, assetIds: string[], memo: string = ''): Promise<unknown> {
    if (!this.currentSession) {
      throw new Error('Please login to transfer NFTs')
    }

    if (!assetIds || assetIds.length === 0) {
      throw new Error('No assets to transfer')
    }

    const actions: TransactionAction[] = assetIds.map(assetId => ({
      account: 'atomicassets',
      name: 'transfer',
      authorization: [{
        actor: this.currentSession?.actor?.toString() || '',
        permission: 'active'
      }],
      data: {
        from: this.currentSession?.actor?.toString() || '',
        to: recipient,
        asset_ids: [assetId],
        memo: memo
      }
    }))

    logger.debug('Bulk NFT transfer transaction actions', 'WHARFKIT', {
      actionsCount: actions.length,
      recipient,
      assetIds: assetIds.length,
      memo
    })

    return this.transact(actions)
  }

  // Execute crafting transaction
  async executeCraftingTransaction(craftingData: CraftingData): Promise<unknown> {
    if (!this.currentSession) {
      throw new Error('No active session')
    }

    const actions: TransactionAction[] = []
    const account = this.currentSession?.actor?.toString() || ''

    // Map recipe IDs to craft_ids based on the provided mapping
    const recipeToCraftId: Record<string, number> = {
      'craft_green': 1832,
      'craft_orange': 1833,
      'craft_purple': 1834,
      'craft_amber': 1835,
      'craft_chartreuse': 1836,
      'craft_violet': 1837,
      'craft_teal': 1838,
      'craft_vermillion': 1839,
      'craft_magenta': 1841
    }

    const craftId = recipeToCraftId[craftingData.recipe]
    if (!craftId) {
      throw new Error(`Unknown recipe: ${craftingData.recipe}`)
    }

    // Use full recipe cost for each individual craft
    const individualPxjCost = craftingData.pxjCost

    // Add nft.hive boost action (only once per transaction)
    actions.push({
      account: 'nft.hive',
      name: 'boost',
      authorization: [{
        actor: account,
        permission: 'active'
      }],
      data: {
        booster: account
      }
    })

    // Create separate craft actions for each individual craft
    for (let i = 0; i < craftingData.quantity; i++) {
      // Get asset IDs for this individual craft
      const craftAssetIds: string[] = []
      for (const ingredient of craftingData.ingredientAssets) {
        // Take the required number of assets for one craft from each ingredient
        const assetsPerCraft = ingredient.assetIds.length / craftingData.quantity
        const startIndex = i * assetsPerCraft
        const endIndex = startIndex + assetsPerCraft
        craftAssetIds.push(...ingredient.assetIds.slice(startIndex, endIndex))
      }

      // Transfer ingredient NFTs to crafting contract for this craft
      if (craftAssetIds.length > 0) {
        actions.push({
          account: 'atomicassets',
          name: 'transfer',
          authorization: [{
            actor: account,
            permission: 'active'
          }],
          data: {
            from: account,
            to: 'nfthivecraft',
            asset_ids: craftAssetIds,
            memo: 'craft'
          }
        })
      }

      // Transfer PXJ for this individual craft
      if (individualPxjCost > 0) {
        actions.push({
          account: 'pixeljourney',
          name: 'transfer',
          authorization: [{
            actor: account,
            permission: 'active'
          }],
          data: {
            from: account,
            to: 'nfthivecraft',
            quantity: `${individualPxjCost.toFixed(8)} PXJ`,
            memo: 'craft'
          }
        })
      }

      // Execute individual crafting action
      actions.push({
        account: 'nfthivecraft',
        name: 'craft',
        authorization: [{
          actor: account,
          permission: 'active'
        }],
        data: {
          craft_id: craftId,
          user_name: account,
          collection_name: 'pixeljourney',
          timestamp: Date.now().toString(),
          asset_ids: craftAssetIds,
          tokens: [`${individualPxjCost.toFixed(8)} PXJ`]
        }
      })
    }

    return this.transact(actions)
  }

  // Subscribe to wallet state changes
  subscribe(callback: (context: WalletContextType) => void): () => void {
    this.listeners.push(callback)
    
    // Call immediately with current state
    callback(this.getWalletContext())
    
    // Return unsubscribe function
    return () => {
      const index = this.listeners.indexOf(callback)
      if (index > -1) {
        this.listeners.splice(index, 1)
      }
    }
  }

  private notifyListeners() {
    const context = this.getWalletContext()
    this.listeners.forEach(callback => callback(context))
  }

  private getWalletContext(): WalletContextType {
    return {
      isConnected: this.isLoggedIn(),
      account: this.getAccountName(),
      session: this.currentSession,
      login: this.login.bind(this),
      logout: this.logout.bind(this),
      transact: this.transact.bind(this),
      purchaseNFT: this.purchaseNFT.bind(this),
      batchPurchaseNFTs: this.batchPurchaseNFTs.bind(this),
      executeCraftingTransaction: this.executeCraftingTransaction.bind(this),
      isLoading: false, // WharfKit handles its own loading states
      executeTransaction: this.executeTransaction.bind(this),
      getTransaction: undefined, // Optional method not implemented
      cancelTransaction: undefined, // Optional method not implemented
      getTransactionHistory: undefined, // Optional method not implemented
      transferNFT: undefined, // Optional method not implemented
      bulkTransferNFT: this.bulkTransferNFT.bind(this),
      transferTokens: this.transferTokens.bind(this)
    }
  }

  // Execute transaction using the centralized transaction service
  async executeTransaction(type: TransactionType, data: any): Promise<string> {
    if (!this.currentSession) {
      throw new Error('No active session. Please login first.')
    }

    try {
      let actions: TransactionAction[] = []
      
      // Build actions based on transaction type
      switch (type) {
        case 'nft_purchase':
          if (data.saleId && data.price) {
            actions = buildPurchaseActions(this.currentSession?.actor?.toString() || '', data.saleId, data.price)
          } else {
            throw new Error('Missing saleId or price for NFT purchase')
          }
          break
        case 'crafting': {
          // Use existing crafting method and extract transaction ID
          const craftingResult = await this.executeCraftingTransaction(data)
          if (craftingResult && typeof craftingResult === 'object' && 'transaction_id' in craftingResult) {
            return (craftingResult as any).transaction_id
          }
          return 'crafting_completed'
        }
        default:
          throw new Error(`Transaction type '${type}' not supported yet`)
      }
      
      // Execute through centralized transaction service
      const result = await transactionService.executeTransaction(type, actions)
      
      if (result.success && result.transactionId) {
        return result.transactionId
      } else {
        throw new Error(result.error || 'Transaction failed')
      }
    } catch (error) {
      logger.error('Transaction execution failed', 'WharfKitService.executeTransaction', error instanceof Error ? error : new Error(String(error)), { type, data })
      throw error
    }
  }

  // Get API client for blockchain queries
  getAPIClient(): APIClient {
    return new APIClient({
      url: API_ENDPOINTS.WAX_CHAIN.GREYMASS
    })
  }
}

// Export both the class and singleton instance
export { WharfKitService }
export const wharfKitService = new WharfKitService()
export default wharfKitService