import React, { useState, useEffect, useMemo } from 'react'
import { useNotifications } from '../hooks/useNotifications'
import { useWallet } from '../contexts/WalletContext'
import type { InventoryNFT } from '../types/inventory'
import MediaRenderer from './MediaRenderer'
import { getAssetImageUrl, getAssetVideoUrl } from '../utils/mediaUtils'

interface BulkAuctionModalProps {
  nfts: InventoryNFT[]
  isOpen: boolean
  onClose: () => void
  onSuccess: () => void
}

const BulkAuctionModal: React.FC<BulkAuctionModalProps> = ({
  nfts,
  isOpen,
  onClose,
  onSuccess
}) => {
  const { showSuccess, showError } = useNotifications()
  const walletContext = useWallet()
  const [startingPrice, setStartingPrice] = useState('')
  const [durationWeeks, setDurationWeeks] = useState(0)
  const [durationDays, setDurationDays] = useState(1)
  const [durationHours, setDurationHours] = useState(0)
  const [durationMinutes, setDurationMinutes] = useState(0)
  const [auctionMemo, setAuctionMemo] = useState('')
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [sortBy, setSortBy] = useState('name_asc')

  // Reset form when modal opens
  useEffect(() => {
    if (isOpen) {
      setStartingPrice('')
      setDurationWeeks(0)
      setDurationDays(1)
      setDurationHours(0)
      setDurationMinutes(0)
      setAuctionMemo('')
      setIsSubmitting(false)
    }
  }, [isOpen])

  // Sort NFTs for display
  const sortedNfts = useMemo(() => {
    const sorted = [...nfts]
    
    switch (sortBy) {
      case 'name_asc':
        return sorted.sort((a, b) => (a.name || '').localeCompare(b.name || ''))
      case 'name_desc':
        return sorted.sort((a, b) => (b.name || '').localeCompare(a.name || ''))
      case 'collection_asc':
        return sorted.sort((a, b) => a.collection.collection_name.localeCompare(b.collection.collection_name))
      case 'collection_desc':
        return sorted.sort((a, b) => b.collection.collection_name.localeCompare(a.collection.collection_name))
      case 'mint_asc':
        return sorted.sort((a, b) => (a.mint_number || 0) - (b.mint_number || 0))
      case 'mint_desc':
        return sorted.sort((a, b) => (b.mint_number || 0) - (a.mint_number || 0))
      default:
        return sorted
    }
  }, [nfts, sortBy])

  const calculateTotalDuration = () => {
    return (durationWeeks * 7 * 24 * 3600) + 
           (durationDays * 24 * 3600) + 
           (durationHours * 3600) + 
           (durationMinutes * 60)
  }

  const updateDurationComponent = (component: 'weeks' | 'days' | 'hours' | 'minutes', value: number) => {
    const clampedValue = Math.max(0, Math.min(value, getMaxValue(component)))
    
    switch (component) {
      case 'weeks':
        setDurationWeeks(clampedValue)
        break
      case 'days':
        setDurationDays(clampedValue)
        break
      case 'hours':
        setDurationHours(clampedValue)
        break
      case 'minutes':
        setDurationMinutes(clampedValue)
        break
    }
  }

  const getMaxValue = (component: 'weeks' | 'days' | 'hours' | 'minutes') => {
    switch (component) {
      case 'weeks': return 4 // Max 4 weeks
      case 'days': return 30 // Max 30 days
      case 'hours': return 23 // Max 23 hours
      case 'minutes': return 59 // Max 59 minutes
      default: return 0
    }
  }

  const formatDurationDisplay = () => {
    const parts = []
    if (durationWeeks > 0) parts.push(`${durationWeeks}w`)
    if (durationDays > 0) parts.push(`${durationDays}d`)
    if (durationHours > 0) parts.push(`${durationHours}h`)
    if (durationMinutes > 0) parts.push(`${durationMinutes}m`)
    return parts.length > 0 ? parts.join(' ') : '0m'
  }

  const handleSubmit = async () => {
    if (!startingPrice || parseFloat(startingPrice) <= 0) {
      showError('Invalid Price', 'Please enter a valid starting price')
      return
    }

    const totalDurationSeconds = calculateTotalDuration()
    if (totalDurationSeconds < 3600) { // Minimum 1 hour
      showError('Invalid Duration', 'Auction duration must be at least 1 hour')
      return
    }

    if (totalDurationSeconds > 2592000) { // Maximum 30 days
      showError('Invalid Duration', 'Auction duration cannot exceed 30 days')
      return
    }

    setIsSubmitting(true)

    try {
      // Create bulk auction transactions using AtomicMarket announceauct action
      const startingBidAmount = `${parseFloat(startingPrice).toFixed(8)} WAX`
      
      // Create separate auction for each NFT
      const actions = nfts.map(nft => ({
        account: 'atomicmarket',
        name: 'announceauct',
        authorization: [{
          actor: walletContext.account || '',
          permission: 'active'
        }],
        data: {
          seller: walletContext.account || '',
          asset_ids: [parseInt(nft.asset_id)],
          starting_bid: startingBidAmount,
          duration: totalDurationSeconds,
          maker_marketplace: 'market.pxj'
        }
      }))
      
      await walletContext.transact(actions)
      
      const durationDisplay = formatDurationDisplay()
      showSuccess(
        'Bulk Auction Created', 
        `Successfully created auctions for ${nfts.length} NFT${nfts.length !== 1 ? 's' : ''} with starting price ${startingPrice} WAX and duration ${durationDisplay}. Remember to transfer your assets to the atomicmarket contract to activate the auctions.`
      )
      
      onSuccess()
      onClose()
    } catch (error) {
      console.error('Bulk auction creation failed:', error)
      showError('Auction Creation Failed', error instanceof Error ? error.message : 'Failed to create bulk auctions. Please try again.')
    } finally {
      setIsSubmitting(false)
    }
  }

  if (!isOpen) return null

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content bulk-auction-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Create Bulk Auction</h2>
          <button className="modal-close" onClick={onClose}>×</button>
        </div>

        <div className="modal-body">
          {/* Selected NFTs Preview */}
          <div className="selected-nfts-section">
            <div className="selected-nfts-header">
              <h3>Selected NFTs ({nfts.length})</h3>
              {nfts.length > 1 && (
                <div className="sort-container">
                  <label htmlFor="auction-sort">Sort by:</label>
                  <select 
                    id="auction-sort"
                    className="sort-select"
                    value={sortBy}
                    onChange={(e) => setSortBy(e.target.value)}
                  >
                    <option value="name_asc">Name (A-Z)</option>
                    <option value="name_desc">Name (Z-A)</option>
                    <option value="collection_asc">Collection (A-Z)</option>
                    <option value="collection_desc">Collection (Z-A)</option>
                    <option value="mint_asc">Mint # (Low-High)</option>
                    <option value="mint_desc">Mint # (High-Low)</option>
                  </select>
                </div>
              )}
            </div>
            <div className="selected-nfts-grid">
              {sortedNfts.slice(0, 6).map((nft) => (
                <div key={nft.asset_id} className="selected-nft-item">
                  <div className="nft-image-container">
                    <MediaRenderer
                      imageUrl={getAssetImageUrl(nft)}
                      videoUrl={getAssetVideoUrl(nft)}
                      alt={nft.name || `Asset #${nft.asset_id}`}
                      className="nft-image"
                    />
                  </div>
                  <div className="nft-info">
                    <span className="nft-name">{nft.name}</span>
                    <span className="nft-collection">{typeof nft.collection === 'string' ? nft.collection : nft.collection?.name || nft.collection?.collection_name || 'Unknown'}</span>
                  </div>
                </div>
              ))}
              {nfts.length > 6 && (
                <div className="more-nfts-indicator">
                  +{nfts.length - 6} more
                </div>
              )}
            </div>
          </div>

          {/* Auction Settings */}
          <div className="auction-settings">
            <div className="form-group">
              <label htmlFor="startingPrice">Starting Price (WAX)</label>
              <input
                type="number"
                id="startingPrice"
                value={startingPrice}
                onChange={(e) => setStartingPrice(e.target.value)}
                placeholder="Enter starting price"
                min="0"
                step="0.01"
                disabled={isSubmitting}
              />
            </div>

            <div className="form-group">
              <label>Auction Duration</label>
              <div className="duration-inputs">
                <div className="duration-field">
                  <label className="duration-label">Weeks</label>
                  <input
                    type="number"
                    className="duration-input"
                    value={durationWeeks}
                    onChange={(e) => updateDurationComponent('weeks', parseInt(e.target.value) || 0)}
                    min="0"
                    max={getMaxValue('weeks')}
                    disabled={isSubmitting}
                  />
                </div>
                <div className="duration-field">
                  <label className="duration-label">Days</label>
                  <input
                    type="number"
                    className="duration-input"
                    value={durationDays}
                    onChange={(e) => updateDurationComponent('days', parseInt(e.target.value) || 0)}
                    min="0"
                    max={getMaxValue('days')}
                    disabled={isSubmitting}
                  />
                </div>
                <div className="duration-field">
                  <label className="duration-label">Hours</label>
                  <input
                    type="number"
                    className="duration-input"
                    value={durationHours}
                    onChange={(e) => updateDurationComponent('hours', parseInt(e.target.value) || 0)}
                    min="0"
                    max={getMaxValue('hours')}
                    disabled={isSubmitting}
                  />
                </div>
                <div className="duration-field">
                  <label className="duration-label">Minutes</label>
                  <input
                    type="number"
                    className="duration-input"
                    value={durationMinutes}
                    onChange={(e) => updateDurationComponent('minutes', parseInt(e.target.value) || 0)}
                    min="0"
                    max={getMaxValue('minutes')}
                    disabled={isSubmitting}
                  />
                </div>
              </div>
              <div className="duration-summary">
                <span className="duration-display">{formatDurationDisplay()}</span>
                <span className="duration-total">({calculateTotalDuration().toLocaleString()}s)</span>
              </div>
            </div>

            <div className="form-group">
              <label htmlFor="auctionMemo">Memo (Optional)</label>
              <textarea
                id="auctionMemo"
                value={auctionMemo}
                onChange={(e) => setAuctionMemo(e.target.value)}
                placeholder="Add a note about this auction batch..."
                rows={3}
                maxLength={256}
                disabled={isSubmitting}
              />
            </div>
          </div>
        </div>

        <div className="modal-footer">
          <button 
            className="cancel-btn" 
            onClick={onClose}
            disabled={isSubmitting}
          >
            Cancel
          </button>
          <button 
            className="create-btn"
            onClick={handleSubmit}
            disabled={
              isSubmitting ||
              !startingPrice ||
              parseFloat(startingPrice) <= 0 ||
              calculateTotalDuration() < 3600
            }
          >
            {isSubmitting ? 'Creating...' : `Create ${nfts.length} Auction${nfts.length !== 1 ? 's' : ''}`}
          </button>
        </div>
      </div>
    </div>
  )
}

export default BulkAuctionModal