// Utility functions for extracting media URLs from different data structures
import { API_ENDPOINTS } from '../config/marketplaceConfig'

// Type definitions for media extraction
export interface MediaAsset {
  asset_id?: string
  image?: string
  video?: string
  img?: string
  data?: Record<string, unknown> | {
    img?: string
    video?: string
    rarity?: string
  }
  template?: {
    template_id: string
    max_supply: string
    immutable_data?: Record<string, unknown> & {
      img?: string
      video?: string
      rarity?: string
      name?: string
    }
  }
}

export interface SaleWithAssets {
  assets?: MediaAsset[]
}

// IPFS gateway with fallback support
const getIPFSUrl = (hash: string): string => {
  // Use a random gateway for load balancing and better reliability
  const gateways = API_ENDPOINTS.IPFS.GATEWAYS
  const randomGateway = gateways[Math.floor(Math.random() * gateways.length)]
  return `${randomGateway}${hash}`
}

export const getAssetImageUrl = (asset: MediaAsset | null | undefined): string | undefined => {
  if (!asset) return undefined
  
  // First check if the asset already has a processed image URL (for marketplace NFTs)
  if (asset.image && typeof asset.image === 'string') {
    return asset.image
  }
  
  // Then check the raw data structure (for inventory/collection NFTs)
  let imgData: string | undefined
  if (asset.data && typeof asset.data === 'object' && 'img' in asset.data) {
    imgData = asset.data.img as string
  }
  if (!imgData) {
    imgData = asset.template?.immutable_data?.img || asset.img
  }
  if (!imgData) return undefined
  if (typeof imgData === 'string') {
    if (imgData.startsWith('http')) return imgData
    if (imgData.startsWith('Qm')) return getIPFSUrl(imgData)
    return imgData
  }
  return undefined
}

export const getAssetVideoUrl = (asset: MediaAsset | null | undefined): string | null => {
  if (!asset) return null
  
  // First check if the asset already has a processed video URL (for marketplace NFTs)
  if (asset.video && typeof asset.video === 'string') {
    return asset.video
  }
  
  // Then check the raw data structure (for inventory/collection NFTs)
  let videoData: string | undefined
  if (asset.data && typeof asset.data === 'object' && 'video' in asset.data) {
    videoData = asset.data.video as string
  }
  if (!videoData) {
    videoData = asset.template?.immutable_data?.video || asset.video
  }
  if (!videoData) return null
  if (typeof videoData === 'string') {
    if (videoData.startsWith('http')) return videoData
    if (videoData.startsWith('Qm')) return getIPFSUrl(videoData)
    return videoData
  }
  return null
}

// Template-specific media functions
export const getTemplateImageUrl = (template: { immutable_data?: Record<string, unknown>; data?: { img?: string }; img?: string; template?: { immutable_data?: { img?: string } } }): string | undefined => {
  if (!template) return undefined
  
  const imgData = template.immutable_data?.img || template.data?.img || template.img || template.template?.immutable_data?.img
  if (!imgData) return undefined
  if (typeof imgData === 'string') {
    if (imgData.startsWith('http')) return imgData
    if (imgData.startsWith('Qm')) return getIPFSUrl(imgData)
    return imgData
  }
  return undefined
}

export const getTemplateVideoUrl = (template: { immutable_data?: Record<string, unknown>; data?: { video?: string }; video?: string; template?: { immutable_data?: { video?: string } } }): string | null => {
  if (!template) return null
  
  const videoData = template.immutable_data?.video || template.data?.video || template.video || template.template?.immutable_data?.video
  if (!videoData) return null
  if (typeof videoData === 'string') {
    if (videoData.startsWith('http')) return videoData
    if (videoData.startsWith('Qm')) return getIPFSUrl(videoData)
    return videoData
  }
  return null
}

export const getSaleAssetImageUrl = (sale: SaleWithAssets | null | undefined): string | undefined => {
  const asset = sale?.assets?.[0]
  return getAssetImageUrl(asset)
}

export const getSaleAssetVideoUrl = (sale: SaleWithAssets | null | undefined): string | null => {
  const asset = sale?.assets?.[0]
  return getAssetVideoUrl(asset)
}

// Utility function to create IPFS URLs with fallback support
export const createIPFSUrl = (hash: string): string => {
  if (!hash) return ''
  if (hash.startsWith('http')) return hash
  return getIPFSUrl(hash)
}

// Utility function to get image URL with IPFS fallback
export const getImageUrlWithFallback = (imgPath: string | undefined, fallback?: string): string => {
  if (!imgPath) return fallback || API_ENDPOINTS.IPFS.GATEWAYS[0] + 'placeholder'
  if (imgPath.startsWith('http')) return imgPath
  if (imgPath.startsWith('Qm')) return getIPFSUrl(imgPath)
  return imgPath
}