import React, { useState } from 'react'
import { generateAchievementShareImage, generateInventoryShareImage, downloadShareImage, convertShareImageToPNG } from '../utils/shareImageGenerator'
import { useNotifications } from '../hooks/useNotifications'
import { log } from '../services/loggingService'
import type { UserBadge } from '../services/badgeService'
import '../css/social-share.css'

interface InventoryItem {
  id: string
  name: string
  collection: string
  rarity: string
  quantity: number
  image?: string
}

interface ShareModalProps {
  isOpen: boolean
  onClose: () => void
  title: string
  description?: string
  imageUrl?: string
  shareUrl: string
  defaultShareText: string
  type?: 'nft' | 'collection' | 'template' | 'achievement' | 'inventory'
  // Achievement-specific props
  badges?: UserBadge[]
  totalBadges?: number
  legendaryCount?: number
  username?: string
  // Inventory-specific props
  selectedItems?: InventoryItem[]
  totalValue?: string
}

const ShareModal: React.FC<ShareModalProps> = ({ 
  isOpen, 
  onClose, 
  title, 
  description, 
  imageUrl, 
  shareUrl, 
  defaultShareText,
  type = 'nft',
  badges,
  totalBadges,
  legendaryCount,
  username,
  selectedItems,
  totalValue
}) => {
  const [copied, setCopied] = useState(false)
  const [shareText, setShareText] = useState('')
  const [generatingImage, setGeneratingImage] = useState(false)
  const { showSuccess } = useNotifications()

  // Generate randomized share messages based on content type
  const getRandomizedShareText = () => {
    let messages: string[] = []
    
    switch (type) {
      case 'nft':
        messages = [
          `🎨 Check out this amazing NFT: ${title}! ✨`,
          `🔥 Discovered this incredible ${title} NFT! 🚀`,
          `✨ Look at this stunning ${title}! NFT art at its finest 🎭`,
          `🌟 Found a gem! ${title} is absolutely beautiful 💎`,
          `🎯 This ${title} NFT caught my eye! Amazing artwork 🎨`,
          `💫 Showcasing ${title} - a true masterpiece! 🖼️`,
          `🚀 Blown away by this ${title} NFT! ⭐`,
          `🎪 Step right up and see ${title}! Fantastic NFT art 🎊`
        ]
        break
      case 'collection':
        messages = [
          `🏛️ Explore the amazing ${title} collection! 🎨`,
          `🔥 This ${title} collection is absolutely fire! 🚀`,
          `✨ Dive into the world of ${title}! Incredible NFT collection 🌟`,
          `💎 Discovered this gem of a collection: ${title}! 🎭`,
          `🎯 ${title} collection has some amazing pieces! 🖼️`,
          `🌈 The ${title} collection is a work of art! ✨`,
          `🚀 Check out the stunning ${title} collection! ⭐`,
          `🎪 Welcome to ${title} - an NFT wonderland! 🎊`
        ]
        break
      case 'template':
        messages = [
          `🎨 Check out this ${title} template! Perfect for collectors ✨`,
          `🔥 This ${title} template is incredible! 🚀`,
          `✨ Found an amazing ${title} template! 🎭`,
          `🌟 ${title} template - a collector's dream! 💎`,
          `🎯 This ${title} template caught my attention! 🎨`,
          `💫 Showcasing the ${title} template! 🖼️`,
          `🚀 Amazing ${title} template available! ⭐`,
          `🎪 Don't miss this ${title} template! 🎊`
        ]
        break
      case 'achievement':
        messages = [
          `🏆 Just unlocked some amazing achievements! 🎉`,
          `🔥 Check out my gaming achievements! 🚀`,
          `✨ Proud of my latest achievements! 🎭`,
          `🌟 Achievement unlocked! Gaming milestones reached 💎`,
          `🎯 Showcasing my gaming achievements! 🎨`,
          `💫 Level up! New achievements earned! 🖼️`,
          `🚀 Gaming achievements on display! ⭐`,
          `🎪 Celebrating my gaming milestones! 🎊`
        ]
        break
      default:
        messages = [defaultShareText]
    }
    
    const randomIndex = Math.floor(Math.random() * messages.length)
    return messages[randomIndex]
  }
  
  // State for current randomized message
  const [currentRandomText, setCurrentRandomText] = useState(() => 
    defaultShareText || getRandomizedShareText()
  )
  
  // Use randomized text if no custom defaultShareText provided
  const finalDefaultText = defaultShareText || currentRandomText
  
  // Ensure we have a proper share URL
  const finalShareUrl = shareUrl || window.location.href
  
  // Function to regenerate random message
  const regenerateMessage = () => {
    if (!defaultShareText) {
      setCurrentRandomText(getRandomizedShareText())
    }
  }

  if (!isOpen) return null

  const copyToClipboard = async (text: string) => {
    try {
      await navigator.clipboard.writeText(text)
      setCopied(true)
      setTimeout(() => setCopied(false), 2000)
    } catch (error) {
      log.error('Failed to copy to clipboard', 'ShareModal', error as Error, { type, shareText })
    }
  }

  const shareToTwitter = () => {
    const text = shareText || finalDefaultText
    const hashtags = type === 'collection' ? 'NFTCollection,WAX,PixelJourney' : 
                    type === 'template' ? 'NFTTemplate,WAX,PixelJourney' :
                    type === 'achievement' ? 'Achievement,Gaming,PixelJourney' :
                    'NFT,WAX,PixelJourney'
    const twitterUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(text)}&url=${encodeURIComponent(finalShareUrl)}&hashtags=${hashtags}`
    window.open(twitterUrl, '_blank', 'width=550,height=420')
  }

  const shareToFacebook = () => {
    const facebookUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(finalShareUrl)}`
    window.open(facebookUrl, '_blank', 'width=550,height=420')
  }

  const shareToReddit = () => {
    const text = shareText || finalDefaultText
    const redditUrl = `https://reddit.com/submit?url=${encodeURIComponent(finalShareUrl)}&title=${encodeURIComponent(text)}`
    window.open(redditUrl, '_blank', 'width=550,height=420')
  }

  const shareToDiscord = () => {
    const text = `${shareText || finalDefaultText}\n${finalShareUrl}`
    copyToClipboard(text)
    showSuccess('Share text copied to clipboard! You can now paste it in Discord.', 'Copied to Clipboard')
  }

  const shareToTelegram = () => {
    const text = shareText || finalDefaultText
    const telegramUrl = `https://t.me/share/url?url=${encodeURIComponent(finalShareUrl)}&text=${encodeURIComponent(text)}`
    window.open(telegramUrl, '_blank', 'width=550,height=420')
  }

  const shareViaEmail = () => {
    const subject = `Check out: ${title}`
    const body = `${shareText || finalDefaultText}\n\nView it here: ${finalShareUrl}`
    const emailUrl = `mailto:?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`
    window.location.href = emailUrl
  }

  const generateAndDownloadShareImage = async () => {
    if (!username) return
    
    try {
      setGeneratingImage(true)
      let imageDataUrl: string
      let filename: string
      
      if (type === 'achievement' && badges && totalBadges !== undefined && legendaryCount !== undefined) {
        imageDataUrl = generateAchievementShareImage(badges, totalBadges, legendaryCount, username)
        filename = `${username}-achievements-${Date.now()}.svg`
      } else if (type === 'inventory' && selectedItems && totalValue) {
        imageDataUrl = generateInventoryShareImage(selectedItems, totalValue, username)
        filename = `${username}-inventory-${Date.now()}.svg`
      } else {
        return
      }
      
      downloadShareImage(imageDataUrl, filename)
    } catch (error) {
      log.error('Error generating share image', 'ShareModal', error as Error, { type, username })
    } finally {
      setGeneratingImage(false)
    }
  }

  const generateAndDownloadPNG = async () => {
    if (!username) return
    
    try {
      setGeneratingImage(true)
      let svgDataUrl: string
      let filename: string
      
      if (type === 'achievement' && badges && totalBadges !== undefined && legendaryCount !== undefined) {
        svgDataUrl = generateAchievementShareImage(badges, totalBadges, legendaryCount, username)
        filename = `${username}-achievements-${Date.now()}.png`
      } else if (type === 'inventory' && selectedItems && totalValue) {
        svgDataUrl = generateInventoryShareImage(selectedItems, totalValue, username)
        filename = `${username}-inventory-${Date.now()}.png`
      } else {
        return
      }
      
      const pngDataUrl = await convertShareImageToPNG(svgDataUrl)
      downloadShareImage(pngDataUrl, filename)
    } catch (error) {
      log.error('Error generating PNG share image', 'ShareModal', error as Error, { type, username })
    } finally {
      setGeneratingImage(false)
    }
  }

  return (
    <div className="social-share-overlay" onClick={onClose}>
      <div className="social-share-modal" onClick={(e) => e.stopPropagation()}>
        <div className="share-header">
          <h3>Share {type === 'collection' ? 'Collection' : type === 'template' ? 'Template' : type === 'achievement' ? 'Achievement' : 'NFT'}</h3>
          <button className="close-btn" onClick={onClose}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        <div className="share-content">
          {imageUrl && (
            <div className="nft-preview share-modal-preview">
              <img src={imageUrl} alt={title} className="nft-image" />
              <div className="nft-info">
                <h4>{title}</h4>
                {description && <p>{description}</p>}
              </div>
            </div>
          )}

          <div className="share-text-section">
            <div className="share-text-header">
              <label htmlFor="share-text">Customize your message:</label>
              {!defaultShareText && (
                <button 
                  className="refresh-message-btn" 
                  onClick={regenerateMessage}
                  title="Generate new random message"
                >
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                  </svg>
                </button>
              )}
            </div>
            <textarea
              id="share-text"
              value={shareText}
              onChange={(e) => setShareText(e.target.value)}
              placeholder={finalDefaultText}
              className="share-textarea"
              rows={3}
            />
          </div>

          <div className="share-url-section">
            <label>Share URL:</label>
            <div className="url-input-container">
              <input
                type="text"
                value={finalShareUrl}
                readOnly
                className="url-input"
              />
              <button
                className={`copy-btn ${copied ? 'copied' : ''}`}
                onClick={() => copyToClipboard(finalShareUrl)}
              >
                {copied ? (
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                  </svg>
                ) : (
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor">
                    <rect x="9" y="9" width="13" height="13" rx="2" ry="2"></rect>
                    <path d="M5 15H4a2 2 0 01-2-2V4a2 2 0 012-2h9a2 2 0 012 2v1"></path>
                  </svg>
                )}
                {copied ? 'Copied!' : 'Copy'}
              </button>
            </div>
          </div>

          <div className="social-buttons">
            <button className="social-btn twitter" onClick={shareToTwitter}>
              <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                <path d="M23.953 4.57a10 10 0 01-2.825.775 4.958 4.958 0 002.163-2.723c-.951.555-2.005.959-3.127 1.184a4.92 4.92 0 00-8.384 4.482C7.69 8.095 4.067 6.13 1.64 3.162a4.822 4.822 0 00-.666 2.475c0 1.71.87 3.213 2.188 4.096a4.904 4.904 0 01-2.228-.616v.06a4.923 4.923 0 003.946 4.827 4.996 4.996 0 01-2.212.085 4.936 4.936 0 004.604 3.417 9.867 9.867 0 01-6.102 2.105c-.39 0-.779-.023-1.17-.067a13.995 13.995 0 007.557 2.209c9.053 0 13.998-7.496 13.998-13.985 0-.21 0-.42-.015-.63A9.935 9.935 0 0024 4.59z"/>
              </svg>
              Twitter
            </button>

            <button className="social-btn facebook" onClick={shareToFacebook}>
              <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                <path d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z"/>
              </svg>
              Facebook
            </button>

            <button className="social-btn reddit" onClick={shareToReddit}>
              <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                <path d="M12 0A12 12 0 0 0 0 12a12 12 0 0 0 12 12 12 12 0 0 0 12-12A12 12 0 0 0 12 0zm5.01 4.744c.688 0 1.25.561 1.25 1.249a1.25 1.25 0 0 1-2.498.056l-2.597-.547-.8 3.747c1.824.07 3.48.632 4.674 1.488.308-.309.73-.491 1.207-.491.968 0 1.754.786 1.754 1.754 0 .716-.435 1.333-1.01 1.614a3.111 3.111 0 0 1 .042.52c0 2.694-3.13 4.87-7.004 4.87-3.874 0-7.004-2.176-7.004-4.87 0-.183.015-.366.043-.534A1.748 1.748 0 0 1 4.028 12c0-.968.786-1.754 1.754-1.754.463 0 .898.196 1.207.49 1.207-.883 2.878-1.43 4.744-1.487l.885-4.182a.342.342 0 0 1 .14-.197.35.35 0 0 1 .238-.042l2.906.617a1.214 1.214 0 0 1 1.108-.701zM9.25 12C8.561 12 8 12.562 8 13.25c0 .687.561 1.248 1.25 1.248.687 0 1.248-.561 1.248-1.249 0-.688-.561-1.249-1.249-1.249zm5.5 0c-.687 0-1.248.561-1.248 1.25 0 .687.561 1.248 1.249 1.248.688 0 1.249-.561 1.249-1.249 0-.687-.562-1.249-1.25-1.249zm-5.466 3.99a.327.327 0 0 0-.231.094.33.33 0 0 0 0 .463c.842.842 2.484.913 2.961.913.477 0 2.105-.056 2.961-.913a.361.361 0 0 0 .029-.463.33.33 0 0 0-.464 0c-.547.533-1.684.73-2.512.73-.828 0-1.979-.196-2.512-.73a.326.326 0 0 0-.232-.095z"/>
              </svg>
              Reddit
            </button>

            <button className="social-btn discord" onClick={shareToDiscord}>
              <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                <path d="M20.317 4.3698a19.7913 19.7913 0 00-4.8851-1.5152.0741.0741 0 00-.0785.0371c-.211.3753-.4447.8648-.6083 1.2495-1.8447-.2762-3.68-.2762-5.4868 0-.1636-.3933-.4058-.8742-.6177-1.2495a.077.077 0 00-.0785-.037 19.7363 19.7363 0 00-4.8852 1.515.0699.0699 0 00-.0321.0277C.5334 9.0458-.319 13.5799.0992 18.0578a.0824.0824 0 00.0312.0561c2.0528 1.5076 4.0413 2.4228 5.9929 3.0294a.0777.0777 0 00.0842-.0276c.4616-.6304.8731-1.2952 1.226-1.9942a.076.076 0 00-.0416-.1057c-.6528-.2476-1.2743-.5495-1.8722-.8923a.077.077 0 01-.0076-.1277c.1258-.0943.2517-.1923.3718-.2914a.0743.0743 0 01.0776-.0105c3.9278 1.7933 8.18 1.7933 12.0614 0a.0739.0739 0 01.0785.0095c.1202.099.246.1981.3728.2924a.077.077 0 01-.0066.1276 12.2986 12.2986 0 01-1.873.8914.0766.0766 0 00-.0407.1067c.3604.698.7719 1.3628 1.225 1.9932a.076.076 0 00.0842.0286c1.961-.6067 3.9495-1.5219 6.0023-3.0294a.077.077 0 00.0313-.0552c.5004-5.177-.8382-9.6739-3.5485-13.6604a.061.061 0 00-.0312-.0286zM8.02 15.3312c-1.1825 0-2.1569-1.0857-2.1569-2.419 0-1.3332.9555-2.4189 2.157-2.4189 1.2108 0 2.1757 1.0952 2.1568 2.419-.0190 1.3332-.9555 2.4189-2.1569 2.4189zm7.9748 0c-1.1825 0-2.1569-1.0857-2.1569-2.419 0-1.3332.9554-2.4189 2.1569-2.4189 1.2108 0 2.1757 1.0952 2.1568 2.419 0 1.3332-.9555 2.4189-2.1568 2.4189Z"/>
              </svg>
              Discord
            </button>

            <button className="social-btn telegram" onClick={shareToTelegram}>
              <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                <path d="M11.944 0A12 12 0 0 0 0 12a12 12 0 0 0 12 12 12 12 0 0 0 12-12A12 12 0 0 0 12 0a12 12 0 0 0-.056 0zm4.962 7.224c.1-.002.321.023.465.14a.506.506 0 0 1 .171.325c.016.093.036.306.02.472-.18 1.898-.962 6.502-1.36 8.627-.168.9-.499 1.201-.82 1.23-.696.065-1.225-.46-1.9-.902-1.056-.693-1.653-1.124-2.678-1.8-1.185-.78-.417-1.21.258-1.91.177-.184 3.247-2.977 3.307-3.23.007-.032.014-.15-.056-.212s-.174-.041-.249-.024c-.106.024-1.793 1.14-5.061 3.345-.48.33-.913.49-1.302.48-.428-.008-1.252-.241-1.865-.44-.752-.245-1.349-.374-1.297-.789.027-.216.325-.437.893-.663 3.498-1.524 5.83-2.529 6.998-3.014 3.332-1.386 4.025-1.627 4.476-1.635z"/>
              </svg>
              Telegram
            </button>

            <button className="social-btn email" onClick={shareViaEmail}>
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
              </svg>
              Email
            </button>
          </div>

          {(type === 'achievement' || type === 'inventory') && username && (
            <div className="share-image-section">
              <h4>Generate Share Image</h4>
              <div className="image-buttons">
                <button 
                  className="generate-btn svg" 
                  onClick={generateAndDownloadShareImage}
                  disabled={generatingImage}
                >
                  {generatingImage ? 'Generating...' : 'Download SVG'}
                </button>
                <button 
                  className="generate-btn png" 
                  onClick={generateAndDownloadPNG}
                  disabled={generatingImage}
                >
                  {generatingImage ? 'Generating...' : 'Download PNG'}
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default ShareModal