import React, { useState, useEffect, useCallback } from 'react'
import { cartService } from '../services/cartService'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import type { CartItem, CartTotals } from '../services/cartService'

import { log } from '../services/loggingService'
import '../css/cart.css'

interface ShoppingCartProps {
  isOpen: boolean
  onClose: () => void
}

const ShoppingCart: React.FC<ShoppingCartProps> = ({ isOpen, onClose }) => {
  const { isConnected, login, purchaseNFT, batchPurchaseNFTs } = useWallet()
  const { showError, showSuccess, showWarning } = useNotifications()
  // Transaction tracking now handled directly by WharfKit through WalletProvider
  const [cartItems, setCartItems] = useState<CartItem[]>([])
  const [totals, setTotals] = useState<CartTotals>({ totalWAX: '0.0000 WAX', totalUSD: '$0.00', itemCount: 0 })
  const [isLoading, setIsLoading] = useState(false)
  const [selectedItems, setSelectedItems] = useState<Set<string>>(new Set())
  const [itemsPerTx, setItemsPerTx] = useState(20)
  const [useBatchPurchase, setUseBatchPurchase] = useState(true)

  const loadCartData = useCallback(async () => {
    setIsLoading(true)
    try {
      // Check availability of items
      await cartService.checkAvailability()
      
      const cart = cartService.getCart()
      setCartItems(cart)
      // Sync selection state with cart service
      setSelectedItems(new Set(cart.filter(item => item.selected).map(item => item.nft.asset_id)))
      
      // Calculate totals inline to avoid circular dependency
      try {
        const newTotals = await cartService.calculateTotals()
        setTotals(newTotals)
      } catch (totalsError) {
        log.error('Failed to calculate totals', 'ShoppingCart', totalsError as Error)
        showError('Calculation Error', 'Unable to calculate cart totals. Some prices may be outdated.')
      }
    } catch (error) {
      log.error('Failed to load cart data', 'ShoppingCart', error as Error)
      showError('Cart Error', 'Unable to load cart data. Please try refreshing the page.')
    } finally {
      setIsLoading(false)
    }
  }, [showError])

  useEffect(() => {
    if (isOpen) {
      loadCartData()
    }

    const handleCartChange = (cart: CartItem[]) => {
      setCartItems(cart)
      // Calculate totals directly here to avoid circular dependency
      cartService.calculateTotals().then(newTotals => {
        setTotals(newTotals)
      }).catch(error => {
        log.error('Failed to calculate totals in cart change handler', 'ShoppingCart', error)
      })
    }

    cartService.addListener(handleCartChange)
    return () => cartService.removeListener(handleCartChange)
  }, [isOpen, loadCartData])



  const handleRemoveItem = (assetId: string) => {
    cartService.removeFromCart(assetId)
    setSelectedItems(prev => {
      const newSet = new Set(prev)
      newSet.delete(assetId)
      return newSet
    })
  }

  const handleToggleSelection = (assetId: string) => {
    cartService.toggleSelection(assetId)
    // Update local state to reflect cart service state
    const cart = cartService.getCart()
    setCartItems(cart)
    setSelectedItems(new Set(cart.filter(item => item.selected).map(item => item.nft.asset_id)))
  }

  const handleSelectAll = () => {
    const availableItems = cartItems.filter(item => item.isAvailable !== false)
    const availableSelectedCount = availableItems.filter(item => item.selected).length
    
    if (availableSelectedCount === availableItems.length && availableItems.length > 0) {
      cartService.deselectAll()
    } else {
      cartService.selectAll()
    }
    
    // Update local state
    const cart = cartService.getCart()
    setCartItems(cart)
    setSelectedItems(new Set(cart.filter(item => item.selected).map(item => item.nft.asset_id)))
  }

  const handleCleanupUnavailable = () => {
    cartService.cleanupUnavailableItems()
  }

  const handleClearCart = () => {
    if (confirm('Are you sure you want to clear your entire cart?')) {
      cartService.clearCart()
      setSelectedItems(new Set())
    }
  }

  const handleBuySelected = async () => {
    const selectedItemsList = cartService.getSelectedItems().filter(item => 
      item.isAvailable !== false
    )
    
    if (selectedItemsList.length === 0) {
      showError('Please select items to purchase', 'No Items Selected')
      return
    }

    if (!isConnected) {
      try {
        await login()
      } catch (error) {
        log.error('Login failed', 'ShoppingCart', error as Error)
        showError('Login Failed', 'Please connect your wallet to purchase NFTs')
        return
      }
    }

    let successCount = 0
    const failedItems: string[] = []
    
    if (useBatchPurchase && selectedItemsList.length > 1) {
      // Process in batches
      const batches = []
      for (let i = 0; i < selectedItemsList.length; i += itemsPerTx) {
        batches.push(selectedItemsList.slice(i, i + itemsPerTx))
      }
      
      for (let batchIndex = 0; batchIndex < batches.length; batchIndex++) {
        const batch = batches[batchIndex]
        const batchPurchases = batch
          .filter(item => item.nft.sale_id && item.nft.price)
          .map(item => ({
            saleId: item.nft.sale_id!,
            price: item.nft.price!.replace(' WAX', '')
          }))
        
        if (batchPurchases.length === 0) {
          batch.forEach(item => {
            if (!item.nft.sale_id) {
              failedItems.push(`${item.nft.name} (not for sale)`)
            }
          })
          continue
        }
        
        try {
          await batchPurchaseNFTs(batchPurchases)
          
          // Remove successfully purchased items from cart
          batch.forEach(item => {
            if (item.nft.sale_id) {
              cartService.removeFromCart(item.nft.asset_id)
              setSelectedItems(prev => {
                const newSet = new Set(prev)
                newSet.delete(item.nft.asset_id)
                return newSet
              })
              successCount++
            }
          })
          
          // Small delay between batches
          if (batchIndex < batches.length - 1) {
            await new Promise(resolve => setTimeout(resolve, 2000))
          }
          
        } catch (error) {
          log.error('Batch purchase failed', 'ShoppingCart', error as Error)
          showError('Batch Purchase Failed', `Failed to purchase batch of ${batch.length} items`)
          batch.forEach(item => {
            failedItems.push(`${item.nft.name} (batch failed: ${error instanceof Error ? error.message : 'Unknown error'})`)
          })
        }
      }
    } else {
      // Process sequentially (original behavior)
      for (const item of selectedItemsList) {
        try {
          if (!item.nft.sale_id || !item.nft.price) {
            failedItems.push(`${item.nft.name} (not for sale)`)
            continue
          }
          
          // Execute purchase transaction directly with WharfKit
          await purchaseNFT(item.nft.sale_id, item.nft.price.replace(' WAX', ''))
          successCount++
          
          // Remove successfully purchased item from cart
          cartService.removeFromCart(item.nft.asset_id)
          setSelectedItems(prev => {
            const newSet = new Set(prev)
            newSet.delete(item.nft.asset_id)
            return newSet
          })
          
          // Small delay between purchases to prevent rate limiting
          if (selectedItemsList.length > 1) {
            await new Promise(resolve => setTimeout(resolve, 1000))
          }
          
        } catch (error) {
          log.error(`Purchase failed for ${item.nft.name}`, 'ShoppingCart', error as Error)
          showError('Purchase Failed', `Failed to purchase ${item.nft.name}`)
          failedItems.push(`${item.nft.name} (${error instanceof Error ? error.message : 'Unknown error'})`)
        }
      }
    }
    
    // Show results
    if (successCount > 0 && failedItems.length === 0) {
      showSuccess(`Successfully purchased ${successCount} item${successCount !== 1 ? 's' : ''}!`, 'Purchase Complete')
    } else if (successCount > 0 && failedItems.length > 0) {
      showWarning(
        `Partially completed: ${successCount} successful, ${failedItems.length} failed. Failed items: ${failedItems.join(', ')}`,
        'Purchase Partially Complete',
        8000
      )
    } else {
      showError(`All purchases failed. Items: ${failedItems.join(', ')}`, 'Purchase Failed')
    }
  }

  const availableItems = cartItems.filter(item => item.isAvailable !== false)
  const unavailableItems = cartItems.filter(item => item.isAvailable === false)

  if (!isOpen) return null

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose()
    }
  }

  return (
    <div 
      className="modal-backdrop" 
      onClick={handleBackdropClick}
      role="dialog"
      aria-modal="true"
      aria-labelledby="cart-title"
    >
      <div className="modal-content shopping-cart-modal">
        <div className="modal-header">
          <h2 className="modal-title" id="cart-title">
            Shopping Cart ({totals.itemCount} {totals.itemCount === 1 ? 'item' : 'items'})
          </h2>
          <button 
            className="modal-close" 
            onClick={onClose}
            aria-label="Close shopping cart"
            type="button"
          >
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" aria-hidden="true">
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </button>
        </div>

        <div className="modal-body cart-body">
          {isLoading ? (
            <div className="cart-loading">
              <div className="loading-spinner"></div>
              <p>Checking item availability...</p>
            </div>
          ) : cartItems.length === 0 ? (
            <div className="cart-empty">
              <div className="empty-cart-icon">
                <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                  <circle cx="9" cy="21" r="1"></circle>
                  <circle cx="20" cy="21" r="1"></circle>
                  <path d="m1 1 4 4 2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"></path>
                </svg>
              </div>
              <h3>Your cart is empty</h3>
              <p>Add some NFTs from the marketplace to get started!</p>
            </div>
          ) : (
            <>
              {/* Cart Controls */}
              <div className="cart-controls">
                <div className="cart-actions-left">
                  <button 
                    className="btn-select-all"
                    onClick={handleSelectAll}
                    aria-label={selectedItems.size === availableItems.length && availableItems.length > 0 
                      ? `Deselect all ${availableItems.length} items` 
                      : `Select all ${availableItems.length} items`
                    }
                    type="button"
                  >
                    {selectedItems.size === availableItems.length && availableItems.length > 0 
                      ? 'Deselect All' 
                      : 'Select All'
                    }
                  </button>
                  {unavailableItems.length > 0 && (
                    <button 
                      className="btn-cleanup"
                      onClick={handleCleanupUnavailable}
                      aria-label={`Remove ${unavailableItems.length} unavailable items from cart`}
                      type="button"
                    >
                      Remove Unavailable ({unavailableItems.length})
                    </button>
                  )}
                </div>
                <button 
                  className="btn-clear-cart"
                  onClick={handleClearCart}
                  aria-label="Clear all items from cart"
                  type="button"
                >
                  Clear Cart
                </button>
              </div>

              {/* Batch Purchase Settings */}
              {availableItems.length > 1 && (
                <div className="batch-settings" role="group" aria-labelledby="batch-settings-label">
                  <div className="batch-toggle">
                    <label>
                      <input
                        type="checkbox"
                        checked={useBatchPurchase}
                        onChange={(e) => setUseBatchPurchase(e.target.checked)}
                        aria-describedby="batch-purchase-description"
                      />
                      <span id="batch-settings-label">Use batch purchasing</span>
                    </label>
                    <div id="batch-purchase-description" className="sr-only">
                      Batch purchasing allows you to buy multiple items in a single transaction, which is faster and more efficient.
                    </div>
                  </div>
                  {useBatchPurchase && (
                    <div className="batch-controls">
                      <label htmlFor="items-per-tx">Items per transaction:</label>
                      <div className="batch-slider">
                        <input
                          id="items-per-tx"
                          type="range"
                          min="1"
                          max="50"
                          value={itemsPerTx}
                          onChange={(e) => setItemsPerTx(parseInt(e.target.value))}
                        />
                        <span className="batch-value">{itemsPerTx}</span>
                      </div>
                      <div className="batch-info">
                        {selectedItems.size > itemsPerTx && (
                          <small>
                            Will create {Math.ceil(selectedItems.size / itemsPerTx)} transaction{Math.ceil(selectedItems.size / itemsPerTx) > 1 ? 's' : ''}
                          </small>
                        )}
                      </div>
                    </div>
                  )}
                </div>
              )}

              {/* Available Items */}
              {availableItems.length > 0 && (
                <div className="cart-section">
                  <h3 className="section-title">Available Items</h3>
                  <div className="cart-items">
                    {availableItems.map((item) => (
                      <div key={item.nft.asset_id} className="cart-item">
                        <div className="item-checkbox">
                          <input
                            type="checkbox"
                            checked={selectedItems.has(item.nft.asset_id)}
                            onChange={() => handleToggleSelection(item.nft.asset_id)}
                            aria-label={`Select ${item.nft.name} for purchase`}
                            id={`item-${item.nft.asset_id}`}
                          />
                        </div>
                        <div className="item-image">
                          {item.nft.video ? (
                            <video 
                              src={item.nft.video} 
                              className="cart-item-media"
                              muted
                              onError={(e) => {
                                const img = document.createElement('img')
                                img.src = item.nft.image
                                img.className = 'cart-item-media'
                                img.alt = item.nft.name
                                e.currentTarget.parentNode?.replaceChild(img, e.currentTarget)
                              }}
                            />
                          ) : (
                            <img 
                              src={item.nft.image} 
                              alt={`${item.nft.name} from ${item.nft.collection}`}
                              className="cart-item-media"
                            />
                          )}
                        </div>
                        <div className="item-details">
                          <h4 className="item-name">{item.nft.name}</h4>
                          <p className="item-collection">{item.nft.collection}</p>
                          <div className="item-price" aria-label={`Price: ${item.nft.price}`}>
                            <span className="price-wax">{item.nft.price}</span>
                          </div>
                        </div>
                        <button 
                          className="btn-remove"
                          onClick={() => handleRemoveItem(item.nft.asset_id)}
                          title="Remove from cart"
                          aria-label={`Remove ${item.nft.name} from cart`}
                          type="button"
                        >
                          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" aria-hidden="true">
                            <polyline points="3,6 5,6 21,6"></polyline>
                            <path d="m19,6v14a2,2 0 0,1 -2,2H7a2,2 0 0,1 -2,-2V6m3,0V4a2,2 0 0,1 2,-2h4a2,2 0 0,1 2,2v2"></path>
                          </svg>
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Unavailable Items */}
              {unavailableItems.length > 0 && (
                <div className="cart-section">
                  <h3 className="section-title unavailable-title">
                    Unavailable Items ({unavailableItems.length})
                  </h3>
                  <div className="cart-items unavailable-items">
                    {unavailableItems.map((item) => (
                      <div key={item.nft.asset_id} className="cart-item unavailable">
                        <div className="item-image">
                          <img 
                            src={item.nft.image} 
                            alt={item.nft.name}
                            className="cart-item-media"
                          />
                          <div className="unavailable-overlay">Unavailable</div>
                        </div>
                        <div className="item-details">
                          <h4 className="item-name">{item.nft.name}</h4>
                          <p className="item-collection">{item.nft.collection}</p>
                          <div className="item-price">
                            <span className="price-wax">{item.nft.price}</span>
                          </div>
                        </div>
                        <button 
                          className="btn-remove"
                          onClick={() => handleRemoveItem(item.nft.asset_id)}
                          title="Remove from cart"
                        >
                          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                            <polyline points="3,6 5,6 21,6"></polyline>
                            <path d="m19,6v14a2,2 0 0,1 -2,2H7a2,2 0 0,1 -2,-2V6m3,0V4a2,2 0 0,1 2,-2h4a2,2 0 0,1 2,2v2"></path>
                          </svg>
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </>
          )}
        </div>

        {cartItems.length > 0 && (
          <div className="modal-footer cart-footer">
            <div className="cart-totals">
              <div className="total-line">
                <span className="total-label">Total ({selectedItems.size} selected):</span>
                <div className="total-prices">
                  <span className="total-wax">{totals.totalWAX}</span>
                  <span className="total-usd">{totals.totalUSD}</span>
                </div>
              </div>
            </div>
            <div className="cart-actions">
              <button 
                className="btn-secondary" 
                onClick={onClose}
                type="button"
                aria-label="Continue shopping and close cart"
              >
                Continue Shopping
              </button>
              <button 
                className="btn-primary"
                onClick={handleBuySelected}
                disabled={selectedItems.size === 0}
                type="button"
                aria-label={`Buy ${selectedItems.size} selected items`}
                aria-describedby="cart-totals"
              >
                Buy Selected ({selectedItems.size})
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}

export default ShoppingCart