import React from 'react';
import { usePWA } from '../hooks/usePWA';
import '../css/offline-indicator.css';

interface OfflineIndicatorProps {
  className?: string;
}

const OfflineIndicator: React.FC<OfflineIndicatorProps> = ({ className = '' }) => {
  const { isOnline } = usePWA();

  // Don't show indicator when online
  if (isOnline) {
    return null;
  }

  return (
    <div 
      className={`offline-indicator ${className}`} 
      role="status" 
      aria-live="polite"
      aria-label="You are currently offline"
    >
      <div className="offline-content">
        <div className="offline-icon">
          <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg" aria-hidden="true">
            <path 
              d="M3 7c0-1.1.9-2 2-2h10c1.1 0 2 .9 2 2v6c0 1.1-.9 2-2 2H5c-1.1 0-2-.9-2-2V7z" 
              stroke="currentColor" 
              strokeWidth="1.5" 
              fill="none"
            />
            <path 
              d="M7 9l6 4M13 9l-6 4" 
              stroke="currentColor" 
              strokeWidth="1.5" 
              strokeLinecap="round"
            />
          </svg>
        </div>
        
        <div className="offline-text">
          <span className="offline-title">You're offline</span>
          <span className="offline-description">Some features may be limited</span>
        </div>
        
        <div className="offline-pulse" aria-hidden="true"></div>
      </div>
    </div>
  );
};

export default OfflineIndicator;