import { logger } from './loggingService'

/**
 * Centralized storage service with proper error handling and fallbacks
 */
class StorageService {
  private isStorageAvailable(storage: Storage): boolean {
    try {
      const test = '__storage_test__'
      storage.setItem(test, test)
      storage.removeItem(test)
      return true
    } catch {
      return false
    }
  }

  private get isLocalStorageAvailable(): boolean {
    return typeof window !== 'undefined' && this.isStorageAvailable(localStorage)
  }

  private get isSessionStorageAvailable(): boolean {
    return typeof window !== 'undefined' && this.isStorageAvailable(sessionStorage)
  }

  /**
   * Get item from storage with fallback and error handling
   */
  getItem(key: string, useSessionStorage = false): string | null {
    try {
      const storage = useSessionStorage ? sessionStorage : localStorage
      
      if (useSessionStorage && this.isSessionStorageAvailable) {
        return storage.getItem(key)
      } else if (!useSessionStorage && this.isLocalStorageAvailable) {
        return storage.getItem(key)
      }
      
      // Fallback to the other storage type
      const fallbackStorage = useSessionStorage ? localStorage : sessionStorage
      const isFallbackAvailable = useSessionStorage ? this.isLocalStorageAvailable : this.isSessionStorageAvailable
      
      if (isFallbackAvailable) {
        logger.warn(`Primary storage unavailable, using fallback for key: ${key}`)
        return fallbackStorage.getItem(key)
      }
      
      logger.warn(`No storage available for key: ${key}`)
      return null
    } catch (error) {
      logger.error('StorageService.getItem failed', 'StorageService', error as Error, { key, useSessionStorage })
      return null
    }
  }

  /**
   * Set item in storage with fallback and error handling
   */
  setItem(key: string, value: string, useSessionStorage = false): boolean {
    try {
      const storage = useSessionStorage ? sessionStorage : localStorage
      
      if (useSessionStorage && this.isSessionStorageAvailable) {
        storage.setItem(key, value)
        return true
      } else if (!useSessionStorage && this.isLocalStorageAvailable) {
        storage.setItem(key, value)
        return true
      }
      
      // Fallback to the other storage type
      const fallbackStorage = useSessionStorage ? localStorage : sessionStorage
      const isFallbackAvailable = useSessionStorage ? this.isLocalStorageAvailable : this.isSessionStorageAvailable
      
      if (isFallbackAvailable) {
        logger.warn(`Primary storage unavailable, using fallback for key: ${key}`)
        fallbackStorage.setItem(key, value)
        return true
      }
      
      logger.warn(`No storage available for key: ${key}`)
      return false
    } catch (error) {
      logger.error('StorageService.setItem failed', 'StorageService', error as Error, { key, useSessionStorage })
      return false
    }
  }

  /**
   * Remove item from storage with error handling
   */
  removeItem(key: string, useSessionStorage = false): boolean {
    try {
      const storage = useSessionStorage ? sessionStorage : localStorage
      
      if (useSessionStorage && this.isSessionStorageAvailable) {
        storage.removeItem(key)
        return true
      } else if (!useSessionStorage && this.isLocalStorageAvailable) {
        storage.removeItem(key)
        return true
      }
      
      // Try to remove from both storages as fallback
      let removed = false
      if (this.isLocalStorageAvailable) {
        localStorage.removeItem(key)
        removed = true
      }
      if (this.isSessionStorageAvailable) {
        sessionStorage.removeItem(key)
        removed = true
      }
      
      if (!removed) {
        logger.warn(`No storage available to remove key: ${key}`)
      }
      
      return removed
    } catch (error) {
      logger.error('StorageService.removeItem failed', 'StorageService', error as Error, { key, useSessionStorage })
      return false
    }
  }

  /**
   * Clear all items from storage with error handling
   */
  clear(useSessionStorage = false): boolean {
    try {
      const storage = useSessionStorage ? sessionStorage : localStorage
      
      if (useSessionStorage && this.isSessionStorageAvailable) {
        storage.clear()
        return true
      } else if (!useSessionStorage && this.isLocalStorageAvailable) {
        storage.clear()
        return true
      }
      
      logger.warn('No storage available to clear')
      return false
    } catch (error) {
      logger.error('StorageService.clear failed', 'StorageService', error as Error, { useSessionStorage })
      return false
    }
  }

  /**
   * Get JSON object from storage with parsing error handling
   */
  getJSON<T>(key: string, defaultValue: T, useSessionStorage = false): T {
    try {
      const item = this.getItem(key, useSessionStorage)
      if (item === null) {
        return defaultValue
      }
      return JSON.parse(item) as T
    } catch (error) {
      logger.error('StorageService.getJSON failed', 'StorageService', error as Error, { key, useSessionStorage })
      return defaultValue
    }
  }

  /**
   * Set JSON object in storage with stringification error handling
   */
  setJSON<T>(key: string, value: T, useSessionStorage = false): boolean {
    try {
      const jsonString = JSON.stringify(value)
      return this.setItem(key, jsonString, useSessionStorage)
    } catch (error) {
      logger.error('StorageService.setJSON failed', 'StorageService', error as Error, { key, useSessionStorage })
      return false
    }
  }

  /**
   * Get storage statistics
   */
  getStorageStats(): {
    localStorage: { available: boolean; used: number; total: number }
    sessionStorage: { available: boolean; used: number; total: number }
  } {
    const getStorageSize = (storage: Storage): { used: number; total: number } => {
      try {
        let used = 0
        for (const key in storage) {
          if (Object.prototype.hasOwnProperty.call(storage, key)) {
            used += storage[key].length + key.length
          }
        }
        
        // Estimate total storage (usually 5-10MB for localStorage)
        const total = 5 * 1024 * 1024 // 5MB estimate
        return { used, total }
      } catch {
        return { used: 0, total: 0 }
      }
    }

    return {
      localStorage: {
        available: this.isLocalStorageAvailable,
        ...getStorageSize(this.isLocalStorageAvailable ? localStorage : {} as Storage)
      },
      sessionStorage: {
        available: this.isSessionStorageAvailable,
        ...getStorageSize(this.isSessionStorageAvailable ? sessionStorage : {} as Storage)
      }
    }
  }

  /**
   * Check if storage quota is exceeded
   */
  isQuotaExceeded(): boolean {
    const stats = this.getStorageStats()
    const localStorageUsage = stats.localStorage.used / stats.localStorage.total
    const sessionStorageUsage = stats.sessionStorage.used / stats.sessionStorage.total
    
    return localStorageUsage > 0.9 || sessionStorageUsage > 0.9
  }

  /**
   * Clean up old or expired data
   */
  cleanup(keyPatterns: string[] = []): number {
    let cleaned = 0
    
    try {
      const storages = [
        { storage: localStorage, available: this.isLocalStorageAvailable },
        { storage: sessionStorage, available: this.isSessionStorageAvailable }
      ]
      
      for (const { storage, available } of storages) {
        if (!available) continue
        
        const keysToRemove: string[] = []
        
        for (let i = 0; i < storage.length; i++) {
          const key = storage.key(i)
          if (!key) continue
          
          // Remove keys matching patterns
          for (const pattern of keyPatterns) {
            if (key.includes(pattern)) {
              keysToRemove.push(key)
              break
            }
          }
        }
        
        for (const key of keysToRemove) {
          storage.removeItem(key)
          cleaned++
        }
      }
    } catch (error) {
      logger.error('StorageService.cleanup failed', 'StorageService', error as Error, { keyPatterns })
    }
    
    return cleaned
  }
}

export const storageService = new StorageService()
export default storageService