import React, { useState, useEffect, useCallback } from 'react'
import { useWallet } from '../hooks/useWallet'
import type { InventoryNFT } from '../types/inventory'
import type { OfferApiResponse } from '../types'
import MediaRenderer from './MediaRenderer'
import { useDisplaySettings } from '../hooks/useDisplaySettings'
import { useNotifications } from '../hooks/useNotifications'
import { apiService } from '../services/api'
import { offerService } from '../services/offerService'
import '../css/modal.css'
import '../css/view-offers-modal.css'

interface ViewOffersModalProps {
  isOpen: boolean
  onClose: () => void
  nft: InventoryNFT | null
}



interface Offer {
  id: string
  buyer: string
  price: string
  symbol: string
  timestamp: string
  memo?: string
}

const ViewOffersModal: React.FC<ViewOffersModalProps> = ({ isOpen, onClose, nft }) => {
  const { account, session } = useWallet()
  const { settings } = useDisplaySettings()
  const { showSuccess, showError } = useNotifications()
  const [offers, setOffers] = useState<Offer[]>([])
  const [loading, setLoading] = useState(false)
  const [acceptingOffer, setAcceptingOffer] = useState<string | null>(null)

  const loadOffers = useCallback(async () => {
    if (!nft) return
    
    setLoading(true)
    try {
      // Fetch real offers from AtomicMarket API
      const offersData = await apiService.getAssetOffers(nft.asset_id)
      
      if (offersData && offersData.length > 0) {
        // Convert to our Offer interface format
        const formattedOffers: Offer[] = offersData.map((offer: OfferApiResponse) => ({
          id: offer.id,
          buyer: offer.buyer,
          price: (parseFloat(offer.price) / 100000000).toFixed(8), // Convert from atomic units
          symbol: offer.token,
          timestamp: offer.created,
          memo: offer.memo
        }))
        
        // Sort by price (highest first)
        const sortedOffers = formattedOffers.sort((a, b) => parseFloat(b.price) - parseFloat(a.price))
        setOffers(sortedOffers)
      } else {
        setOffers([])
      }
    } catch (error) {
      console.error('Failed to load offers:', error)
      setOffers([])
    } finally {
      setLoading(false)
    }
  }, [nft])

  useEffect(() => {
    if (isOpen && nft) {
      loadOffers()
    }
  }, [isOpen, nft, loadOffers])

  const handleAcceptOffer = async (offer: Offer) => {
    if (!account || !session || !nft) {
      showError('Please connect your wallet first', 'Wallet Required')
      return
    }

    const confirmed = window.confirm(
      `Accept offer of ${offer.price} ${offer.symbol} from ${offer.buyer}?\n\nThis action cannot be undone.`
    )
    
    if (!confirmed) return

    setAcceptingOffer(offer.id)
    
    try {
      if (!session) {
        showError('Wallet Not Connected', 'Please connect your wallet to accept offers.')
        return
      }

      await offerService.acceptBuyOffer(session, offer.id)
      
      showSuccess(
        'Offer Accepted!',
        `${offer.price} ${offer.symbol} will be transferred to your account.`
      )
      onClose()
    } catch (error) {
      showError(
        'Failed to Accept Offer',
        error instanceof Error ? error.message : 'Failed to accept the offer. Please try again.'
      )
    } finally {
      setAcceptingOffer(null)
    }
  }

  const formatTimeAgo = (timestamp: string) => {
    const now = new Date()
    const offerTime = new Date(timestamp)
    const diffMs = now.getTime() - offerTime.getTime()
    const diffHours = Math.floor(diffMs / (1000 * 60 * 60))
    const diffDays = Math.floor(diffHours / 24)
    
    if (diffDays > 0) {
      return `${diffDays} day${diffDays > 1 ? 's' : ''} ago`
    } else if (diffHours > 0) {
      return `${diffHours} hour${diffHours > 1 ? 's' : ''} ago`
    } else {
      const diffMinutes = Math.floor(diffMs / (1000 * 60))
      return `${diffMinutes} minute${diffMinutes > 1 ? 's' : ''} ago`
    }
  }

  if (!isOpen || !nft) return null

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content view-offers-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>View Offers</h2>
          <button className="modal-close" onClick={onClose}>×</button>
        </div>
        
        <div className="offers-content">
          <div className="view-offers-nft-info">
            <div className="nft-image">
              <MediaRenderer
                imageUrl={nft.img}
                alt={nft.name}
                enableAnimations={settings.enableAnimatedNFTs}
              />
            </div>
            <div className="nft-details">
              <h3>{nft.name}</h3>
              <div className="nft-id">#{nft.asset_id}</div>
              <div className="collection-info">
                {typeof nft.collection === 'string' ? nft.collection : nft.collection?.name || nft.collection?.collection_name || 'Unknown'} • {nft.schema.schema_name}
              </div>
            </div>
          </div>

          <div className="offers-section">
            <div className="offers-header">
              <h3>Active Offers ({offers.length})</h3>
              <button className="refresh-btn" onClick={loadOffers} disabled={loading}>
                {loading ? '🔄' : '↻'} Refresh
              </button>
            </div>
            
            {loading ? (
              <div className="loading-state">
                <div className="loading-spinner"></div>
                <p>Loading offers...</p>
              </div>
            ) : offers.length === 0 ? (
              <div className="empty-offers">
                <div className="empty-icon">📭</div>
                <h4>No Offers Yet</h4>
                <p>This NFT hasn't received any offers yet. List it on the marketplace to attract buyers!</p>
              </div>
            ) : (
              <div className="offers-list">
                {offers.map((offer, index) => (
                  <div key={offer.id} className={`offer-card ${index === 0 ? 'highest-offer' : ''}`}>
                    <div className="offer-header">
                      <div className="buyer-info">
                        <strong>{offer.buyer}</strong>
                        {index === 0 && <span className="highest-badge">🏆 Highest</span>}
                      </div>
                      <div className="offer-time">{formatTimeAgo(offer.timestamp)}</div>
                    </div>
                    
                    <div className="offer-price">
                      <span className="price-amount">{parseFloat(offer.price).toFixed(2)}</span>
                      <span className="price-symbol">{offer.symbol}</span>
                    </div>
                    
                    {offer.memo && (
                      <div className="offer-memo">
                        <em>"{offer.memo}"</em>
                      </div>
                    )}
                    
                    <div className="offer-actions">
                      <button 
                        className="accept-btn"
                        onClick={() => handleAcceptOffer(offer)}
                        disabled={acceptingOffer !== null}
                      >
                        {acceptingOffer === offer.id ? 'Accepting...' : 'Accept Offer'}
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
        
        <div className="modal-footer">
          <button className="btn-secondary" onClick={onClose}>
            Close
          </button>
        </div>
      </div>
    </div>
  )
}

export default ViewOffersModal