import React from 'react'
import '../css/error-message.css'
import { type ErrorMessageConfig, ErrorMessageService } from '../services/errorMessageService'

interface ErrorMessageProps {
  title?: string
  message?: string
  onRetry?: () => void
  showRetry?: boolean
  error?: unknown
  errorConfig?: ErrorMessageConfig
  errorType?: keyof typeof ErrorMessageService.MESSAGES
  isRetrying?: boolean
  retryCount?: number
  maxRetries?: number
}

const ErrorMessage: React.FC<ErrorMessageProps> = ({ 
  title, 
  message,
  onRetry,
  showRetry = true,
  error,
  errorConfig,
  errorType,
  isRetrying = false,
  retryCount = 0,
  maxRetries = 3
}) => {
  // Determine which error configuration to use
  let finalConfig: ErrorMessageConfig
  
  if (errorConfig) {
    finalConfig = errorConfig
  } else if (errorType) {
    finalConfig = ErrorMessageService.getMessage(errorType)
  } else if (error) {
    finalConfig = ErrorMessageService.formatError(error)
  } else {
    finalConfig = {
      title: title || "Oh no! Something went wrong",
      message: message || "We're having trouble loading the data right now. Please try again in a moment.",
      action: "Try Again"
    }
  }
  
  const displayTitle = title || finalConfig.title
  const displayMessage = message || finalConfig.message
  const actionText = isRetrying ? "Retrying..." : (finalConfig.action || "Try Again")
  
  // Determine error icon based on error type
  const getErrorIcon = () => {
    if (isRetrying) return "🔄"
    
    switch (errorType) {
      case 'NETWORK_ERROR':
        return "📡"
      case 'TIMEOUT_ERROR':
        return "⏱️"
      case 'API_ERROR':
        return "⚠️"
      case 'LOAD_FAILED':
        return "📂"
      default:
        return "😅"
    }
  }
  
  return (
    <div className={`error-message ${isRetrying ? 'retrying' : ''}`}>
      <div className="error-content">
        <div className={`error-icon ${isRetrying ? 'spinning' : ''}`}>
          {getErrorIcon()}
        </div>
        <h3 className="error-title">{displayTitle}</h3>
        <p className="error-text">{displayMessage}</p>
        
        {retryCount > 0 && !isRetrying && (
          <div className="retry-status">
            <span className="retry-count">Attempt {retryCount} of {maxRetries}</span>
          </div>
        )}
        
        {showRetry && onRetry && (
          <button 
            className={`error-retry-btn ${isRetrying ? 'retrying' : ''}`} 
            onClick={onRetry}
            disabled={isRetrying || retryCount >= maxRetries}
          >
            {isRetrying && <span className="retry-spinner"></span>}
            {actionText}
          </button>
        )}
        
        {retryCount >= maxRetries && (
          <div className="max-retries-reached">
            <p>Maximum retry attempts reached. Please refresh the page or try again later.</p>
            <button 
              className="error-refresh-btn" 
              onClick={() => window.location.reload()}
            >
              Refresh Page
            </button>
          </div>
        )}
      </div>
    </div>
  )
}

export default ErrorMessage