import React, { useState } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import type { InventoryNFT } from '../types/inventory'
import { MarketplaceConfigService } from '../config/marketplaceConfig'
import { log } from '../services/loggingService'
import { WharfKitService } from '../services/wharfkit'
import '../css/modal.css'

interface BulkBurnModalProps {
  nfts: InventoryNFT[]
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
}

const BulkBurnModal: React.FC<BulkBurnModalProps> = ({ nfts, isOpen, onClose, onSuccess }) => {
  const [isBurning, setIsBurning] = useState(false)
  const [burnProgress, setBurnProgress] = useState(0)
  const [confirmText, setConfirmText] = useState('')
  const { session } = useWallet()
  const { showError, showSuccess, showWarning } = useNotifications()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!session || nfts.length === 0) {
      showError('Please connect your wallet and select NFTs to burn', 'Wallet Required')
      return
    }

    // Filter out WaxRock Pixal PFPs
    const waxrockPixals: InventoryNFT[] = []
    const burnableNfts = nfts.filter(nft => {
      if (MarketplaceConfigService.isPixalPFP(nft.template.template_id)) {
        const waxRock = nft.data?.WaxRock as string
        if (waxRock && MarketplaceConfigService.isValidWaxRock(waxRock)) {
          waxrockPixals.push(nft)
          return false // Exclude from burning
        }
      }
      return true // Include in burning
    })

    // Show warning if WaxRock Pixals were found
    if (waxrockPixals.length > 0) {
      const waxrockNames = waxrockPixals.map(nft => `${nft.name} (${nft.data?.WaxRock})`).join(', ')
      showWarning(
        `${waxrockPixals.length} WaxRock Pixal PFP(s) were excluded from burning for protection: ${waxrockNames}. WaxRock Pixals are protected from burning and recycling on our platform.`,
        'Protected NFTs Excluded',
        8000
      )
      
      if (burnableNfts.length === 0) {
        showError('No NFTs remaining to burn after excluding WaxRock Pixals.', 'No Burnable NFTs')
        return
      }
    }

    if (confirmText !== 'BURN') {
      showError('Please type "BURN" to confirm this action', 'Confirmation Required')
      return
    }

    setIsBurning(true)
    setBurnProgress(0)

    try {
      // Create burn actions for burnable NFTs only
      const actions = burnableNfts.map(nft => ({
        account: 'atomicassets',
        name: 'burnasset',
        authorization: [{
          actor: WharfKitService.safeGetActor(session),
          permission: WharfKitService.safeGetPermission(session)
        }],
        data: {
          asset_owner: WharfKitService.safeGetActor(session),
          asset_id: nft.asset_id
        }
      }))

      // Execute burns in batches to avoid transaction size limits
      const batchSize = 10 // Burn up to 10 NFTs per transaction
      const batches = []
      
      for (let i = 0; i < actions.length; i += batchSize) {
        batches.push(actions.slice(i, i + batchSize))
      }

      let completedBatches = 0
      
      for (const batch of batches) {
        const result = await session.transact({ actions: batch })
        
        if (result) {
          completedBatches++
          setBurnProgress((completedBatches / batches.length) * 100)
        }
      }
      
      showSuccess(`Successfully burned ${burnableNfts.length} NFT(s)!`, 'Burn Complete')
      onSuccess?.()
      onClose()
      setConfirmText('')
      
    } catch (error) {
      log.error('Bulk burn failed', 'BulkBurnModal.handleSubmit', error as Error, { nftCount: burnableNfts.length })
      showError(`Burn failed: ${error instanceof Error ? error.message : 'Unknown error'}`, 'Burn Failed')
    } finally {
      setIsBurning(false)
      setBurnProgress(0)
    }
  }

  if (!isOpen) return null

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content bulk-burn-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Bulk Burn NFTs</h2>
          <button className="modal-close" onClick={onClose}>
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </button>
        </div>

        <div className="modal-body">
          <div className="burn-warning">
            <div className="warning-icon">
              <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
                <path d="m21.73 18-8-14a2 2 0 0 0-3.48 0l-8 14A2 2 0 0 0 4 21h16a2 2 0 0 0 1.73-3Z"></path>
                <line x1="12" y1="9" x2="12" y2="13"></line>
                <line x1="12" y1="17" x2="12.01" y2="17"></line>
              </svg>
            </div>
            <h3>⚠️ Warning: This action cannot be undone!</h3>
            <p>You are about to permanently burn <strong>{nfts.length} NFT{nfts.length !== 1 ? 's' : ''}</strong>. This will completely destroy these assets and they cannot be recovered.</p>
          </div>

          <div className="nfts-to-burn">
            <h4>NFTs to be burned:</h4>
            <div className="burn-list-container">
              {nfts.map((nft) => (
                <div key={nft.asset_id} className="burn-nft-item">
                  <img 
                    src={nft.img?.startsWith('http') ? nft.img : `https://ipfs.io/ipfs/${nft.img}`}
                    alt={nft.name}
                    onError={(e) => {
                      const target = e.target as HTMLImageElement
                      target.src = 'https://pixeljourney.xyz/img/toppixals/10.webp'
                    }}
                  />
                  <div className="nft-details">
                    <span className="nft-name">{nft.name}</span>
                    <span className="nft-collection">{typeof nft.collection === 'string' ? nft.collection : nft.collection?.collection_name || nft.collection?.name || 'Unknown'}</span>
                    {nft.mint_number && (
                      <span className="nft-mint">#{nft.mint_number}</span>
                    )}
                    <span className="nft-id">ID: {nft.asset_id}</span>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <form onSubmit={handleSubmit} className="burn-form">
            <div className="confirmation-section">
              <label htmlFor="confirmText">
                Type <strong>"BURN"</strong> to confirm this action:
              </label>
              <input
                id="confirmText"
                type="text"
                value={confirmText}
                onChange={(e) => setConfirmText(e.target.value.toUpperCase())}
                placeholder="Type BURN to confirm"
                disabled={isBurning}
                required
              />
            </div>

            {isBurning && (
              <div className="burn-progress">
                <div className="progress-bar">
                  <div 
                    className="progress-fill" 
                    style={{ width: `${burnProgress}%` }}
                  ></div>
                </div>
                <p>Burning NFTs... {Math.round(burnProgress)}% complete</p>
              </div>
            )}

            <div className="modal-actions">
              <button 
                type="button" 
                className="btn-secondary" 
                onClick={onClose}
                disabled={isBurning}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn-danger"
                disabled={isBurning || confirmText !== 'BURN'}
              >
                {isBurning ? 'Burning...' : `🔥 Burn ${nfts.length} NFT${nfts.length !== 1 ? 's' : ''}`}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default BulkBurnModal