import React, { Component, type ReactNode } from 'react'
import ErrorMessage from './ErrorMessage'
import { ErrorMessageService } from '../services/errorMessageService'
import { log } from '../services/loggingService'


interface Props {
  children: ReactNode
  fallback?: ReactNode
  onError?: (error: Error, errorInfo: React.ErrorInfo) => void
}

interface State {
  hasError: boolean
  error?: Error
  errorInfo?: React.ErrorInfo
  retryCount: number
  isRetrying: boolean
  lastRetryTime?: number
  errorCategory: 'network' | 'api' | 'timeout' | 'unknown'
}

class APIErrorBoundary extends Component<Props, State> {
  private maxRetries = 3
  private retryTimeouts: NodeJS.Timeout[] = []

  constructor(props: Props) {
    super(props)
    this.state = { 
      hasError: false, 
      retryCount: 0,
      isRetrying: false,
      errorCategory: 'unknown'
    }
  }

  componentWillUnmount() {
    // Clear any pending retry timeouts
    this.retryTimeouts.forEach(timeout => clearTimeout(timeout))
  }

  static getDerivedStateFromError(error: Error): Partial<State> {
    // Categorize the error
    const errorMessage = error.message.toLowerCase()
    let errorCategory: State['errorCategory'] = 'unknown'
    
    if (errorMessage.includes('fetch') || errorMessage.includes('network') || errorMessage.includes('connection')) {
      errorCategory = 'network'
    } else if (errorMessage.includes('timeout') || errorMessage.includes('aborted')) {
      errorCategory = 'timeout'
    } else if (errorMessage.includes('http') || errorMessage.includes('api') || errorMessage.includes('failed to load')) {
      errorCategory = 'api'
    }
    
    // Check if this is an error we should handle
    const shouldHandle = ['network', 'api', 'timeout'].includes(errorCategory) ||
                        error.name === 'ChunkLoadError' ||
                        error.name === 'TypeError' && errorMessage.includes('fetch')
    
    if (shouldHandle) {
      return { hasError: true, error, errorCategory }
    }
    
    // Let other error boundaries handle non-API errors
    throw error
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    // Use centralized error logging
    ErrorMessageService.logError('APIErrorBoundary', error, {
      errorInfo,
      retryCount: this.state.retryCount,
      componentStack: errorInfo.componentStack
    })
    
    this.setState({
      error,
      errorInfo
    })

    // Call custom error handler if provided
    if (this.props.onError) {
      this.props.onError(error, errorInfo)
    }
  }

  handleRetry = () => {
    if (this.state.retryCount >= this.maxRetries) {
      log.warn('Max retries reached for API error boundary')
      return
    }

    // Calculate exponential backoff delay
    const baseDelay = 1000 // 1 second
    const delay = baseDelay * Math.pow(2, this.state.retryCount)
    const jitter = Math.random() * 0.1 * delay // Add 10% jitter
    const finalDelay = Math.min(delay + jitter, 30000) // Cap at 30 seconds

    this.setState({ isRetrying: true, lastRetryTime: Date.now() })

    const timeout = setTimeout(() => {
      this.setState(prevState => ({ 
        hasError: false, 
        error: undefined, 
        errorInfo: undefined,
        retryCount: prevState.retryCount + 1,
        isRetrying: false
      }))
    }, finalDelay)

    this.retryTimeouts.push(timeout)
  }

  handleReset = () => {
    this.setState({ 
      hasError: false, 
      error: undefined, 
      errorInfo: undefined,
      retryCount: 0
    })
  }

  render() {
    if (this.state.hasError) {
      // Custom fallback UI
      if (this.props.fallback) {
        return this.props.fallback
      }

      const canRetry = this.state.retryCount < this.maxRetries
      
      // Determine appropriate error type based on error category
      let errorType: keyof typeof ErrorMessageService.MESSAGES
      
      switch (this.state.errorCategory) {
        case 'network':
          errorType = 'NETWORK_ERROR'
          break
        case 'timeout':
          errorType = 'TIMEOUT_ERROR'
          break
        case 'api':
          errorType = 'API_ERROR'
          break
        default:
          errorType = 'LOAD_FAILED'
      }
      
      return (
        <div className="error-boundary-container">
          <ErrorMessage
            errorType={errorType}
            onRetry={canRetry ? this.handleRetry : this.handleReset}
            showRetry={true}
            isRetrying={this.state.isRetrying}
            retryCount={this.state.retryCount}
            maxRetries={this.maxRetries}
          />
          {this.state.retryCount > 0 && (
            <div className="error-retry-info">
              <p>Retry attempt {this.state.retryCount} of {this.maxRetries}</p>
              {this.state.isRetrying && (
                <div className="retry-progress">
                  <div className="retry-spinner"></div>
                  <span>Retrying...</span>
                </div>
              )}
            </div>
          )}
          {process.env.NODE_ENV === 'development' && this.state.error && (
            <details className="error-details">
              <summary>Error Details (Development)</summary>
              <pre>{this.state.error.stack}</pre>
              {this.state.errorInfo && (
                <pre>{this.state.errorInfo.componentStack}</pre>
              )}
            </details>
          )}
        </div>
      )
    }

    return this.props.children
  }
}

export default APIErrorBoundary