import React, { useState, useEffect } from 'react'
import { useWallet } from '../contexts/WalletContext'
import { useNotifications } from '../hooks/useNotifications'
import { featuredTokensService, type FeaturedTokenPromo, type PromoPayment } from '../services/featuredTokensService'
import { log } from '../services/loggingService'
import '../css/featured-tokens.css'

interface FeaturedTokensProps {
  className?: string
}

const FeaturedTokens: React.FC<FeaturedTokensProps> = ({ className = '' }) => {
  const { account, session } = useWallet()
  const { showSuccess, showError } = useNotifications()
  const [featuredTokens, setFeaturedTokens] = useState<FeaturedTokenPromo[]>([])
  const [loading, setLoading] = useState(true)
  const [showPromoModal, setShowPromoModal] = useState(false)
  const [promoForm, setPromoForm] = useState<PromoPayment>({
    amount: 500000,
    duration: 30,
    tokenSymbol: '',
    tokenContract: '',
    description: '',
    imageUrl: '',
    websiteUrl: ''
  })

  useEffect(() => {
    loadFeaturedTokens()
  }, [])

  const loadFeaturedTokens = async () => {
    try {
      setLoading(true)
      const tokens = await featuredTokensService.getFeaturedTokens()
      setFeaturedTokens(tokens)
    } catch (error) {
      log.error('Failed to load featured tokens:', 'FeaturedTokens.loadFeaturedTokens', error as Error)
    } finally {
      setLoading(false)
    }
  }

  const handleAmountChange = (amount: number) => {
    const duration = featuredTokensService.calculateDuration(amount)
    setPromoForm(prev => ({ ...prev, amount, duration }))
  }



  const submitPromotion = async () => {
    if (!account || !session) {
      showError('Please connect your wallet first')
      return
    }

    const validation = featuredTokensService.validatePayment(promoForm)
    if (!validation.valid) {
      showError(validation.error || 'Invalid promotion parameters')
      return
    }

    try {
      const memo = featuredTokensService.generatePromoMemo(promoForm)
      
      const transaction = {
        actions: [{
          account: 'pixeljourney',
          name: 'transfer',
          authorization: [{
            actor: account,
            permission: 'active'
          }],
          data: {
            from: account,
            to: 'feature.pxj',
            quantity: `${promoForm.amount.toFixed(4)} PXJ`,
            memo: memo
          }
        }]
      }

      const result = await session.transact(transaction) as any
      
      // Extract transaction ID from various possible response formats
      let transactionId: string | null = null
      
      if (result) {
        transactionId = result.transaction_id || 
                       result.id || 
                       result.processed?.id || 
                       result.processed?.transaction_id ||
                       (result.response && result.response.transaction_id) ||
                       (result.transaction && result.transaction.id)
      }
      
      if (transactionId) {
        showSuccess(`Promotion submitted successfully! Transaction: ${transactionId}`)
      } else {
        showSuccess('Promotion submitted successfully!')
      }
      setShowPromoModal(false)
      
      // Reload featured tokens after a short delay
      setTimeout(() => {
        loadFeaturedTokens()
      }, 3000)
      
    } catch (error) {
      log.error('Failed to submit promotion:', 'FeaturedTokens.submitPromotion', error as Error)
      showError(`Failed to submit promotion: ${error instanceof Error ? error.message : 'Unknown error'}`)
    }
  }

  const formatTimeRemaining = (endDate: Date): string => {
    const now = new Date()
    const diff = endDate.getTime() - now.getTime()
    const days = Math.floor(diff / (1000 * 60 * 60 * 24))
    const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
    
    if (days > 0) {
      return `${days}d ${hours}h remaining`
    } else if (hours > 0) {
      return `${hours}h remaining`
    } else {
      return 'Expires soon'
    }
  }

  return (
    <div className={`featured-tokens ${className}`}>
      <div className="featured-tokens-header">
        <h3>🌟 Featured Tokens</h3>
        <button 
          className="promote-button"
          onClick={() => setShowPromoModal(true)}
          disabled={!account}
        >
          Promote Your Token
        </button>
      </div>

      {loading ? (
        <div className="featured-tokens-loading">
          <div className="loading-spinner"></div>
          <span>Loading featured tokens...</span>
        </div>
      ) : featuredTokens.length > 0 ? (
        <div className="featured-tokens-grid">
          {featuredTokens.slice(0, 6).map((token) => (
            <div key={token.id} className="featured-token-card">
              {token.imageUrl && (
                <div className="token-image">
                  <img src={token.imageUrl} alt={token.tokenSymbol} />
                </div>
              )}
              <div className="token-info">
                <div className="token-header">
                  <span className="token-symbol">{token.tokenSymbol}</span>
                  <span className="token-contract">@{token.tokenContract}</span>
                </div>
                <p className="token-description">{token.description}</p>
                <div className="token-meta">
                  <span className="promo-amount">{token.paidAmount.toLocaleString()} PXJ</span>
                  <span className="time-remaining">{formatTimeRemaining(token.endDate)}</span>
                </div>
                {token.websiteUrl && (
                  <a 
                    href={token.websiteUrl} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="token-website"
                  >
                    Visit Website →
                  </a>
                )}
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="no-featured-tokens">
          <p>No featured tokens at the moment.</p>
          <p>Be the first to promote your token!</p>
        </div>
      )}

      {/* Promotion Modal */}
      {showPromoModal && (
        <div className="promo-modal-overlay" onClick={() => setShowPromoModal(false)}>
          <div className="promo-modal" onClick={(e) => e.stopPropagation()}>
            <div className="promo-modal-header">
              <h3>Promote Your Token</h3>
              <button 
                className="close-button"
                onClick={() => setShowPromoModal(false)}
              >
                ×
              </button>
            </div>

            <div className="promo-form">
              <div className="form-group">
                <label>Token Symbol</label>
                <input
                  type="text"
                  value={promoForm.tokenSymbol}
                  onChange={(e) => setPromoForm(prev => ({ ...prev, tokenSymbol: e.target.value.toUpperCase() }))}
                  placeholder="e.g., MYTOKEN"
                />
              </div>

              <div className="form-group">
                <label>Token Contract</label>
                <input
                  type="text"
                  value={promoForm.tokenContract}
                  onChange={(e) => setPromoForm(prev => ({ ...prev, tokenContract: e.target.value }))}
                  placeholder="e.g., mycontract12"
                />
              </div>

              <div className="form-group">
                <label>Description</label>
                <textarea
                  value={promoForm.description}
                  onChange={(e) => setPromoForm(prev => ({ ...prev, description: e.target.value }))}
                  placeholder="Describe your token and why users should trade it..."
                  rows={3}
                />
              </div>

              <div className="form-group">
                <label>Image URL (optional)</label>
                <input
                  type="url"
                  value={promoForm.imageUrl}
                  onChange={(e) => setPromoForm(prev => ({ ...prev, imageUrl: e.target.value }))}
                  placeholder="https://example.com/token-logo.png"
                />
              </div>

              <div className="form-group">
                <label>Website URL (optional)</label>
                <input
                  type="url"
                  value={promoForm.websiteUrl}
                  onChange={(e) => setPromoForm(prev => ({ ...prev, websiteUrl: e.target.value }))}
                  placeholder="https://yourtoken.com"
                />
              </div>

              <div className="form-group">
                <label>Promotion Amount: {promoForm.amount.toLocaleString()} PXJ</label>
                <input
                  type="range"
                  min={500000}
                  max={5000000}
                  step={50000}
                  value={promoForm.amount}
                  onChange={(e) => handleAmountChange(parseInt(e.target.value))}
                  className="amount-slider"
                />
                <div className="slider-labels">
                  <span>500K PXJ (30 days)</span>
                  <span>5M PXJ (365 days)</span>
                </div>
              </div>

              <div className="promotion-summary">
                <div className="summary-item">
                  <span>Duration:</span>
                  <span>{promoForm.duration} days</span>
                </div>
                <div className="summary-item">
                  <span>Cost:</span>
                  <span>{promoForm.amount.toLocaleString()} PXJ</span>
                </div>
              </div>

              <div className="form-actions">
                <button 
                  className="cancel-button"
                  onClick={() => setShowPromoModal(false)}
                >
                  Cancel
                </button>
                <button 
                  className="submit-button"
                  onClick={submitPromotion}
                  disabled={!promoForm.tokenSymbol || !promoForm.tokenContract || !promoForm.description}
                >
                  Submit Promotion
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

export default FeaturedTokens