export interface UserBadge {
  id: string
  name: string
  description: string
  icon: string
  color: string
  borderColor: string
  type: 'ownership' | 'holdings' | 'achievement' | 'special' | 'trading' | 'social'
  requirements?: string
  tier?: number
  rarity?: 'common' | 'rare' | 'epic' | 'legendary'
  unlockedAt?: Date
  progress?: {
    current: number
    required: number
    percentage: number
  }
}

export interface BadgeCheckResult {
  badges: UserBadge[]
  pxjBalance: number
  journeyAssets: number
  pixalPfpOwned: boolean
  waxrockOwned: boolean
  ingredientsOwned: boolean
  tradingVolume?: number
  totalTransactions?: number
  accountAge?: number
}

export interface BadgeProgress {
  badgeId: string
  current: number
  required: number
  percentage: number
  nextMilestone?: string
}

interface CachedBadgeResult {
  result: BadgeCheckResult
  timestamp: number
  expiresAt: number
}

