import React, { useState, useEffect } from 'react'
import { accessibilityService, type AccessibilityPreferences } from '../services/accessibilityService'
import { monitoringService } from '../services/monitoringService'
import '../css/accessibility-modal.css'

interface AccessibilitySettingsProps {
  isOpen: boolean
  onClose: () => void
}

const AccessibilitySettings: React.FC<AccessibilitySettingsProps> = ({ isOpen, onClose }) => {
  const [preferences, setPreferences] = useState<AccessibilityPreferences>({
    reducedMotion: false,
    highContrast: false,
    largeText: false,
    screenReader: false,
    keyboardNavigation: true
  })
  const [isLoading, setIsLoading] = useState(false)

  useEffect(() => {
    if (isOpen) {
      // Load current preferences when modal opens
      const currentPrefs = accessibilityService.getPreferences()
      setPreferences(currentPrefs)
      
      // Focus trap setup
      accessibilityService.announceToScreenReader('Accessibility settings dialog opened')
      
      // Focus the first interactive element
      const firstButton = document.querySelector('.accessibility-modal button') as HTMLElement
      if (firstButton) {
        accessibilityService.focusElement(firstButton)
      }
    }
  }, [isOpen])

  const handlePreferenceChange = async (key: keyof AccessibilityPreferences, value: boolean) => {
    setIsLoading(true)
    
    try {
      // Update local state immediately for responsive UI
      const newPreferences = { ...preferences, [key]: value }
      setPreferences(newPreferences)
      
      // Update the service
      accessibilityService.updatePreference(key, value)
      
      // Announce the change to screen readers
      const preferenceNames = {
        reducedMotion: 'Reduced motion',
        highContrast: 'High contrast',
        largeText: 'Large text',
        screenReader: 'Screen reader optimizations',
        keyboardNavigation: 'Keyboard navigation'
      }
      
      accessibilityService.announceToScreenReader(
        `${preferenceNames[key]} ${value ? 'enabled' : 'disabled'}`,
        'assertive'
      )
      
      monitoringService.trackEvent('accessibility_preference_changed', {
        preference: key,
        value,
        source: 'settings_modal'
      }) 
      
    } catch (error) {
      console.error('Failed to update accessibility preference:', error as Error)
      monitoringService.reportError(error as Error, 'accessibility_preference_update_error')
      
      // Revert local state on error
      setPreferences(prev => ({ ...prev, [key]: !value }))
      
      accessibilityService.announceToScreenReader(
        'Failed to update preference. Please try again.',
        'assertive'
      )
    } finally {
      setIsLoading(false)
    }
  }

  const handleClose = () => {
    accessibilityService.announceToScreenReader('Accessibility settings dialog closed')
    accessibilityService.restoreFocus()
    onClose()
  }

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Escape') {
      handleClose()
    }
  }

  const resetToDefaults = () => {
    const defaults: AccessibilityPreferences = {
      reducedMotion: false,
      highContrast: false,
      largeText: false,
      screenReader: false,
      keyboardNavigation: true
    }
    
    setPreferences(defaults)
    
    // Update all preferences
    Object.entries(defaults).forEach(([key, value]) => {
      accessibilityService.updatePreference(key as keyof AccessibilityPreferences, value)
    })
    
    accessibilityService.announceToScreenReader('Accessibility settings reset to defaults', 'assertive')
    
    monitoringService.trackEvent('accessibility_settings_reset', {
      source: 'settings_modal'
    })
  }

  if (!isOpen) return null

  return (
    <>
      {/* Backdrop */}
      <div 
        className="accessibility-modal-backdrop" 
        onClick={handleClose}
        aria-hidden="true"
      />
      
      {/* Modal */}
      <div
         className={`accessibility-modal ${isLoading ? 'loading' : ''}`}
         role="dialog"
         aria-modal="true"
        aria-labelledby="accessibility-settings-title"
        aria-describedby="accessibility-settings-description"
        onKeyDown={handleKeyDown}
      >
        <div className="accessibility-modal-content">
          {/* Header */}
          <div className="accessibility-modal-header">
            <h2 
              id="accessibility-settings-title" 
              className="accessibility-modal-title"
            >
              Accessibility Settings
            </h2>
            <button
              onClick={handleClose}
              className="accessibility-modal-close"
              aria-label="Close accessibility settings"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" aria-hidden="true">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
          
          {/* Description */}
          <p 
            id="accessibility-settings-description" 
            className="accessibility-modal-description"
          >
            Customize your accessibility preferences to improve your experience with the application.
          </p>
          
          {/* Settings */}
          <div className="accessibility-settings-list">
            {/* Reduced Motion */}
            <div className="accessibility-setting-item">
              <div className="accessibility-setting-info">
                <label className="accessibility-setting-label">
                  Reduced Motion
                </label>
                <p className="accessibility-setting-description">
                  Minimize animations and transitions for a calmer experience.
                </p>
              </div>
              <button
                onClick={() => handlePreferenceChange('reducedMotion', !preferences.reducedMotion)}
                className={`accessibility-toggle ${preferences.reducedMotion ? 'active' : ''}`}
                role="switch"
                aria-checked={preferences.reducedMotion}
                disabled={isLoading}
              >
                <span className="accessibility-toggle-thumb" />
              </button>
            </div>
            
            {/* High Contrast */}
            <div className="accessibility-setting-item">
              <div className="accessibility-setting-info">
                <label className="accessibility-setting-label">
                  High Contrast
                </label>
                <p className="accessibility-setting-description">
                  Increase color contrast for better visibility
                </p>
              </div>
              <button
                onClick={() => handlePreferenceChange('highContrast', !preferences.highContrast)}
                className={`accessibility-toggle ${preferences.highContrast ? 'active' : ''}`}
                role="switch"
                aria-checked={preferences.highContrast}
                disabled={isLoading}
              >
                <span className="accessibility-toggle-thumb" />
              </button>
            </div>
            
            {/* Large Text */}
            <div className="accessibility-setting-item">
              <div className="accessibility-setting-info">
                <label className="accessibility-setting-label">
                  Large Text
                </label>
                <p className="accessibility-setting-description">
                  Increase text size throughout the application
                </p>
              </div>
              <button
                onClick={() => handlePreferenceChange('largeText', !preferences.largeText)}
                className={`accessibility-toggle ${preferences.largeText ? 'active' : ''}`}
                role="switch"
                aria-checked={preferences.largeText}
                disabled={isLoading}
              >
                <span className="accessibility-toggle-thumb" />
              </button>
            </div>
            
            {/* Screen Reader Optimizations */}
            <div className="accessibility-setting-item">
              <div className="accessibility-setting-info">
                <label className="accessibility-setting-label">
                  Screen Reader Optimizations
                </label>
                <p className="accessibility-setting-description">
                  Optimize interface for screen reader users
                </p>
              </div>
              <button
                onClick={() => handlePreferenceChange('screenReader', !preferences.screenReader)}
                className={`accessibility-toggle ${preferences.screenReader ? 'active' : ''}`}
                role="switch"
                aria-checked={preferences.screenReader}
                disabled={isLoading}
              >
                <span className="accessibility-toggle-thumb" />
              </button>
            </div>
            
            {/* Keyboard Navigation */}
            <div className="accessibility-setting-item">
              <div className="accessibility-setting-info">
                <label className="accessibility-setting-label">
                  Enhanced Keyboard Navigation
                </label>
                <p className="accessibility-setting-description">
                  Show focus indicators and enable keyboard shortcuts
                </p>
              </div>
              <button
                onClick={() => handlePreferenceChange('keyboardNavigation', !preferences.keyboardNavigation)}
                className={`accessibility-toggle ${preferences.keyboardNavigation ? 'active' : ''}`}
                role="switch"
                aria-checked={preferences.keyboardNavigation}
                disabled={isLoading}
              >
                <span className="accessibility-toggle-thumb" />
              </button>
            </div>
          </div>
          
          {/* Actions */}
          <div className="accessibility-modal-actions">
            <button
              onClick={resetToDefaults}
              disabled={isLoading}
              className="accessibility-modal-reset"
            >
              Reset to Defaults
            </button>
            <div className="accessibility-modal-buttons">
              <button
                onClick={handleClose}
                disabled={isLoading}
                className="accessibility-modal-cancel"
              >
                Cancel
              </button>
              <button
                onClick={handleClose}
                disabled={isLoading}
                className="accessibility-modal-done"
              >
                {isLoading ? 'Saving...' : 'Done'}
              </button>
            </div>
          </div>
          
          {/* Keyboard Shortcuts Info */}
          <div className="accessibility-shortcuts">
            <h3 className="accessibility-shortcuts-title">
              Keyboard Shortcuts
            </h3>
            <div className="accessibility-shortcuts-list">
              <div className="accessibility-shortcut">
                <span>Close modal</span>
                <kbd className="accessibility-kbd">Esc</kbd>
              </div>
              <div className="accessibility-shortcut">
                <span>Navigate settings</span>
                <kbd className="accessibility-kbd">Tab</kbd>
              </div>
              <div className="accessibility-shortcut">
                <span>Toggle setting</span>
                <kbd className="accessibility-kbd">Space</kbd>
              </div>
              <div className="accessibility-shortcut">
                <span>Activate buttons</span>
                <kbd className="accessibility-kbd">Enter</kbd>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default AccessibilitySettings