import React, { useState, useEffect, useMemo, useCallback } from 'react'
import { badgeService } from '../services/badgeService'
import type { UserBadge } from '../services/badgeService'
import { useWallet } from '../contexts/WalletContext'
import ShareModal from './ShareModal'
import { log } from '../services/loggingService'
import '../css/pxbadges.css'

interface BadgeCategory {
  id: string
  name: string
  icon: string
  description: string
  badges: UserBadge[]
}

interface AchievementSeries {
  name: string
  badges: UserBadge[]
  steps: { tier: number; earned: boolean; current?: boolean }[]
}

interface BadgeStats {
  totalBadges: number
  badgesByType: Record<string, number>
  badgesByRarity: Record<string, number>
  recentBadges: UserBadge[]
  progressBadges: UserBadge[]
}

const PxBadges: React.FC = () => {
  const { account } = useWallet()
  const [badges, setBadges] = useState<UserBadge[]>([])
  const [loading, setLoading] = useState(true)
  const [collapsedCategories, setCollapsedCategories] = useState<Set<string>>(new Set())
  const [badgeStats, setBadgeStats] = useState<BadgeStats | null>(null)
  const [showShareModal, setShowShareModal] = useState(false)

  const loadUserBadges = useCallback(async () => {
    if (!account) return
    
    try {
      setLoading(true)
      const result = await badgeService.checkUserBadges(account)
      const stats = await badgeService.getUserBadgeStats(account)
      setBadges(result.badges)
      setBadgeStats(stats)
    } catch (error) {
      log.error('Error loading badges', 'PxBadges.loadUserBadges', error as Error, { account })
    } finally {
      setLoading(false)
    }
  }, [account])

  useEffect(() => {
    if (account) {
      loadUserBadges()
    } else {
      setBadges([])
      setLoading(false)
    }
  }, [account, loadUserBadges])

  const categories = useMemo((): BadgeCategory[] => {
    const allBadgeDefinitions = badgeService.getAllBadgeDefinitions()
    
    const categoryMap: Record<string, BadgeCategory> = {
      holdings: {
        id: 'holdings',
        name: 'Token Holdings',
        icon: '💰',
        description: 'Achievements for holding various tokens',
        badges: []
      },
      ownership: {
        id: 'ownership',
        name: 'NFT Collections',
        icon: '🎭',
        description: 'Achievements for owning NFTs and collectibles',
        badges: []
      },
      trading: {
        id: 'trading',
        name: 'Trading Mastery',
        icon: '📈',
        description: 'Achievements for trading activities',
        badges: []
      },
      social: {
        id: 'social',
        name: 'Community',
        icon: '👥',
        description: 'Achievements for community participation',
        badges: []
      },
      achievement: {
        id: 'achievement',
        name: 'Special Achievements',
        icon: '🏆',
        description: 'Rare and special accomplishments',
        badges: []
      },
      special: {
        id: 'special',
        name: 'Legendary',
        icon: '⭐',
        description: 'Ultra-rare legendary achievements',
        badges: []
      }
    }

    // Add all badge definitions to their respective categories
    allBadgeDefinitions.forEach(badgeDef => {
      const category = categoryMap[badgeDef.type] || categoryMap.achievement
      const earnedBadge = badges.find(b => b.id === badgeDef.id)
      
      category.badges.push(earnedBadge || {
        ...badgeDef,
        unlockedAt: undefined
      })
    })

    return Object.values(categoryMap).filter(cat => cat.badges.length > 0)
  }, [badges])

  const achievementSeries = useMemo((): AchievementSeries[] => {
    const series: AchievementSeries[] = []
    
    // Pixal PFP Collector Series
    const pixalCollectorBadges = badges.filter(b => b.id.startsWith('pixal-collector'))
      .sort((a, b) => (a.tier || 0) - (b.tier || 0))
    
    if (pixalCollectorBadges.length > 0) {
      const steps = [1, 2, 3, 4, 5].map(tier => {
        const badge = pixalCollectorBadges.find(b => b.tier === tier)
        return {
          tier,
          earned: !!badge?.unlockedAt,
          current: badge?.progress && !badge.unlockedAt
        }
      })
      
      series.push({
        name: 'Pixal Collector',
        badges: pixalCollectorBadges,
        steps
      })
    }
    
    // WaxRock Collector Series
    const waxrockCollectorBadges = badges.filter(b => b.id.startsWith('waxrock-collector'))
      .sort((a, b) => (a.tier || 0) - (b.tier || 0))
    
    if (waxrockCollectorBadges.length > 0) {
      const steps = [1, 2, 3].map(tier => {
        const badge = waxrockCollectorBadges.find(b => b.tier === tier)
        return {
          tier,
          earned: !!badge?.unlockedAt,
          current: badge?.progress && !badge.unlockedAt
        }
      })
      
      series.push({
        name: 'WaxRock Collector',
        badges: waxrockCollectorBadges,
        steps
      })
    }
    
    return series
  }, [badges])

  const toggleCategory = (categoryId: string) => {
    const newCollapsed = new Set(collapsedCategories)
    if (newCollapsed.has(categoryId)) {
      newCollapsed.delete(categoryId)
    } else {
      newCollapsed.add(categoryId)
    }
    setCollapsedCategories(newCollapsed)
  }

  const getRarityClass = (badge: UserBadge): string => {
    if (badge.rarity === 'legendary') return 'legendary'
    if (badge.rarity === 'epic') return 'epic'
    if (badge.rarity === 'rare') return 'rare'
    return ''
  }

  const formatDate = (date: Date | string | undefined): string => {
    if (!date) return ''
    const d = new Date(date)
    return d.toLocaleDateString('en-US', { 
      month: 'short', 
      day: 'numeric', 
      year: 'numeric' 
    })
  }

  if (!account) {
    return (
      <div className="pxbadges-container">
        <div className="no-achievements">
          <div className="no-achievements-icon">🔒</div>
          <div className="no-achievements-text">Connect Your Wallet</div>
          <div className="no-achievements-subtitle">
            Connect your wallet to view your achievements and progress
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="pxbadges-container">
      <div className="pxbadges-header">
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', gap: '1rem', marginBottom: '1rem' }}>
          <h1 className="pxbadges-title">Achievements</h1>
          <button
            onClick={() => setShowShareModal(true)}
            style={{
              background: 'rgba(255, 215, 0, 0.2)',
              border: '2px solid #ffd700',
              borderRadius: '50%',
              width: '48px',
              height: '48px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              cursor: 'pointer',
              transition: 'all 0.3s ease',
              color: '#ffd700'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.background = 'rgba(255, 215, 0, 0.3)'
              e.currentTarget.style.transform = 'scale(1.1)'
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.background = 'rgba(255, 215, 0, 0.2)'
              e.currentTarget.style.transform = 'scale(1)'
            }}
            title="Share Achievements"
          >
            <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
              <path d="M18 16.08c-.76 0-1.44.3-1.96.77L8.91 12.7c.05-.23.09-.46.09-.7s-.04-.47-.09-.7l7.05-4.11c.54.5 1.25.81 2.04.81 1.66 0 3-1.34 3-3s-1.34-3-3-3-3 1.34-3 3c0 .24.04.47.09.7L8.04 9.81C7.5 9.31 6.79 9 6 9c-1.66 0-3 1.34-3 3s1.34 3 3 3c.79 0 1.5-.31 2.04-.81l7.12 4.16c-.05.21-.08.43-.08.65 0 1.61 1.31 2.92 2.92 2.92s2.92-1.31 2.92-2.92-1.31-2.92-2.92-2.92z"/>
            </svg>
          </button>
        </div>
        <p className="pxbadges-subtitle">
          Track your progress and unlock prestigious badges in the Pixel Journey ecosystem
        </p>
        
        {badgeStats && (
          <div className="achievement-stats">
            <div className="stat-card">
              <span className="stat-number">{badgeStats.totalBadges}</span>
              <span className="stat-label">Total Earned</span>
            </div>
            <div className="stat-card">
              <span className="stat-number">{badgeStats.badgesByRarity?.legendary || 0}</span>
              <span className="stat-label">Legendary</span>
            </div>
            <div className="stat-card">
              <span className="stat-number">{badgeStats.badgesByRarity?.epic || 0}</span>
              <span className="stat-label">Epic</span>
            </div>
            <div className="stat-card">
              <span className="stat-number">{badgeStats.badgesByRarity?.rare || 0}</span>
              <span className="stat-label">Rare</span>
            </div>
            <div className="stat-card">
              <span className="stat-number">{badgeStats.progressBadges?.length || 0}</span>
              <span className="stat-label">In Progress</span>
            </div>
          </div>
        )}
      </div>

      {loading ? (
        <div className="achievements-loading">
          <div className="loading-spinner"></div>
          Loading achievements...
        </div>
      ) : (
        <div className="achievement-categories">
          {/* Achievement Series */}
          {achievementSeries.length > 0 && (
            <div className="category-section">
              <div className="category-header">
                <span className="category-icon">🏆</span>
                <h2 className="category-title">Achievement Series</h2>
              </div>
              
              {achievementSeries.map(series => (
                <div key={series.name} className="achievement-card earned">
                  <div className="achievement-header">
                    <span className="achievement-icon">🎯</span>
                    <div className="achievement-info">
                      <h3 className="achievement-name">{series.name}</h3>
                      <p className="achievement-description">
                        Multi-tier achievement series with {series.steps.length} levels
                      </p>
                    </div>
                  </div>
                  
                  <div className="achievement-series">
                    {series.steps.map(step => (
                      <div 
                        key={step.tier}
                        className={`series-step ${
                          step.earned ? 'completed' : step.current ? 'current' : ''
                        }`}
                      >
                        {step.tier}
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          )}
          
          {/* Regular Categories */}
          {categories.map(category => {
            const earnedCount = category.badges.filter(b => b.unlockedAt).length
            const totalCount = category.badges.length
            const isCollapsed = collapsedCategories.has(category.id)
            
            return (
              <div key={category.id} className="category-section">
                <div 
                  className="category-header"
                  onClick={() => toggleCategory(category.id)}
                >
                  <span className="category-icon">{category.icon}</span>
                  <h2 className="category-title">{category.name}</h2>
                  <span className="category-progress">
                    {earnedCount}/{totalCount}
                  </span>
                  <span className={`category-toggle ${isCollapsed ? 'collapsed' : ''}`}>
                    ▼
                  </span>
                </div>
                
                {!isCollapsed && (
                  <div className="achievements-grid">
                    {category.badges.map(badge => {
                      const isEarned = !!badge.unlockedAt
                      const rarityClass = getRarityClass(badge)
                      
                      return (
                        <div 
                          key={badge.id}
                          className={`achievement-card ${
                            isEarned ? 'earned' : ''
                          } ${rarityClass}`}
                        >
                          <div className="achievement-header">
                            <span className="achievement-icon">{badge.icon}</span>
                            <div className="achievement-info">
                              <h3 className="achievement-name">{badge.name}</h3>
                              <p className="achievement-description">{badge.description}</p>
                              <p className="achievement-requirements">{badge.requirements}</p>
                            </div>
                          </div>
                          
                          {badge.tier && (
                            <div className="achievement-tier">
                              Tier {badge.tier}
                            </div>
                          )}
                          
                          {badge.progress && (
                            <div className="achievement-progress">
                              <div className="progress-bar">
                                <div 
                                  className="progress-fill"
                                  style={{ width: `${badge.progress.percentage}%` }}
                                ></div>
                              </div>
                              <div className="progress-text">
                                {badge.progress.current.toLocaleString()} / {badge.progress.required.toLocaleString()}
                                ({badge.progress.percentage}%)
                              </div>
                            </div>
                          )}
                          
                          {isEarned && badge.unlockedAt && (
                            <div className="achievement-date">
                              {formatDate(badge.unlockedAt)}
                            </div>
                          )}
                        </div>
                      )
                    })}
                  </div>
                )}
              </div>
            )
          })}
          
          {categories.length === 0 && (
            <div className="no-achievements">
              <div className="no-achievements-icon">🎯</div>
              <div className="no-achievements-text">No Achievements Yet</div>
              <div className="no-achievements-subtitle">
                Start participating in the Pixel Journey ecosystem to earn your first badges!
              </div>
            </div>
          )}
        </div>
      )}
      
      <ShareModal
        isOpen={showShareModal}
        onClose={() => setShowShareModal(false)}
        title={`${account}'s Achievements`}
        description={`Check out my achievements in the Pixel Journey ecosystem! I've earned ${badgeStats?.totalBadges || 0} badges including ${badgeStats?.badgesByRarity?.legendary || 0} legendary achievements.`}
        shareUrl={`${window.location.origin}/pxbadges?user=${account}`}
        defaultShareText={`Check out my achievements in the Pixel Journey ecosystem! I've earned ${badgeStats?.totalBadges || 0} badges. #PixelJourney #Achievements`}
        type="achievement"
        badges={badges}
        totalBadges={badgeStats?.totalBadges || 0}
        legendaryCount={badgeStats?.badgesByRarity?.legendary || 0}
        username={account || ''}
      />
    </div>
  )
}

export default PxBadges