import React from 'react'
import { getCollectionTier, getTierBadgeConfig } from '../config/collectionTiers'
import '../css/quick-links.css'

interface QuickLinksProps {
  collectionName: string
}

const QuickLinks: React.FC<QuickLinksProps> = ({ collectionName }) => {
  const tierInfo = getCollectionTier(collectionName)
  
  if (!tierInfo) {
    return null
  }

  const badgeConfig = getTierBadgeConfig(tierInfo.tier)
  const hasQuickLinks = tierInfo.quickLinks && tierInfo.quickLinks.length > 0

  return (
    <div className="quick-links-container">
      {/* Tier Badge */}
      <div className="tier-section">
        <div className={`tier-badge ${badgeConfig.className}`}>
          <span className="tier-icon">{badgeConfig.icon}</span>
          <span className="tier-label">{badgeConfig.label}</span>
          {tierInfo.verified && <span className="verified-icon">✓</span>}
        </div>
      </div>

      {/* Quick Links */}
      {hasQuickLinks && (
        <div className="links-section">
          <h4 className="links-title">Quick Links</h4>
          <div className="links-grid">
            {tierInfo.quickLinks!.map((link, index) => (
              <a
                key={index}
                href={link.url}
                target="_blank"
                rel="noopener noreferrer"
                className="quick-link"
              >
                {link.icon && <span className="link-icon">{link.icon}</span>}
                <span className="link-label">{link.label}</span>
              </a>
            ))}
          </div>
        </div>
      )}

      {/* Collection Owner Message & Discord */}
      {(tierInfo.ownerMessage || tierInfo.discordUrl) && (
        <div className="owner-section">
          {tierInfo.ownerMessage && (
            <div className="owner-message">
              <div className="owner-title-container" style={{
                display: 'flex',
                alignItems: 'center',
                gap: '8px',
                marginBottom: '8px'
              }}>
                <h4 className="owner-title">From the Collection Owner</h4>
                <div 
                  className="info-tooltip"
                  title="This message is provided directly by the collection owner to share information about their project, community, or special announcements with collectors and fans."
                  style={{
                    display: 'inline-flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    width: '16px',
                    height: '16px',
                    borderRadius: '50%',
                    backgroundColor: 'rgba(255, 255, 255, 0.1)',
                    border: '1px solid rgba(255, 255, 255, 0.2)',
                    color: 'rgba(255, 255, 255, 0.7)',
                    fontSize: '12px',
                    fontWeight: 'bold',
                    cursor: 'help',
                    transition: 'all 0.2s ease'
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.backgroundColor = 'rgba(255, 255, 255, 0.15)'
                    e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.3)'
                    e.currentTarget.style.color = 'rgba(255, 255, 255, 0.9)'
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.backgroundColor = 'rgba(255, 255, 255, 0.1)'
                    e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.2)'
                    e.currentTarget.style.color = 'rgba(255, 255, 255, 0.7)'
                  }}
                >
                  ?
                </div>
              </div>
              <p className="owner-text">{tierInfo.ownerMessage}</p>
            </div>
          )}
          {tierInfo.discordUrl && (
            <div className="discord-section">
              <a
                href={tierInfo.discordUrl}
                target="_blank"
                rel="noopener noreferrer"
                className="discord-link"
              >
                <span className="discord-icon">💬</span>
                <span className="discord-text">Join Our Discord Community</span>
                <span className="external-icon">↗</span>
              </a>
            </div>
          )}
        </div>
      )}

      {/* Learn About Grid */}
      {tierInfo.learnAboutGrid && (
        <div className="learn-about-section" style={{ marginTop: '2rem' }}>
          <h3 className="learn-about-title">Learn About Pixel Journey</h3>
          <div className="learn-about-grid" style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))',
            gap: '16px',
            marginTop: '16px'
          }}>
            {tierInfo.learnAboutGrid.map((item, index) => (
              <div 
                key={index}
                className="learn-about-card"
                style={{
                  background: 'rgba(255, 255, 255, 0.05)',
                  border: '1px solid rgba(255, 255, 255, 0.1)',
                  borderRadius: '12px',
                  padding: '20px',
                  transition: 'all 0.3s ease',
                  cursor: item.url ? 'pointer' : 'default'
                }}
                onClick={() => item.url && window.open(item.url, '_blank')}
                onMouseEnter={(e) => {
                  if (item.url) {
                    e.currentTarget.style.background = 'rgba(255, 255, 255, 0.08)'
                    e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.2)'
                    e.currentTarget.style.transform = 'translateY(-2px)'
                  }
                }}
                onMouseLeave={(e) => {
                  if (item.url) {
                    e.currentTarget.style.background = 'rgba(255, 255, 255, 0.05)'
                    e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.1)'
                    e.currentTarget.style.transform = 'translateY(0)'
                  }
                }}
              >
                <div className="learn-card-header" style={{
                  display: 'flex',
                  alignItems: 'center',
                  gap: '12px',
                  marginBottom: '12px'
                }}>
                  <span className="learn-card-icon" style={{
                    fontSize: '24px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    width: '40px',
                    height: '40px',
                    background: 'rgba(255, 255, 255, 0.1)',
                    borderRadius: '8px'
                  }}>
                    {item.icon}
                  </span>
                  <h4 className="learn-card-title" style={{
                    margin: 0,
                    fontSize: '16px',
                    fontWeight: '600',
                    color: 'white'
                  }}>
                    {item.title}
                  </h4>
                  {item.url && (
                    <span className="external-link-icon" style={{
                      marginLeft: 'auto',
                      fontSize: '14px',
                      color: 'rgba(255, 255, 255, 0.6)'
                    }}>
                      ↗
                    </span>
                  )}
                </div>
                <p className="learn-card-description" style={{
                  margin: 0,
                  fontSize: '14px',
                  lineHeight: '1.5',
                  color: 'rgba(255, 255, 255, 0.8)'
                }}>
                  {item.description}
                </p>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  )
}

export default QuickLinks