import React, { useState, useEffect, useCallback } from 'react'
import { useTransactionManager } from '../hooks/useTransactionManager'
import { useNotifications } from '../hooks/useNotifications'

import '../css/transaction-notifications.css'

interface NotificationItem {
  id: string
  type: 'success' | 'error' | 'info' | 'warning'
  title: string
  message: string
  timestamp: number
  transactionId?: string
  autoHide?: boolean
  duration?: number
}

const TransactionNotifications: React.FC = () => {
  const [notifications, setNotifications] = useState<NotificationItem[]>([])
  const [processedTransactions, setProcessedTransactions] = useState<Set<string>>(new Set())
  const { activeTransactions, completedTransactions } = useTransactionManager()
  const { showSuccess, showError } = useNotifications()

  const addNotification = useCallback((notification: Omit<NotificationItem, 'id' | 'timestamp'>) => {
    const newNotification: NotificationItem = {
      ...notification,
      id: `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      timestamp: Date.now(),
      autoHide: notification.autoHide ?? true,
      duration: notification.duration ?? 5000
    }

    setNotifications(prev => [newNotification, ...prev.slice(0, 4)]) // Keep max 5 notifications

    // Auto-hide notification
    if (newNotification.autoHide) {
      setTimeout(() => {
        removeNotification(newNotification.id)
      }, newNotification.duration)
    }
  }, [])

  const removeNotification = useCallback((id: string) => {
    setNotifications(prev => prev.filter(n => n.id !== id))
  }, [])

  // Monitor transaction status changes
  useEffect(() => {
    const allTransactions = [...activeTransactions, ...completedTransactions]
    
    allTransactions.forEach(tx => {
      if (processedTransactions.has(tx.id)) return
      
      // Mark as processed to avoid duplicate notifications
      setProcessedTransactions(prev => new Set([...prev, tx.id]))
      
      // Handle different transaction statuses
      switch (tx.status) {
        case 'pending':
          addNotification({
            type: 'info',
            title: 'Transaction Started',
            message: `${getTransactionTypeLabel(tx.type)} transaction is being prepared...`,
            transactionId: tx.id,
            duration: 3000
          })
          break
          
        case 'processing':
          addNotification({
            type: 'info',
            title: 'Processing Transaction',
            message: `${getTransactionTypeLabel(tx.type)} is being processed on the blockchain...`,
            transactionId: tx.id,
            duration: 4000
          })
          break
          
        case 'success':
          addNotification({
            type: 'success',
            title: 'Transaction Successful!',
            message: `${getTransactionTypeLabel(tx.type)} completed successfully`,
            transactionId: tx.id,
            duration: 6000
          })
          
          // Also use the global notification system
          showSuccess(
            'Transaction Complete',
            `Your ${getTransactionTypeLabel(tx.type).toLowerCase()} has been processed successfully!`
          )
          break
          
        case 'failed':
          addNotification({
            type: 'error',
            title: 'Transaction Failed',
            message: tx.error || `${getTransactionTypeLabel(tx.type)} failed to process`,
            transactionId: tx.id,
            autoHide: false // Keep error notifications visible
          })
          
          // Also use the global notification system
          showError(
            'Transaction Failed',
            tx.error || 'Your transaction failed to process. Please try again.'
          )
          break
          
        case 'cancelled':
          addNotification({
            type: 'warning',
            title: 'Transaction Cancelled',
            message: `${getTransactionTypeLabel(tx.type)} was cancelled`,
            transactionId: tx.id,
            duration: 4000
          })
          break
      }
    })
  }, [activeTransactions, completedTransactions, processedTransactions, addNotification, showSuccess, showError])

  const getTransactionTypeLabel = (type: string): string => {
    switch (type) {
      case 'nft_purchase':
        return 'NFT Purchase'
      case 'nft_transfer':
        return 'NFT Transfer'
      case 'token_transfer':
        return 'Token Transfer'
      case 'nft_listing':
        return 'NFT Listing'
      case 'listing_cancel':
        return 'Cancel Listing'
      case 'crafting':
        return 'Crafting'
      case 'blend':
        return 'Blend'
      case 'burn':
        return 'Burn Assets'
      case 'unpack':
        return 'Unpack Asset'
      case 'auction_bid':
        return 'Auction Bid'
      case 'tokenize_wax':
        return 'Tokenize WAX'
      default:
        return type.replace('_', ' ').replace(/\b\w/g, l => l.toUpperCase())
    }
  }

  const getNotificationIcon = (type: string) => {
    switch (type) {
      case 'success':
        return '✅'
      case 'error':
        return '❌'
      case 'warning':
        return '⚠️'
      case 'info':
      default:
        return 'ℹ️'
    }
  }

  const formatTimeAgo = (timestamp: number): string => {
    const now = Date.now()
    const diffMs = now - timestamp
    const diffSecs = Math.floor(diffMs / 1000)
    const diffMins = Math.floor(diffSecs / 60)
    
    if (diffSecs < 60) return 'Just now'
    if (diffMins < 60) return `${diffMins}m ago`
    
    const diffHours = Math.floor(diffMins / 60)
    if (diffHours < 24) return `${diffHours}h ago`
    
    return new Date(timestamp).toLocaleDateString()
  }

  if (notifications.length === 0) return null

  return (
    <div className="transaction-notifications">
      {notifications.map(notification => (
        <div 
          key={notification.id}
          className={`notification notification-${notification.type}`}
        >
          <div className="notification-content">
            <div className="notification-header">
              <span className="notification-icon">
                {getNotificationIcon(notification.type)}
              </span>
              <span className="notification-title">
                {notification.title}
              </span>
              <span className="notification-time">
                {formatTimeAgo(notification.timestamp)}
              </span>
              <button 
                className="notification-close"
                onClick={() => removeNotification(notification.id)}
              >
                ×
              </button>
            </div>
            
            <div className="notification-message">
              {notification.message}
            </div>
            
            {notification.transactionId && (
              <div className="notification-transaction-id">
                ID: {notification.transactionId.slice(0, 8)}...{notification.transactionId.slice(-8)}
              </div>
            )}
          </div>
          
          {notification.autoHide && (
            <div 
              className="notification-progress"
              style={{
                animationDuration: `${notification.duration}ms`
              }}
            />
          )}
        </div>
      ))}
    </div>
  )
}

export default TransactionNotifications