import React, { useEffect } from 'react'
import { watchlistService } from '../services/watchlistService'
import { useWallet } from '../hooks/useWallet'
import { log } from '../services/loggingService'
import type { NFTAsset } from '../types'

/**
 * Component that checks for triggered watchlist alerts on page load
 * and displays notifications for any alerts that have been triggered
 * since the user's last visit.
 */
const WatchlistAlertChecker: React.FC = () => {
  const { isConnected, account } = useWallet()

  useEffect(() => {
    if (!isConnected || !account) return

    const checkTriggeredAlerts = async () => {
      try {
        // Get the last check timestamp from localStorage
        const lastCheckKey = `watchlist_last_check_${account}`
        const lastCheck = localStorage.getItem(lastCheckKey)
        const lastCheckTime = lastCheck ? parseInt(lastCheck) : 0
        const now = Date.now()
        
        // Only check if it's been more than 5 minutes since last check
        if (now - lastCheckTime < 5 * 60 * 1000) {
          return
        }

        // Get watchlist items
        const watchlistItems = await watchlistService.getWatchlist()
        
        if (watchlistItems.length === 0) {
          // Update last check time even if no items
          localStorage.setItem(lastCheckKey, now.toString())
          return
        }

        // Check each item for recent alerts
        interface AlertData {
          targetPrice: number
          alertType: 'below' | 'above' | 'any'
          enabled: boolean
          lastTriggered?: number
        }
        
        interface WatchlistItem {
          nft: NFTAsset
          targetPrice?: number
          alertType: 'below' | 'above' | 'any'
          addedAt: number
          lastPrice: number
          priceChange?: number
        }
        
        interface RecentAlert {
          alert: AlertData
          item: WatchlistItem
          message: string
          triggeredAt: number
        }
        
        const recentAlerts: RecentAlert[] = []
        
        for (const item of watchlistItems) {
          const alert = watchlistService.getPriceAlert(item.nft.asset_id)
          
          if (alert && alert.enabled && alert.lastTriggered) {
            // Check if alert was triggered since last check
            if (alert.lastTriggered > lastCheckTime) {
              // Create alert message based on alert type
              let message = ''
              const currentPrice = item.lastPrice || parseFloat(item.nft.price || '0')
              
              switch (alert.alertType) {
                case 'below':
                  message = `${item.nft.name} price dropped to ${currentPrice.toFixed(2)} WAX (target: ${alert.targetPrice} WAX)`
                  break
                case 'above':
                  message = `${item.nft.name} price rose to ${currentPrice.toFixed(2)} WAX (target: ${alert.targetPrice} WAX)`
                  break
                case 'any': {
                  const changePercent = Math.abs(item.priceChange || 0)
                  const direction = (item.priceChange || 0) > 0 ? 'increased' : 'decreased'
                  message = `${item.nft.name} price ${direction} by ${changePercent.toFixed(1)}% to ${currentPrice.toFixed(2)} WAX`
                  break
                }
              }
              
              recentAlerts.push({
                alert,
                item,
                message,
                triggeredAt: alert.lastTriggered
              })
            }
          }
        }

        // Sort alerts by trigger time (most recent first)
        recentAlerts.sort((a, b) => b.triggeredAt - a.triggeredAt)
        
        // Display alerts with a small delay between each
        recentAlerts.forEach((alertData, index) => {
          setTimeout(() => {
            // Dispatch custom event for PriceAlertManager to handle
            window.dispatchEvent(new CustomEvent('priceAlert', {
              detail: {
                item: alertData.item,
                message: alertData.message
              }
            }))
          }, index * 1000) // 1 second delay between alerts
        })

        // Update last check timestamp
        localStorage.setItem(lastCheckKey, now.toString())
        
        if (recentAlerts.length > 0) {
          log.info(`Found ${recentAlerts.length} triggered watchlist alerts since last visit`)
        }
        
      } catch (error) {
        log.error('Error checking watchlist alerts:', 'WatchlistAlertChecker.checkTriggeredAlerts', error as Error)
      }
    }

    // Check alerts after a short delay to ensure everything is loaded
    const timeoutId = setTimeout(checkTriggeredAlerts, 2000)
    
    return () => clearTimeout(timeoutId)
  }, [isConnected, account])

  // This component doesn't render anything visible
  return null
}

export default WatchlistAlertChecker