import React, { useState, useEffect, useCallback } from 'react'
import '../css/price-alert-notification.css'

interface PriceAlertEvent {
  item: {
    nft: {
      asset_id: string
      name: string
      image: string
      price: string
    }
  }
  message: string
}

interface NotificationProps {
  alert: PriceAlertEvent
  onClose: () => void
}

const PriceAlertNotification: React.FC<NotificationProps> = ({ alert, onClose }) => {
  const [isVisible, setIsVisible] = useState(false)
  const [isClosing, setIsClosing] = useState(false)

  const handleClose = useCallback(() => {
    setIsClosing(true)
    setTimeout(() => {
      onClose()
    }, 300)
  }, [onClose])

  useEffect(() => {
    // Show notification with animation
    const showTimer = setTimeout(() => setIsVisible(true), 100)
    
    // Auto-close after 8 seconds
    const autoCloseTimer = setTimeout(() => {
      handleClose()
    }, 8000)

    return () => {
      clearTimeout(showTimer)
      clearTimeout(autoCloseTimer)
    }
  }, [handleClose])

  const handleViewWatchlist = () => {
    window.location.href = '/watchlist'
    handleClose()
  }

  return (
    <div className={`price-alert-notification ${
      isVisible && !isClosing ? 'visible' : ''
    } ${
      isClosing ? 'closing' : ''
    }`}>
      <div className="notification-content">
        <div className="notification-header">
          <div className="notification-icon">
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <path d="M15 17h5l-5 5-5-5h5v-5a7.5 7.5 0 1 0-15 0v5h5l-5 5-5-5h5v-5a12.5 12.5 0 1 1 25 0v5z"></path>
            </svg>
          </div>
          <div className="notification-title">
            <h4>Price Alert</h4>
            <span className="notification-time">{new Date().toLocaleTimeString()}</span>
          </div>
          <button className="notification-close" onClick={handleClose}>
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </button>
        </div>
        
        <div className="notification-body">
          <div className="nft-preview">
            <img 
              src={alert.item.nft.image} 
              alt={alert.item.nft.name}
              onError={(e) => {
                const target = e.target as HTMLImageElement
                target.src = 'https://pixeljourney.xyz/img/toppixals/10.webp'
              }}
            />
          </div>
          <div className="alert-details">
            <h5 className="nft-name">{alert.item.nft.name}</h5>
            <p className="alert-message">{alert.message}</p>
          </div>
        </div>
        
        <div className="notification-actions">
          <button className="btn-secondary" onClick={handleClose}>
            Dismiss
          </button>
          <button className="btn-primary" onClick={handleViewWatchlist}>
            View Watchlist
          </button>
        </div>
      </div>
    </div>
  )
}

const PriceAlertManager: React.FC = () => {
  const [alerts, setAlerts] = useState<PriceAlertEvent[]>([])

  useEffect(() => {
    const handlePriceAlert = (event: CustomEvent<PriceAlertEvent>) => {
      setAlerts(prev => [...prev, event.detail])
    }

    window.addEventListener('priceAlert', handlePriceAlert as EventListener)
    
    return () => {
      window.removeEventListener('priceAlert', handlePriceAlert as EventListener)
    }
  }, [])

  const removeAlert = (index: number) => {
    setAlerts(prev => prev.filter((_, i) => i !== index))
  }

  return (
    <div className="price-alert-manager">
      {alerts.map((alert, index) => (
        <PriceAlertNotification
          key={`${alert.item.nft.asset_id}-${Date.now()}-${index}`}
          alert={alert}
          onClose={() => removeAlert(index)}
        />
      ))}
    </div>
  )
}

export default PriceAlertManager
export { PriceAlertNotification }