import React, { useState, useMemo } from 'react'
import { useWallet } from '../hooks/useWallet'
import type { InventoryNFT } from '../types/inventory'
import { useNotifications } from "../hooks/useNotifications";
import { log } from "../services/loggingService";

import '../css/modal.css'

interface DistributeModalProps {
  nfts: InventoryNFT[]
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
}

interface NFTDistribution {
  nft: InventoryNFT
  recipient: string
}

const DistributeModal: React.FC<DistributeModalProps> = ({ nfts, isOpen, onClose, onSuccess }) => {
  const [distributions, setDistributions] = useState<NFTDistribution[]>(() => 
    nfts.map(nft => ({ nft, recipient: '' }))
  )
  const [bulkAddresses, setBulkAddresses] = useState('')
  const [memo, setMemo] = useState('')
  const [isDistributing, setIsDistributing] = useState(false)
  const [distributeProgress, setDistributeProgress] = useState(0)
  const [batchSize, setBatchSize] = useState(10)
  const [sortBy, setSortBy] = useState('name_asc')
  const { session } = useWallet()
  const { showSuccess, showError } = useNotifications()

  // Sort NFTs for display
  const sortedDistributions = useMemo(() => {
    const sorted = [...distributions]
    
    switch (sortBy) {
      case 'name_asc':
        return sorted.sort((a, b) => (a.nft.name || '').localeCompare(b.nft.name || ''))
      case 'name_desc':
        return sorted.sort((a, b) => (b.nft.name || '').localeCompare(a.nft.name || ''))
      case 'collection_asc':
        return sorted.sort((a, b) => a.nft.collection.collection_name.localeCompare(b.nft.collection.collection_name))
      case 'collection_desc':
        return sorted.sort((a, b) => b.nft.collection.collection_name.localeCompare(a.nft.collection.collection_name))
      case 'mint_asc':
        return sorted.sort((a, b) => (a.nft.mint_number || 0) - (b.nft.mint_number || 0))
      case 'mint_desc':
        return sorted.sort((a, b) => (b.nft.mint_number || 0) - (a.nft.mint_number || 0))
      default:
        return sorted
    }
  }, [distributions, sortBy])

  // Update distributions when NFTs change
  React.useEffect(() => {
    setDistributions(nfts.map(nft => ({ nft, recipient: '' })))
  }, [nfts])

  const handleRecipientChange = (assetId: string, recipient: string) => {
    setDistributions(prev => 
      prev.map(dist => 
        dist.nft.asset_id === assetId 
          ? { ...dist, recipient }
          : dist
      )
    )
  }

  const handleBulkAddressesChange = (addresses: string) => {
    setBulkAddresses(addresses)
    
    if (addresses.trim()) {
      const addressList = addresses.split(',').map(addr => addr.trim()).filter(addr => addr)
      
      if (addressList.length > 0) {
        setDistributions(prev => 
          prev.map((dist, index) => ({
            ...dist,
            recipient: addressList[index % addressList.length] || ''
          }))
        )
      }
    }
  }

  const randomizeDistribution = () => {
    if (!bulkAddresses.trim()) {
      showError('No addresses provided', 'Please enter comma-separated addresses first')
      return
    }
    
    const addressList = bulkAddresses.split(',').map(addr => addr.trim()).filter(addr => addr)
    
    if (addressList.length === 0) {
      showError('Invalid addresses', 'Please provide valid comma-separated addresses')
      return
    }
    
    setDistributions(prev => 
      prev.map(dist => ({
        ...dist,
        recipient: addressList[Math.floor(Math.random() * addressList.length)]
      }))
    )
  }

  const clearAllRecipients = () => {
    setDistributions(prev => prev.map(dist => ({ ...dist, recipient: '' })))
    setBulkAddresses('')
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!session) {
      showError('Wallet Required', 'Please connect your wallet first')
      return
    }

    // Validate all distributions have recipients
    const invalidDistributions = distributions.filter(dist => !dist.recipient.trim())
    if (invalidDistributions.length > 0) {
      showError('Missing Recipients', `Please specify recipients for all ${invalidDistributions.length} NFT(s)`)
      return
    }

    setIsDistributing(true)
    setDistributeProgress(0)

    try {
      const totalTransfers = distributions.length
      let completedTransfers = 0

      // Process in batches
      for (let i = 0; i < distributions.length; i += batchSize) {
        const batch = distributions.slice(i, i + batchSize)
        
        // Create batch transaction
        const actions = batch.map(({ nft, recipient }) => ({
          account: 'atomicassets',
          name: 'transfer',
          authorization: [{
            actor: session.actor.toString(),
            permission: 'active'
          }],
          data: {
            from: session.actor.toString(),
            to: recipient.trim(),
            asset_ids: [nft.asset_id],
            memo: memo || `Distributed via PxMarket`
          }
        }))

        // Execute batch transaction
        const result = await session.transact({
          actions
        }, {
           expireSeconds: 30
        })

        completedTransfers += batch.length
        setDistributeProgress((completedTransfers / totalTransfers) * 100)
        
        log.info('Batch distribution completed', 'DistributeModal', {
          batchSize: batch.length,
          completed: completedTransfers,
          total: totalTransfers,
          transactionId: result.transaction
        })
      }

      showSuccess('Distribution Complete', `Successfully distributed ${totalTransfers} NFT${totalTransfers !== 1 ? 's' : ''}`)
      onSuccess?.()
      onClose()
      
    } catch (error) {
      log.error('Distribution failed', 'DistributeModal', error as Error)
      showError(
        'Distribution Failed',
        error instanceof Error ? error.message : 'Unknown error occurred'
      )
    } finally {
      setIsDistributing(false)
      setDistributeProgress(0)
    }
  }

  if (!isOpen) return null

  const validDistributions = distributions.filter(dist => dist.recipient.trim())
  const readyToDistribute = validDistributions.length === distributions.length

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content distribute-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Distribute NFTs</h2>
          <button className="modal-close" onClick={onClose}>
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </button>
        </div>

        <div className="modal-body">
          <div className="distribute-summary">
            <h3>Distribution Summary</h3>
            <p>You are about to distribute <strong>{nfts.length} NFT{nfts.length !== 1 ? 's' : ''}</strong> to individual recipients</p>
            
            {/* Bulk Address Input */}
            <div className="bulk-addresses-section">
              <h4>Bulk Address Input</h4>
              <div className="form-group">
                <label htmlFor="bulk-addresses">Comma-separated addresses (optional)</label>
                <textarea
                  id="bulk-addresses"
                  className="form-control"
                  value={bulkAddresses}
                  onChange={(e) => handleBulkAddressesChange(e.target.value)}
                  placeholder="pixeljourney,market.pxj,council.pxj"
                  rows={3}
                />
                <div className="bulk-actions">
                  <button 
                    type="button" 
                    className="btn btn-secondary"
                    onClick={randomizeDistribution}
                    disabled={!bulkAddresses.trim()}
                  >
                    🎲 Randomize Distribution
                  </button>
                  <button 
                    type="button" 
                    className="btn btn-secondary"
                    onClick={clearAllRecipients}
                  >
                    🗑️ Clear All
                  </button>
                </div>
              </div>
            </div>

            {/* Batch Size Configuration */}
            <div className="batch-config">
              <h4>Transaction Settings</h4>
              <div className="form-group">
                <label htmlFor="batch-size">NFTs per transaction (5-200)</label>
                <input
                  type="number"
                  id="batch-size"
                  className="form-control"
                  value={batchSize}
                  onChange={(e) => setBatchSize(Math.max(5, Math.min(200, parseInt(e.target.value) || 10)))}
                  min={5}
                  max={200}
                />
                <small>Larger batches are more efficient but may fail if too large</small>
              </div>
            </div>
          </div>

          {/* Individual NFT Distribution List */}
          <div className="distribution-list">
            <div className="distribution-header">
              <h4>Individual Recipients ({validDistributions.length}/{distributions.length} configured)</h4>
              {nfts.length > 1 && (
                <div className="sort-container">
                  <label htmlFor="distribute-sort">Sort by:</label>
                  <select 
                    id="distribute-sort"
                    className="sort-select"
                    value={sortBy}
                    onChange={(e) => setSortBy(e.target.value)}
                  >
                    <option value="name_asc">Name (A-Z)</option>
                    <option value="name_desc">Name (Z-A)</option>
                    <option value="collection_asc">Collection (A-Z)</option>
                    <option value="collection_desc">Collection (Z-A)</option>
                    <option value="mint_asc">Mint # (Low-High)</option>
                    <option value="mint_desc">Mint # (High-Low)</option>
                  </select>
                </div>
              )}
            </div>
            
            <div className="distribution-items">
              {sortedDistributions.map(({ nft, recipient }) => (
                <div key={nft.asset_id} className="distribution-item">
                  <div className="nft-preview">
                    <img 
                      src={nft.img?.startsWith('http') ? nft.img : `https://ipfs.io/ipfs/${nft.img}`}
                      alt={nft.name}
                      onError={(e) => {
                        const target = e.target as HTMLImageElement
                        target.src = 'https://pixeljourney.xyz/img/toppixals/10.webp'
                      }}
                    />
                    <div className="nft-info">
                      <span className="nft-name">{nft.name}</span>
                      <span className="nft-collection">{nft.collection.collection_name}</span>
                      <span className="nft-mint">#{nft.mint_number || 'N/A'}</span>
                    </div>
                  </div>
                  <div className="recipient-input">
                    <input
                      type="text"
                      className="form-control"
                      value={recipient}
                      onChange={(e) => handleRecipientChange(nft.asset_id, e.target.value)}
                      placeholder="Recipient account name"
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>

          <form onSubmit={handleSubmit} className="distribute-form">
            <div className="form-group">
              <label htmlFor="memo">Memo (optional)</label>
              <input
                type="text"
                id="memo"
                className="form-control"
                value={memo}
                onChange={(e) => setMemo(e.target.value)}
                placeholder="Optional transfer memo"
                maxLength={256}
              />
            </div>

            {isDistributing && (
              <div className="progress-container">
                <div className="progress-bar">
                  <div 
                    className="progress-fill" 
                    style={{ width: `${distributeProgress}%` }}
                  ></div>
                </div>
                <p>Distributing NFTs... {Math.round(distributeProgress)}%</p>
              </div>
            )}

            <div className="form-actions">
              <button 
                type="button" 
                className="btn btn-secondary"
                onClick={onClose}
                disabled={isDistributing}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn btn-primary"
                disabled={isDistributing || !readyToDistribute}
              >
                {isDistributing ? 'Distributing...' : `Distribute ${nfts.length} NFT${nfts.length !== 1 ? 's' : ''}`}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default DistributeModal