import React, { createContext, useState, useEffect, type ReactNode } from 'react'
import type { Notification, NotificationContextType } from '../hooks/useNotifications'
import { log } from '../services/loggingService'


interface ConfirmDialogState {
  title: string
  message: string
  confirmText: string
  cancelText: string
}

const NotificationContext = createContext<NotificationContextType | undefined>(undefined)

interface NotificationProviderProps {
  children: ReactNode
}

const NotificationProvider: React.FC<NotificationProviderProps> = ({ children }) => {
  const [notifications, setNotifications] = useState<Notification[]>([])
  const confirmResolveRef = React.useRef<((value: boolean) => void) | null>(null)
  const [confirmDialog, setConfirmDialog] = useState<ConfirmDialogState | null>(null)
  const [soundEnabled, setSoundEnabled] = useState(() => {
    const saved = localStorage.getItem('notification-sound-enabled')
    return saved !== null ? JSON.parse(saved) : true
  })
  const [maxNotifications] = useState(5) // Limit concurrent notifications

  // Save sound preference to localStorage
  useEffect(() => {
    localStorage.setItem('notification-sound-enabled', JSON.stringify(soundEnabled))
  }, [soundEnabled])

  const addNotification = (notification: Omit<Notification, 'id'>) => {
    const id = Date.now().toString() + Math.random().toString(36).substr(2, 9)
    const newNotification: Notification = {
      ...notification,
      id,
      duration: notification.duration ?? 5000
    }
    
    setNotifications(prev => {
      // Limit the number of concurrent notifications
      const updated = [...prev, newNotification]
      if (updated.length > maxNotifications) {
        return updated.slice(-maxNotifications)
      }
      return updated
    })
    
    // Auto-remove notification after duration
    if (newNotification.duration && newNotification.duration > 0) {
      setTimeout(() => {
        removeNotification(id)
      }, newNotification.duration)
    }
  }

  const removeNotification = (id: string) => {
    setNotifications(prev => prev.filter(notification => notification.id !== id))
  }

  const clearAllNotifications = () => {
    setNotifications([])
  }

  const toggleSound = () => {
    setSoundEnabled((prev: boolean) => !prev)
  }

  const showSuccess = (message: string, title?: string, duration?: number) => {
    addNotification({ type: 'success', message, title, duration })
  }

  const showError = (message: string, title?: string, duration?: number) => {
    // Log error notifications to centralized logging service
    log.warn('User error notification displayed', 'NotificationContext.showError', { message, title, duration })
    addNotification({ type: 'error', message, title, duration })
  }

  const showWarning = (message: string, title?: string, duration?: number) => {
    // Log warning notifications for monitoring user experience issues
    log.info('User warning notification displayed', 'NotificationContext.showWarning', { message, title, duration })
    addNotification({ type: 'warning', message, title, duration })
  }

  const showInfo = (message: string, title?: string, duration?: number) => {
    addNotification({ type: 'info', message, title, duration })
  }

  const confirm = (title: string, message: string, confirmText: string = 'Confirm', cancelText: string = 'Cancel'): Promise<boolean> => {
    return new Promise((resolve) => {
      setConfirmDialog({
        title,
        message,
        confirmText,
        cancelText
      })
      confirmResolveRef.current = resolve
    })
  }

  const resolveConfirm = (result: boolean) => {
    if (confirmResolveRef.current) {
      confirmResolveRef.current(result)
      confirmResolveRef.current = null
    }
    setConfirmDialog(null)
  }

  const value: NotificationContextType = {
    notifications,
    addNotification,
    removeNotification,
    clearAllNotifications,
    showSuccess,
    showError,
    showWarning,
    showInfo,
    confirm,
    confirmDialog,
    resolveConfirm,
    soundEnabled,
    toggleSound
  }

  return (
    <NotificationContext.Provider value={value}>
      {children}
      {/* Confirmation Dialog */}
      {confirmDialog && (
        <div className="modal-overlay">
          <div className="modal-content confirm-dialog">
            <div className="modal-header">
              <h3>{confirmDialog.title || 'Confirm Action'}</h3>
            </div>
            <div className="modal-body">
              <p>{confirmDialog.message}</p>
            </div>
            <div className="modal-actions">
              <button 
                className="btn-secondary" 
                onClick={() => resolveConfirm(false)}
              >
                {confirmDialog.cancelText}
              </button>
              <button 
                className="btn-primary" 
                onClick={() => resolveConfirm(true)}
              >
                {confirmDialog.confirmText}
              </button>
            </div>
          </div>
        </div>
      )}
    </NotificationContext.Provider>
  )
}

export default NotificationProvider
export { NotificationProvider, NotificationContext }