import React, { useState, useEffect } from 'react'
import { rwaxService, type TokenizedAsset, type FeeToken } from '../services/rwaxService'
import MediaRenderer from './MediaRenderer'
import { useWallet } from '../contexts/WalletContext'
import { useNotifications } from '../hooks/useNotifications'
import { log } from '../services/loggingService'

interface AssetDetailModalProps {
  asset: TokenizedAsset | null
  isOpen: boolean
  onClose: () => void
  onRedeem?: (asset: TokenizedAsset, feeToken: FeeToken) => void
}

const AssetDetailModal: React.FC<AssetDetailModalProps> = ({
  asset,
  isOpen,
  onClose,
  onRedeem
}) => {
  const { account } = useWallet()
  const { showError } = useNotifications()
  
  const [feeTokens, setFeeTokens] = useState<FeeToken[]>([])
  const [selectedFeeToken, setSelectedFeeToken] = useState<FeeToken | null>(null)
  const [loading, setLoading] = useState(false)
  const [showRedeemSection, setShowRedeemSection] = useState(false)

  const loadFeeTokens = async () => {
    try {
      const tokens = await rwaxService.getFeeTokens()
      setFeeTokens(tokens)
    } catch (error) {
      log.error('Failed to load fee tokens', 'AssetDetailModal.loadFeeTokens', error as Error)
      showError('Failed to load fee tokens')
    }
  }

  // Load fee tokens when modal opens
  useEffect(() => {
    if (isOpen && asset) {
      loadFeeTokens()
    }
  }, [isOpen, asset, loadFeeTokens])

  const handleRedeem = async () => {
    if (!asset || !selectedFeeToken || !account) {
      showError('Please select a fee token')
      return
    }

    setLoading(true)
    try {
      if (onRedeem) {
        await onRedeem(asset, selectedFeeToken)
      }
      onClose()
    } catch (error) {
      log.error('Asset redemption failed', 'AssetDetailModal.handleRedeem', error as Error, { assetId: asset?.assetId, feeToken: selectedFeeToken?.symbol })
      showError('Redemption failed. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  if (!isOpen || !asset) return null

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="asset-detail-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Asset Details</h2>
          <button className="close-button" onClick={onClose}>×</button>
        </div>
        
        <div className="modal-body">
          <div className="asset-main-info">
            <div className="asset-image-section">
              <MediaRenderer
                imageUrl={asset.image}
        alt={asset.name}
                className="asset-image"
              />
            </div>
            
            <div className="asset-info-section">
              <h3 className="asset-name">{asset.name}</h3>
              <div className="asset-metadata">
                <div className="metadata-item">
                  <span className="label">Asset ID:</span>
                  <span className="value">{asset.assetId}</span>
                </div>
                <div className="metadata-item">
                  <span className="label">Collection:</span>
                  <span className="value">{asset.collection?.displayName || asset.collection?.collectionName}</span>
                </div>
                <div className="metadata-item">
                  <span className="label">Schema:</span>
                  <span className="value">{asset.schema}</span>
                </div>
                <div className="metadata-item">
                  <span className="label">Mint #:</span>
                  <span className="value">{asset.mint}</span>
                </div>
                <div className="metadata-item">
                  <span className="label">Template ID:</span>
                  <span className="value">{asset.template?.templateId}</span>
                </div>
                <div className="metadata-item">
                  <span className="label">WPIXAL Amount:</span>
                  <span className="value highlight">{asset.rwax.redeemAmount.toFixed(8)} WPIXAL</span>
                </div>
                {asset.rarityScore && (
                  <div className="metadata-item">
                    <span className="label">Rarity Score:</span>
                    <span className="value highlight">{asset.rarityScore.toFixed(2)}</span>
                  </div>
                )}
                {asset.attributesFloor && (
                  <div className="metadata-item">
                    <span className="label">Attributes Floor:</span>
                    <span className="value highlight">{asset.attributesFloor.toFixed(8)} WPIXAL</span>
                  </div>
                )}
                <div className="metadata-item">
                  <span className="label">Contract:</span>
                  <span className="value">{asset.contract}</span>
                </div>
                <div className="metadata-item">
                  <span className="label">Owner:</span>
                  <span className="value">{asset.owner}</span>
                </div>
              </div>
              
              {/* Asset Attributes/Traits */}
              {asset.immutableData && asset.immutableData.length > 0 && (
                <div className="asset-attributes">
                  <h4>NFT Traits</h4>
                  <div className="attributes-grid">
                    {asset.immutableData
                      .filter(({ key, value }) => {
                        // Filter out technical fields and focus on actual traits
                        const excludeKeys = ['image', 'img', 'video', 'audio', 'model', 'description', 'name']
                        return !excludeKeys.includes(key.toLowerCase()) && value && value[1] !== null && value[1] !== undefined && value[1] !== ''
                      })
                      .map(({ key, value }) => {
                        // Format key to be more readable
                        const formattedKey = key.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())
                        const displayValue = Array.isArray(value) ? value[1] : value
                        
                        return (
                          <div key={key} className="trait-item">
                            <div className="trait-label">{formattedKey}</div>
                            <div className="trait-value">{String(displayValue)}</div>
                          </div>
                        )
                      })}
                  </div>
                  
                  {/* Show description if available */}
                  {(() => {
                    const descriptionItem = asset.immutableData.find(item => item.key.toLowerCase() === 'description')
                    const description = descriptionItem ? (Array.isArray(descriptionItem.value) ? descriptionItem.value[1] : descriptionItem.value) : null
                    return description && String(description).trim() ? (
                      <div className="asset-description">
                        <h4>Description</h4>
                        <p>{String(description)}</p>
                      </div>
                    ) : null
                  })()}
                </div>
              )}
            </div>
          </div>
          
          {/* Redemption Section */}
          {account && (
            <div className="redemption-section">
              {!showRedeemSection ? (
                <button 
                  className="show-redeem-button"
                  onClick={() => setShowRedeemSection(true)}
                >
                  Redeem this Asset
                </button>
              ) : (
                <div className="redeem-form">
                  <h4>Redeem Asset</h4>
                  <p className="redeem-description">
                    Redeem this tokenized asset by paying {asset.rwax.redeemAmount.toFixed(8)} WPIXAL plus a fee.
                  </p>
                  
                  <div className="fee-selection">
                    <h5>Select Fee Token:</h5>
                    <div className="fee-options">
                      {feeTokens.map((token, index) => (
                        <div 
                          key={index}
                          className={`fee-option ${selectedFeeToken === token ? 'selected' : ''}`}
                          onClick={() => setSelectedFeeToken(token)}
                        >
                          <div className="fee-info">
                            <span className="fee-amount">{token.fee}</span>
                            <span className="fee-contract">({token.contract})</span>
                          </div>
                          {token.wax_value && token.symbol !== 'WAX' && (
                            <div className="wax-equivalent">
                              ≈ {parseFloat(token.wax_value).toFixed(4)} WAX
                            </div>
                          )}
                        </div>
                      ))}
                    </div>
                  </div>
                  
                  <div className="redeem-actions">
                    <button 
                      className="cancel-redeem-button"
                      onClick={() => setShowRedeemSection(false)}
                    >
                      Cancel
                    </button>
                    <button 
                      className="confirm-redeem-button"
                      onClick={handleRedeem}
                      disabled={!selectedFeeToken || loading}
                    >
                      {loading ? 'Processing...' : 'Confirm Redemption'}
                    </button>
                  </div>
                </div>
              )}
            </div>
          )}
          
          {!account && (
            <div className="wallet-connect-message">
              <p>Connect your wallet to redeem this asset</p>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default AssetDetailModal