import React, { useState, useEffect, useCallback } from 'react'
import { useParams, Link } from 'react-router-dom'
import { apiService } from '../services/api'
import TruncatedText from './TruncatedText'
import MediaRenderer from './MediaRenderer'
import { getSaleAssetImageUrl, getSaleAssetVideoUrl } from '../utils/mediaUtils'
import { useDisplaySettings } from '../hooks/useDisplaySettings'
import { getCollectionTier, getTierBadgeConfig } from '../config/collectionTiers'
import QuickLinks from './QuickLinks'
import ChatWidget from './ChatWidget'
import ImageZoom from './ImageZoom'
import ShareModal from './ShareModal'

import { API_ENDPOINTS } from '../config/marketplaceConfig'
import { log } from '../services/loggingService'
import '../css/collection-detail.css'
import '../css/CollectionProfile.css'
import '../css/quick-links.css'
import '../css/rwax.css'



interface SchemaFormat {
  name: string
  type: string
}

interface Schema {
  schema_name: string
  format: SchemaFormat[]
  created_at_time: string
  created_at_block: string
}

interface Template {
  template_id: string
  max_supply: string
  is_transferable: boolean
  is_burnable: boolean
  issued_supply: string
  immutable_data: Record<string, unknown>
  created_at_time: string
  created_at_block: string
  schema_name: string
}

interface CollectionData {
  collection_name: string
  name: string
  img?: string
  author: string
  allow_notify: boolean
  authorized_accounts: string[]
  notify_accounts: string[]
  market_fee: number
  data?: Record<string, string | number | boolean>
  created_at_block: string
  created_at_time: string
  assets?: string | number
}

interface CollectionAnalytics {
  volume: string
  sales: string
  floor_price: string
  market_cap: string
  holders: number
  assets: string
  volume_24h?: string
}

interface Holder {
  account: string
  assets: string
}

interface SimilarCollection {
  collection_name: string
  name: string
  similarity_score?: number
}

interface TopSale {
  sale_id: string
  assets: Array<{
    data?: {
      img?: string
      video?: string
      name?: string
      [key: string]: string | number | boolean | undefined
    }
    template?: {
      template_id: string
      max_supply: string
      immutable_data?: Record<string, string | number | boolean>
    }
    collection?: {
      collection_name: string
      name: string
      img?: string
    }
    schema?: {
      schema_name: string
    }
    asset_id?: string
    name?: string
  }>
  listing_price: string
  listing_symbol: string
  seller: string
  buyer?: string
  updated_at_time: string
  state: number
  collection_name?: string
  price?: {
    amount: string
    token_symbol: string
    token_precision: number
    token_contract: string
  }
}

const CollectionDetail: React.FC = React.memo(() => {
  const { collectionName } = useParams<{ collectionName: string }>()
  const { settings } = useDisplaySettings()
  const [collection, setCollection] = useState<CollectionData | null>(null)
  const [schemas, setSchemas] = useState<Schema[]>([])
  const [templates, setTemplates] = useState<Template[]>([])
  const [analytics, setAnalytics] = useState<CollectionAnalytics | null>(null)
  const [volumePeriod, setVolumePeriod] = useState<'24' | '7' | '30' | '90' | '365'>('24')
  const [volumeData, setVolumeData] = useState<{[key: string]: string}>({})
  const [loadingVolume, setLoadingVolume] = useState(false)
  const [marketCapCurrency, setMarketCapCurrency] = useState<'WAX' | 'USD'>('WAX')
  const [waxToUsdRate, setWaxToUsdRate] = useState<number>(0.05) // Default WAX price
  
  const [holders, setHolders] = useState<Holder[]>([])
  const [holdersPage, setHoldersPage] = useState(1)
  const [loadingMoreHolders, setLoadingMoreHolders] = useState(false)
  const [hasMoreHolders, setHasMoreHolders] = useState(true)
  const [topSales, setTopSales] = useState<TopSale[]>([])
  const [displayedSales, setDisplayedSales] = useState<TopSale[]>([])
  const [salesPage, setSalesPage] = useState(1)
  const [salesPerPage] = useState(10)
  const [hasMoreSales, setHasMoreSales] = useState(false)
  const [similarCollections, setSimilarCollections] = useState<SimilarCollection[]>([])
  const [atomicMarketStats, setAtomicMarketStats] = useState<{
    sales: string
    volume: string
    suggested_median: string
    suggested_average: string
  } | null>(null)
  const [selectedSchema, setSelectedSchema] = useState<string>('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [activeTab, setActiveTab] = useState<'sales' | 'holders' | 'schemas' | 'templates' | 'similar' | 'chat'>('sales')
  const [searchTerm, setSearchTerm] = useState('')
  const [sortBy, setSortBy] = useState<'name' | 'created' | 'supply'>('name')
  const [showImageZoom, setShowImageZoom] = useState(false)
  const [zoomedImage, setZoomedImage] = useState<{url: string, name: string}>({url: '', name: ''})
  const [showShareModal, setShowShareModal] = useState(false)



  // Filter and sort schemas based on search term
  const filteredSchemas = schemas
    .filter(schema => 
      schema.schema_name.toLowerCase().includes(searchTerm.toLowerCase())
    )
    .sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.schema_name.localeCompare(b.schema_name)
        case 'created':
          return new Date(b.created_at_time).getTime() - new Date(a.created_at_time).getTime()
        default:
          return 0
      }
    })

  // Filter and sort templates based on search term and selected schema
  const filteredTemplates = templates
    .filter(template => {
      const matchesSearch = 
        (template.immutable_data?.name && String(template.immutable_data.name).toLowerCase().includes(searchTerm.toLowerCase())) ||
        template.template_id.toLowerCase().includes(searchTerm.toLowerCase())
      
      const matchesSchema = !selectedSchema || template.schema_name === selectedSchema
      
      return matchesSearch && matchesSchema
    })
    .sort((a, b) => {
      switch (sortBy) {
        case 'name': {
          const nameA = String(a.immutable_data?.name || `Template #${a.template_id}`)
          const nameB = String(b.immutable_data?.name || `Template #${b.template_id}`)
          return nameA.localeCompare(nameB)
        }
        case 'created':
          return new Date(b.created_at_time).getTime() - new Date(a.created_at_time).getTime()
        case 'supply':
          return parseInt(b.issued_supply) - parseInt(a.issued_supply)
        default:
          return 0
      }
    })

  // Function to fetch volume data from HiveBP API
  const fetchVolumeData = useCallback(async (days: string) => {
    if (!collectionName) return '0'
    
    try {
      const response = await fetch(`https://wax-api.hivebp.io/v3/volume/${days}/${collectionName}`)
      if (!response.ok) throw new Error('Failed to fetch volume data')
      const data = await response.json()
      return data.volume ? String(data.volume) : '0'
    } catch (error) {
      console.warn(`Failed to fetch ${days}d volume data:`, error)
      return '0'
    }
  }, [collectionName])

  // Function to fetch collection stats from HiveBP API
  const fetchCollectionStats = useCallback(async () => {
    if (!collectionName) return null
    
    try {
      const response = await fetch(`https://wax-api.hivebp.io/v3/collection-stats/${collectionName}`)
      if (!response.ok) throw new Error('Failed to fetch collection stats')
      const data = await response.json()
      return {
        numUsers: data.numUsers || 0,
        usdMarketCap: data.usdMarketCap || 0,
        waxMarketCap: data.waxMarketCap || 0,
        numAssets: data.numAssets || 0
      }
    } catch (error) {
      console.warn('Failed to fetch collection stats:', error)
      return null
    }
  }, [collectionName])

  // Function to handle volume period switching
  const handleVolumePeriodChange = useCallback(async () => {
    if (!collectionName) return
    
    setLoadingVolume(true)
    const periods: Array<'24' | '7' | '30' | '90' | '365'> = ['24', '7', '30', '90', '365']
    const currentIndex = periods.indexOf(volumePeriod)
    const nextPeriod = periods[(currentIndex + 1) % periods.length]
    
    // Check if we already have this data cached
    if (volumeData[nextPeriod]) {
      setVolumePeriod(nextPeriod)
      setLoadingVolume(false)
      return
    }
    
    // Fetch new volume data
    const newVolumeValue = await fetchVolumeData(nextPeriod)
    setVolumeData(prev => ({ ...prev, [nextPeriod]: newVolumeValue }))
    setVolumePeriod(nextPeriod)
    setLoadingVolume(false)
  }, [collectionName, volumePeriod, volumeData, fetchVolumeData])

  const handleMarketCapToggle = async () => {
    if (marketCapCurrency === 'WAX') {
      // Fetch current WAX price when switching to USD
      try {
        const response = await fetch('https://api.coingecko.com/api/v3/simple/price?ids=wax&vs_currencies=usd')
        const data = await response.json()
        if (data.wax && data.wax.usd) {
          setWaxToUsdRate(data.wax.usd)
        }
      } catch (error) {
        console.warn('Could not fetch WAX price:', error)
      }
      setMarketCapCurrency('USD')
    } else {
      setMarketCapCurrency('WAX')
    }
  }

  const loadCollectionData = useCallback(async () => {
    if (!collectionName) return
    
    setLoading(true)
    setError(null)
    
    try {
      // Load basic collection data first
      const [collectionInfo, schemasData, templatesData] = await Promise.all([
        apiService.getCollectionDetails(collectionName),
        apiService.getCollectionSchemas(collectionName),
        apiService.getCollectionTemplates(collectionName)
      ])
      
      setCollection(collectionInfo)
      setSchemas(schemasData)
      setTemplates(templatesData)
      
      // Load enhanced analytics from HiveBP API and holder stats (non-blocking)
      Promise.all([
        apiService.getCollectionAnalytics(collectionName),
        apiService.getEnhancedCollectionStats(collectionName),
        apiService.getCollectionVolumeGraph(collectionName, 30),
        apiService.getSimilarCollections(collectionName),
        apiService.getCollectionHolders(collectionName, 500),
        apiService.getCollectionTopSales(collectionName, 50),
        fetchVolumeData('24'), // Fetch initial 24h volume data
        fetchCollectionStats(), // Fetch collection stats from HiveBP API
        apiService.getCollectionMarketStats({ collection_name: collectionName, symbol: 'WAX' }) // Fetch atomicmarket stats
      ]).then(([analyticsData, enhancedStats, , similarData, holdersData, salesData, volume24h, collectionStats, marketStats]) => {
        
        // Cache the initial 24h volume data
        setVolumeData(prev => ({ ...prev, '24': volume24h }))
        
        // Store atomicmarket stats with proper WAX decimal conversion
        const processedMarketStats = marketStats.success && marketStats.data ? {
          sales: marketStats.data.sales || '0',
          volume: marketStats.data.volume ? String(parseFloat(marketStats.data.volume) / Math.pow(10, 8)) : '0',
          suggested_median: marketStats.data.suggested_median ? String(parseFloat(marketStats.data.suggested_median) / Math.pow(10, 8)) : '0',
          suggested_average: marketStats.data.suggested_average ? String(parseFloat(marketStats.data.suggested_average) / Math.pow(10, 8)) : '0'
        } : null
        
        setAtomicMarketStats(processedMarketStats)
        
        // Merge analytics data with enhanced HiveBP stats
        const combinedAnalytics: CollectionAnalytics = {
           volume: processedMarketStats?.volume || analyticsData?.volume || '0',
           sales: processedMarketStats?.sales || analyticsData?.sales || '0',
           floor_price: enhancedStats.floorPrice && !isNaN(enhancedStats.floorPrice) ? String(enhancedStats.floorPrice) : 
             (analyticsData?.floor_price && !isNaN(parseFloat(analyticsData.floor_price)) ? analyticsData.floor_price : 
             (analyticsData?.floor && !isNaN(parseFloat(analyticsData.floor)) ? analyticsData.floor : 
             (processedMarketStats?.suggested_median && !isNaN(parseFloat(processedMarketStats.suggested_median)) ? processedMarketStats.suggested_median :
             (salesData && salesData.length > 0 ? (() => {
               const validPrices = salesData.slice(-5).map(sale => {
                 const price = typeof sale.price === 'string' ? sale.price : (sale.price?.amount || '0')
                 return parseFloat(price || '0')
               }).filter(price => !isNaN(price) && price > 0)
               return validPrices.length > 0 ? String(Math.min(...validPrices).toFixed(2)) : '0'
             })() : '0')))),
           market_cap: collectionStats?.waxMarketCap ? String(collectionStats.waxMarketCap) : enhancedStats.marketCap ? String(enhancedStats.marketCap) : (analyticsData?.market_cap || '0'),
           holders: collectionStats?.numUsers || analyticsData?.holders || 0,
           assets: collectionStats?.numAssets ? String(collectionStats.numAssets) : (analyticsData?.assets || '0'),
           volume_24h: volume24h || (enhancedStats.volume24h ? String(enhancedStats.volume24h) : (analyticsData?.volume_24h || analyticsData?.volume || '0'))
         }
        
        setAnalytics(combinedAnalytics)
        // setVolumeGraph(volumeData) // Unused
        setSimilarCollections(similarData)
        setHolders(holdersData)
        setTopSales(salesData || [])
      }).catch(error => {
        log.error('Enhanced analytics failed to load', 'CollectionDetail.loadCollectionData', error as Error, { collectionName })
      })
      
    } catch (error) {
      log.error('Failed to load collection data', 'CollectionDetail.loadCollectionData', error as Error, { collectionName })
      setError(`Failed to load collection "${collectionName}". This collection may not exist or be temporarily unavailable.`)
    } finally {
      setLoading(false)
    }
  }, [collectionName])

  const loadTemplates = useCallback(async (schemaName: string) => {
    if (!collectionName) return
    
    try {
      const templatesData = await apiService.getCollectionTemplates(collectionName, schemaName)
      setTemplates(templatesData)
    } catch (error) {
      log.error('Failed to load collection templates', 'CollectionDetail.loadTemplates', error as Error, { collectionName, schemaName })
    }
  }, [collectionName])

  useEffect(() => {
    if (collectionName) {
      loadCollectionData()
    }
  }, [collectionName, loadCollectionData])

  useEffect(() => {
    if (selectedSchema && collectionName) {
      loadTemplates(selectedSchema)
    }
  }, [selectedSchema, collectionName, loadTemplates])

  const loadMoreHolders = async () => {
    if (!collectionName || loadingMoreHolders || !hasMoreHolders) return
    
    setLoadingMoreHolders(true)
    try {
      const nextPage = holdersPage + 1
      const offset = (nextPage - 1) * 500
      const newHolders = await apiService.getCollectionHolders(collectionName, 500, offset)
      
      if (newHolders && newHolders.length > 0) {
        setHolders(prev => [...prev, ...newHolders])
        setHoldersPage(nextPage)
        setHasMoreHolders(newHolders.length === 500) // If we got less than 500, no more pages
      } else {
        setHasMoreHolders(false)
      }
    } catch (error) {
      console.error('Error loading more holders:', error as Error)
      setHasMoreHolders(false)
    } finally {
      setLoadingMoreHolders(false)
    }
  }

  // Update displayed sales when topSales changes or page changes
  useEffect(() => {
    const startIndex = 0
    const endIndex = salesPage * salesPerPage
    const newDisplayedSales = topSales.slice(startIndex, endIndex)
    setDisplayedSales(newDisplayedSales)
    setHasMoreSales(endIndex < topSales.length)
  }, [topSales, salesPage, salesPerPage])

  const loadMoreSales = () => {
    if (hasMoreSales) {
      setSalesPage(prev => prev + 1)
    }
  }

  const getImageUrl = (imgPath: string): string | undefined => {
    if (!imgPath) return undefined
    if (imgPath.startsWith('http')) return imgPath
    return `https://ipfs.io/ipfs/${imgPath}`
  }

  const getTemplateImage = (template: Template): string => {
    if (template.immutable_data?.img) {
      const img = template.immutable_data.img
      if (typeof img === 'string') {
        return img.startsWith('Qm') ? `https://ipfs.io/ipfs/${img}` : img
      }
    }
    return 'https://pixeljourney.xyz/img/toppixals/10.webp'
  }

  const getVideoUrl = (template: Template): string | null => {
    if (template.immutable_data?.video) {
      const video = template.immutable_data.video
      if (typeof video === 'string') {
        return video.startsWith('Qm') ? `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${video}` : video
      }
    }
    return null
  }





  const formatSaleDate = (dateString: string): string => {
    if (!dateString) return 'N/A'
    try {
      // Handle different date formats
      let date: Date
      if (dateString.includes('T')) {
        // ISO string format
        date = new Date(dateString)
      } else {
        // Handle timestamp - AtomicMarket API returns timestamps in seconds
        const timestamp = parseInt(dateString)
        // Check if timestamp is reasonable (after year 2000 and before year 2100)
        if (timestamp > 946684800 && timestamp < 4102444800) {
          // Timestamp is in seconds, convert to milliseconds
          date = new Date(timestamp * 1000)
        } else if (timestamp > 946684800000 && timestamp < 4102444800000) {
          // Timestamp is already in milliseconds
          date = new Date(timestamp)
        } else {
          // Invalid timestamp
          return 'N/A'
        }
      }
      
      if (isNaN(date.getTime())) {
        return 'N/A'
      }
      
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      })
    } catch {
      return 'N/A'
    }
  }

  const renderMediaElement = (template: Template, className: string = 'template-image') => {
    const videoUrl = getVideoUrl(template)
    const imageUrl = getTemplateImage(template)
    
    return (
      <MediaRenderer
        imageUrl={imageUrl}
        videoUrl={videoUrl}
        alt={String(template.immutable_data?.name || 'NFT')}
        className={className}
        enableAnimations={settings.enableAnimatedNFTs}
      />
    )
  }

  const formatSupply = (current: string, max: string): string => {
    if (max === '0') return `${current} (Unlimited)`
    return `${current} / ${max}`
  }

  if (loading) {
    return (
      <div className="collection-detail-container">
        <div className="loading-state">
          <div className="spinner"></div>
          <p>Loading collection details...</p>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="collection-detail-container">
        <div className="error-state">
          <h2>Collection Error</h2>
          <p>{error}</p>
          <Link to="/collections" className="back-link">← Back to Collections</Link>
        </div>
      </div>
    )
  }

  if (!collection) {
    return (
      <div className="collection-detail-container">
        <div className="error-state">
          <h2>Collection Not Found</h2>
          <p>The collection "{collectionName}" could not be found.</p>
          <Link to="/collections" className="back-link">← Back to Collections</Link>
        </div>
      </div>
    )
  }

  return (
    <div className="collection-profile-page">
      {/* Hero Section */}
      <div className="collection-hero">
        <div className="hero-background">
          <img 
            src={getImageUrl(collection.img || '')} 
            alt={collection.name || collection.collection_name}
            className="hero-bg-image"
          />
          <div className="hero-overlay"></div>
        </div>
        
        <div className="hero-content">
          <div className="breadcrumb">
            <Link to="/" className="breadcrumb-link">Home</Link>
            <span className="breadcrumb-separator">›</span>
            <Link to="/collections" className="breadcrumb-link">Collections</Link>
            <span className="breadcrumb-separator">›</span>
            <span className="breadcrumb-current">{collection.name || collection.collection_name}</span>
          </div>
          
          <div className="collection-profile">
            <div className="collection-logo-container">
              <img 
                src={getImageUrl(collection.img || '')} 
                alt={collection.name || collection.collection_name}
                className="collection-logo"
              />
            </div>
            
            <div className="profile-info">
              <div className="title-section">
                <h1 className="collection-title">{collection.name || collection.collection_name}</h1>
              </div>
              
              {/* Collection Tier and Share Button Row */}
              <div className="collection-actions-row" style={{
                display: 'flex',
                alignItems: 'center',
                gap: '12px',
                marginTop: '8px',
                marginBottom: '12px'
              }}>
                {(() => {
                  const tierInfo = getCollectionTier(collection.collection_name)
                  if (tierInfo) {
                    const badgeConfig = getTierBadgeConfig(tierInfo.tier)
                    return (
                      <div className={`collection-tier-badge ${badgeConfig.className}`}>
                        <span className="tier-icon">{badgeConfig.icon}</span>
                        <span className="tier-label">{badgeConfig.label}</span>
                        {tierInfo.verified && <span className="verified-icon">✓</span>}
                      </div>
                    )
                  }
                  return null
                })()}
                <button 
                  className="share-button"
                  onClick={() => setShowShareModal(true)}
                  style={{
                    background: 'rgba(255, 255, 255, 0.1)',
                    border: '1px solid rgba(255, 255, 255, 0.2)',
                    borderRadius: '8px',
                    padding: '8px 12px',
                    color: 'white',
                    cursor: 'pointer',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '6px',
                    fontSize: '14px',
                    fontWeight: '500',
                    transition: 'all 0.2s ease'
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.background = 'rgba(255, 255, 255, 0.15)'
                    e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.3)'
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.background = 'rgba(255, 255, 255, 0.1)'
                    e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.2)'
                  }}
                >
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M18 16.08C17.24 16.08 16.56 16.38 16.04 16.85L8.91 12.7C8.96 12.47 9 12.24 9 12C9 11.76 8.96 11.53 8.91 11.3L15.96 7.19C16.5 7.69 17.21 8 18 8C19.66 8 21 6.66 21 5C21 3.34 19.66 2 18 2C16.34 2 15 3.34 15 5C15 5.24 15.04 5.47 15.09 5.7L8.04 9.81C7.5 9.31 6.79 9 6 9C4.34 9 3 10.34 3 12C3 13.66 4.34 15 6 15C6.79 15 7.5 14.69 8.04 14.19L15.16 18.34C15.11 18.55 15.08 18.77 15.08 19C15.08 20.61 16.39 21.92 18 21.92C19.61 21.92 20.92 20.61 20.92 19C20.92 17.39 19.61 16.08 18 16.08Z" fill="currentColor"/>
                  </svg>
                  Share Collection
                </button>
              </div>
              <p className="collection-handle">@{collection.collection_name}</p>
              <p className="collection-creator">Created by {collection.author}</p>
              
              {collection.data?.description && (
                <div className="collection-description">
                  <TruncatedText 
                    text={String(collection.data.description || '')} 
                    maxLength={100}
                    className="description-text"
                  />
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Stats Dashboard */}
      <div className="stats-dashboard">
        {/* Basic Stats Row */}
        <div className="stats-grid">
          <div className="stat-card">
            <div className="stat-value">{analytics?.assets || collection.assets || 0}</div>
            <div className="stat-label">Total Assets</div>
          </div>
          <div className="stat-card">
            <div className="stat-value">{schemas.length}</div>
            <div className="stat-label">Schemas</div>
          </div>
          <div className="stat-card">
            <div className="stat-value">{templates.length}</div>
            <div className="stat-label">Templates</div>
          </div>
          <div className="stat-card">
            <div className="stat-value">{holders.length}</div>
            <div className="stat-label">Holders</div>
          </div>
        </div>
        
        {/* Premium Stats Row */}
        {analytics && (
          <div className="stats-grid">
            <div className="stat-card premium clickable" onClick={handleMarketCapToggle} style={{ cursor: 'pointer' }}>
              <div className="stat-value">
                {analytics.market_cap && analytics.market_cap !== '0' 
                  ? marketCapCurrency === 'WAX'
                    ? `${parseFloat(analytics.market_cap).toLocaleString()} WAX`
                    : `$${(parseFloat(analytics.market_cap) * waxToUsdRate).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`
                  : 'N/A'
                }
              </div>
              <div className="stat-label">Market Cap ({marketCapCurrency})</div>
            </div>
            <div className="stat-card premium">
              <div className="stat-value">
                {atomicMarketStats?.sales && atomicMarketStats.sales !== '0'
                  ? `${parseInt(atomicMarketStats.sales).toLocaleString()}`
                  : analytics?.sales && analytics.sales !== '0'
                  ? `${parseInt(analytics.sales).toLocaleString()}`
                  : 'N/A'
                }
              </div>
              <div className="stat-label">All Time Sales</div>
            </div>
            <div className="stat-card premium">
              <div className="stat-value">
                {atomicMarketStats?.volume && atomicMarketStats.volume !== '0'
                  ? `${parseFloat(atomicMarketStats.volume).toLocaleString()} WAX`
                  : analytics?.volume && analytics.volume !== '0'
                  ? `${parseFloat(analytics.volume).toLocaleString()} WAX`
                  : 'N/A'
                }
              </div>
              <div className="stat-label">All Time Volume</div>
            </div>
            <div className="stat-card premium clickable" onClick={handleVolumePeriodChange} style={{ cursor: 'pointer' }}>
              <div className="stat-value">
                {loadingVolume ? (
                  <span style={{ opacity: 0.6 }}>Loading...</span>
                ) : (
                  volumeData[volumePeriod] && volumeData[volumePeriod] !== '0' 
                    ? `${parseFloat(volumeData[volumePeriod]).toLocaleString()} WAX` 
                    : analytics?.volume_24h && analytics.volume_24h !== '0'
                      ? `${parseFloat(analytics.volume_24h).toLocaleString()} WAX`
                      : 'N/A'
                )}
              </div>
              <div className="stat-label">
                {volumePeriod === '24' ? '24H' : volumePeriod === '7' ? '7D' : volumePeriod === '30' ? '30D' : volumePeriod === '90' ? '90D' : '365D'} Volume
                <span style={{ fontSize: '0.8em', opacity: 0.7, marginLeft: '4px' }}>↻</span>
              </div>
            </div>
            <div className="stat-card premium">
              <div className="stat-value">
                {analytics.floor_price && analytics.floor_price !== '0' && !isNaN(parseFloat(analytics.floor_price)) && parseFloat(analytics.floor_price) > 0
                  ? `${parseFloat(analytics.floor_price).toFixed(2)} WAX` 
                  : atomicMarketStats?.suggested_median && atomicMarketStats.suggested_median !== '0' && !isNaN(parseFloat(atomicMarketStats.suggested_median))
                  ? `${parseFloat(atomicMarketStats.suggested_median).toFixed(2)} WAX`
                  : topSales && topSales.length > 0
                  ? (() => {
                      const validPrices = topSales.slice(-3).map(sale => {
                        const price = typeof sale.price === 'string' ? sale.price : (sale.price?.amount || '0')
                        return parseFloat(price || '0')
                      }).filter(price => !isNaN(price) && price > 0)
                      return validPrices.length > 0 ? `~${Math.min(...validPrices).toFixed(2)} WAX` : 'N/A'
                    })()
                  : 'N/A'
                }
              </div>
              <div className="stat-label">Floor Price</div>
            </div>
          </div>
        )}
      </div>
      
      {/* Quick Links Component */}
      <QuickLinks collectionName={collection.collection_name} />
        
      {/* Modern Pane Switcher */}
      <div className="collection-content-navigation">
        <div className="collection-nav-container">
          <div className="collection-nav-tabs">
             <button 
               className={`collection-nav-tab ${activeTab === 'sales' ? 'active' : ''}`}
               onClick={() => setActiveTab('sales')}
             >
               <div className="tab-content">
                 <span className="tab-icon">💰</span>
                 <span className="tab-text">Top Sales</span>
                 <span className="tab-count">{displayedSales.length}</span>
               </div>
             </button>
             <button 
               className={`collection-nav-tab ${activeTab === 'holders' ? 'active' : ''}`}
               onClick={() => setActiveTab('holders')}
             >
               <div className="tab-content">
                 <span className="tab-icon">👥</span>
                 <span className="tab-text">Holders</span>
                 <span className="tab-count">{holders.length}</span>
               </div>
             </button>
             <button 
               className={`collection-nav-tab ${activeTab === 'schemas' ? 'active' : ''}`}
               onClick={() => setActiveTab('schemas')}
             >
               <div className="tab-content">
                 <span className="tab-icon">📋</span>
                 <span className="tab-text">Schemas</span>
                 <span className="tab-count">{filteredSchemas.length}</span>
               </div>
             </button>
             <button 
               className={`collection-nav-tab ${activeTab === 'templates' ? 'active' : ''}`}
               onClick={() => setActiveTab('templates')}
             >
               <div className="tab-content">
                 <span className="tab-icon">🎨</span>
                 <span className="tab-text">Templates</span>
                 <span className="tab-count">{filteredTemplates.length}</span>
               </div>
             </button>
             <button 
               className={`collection-nav-tab ${activeTab === 'similar' ? 'active' : ''}`}
               onClick={() => setActiveTab('similar')}
             >
               <div className="tab-content">
                 <span className="tab-icon">🔗</span>
                 <span className="tab-text">Similar</span>
                 <span className="tab-count">{similarCollections.length}</span>
               </div>
             </button>
             <button 
               className={`collection-nav-tab ${activeTab === 'chat' ? 'active' : ''}`}
               onClick={() => setActiveTab('chat')}
             >
               <div className="tab-content">
                 <span className="tab-icon">💬</span>
                 <span className="tab-text">Chat</span>
                 <span className="tab-count">0</span>
               </div>
             </button>
           </div>
          <div className="nav-indicator"></div>
        </div>
      </div>

        {/* Tab Content */}
        <div className="tab-content-area">
          {activeTab === 'sales' && (
            <div className="sales-section">
              <div className="sales-grid">
                {displayedSales.length > 0 ? (
                  displayedSales.map((sale: TopSale, index: number) => (
                    <div key={sale.sale_id || index} className="sale-item">
                      <div className="sale-rank">#{index + 1}</div>
                      <div className="sale-media">
                        <MediaRenderer
                          imageUrl={getSaleAssetImageUrl(sale)}
                          videoUrl={getSaleAssetVideoUrl(sale)}
                          alt={String(sale.assets?.[0]?.data?.name || sale.assets?.[0]?.template?.immutable_data?.name || 'NFT')}
                          className="sale-image"
                          controls={true}
                          muted={true}
                          loop={true}
                          enableAnimations={settings.enableAnimatedNFTs}
                        />
                        <div 
                          className="zoom-overlay"
                          onClick={() => {
                            const imageUrl = getSaleAssetImageUrl(sale)
                            const name = String(sale.assets?.[0]?.data?.name || sale.assets?.[0]?.template?.immutable_data?.name || 'NFT')
                            if (imageUrl) {
                              setZoomedImage({url: imageUrl, name})
                            }
                            setShowImageZoom(true)
                          }}
                        >
                          <svg viewBox="0 0 24 24" fill="currentColor">
                            <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
                            <path d="M12 10h-2v2H9v-2H7V9h2V7h1v2h2v1z"/>
                          </svg>
                        </div>
                      </div>
                      <div className="sale-details">
                        <div className="sale-name">{sale.assets?.[0]?.data?.name || sale.assets?.[0]?.template?.immutable_data?.name || 'Unnamed NFT'}</div>
                        <div className="sale-template">Template #{sale.assets?.[0]?.template?.template_id}</div>
                        <div className="sale-price">
                          {sale.price ? (parseInt(sale.price.amount) / Math.pow(10, sale.price.token_precision)).toFixed(sale.price.token_precision) + ' ' + sale.price.token_symbol : 'N/A'}
                        </div>
                        <div className="sale-date">
                          {formatSaleDate(sale.updated_at_time || '')}
                        </div>
                      </div>
                    </div>
                  ))
                ) : (
                  <div className="no-data">No sales data available</div>
                )}
              </div>
              {hasMoreSales && (
                <div className="load-more-section">
                  <button 
                    className="load-more-btn" 
                    onClick={loadMoreSales}
                  >
                    Load More Sales
                  </button>
                </div>
              )}
            </div>
          )}

          {activeTab === 'holders' && (
            <div className="holders-section">
              <div className="holders-grid">
                {holders.length > 0 ? (
                  holders.map((holder: Holder, index: number) => (
                    <div key={index} className="holder-item">
                      <div className="holder-rank">#{index + 1}</div>
                      <Link to={`/profile-lookup/${holder.account}`} className="holder-account clickable">
                        {holder.account}
                      </Link>
                      <div className="holder-count">{holder.assets} assets</div>
                    </div>
                  ))
                ) : (
                  <div className="no-data">No holders data available</div>
                )}
              </div>
              {hasMoreHolders && (
                <div className="load-more-section">
                  <button 
                    className="load-more-btn" 
                    onClick={loadMoreHolders}
                    disabled={loadingMoreHolders}
                  >
                    {loadingMoreHolders ? 'Loading...' : 'Load More Holders'}
                  </button>
                </div>
              )}
            </div>
          )}

          {activeTab === 'similar' && (
            <div className="similar-collections">
              <div className="similar-collections-grid">
                {similarCollections.length > 0 ? (
                  similarCollections.slice(0, 12).map((similar: SimilarCollection, index: number) => (
                    <div key={index} className="similar-collection-item">
                      <Link to={`/collections/${similar.collection_name}`}>
                        <div className="similar-collection-name">{similar.collection_name}</div>
                        <div className="similar-collection-volume">Similar Collection</div>
                      </Link>
                    </div>
                  ))
                ) : (
                  <div className="no-data">No similar collections found</div>
                )}
              </div>
            </div>
          )}

          {activeTab === 'chat' && (
            <div className="chat-section">
              <div className="chat-container">
                <ChatWidget 
                  collectionName={collectionName}
                />
              </div>
            </div>
          )}

        {/* Search and Filter Section */}
        <div className="search-filter-section">
          <div className="search-controls">
            <div className="search-input-container">
              <svg className="search-icon" viewBox="0 0 24 24" fill="currentColor">
                <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
              </svg>
              <input
                type="text"
                placeholder={`Search ${activeTab}...`}
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="search-input"
              />
              {searchTerm && (
                <button 
                  onClick={() => setSearchTerm('')}
                  className="clear-search"
                  aria-label="Clear search"
                >
                  <svg viewBox="0 0 24 24" fill="currentColor">
                    <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/>
                  </svg>
                </button>
              )}
            </div>
            <div className="sort-container">
              <label htmlFor="sortBy">Sort by:</label>
              <select
                id="sortBy"
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value as 'name' | 'created' | 'supply')}
                className="sort-select"
              >
                <option value="name">Name</option>
                <option value="created">Created Date</option>
                {activeTab === 'templates' && <option value="supply">Supply</option>}
              </select>
            </div>
          </div>
          {searchTerm && (
            <div className="search-results-info">
              <p>Found {activeTab === 'schemas' ? filteredSchemas.length : filteredTemplates.length} results for "{searchTerm}"</p>
            </div>
          )}

          {activeTab === 'schemas' && (
            <div className="schemas-section">
              {filteredSchemas.length === 0 ? (
                <div className="empty-state">
                  {searchTerm ? (
                    <>
                      <svg className="empty-icon" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
                      </svg>
                      <h3>No schemas found</h3>
                      <p>No schemas match your search "{searchTerm}"</p>
                      <button onClick={() => setSearchTerm('')} className="clear-search-button">
                        Clear Search
                      </button>
                    </>
                  ) : (
                    <p>No schemas found for this collection.</p>
                  )}
                </div>
              ) : (
                <div className="schemas-grid">
                  {filteredSchemas.map((schema) => (
                    <div key={schema.schema_name} className="schema-card">
                      <div className="schema-header">
                        <h3>{schema.schema_name}</h3>
                        <button 
                          className="view-templates-btn"
                          onClick={() => {
                            setSelectedSchema(schema.schema_name)
                            setActiveTab('templates')
                          }}
                        >
                          View Templates
                        </button>
                      </div>
                      <div className="schema-format">
                        <h4>Attributes:</h4>
                        <div className="attributes-list">
                          {schema.format.map((attr, index) => (
                            <div key={index} className="attribute">
                              <span className="attr-name">{attr.name}</span>
                              <span className="attr-type">{attr.type}</span>
                            </div>
                          ))}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}

          {activeTab === 'templates' && (
            <div className="templates-section">
              {selectedSchema && (
                <div className="schema-filter">
                  <label>Filter by Schema:</label>
                  <select 
                    value={selectedSchema} 
                    onChange={(e) => setSelectedSchema(e.target.value)}
                  >
                    <option value="">All Schemas</option>
                    {schemas.map((schema) => (
                      <option key={schema.schema_name} value={schema.schema_name}>
                        {schema.schema_name}
                      </option>
                    ))}
                  </select>
                </div>
              )}
              
              {filteredTemplates.length === 0 ? (
                <div className="empty-state">
                  {searchTerm ? (
                    <>
                      <svg className="empty-icon" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
                      </svg>
                      <h3>No templates found</h3>
                      <p>No templates match your search "{searchTerm}"</p>
                      <button onClick={() => setSearchTerm('')} className="clear-search-button">
                        Clear Search
                      </button>
                    </>
                  ) : (
                    <p>No templates found{selectedSchema ? ` for schema "${selectedSchema}"` : ' for this collection'}.</p>
                  )}
                </div>
              ) : (
                <div className="templates-grid">
                  {filteredTemplates.map((template) => (
                    <Link 
                      key={template.template_id} 
                      to={`/collections/${collectionName}/templates/${template.template_id}`}
                      className="template-card"
                    >
                      {renderMediaElement(template, 'template-image')}
                      <div className="template-info">
                        <h4>{String(template.immutable_data?.name || `Template #${template.template_id}`)}</h4>
                        <p className="template-schema">Template ID: {template.template_id}</p>
                        <p className="template-supply">
                          Supply: {formatSupply(template.issued_supply, template.max_supply)}
                        </p>
                      </div>
                    </Link>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>
      </div>
      
      {/* Image Zoom Modal */}
      {showImageZoom && (
        <ImageZoom
          isOpen={showImageZoom}
          imageUrl={zoomedImage.url}
          imageName={zoomedImage.name}
          onClose={() => setShowImageZoom(false)}
        />
      )}
       
      {/* Share Modal */}
      {showShareModal && (
        <ShareModal
          isOpen={showShareModal}
          onClose={() => setShowShareModal(false)}
          title={collection?.name || collection?.collection_name || 'Collection'}
          description={String(collection?.data?.description || `Check out this amazing collection: ${collection?.name || collection?.collection_name}`)}
          imageUrl={getImageUrl(collection?.img || '')}
          shareUrl={`${window.location.origin}/collections/${collection?.collection_name}`}
          defaultShareText={`Check out the ${collection?.name || collection?.collection_name} collection on PxMarket! 🎮✨ Explore it at market.pixeljourney.xyz`}
          type="collection"
        />
      )}
    </div>
  )
})

CollectionDetail.displayName = 'CollectionDetail'

export default CollectionDetail