import React, { useState } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import { log } from '../services/loggingService'
import '../css/modal.css'

interface StakingModalProps {
  isOpen: boolean
  onClose: () => void
  resourceType: 'cpu' | 'net'
  action: 'stake' | 'unstake'
  onSuccess?: () => void
}

const StakingModal: React.FC<StakingModalProps> = ({ 
  isOpen, 
  onClose, 
  resourceType, 
  action, 
  onSuccess 
}) => {
  const [amount, setAmount] = useState('')
  const [isProcessing, setIsProcessing] = useState(false)
  const { session, transact } = useWallet()
  const { showError } = useNotifications()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!session || !amount || parseFloat(amount) <= 0) return

    setIsProcessing(true)
    try {
      const waxAmount = `${parseFloat(amount).toFixed(8)} WAX`
      
      if (action === 'stake') {
        // Stake CPU or NET using delegatebw
        const actions = [{
          account: 'eosio',
          name: 'delegatebw',
          authorization: [{
            actor: session.actor.toString(),
            permission: session.permission.toString()
          }],
          data: {
            from: session.actor.toString(),
            receiver: session.actor.toString(),
            stake_net_quantity: resourceType === 'net' ? waxAmount : '0.00000000 WAX',
            stake_cpu_quantity: resourceType === 'cpu' ? waxAmount : '0.00000000 WAX',
            transfer: false
          }
        }]
        
        await transact(actions)
      } else {
        // Unstake CPU or NET using undelegatebw
        const actions = [{
          account: 'eosio',
          name: 'undelegatebw',
          authorization: [{
            actor: session.actor.toString(),
            permission: session.permission.toString()
          }],
          data: {
            from: session.actor.toString(),
            receiver: session.actor.toString(),
            unstake_net_quantity: resourceType === 'net' ? waxAmount : '0.00000000 WAX',
            unstake_cpu_quantity: resourceType === 'cpu' ? waxAmount : '0.00000000 WAX'
          }
        }]
        
        await transact(actions)
      }
      
      onSuccess?.()
      onClose()
    } catch (error) {
      log.error(`Failed to ${action} ${resourceType.toUpperCase()}`, 'StakingModal.handleSubmit', error as Error, { action, resourceType, amount })
      showError(`Failed to ${action} ${resourceType.toUpperCase()}. Please try again.`, 'Transaction Failed')
    } finally {
      setIsProcessing(false)
    }
  }

  if (!isOpen) return null

  const resourceName = resourceType.toUpperCase()
  const actionName = action === 'stake' ? 'Stake' : 'Unstake'

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>{actionName} {resourceName}</h2>
          <button className="modal-close" onClick={onClose}>
            ×
          </button>
        </div>
        
        <div className="modal-body">
          <div className="resource-info">
            <div className="resource-icon">
              {resourceType === 'cpu' ? '⚡' : '🌐'}
            </div>
            <div className="resource-description">
              <h3>{resourceName} {actionName}</h3>
              <p>
                {action === 'stake' 
                  ? `Stake WAX to increase your ${resourceName} resources. Staked tokens will be locked for 3 days after unstaking.`
                  : `Unstake WAX from your ${resourceName} resources. Tokens will be available after a 3-day waiting period.`
                }
              </p>
            </div>
          </div>
          
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="amount">Amount (WAX)</label>
              <input
                type="number"
                id="amount"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                placeholder="Enter WAX amount"
                min="0.00000001"
                step="0.00000001"
                required
                disabled={isProcessing}
              />
            </div>
            
            <div className="form-actions">
              <button 
                type="button" 
                className="btn-secondary" 
                onClick={onClose}
                disabled={isProcessing}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn-primary"
                disabled={isProcessing || !amount || parseFloat(amount) <= 0}
              >
                {isProcessing ? 'Processing...' : `${actionName} ${resourceName}`}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default StakingModal