import React, { useState, useEffect } from 'react'
import { createPortal } from 'react-dom'
import { useNotifications } from '../hooks/useNotifications'
import { rwaxService, type FeeToken } from '../services/rwaxService'
import { log } from '../services/loggingService'
import { normalizeIPFSUrl } from '../utils/ipfsUtils'
import '../css/modal.css'

interface NFT {
  asset_id: string
  name: string
  img: string
  [key: string]: any
}

interface TokenizeFeeModalProps {
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
  nft: NFT | null
}

const TokenizeFeeModal: React.FC<TokenizeFeeModalProps> = ({
  isOpen,
  onClose,
  onSuccess,
  nft
}) => {
  const [feeTokens, setFeeTokens] = useState<FeeToken[]>([])
  const [selectedFeeToken, setSelectedFeeToken] = useState<FeeToken | null>(null)
  const [loading, setLoading] = useState(false)
  const { showError } = useNotifications()

  // Load fee tokens when modal opens
  useEffect(() => {
    if (isOpen) {
      loadFeeTokens()
    }
  }, [isOpen])

  const loadFeeTokens = async () => {
    try {
      setLoading(true)
      const tokens = await rwaxService.getFeeTokens()
      setFeeTokens(tokens)
      
      // Auto-select WAX if available
      const waxToken = tokens.find(token => token.symbol === 'WAX')
      if (waxToken) {
        setSelectedFeeToken(waxToken)
      }
    } catch (error) {
      log.error('Failed to load fee tokens', 'TokenizeFeeModal.loadFeeTokens', error as Error)
      showError('Failed to load fee tokens')
    } finally {
      setLoading(false)
    }
  }

  const handleConfirm = async () => {
    if (selectedFeeToken && nft) {
      try {
        setLoading(true)
        await rwaxService.executeTokenization([nft.asset_id], selectedFeeToken)
        onSuccess?.()
        handleClose()
      } catch (error) {
        log.error('Failed to tokenize asset', 'TokenizeFeeModal.handleConfirm', error as Error)
        showError('Failed to tokenize asset')
      } finally {
        setLoading(false)
      }
    }
  }

  const handleClose = () => {
    setSelectedFeeToken(null)
    onClose()
  }

  if (!isOpen) return null

  return createPortal(
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content tokenize-fee-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>🪙 Tokenize into WPIXAL</h2>
          <button className="close-button" onClick={handleClose}>×</button>
        </div>
        
        <div className="modal-body">
          {nft && (
            <div className="tokenize-asset-preview">
              <img 
                src={normalizeIPFSUrl(nft.img)} 
                alt={nft.name} 
                className="tokenize-asset-image"
                onError={(e) => {
                  const target = e.target as HTMLImageElement
                  target.src = 'https://pixeljourney.xyz/img/toppixals/10.webp'
                }}
              />
              <h3>{nft.name}</h3>
              <p className="tokenize-description">
                This will tokenize your Pixal PFP into WPIXAL tokens on the RWAX platform.
              </p>
            </div>
          )}
          
          {loading ? (
            <div className="loading-state">
              <div className="spinner"></div>
              <p>Loading fee options...</p>
            </div>
          ) : (
            <div className="fee-selection">
              <h4>Select Fee Token:</h4>
              <div className="fee-options">
                {feeTokens.map((token, index) => (
                  <div 
                    key={index}
                    className={`fee-option ${selectedFeeToken === token ? 'selected' : ''}`}
                    onClick={() => setSelectedFeeToken(token)}
                  >
                    <div className="fee-info">
                      <span className="fee-amount">{token.fee}</span>
                      <span className="fee-contract">({token.contract})</span>
                    </div>
                    {token.wax_value && token.symbol !== 'WAX' && (
                      <div className="wax-equivalent">
                        ≈ {parseFloat(token.wax_value).toFixed(4)} WAX
                      </div>
                    )}
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
        
        <div className="modal-footer">
          <button className="cancel-button" onClick={handleClose}>
            Cancel
          </button>
          <button 
            className="confirm-button tokenize-confirm"
            onClick={handleConfirm}
            disabled={!selectedFeeToken || loading}
          >
            Tokenize Asset
          </button>
        </div>
      </div>
    </div>,
    document.body
  )
}

export default TokenizeFeeModal