import React, { useState, useEffect, useMemo, useCallback } from 'react'
import { createPortal } from 'react-dom'
import { useWallet } from '../hooks/useWallet'
import { useDisplaySettings } from '../hooks/useDisplaySettings'
import { useNotifications } from '../hooks/useNotifications'

import { inventoryService } from '../services/inventoryService'
import type { InventoryItem, UserBalance, IngredientTemplate } from '../types/ingredients'
import type { InventoryNFT } from '../types/inventory'
import { 
  CRAFTING_RECIPES,
  getTemplateById
} from '../data/ingredients'
import type { CraftingRecipe } from '../types/ingredients'
import '../css/crafting-modal.css'

interface CraftingModalProps {
  isOpen: boolean
  onClose: () => void
  ingredient?: IngredientTemplate | InventoryNFT | null
}



interface CraftingQueue {
  recipe: CraftingRecipe
  quantity: number
}

const CraftingModal: React.FC<CraftingModalProps> = ({ isOpen, onClose, ingredient }) => {
  const { account, session, transferTokens, executeCraftingTransaction } = useWallet()
  useDisplaySettings()
  const { showError, showSuccess, showWarning } = useNotifications()
  
  const [activeTab, setActiveTab] = useState<'craft' | 'upgrade'>('craft')
  const [selectedRecipe, setSelectedRecipe] = useState<CraftingRecipe | null>(null)
  const [craftingQueue, setCraftingQueue] = useState<CraftingQueue[]>([])
  const [isProcessing, setIsProcessing] = useState(false)
  const [inventory, setInventory] = useState<InventoryItem[]>([])
  const [balances, setBalances] = useState<UserBalance>({ pxj: 0, wuf: 0 })
  const [loadingInventory, setLoadingInventory] = useState(false)
  const [craftQuantity, setCraftQuantity] = useState<number>(1)
  const [maxCraftable, setMaxCraftable] = useState<number>(0)
  const [sortBy, setSortBy] = useState<string>('name_asc')

  const loadInventoryData = useCallback(async () => {
    if (!account) {
      showError('Wallet Required', 'Please connect your wallet first')
      return
    }

    setLoadingInventory(true)
    try {
      const [inventoryData, balanceData] = await Promise.all([
        inventoryService.fetchInventory(account),
        inventoryService.fetchBalances(account)
      ])
      
      setInventory(inventoryData)
      setBalances(balanceData)
    } catch (error) {
      console.error('Failed to load inventory:', error)
      showError('Failed to load inventory data', error instanceof Error ? error.message : 'Unknown error')
    } finally {
      setLoadingInventory(false)
    }
  }, [account, showError])

  useEffect(() => {
    if (isOpen && account) {
      loadInventoryData()
    }
  }, [isOpen, account, loadInventoryData])

  const getInventoryQuantity = (templateId: number): number => {
     const item = inventory.find(inv => inv.templateId === templateId)
     return item ? item.quantity : 0
   }

  const calculateMaxCraftable = (recipe: CraftingRecipe): number => {
    if (!recipe) return 0
    
    let maxFromIngredients = Infinity
    
    // Check ingredient availability
    for (const ingredient of recipe.ingredients) {
      const available = getInventoryQuantity(ingredient.templateId)
      const needed = ingredient.quantity
      const possibleFromThisIngredient = Math.floor(available / needed)
      maxFromIngredients = Math.min(maxFromIngredients, possibleFromThisIngredient)
    }
    
    // Check PXJ availability
    const maxFromPxj = Math.floor(balances.pxj / recipe.pxjCost)
    
    return Math.min(maxFromIngredients, maxFromPxj)
  }

  useEffect(() => {
    if (selectedRecipe) {
      const max = calculateMaxCraftable(selectedRecipe)
      setMaxCraftable(max)
      setCraftQuantity(Math.min(craftQuantity, max))
    }
  }, [selectedRecipe, inventory, balances.pxj, calculateMaxCraftable, craftQuantity])

  // Sort inventory items
  const sortedInventory = useMemo(() => {
    const sorted = [...inventory]
    
    switch (sortBy) {
      case 'name_asc':
        return sorted.sort((a, b) => a.name.localeCompare(b.name))
      case 'name_desc':
        return sorted.sort((a, b) => b.name.localeCompare(a.name))
      case 'quantity_asc':
        return sorted.sort((a, b) => a.quantity - b.quantity)
      case 'quantity_desc':
        return sorted.sort((a, b) => b.quantity - a.quantity)
      case 'type_asc':
        return sorted.sort((a, b) => {
          const templateA = getTemplateById(a.templateId)
          const templateB = getTemplateById(b.templateId)
          const typeA = templateA?.type || 'Unknown'
          const typeB = templateB?.type || 'Unknown'
          return typeA.localeCompare(typeB)
        })
      case 'type_desc':
        return sorted.sort((a, b) => {
          const templateA = getTemplateById(a.templateId)
          const templateB = getTemplateById(b.templateId)
          const typeA = templateA?.type || 'Unknown'
          const typeB = templateB?.type || 'Unknown'
          return typeB.localeCompare(typeA)
        })
      default:
        return sorted
    }
  }, [inventory, sortBy])

  const canCraft = (recipe: CraftingRecipe, quantity: number = 1): boolean => {
    // Check ingredients
    for (const ingredient of recipe.ingredients) {
      const available = getInventoryQuantity(ingredient.templateId)
      const needed = ingredient.quantity * quantity
      if (available < needed) return false
    }
    
    // Check PXJ
    const pxjNeeded = recipe.pxjCost * quantity
    if (balances.pxj < pxjNeeded) return false
    
    return true
  }

  const addToQueue = () => {
    if (!selectedRecipe || craftQuantity <= 0) return
    
    if (!canCraft(selectedRecipe, craftQuantity)) {
      showWarning('Insufficient Resources', 'You don\'t have enough ingredients or PXJ for this craft.')
      return
    }

    const existingIndex = craftingQueue.findIndex(item => item.recipe.id === selectedRecipe.id)
    
    if (existingIndex >= 0) {
      const newQueue = [...craftingQueue]
      newQueue[existingIndex].quantity += craftQuantity
      setCraftingQueue(newQueue)
    } else {
      setCraftingQueue([...craftingQueue, { recipe: selectedRecipe, quantity: craftQuantity }])
    }
    
    showSuccess('Added to Queue', `Added ${craftQuantity}x ${selectedRecipe.name} to crafting queue`)
    setCraftQuantity(1)
  }

  const removeFromQueue = (recipeId: string) => {
    setCraftingQueue(craftingQueue.filter(item => item.recipe.id !== recipeId))
  }

  const clearQueue = () => {
    setCraftingQueue([])
  }

  const executeCraftingQueue = async () => {
    if (craftingQueue.length === 0) {
      showWarning('Empty Queue', 'No items in crafting queue')
      return
    }

    if (!account || !session) {
      showError('Wallet Required', 'Please connect your wallet to craft ingredients')
      return
    }

    // Prevent duplicate execution
    if (isProcessing) {
      return
    }

    setIsProcessing(true)
    let successCount = 0
    
    try {
      for (const queueItem of craftingQueue) {
        const { recipe, quantity } = queueItem
        
        // Get asset IDs for ingredients
        const ingredientAssets: { templateId: number; assetIds: string[] }[] = []
        let canProceed = true
        
        for (const ingredient of recipe.ingredients) {
          const neededQuantity = ingredient.quantity * quantity
          const assetIds = inventoryService.getIngredientAssetIds(ingredient.templateId, neededQuantity)
          
          if (assetIds.length < neededQuantity) {
            showError('Insufficient Ingredients', `Not enough ${ingredient.name} available`)
            canProceed = false
            break
          }
          
          ingredientAssets.push({
            templateId: ingredient.templateId,
            assetIds: assetIds
          })
        }
        
        if (!canProceed) continue
        
        // Prepare crafting transaction data

        // Execute blockchain transaction using crafting service
        try {
          if (!executeCraftingTransaction) {
            showError('Crafting Error', 'Crafting service not available')
            continue
          }
          
          // Prepare crafting data for the transaction
          const craftingData = {
            recipe: recipe.id,
            quantity: quantity,
            ingredientAssets: ingredientAssets,
            pxjCost: recipe.pxjCost
          }
          
          const result = await executeCraftingTransaction(craftingData)
          
          if (result) {
            successCount++
            
            // Update inventory optimistically
            const usedIngredients = recipe.ingredients.map(ing => ({
              templateId: ing.templateId,
              quantity: ing.quantity * quantity
            }))
            inventoryService.updateInventoryAfterCraft(usedIngredients, recipe.pxjCost * quantity)
            
            showSuccess('Craft Successful!', `Successfully crafted ${quantity}x ${recipe.name}`)
          } else {
            showError('Craft Failed', `Failed to craft ${recipe.name}`)
          }
        } catch (craftError) {
          console.error('Craft error:', craftError)
          showError('Craft Failed', craftError instanceof Error ? craftError.message : 'Unknown crafting error')
        }
      }
      
      if (successCount > 0) {
        showSuccess('Crafting Complete!', `Successfully crafted ${successCount} recipe(s)`)
        setCraftingQueue([])
        loadInventoryData() // Refresh inventory and balances from blockchain
        onClose()
      }
    } catch (error) {
      console.error('Crafting failed:', error)
      showError('Crafting Failed', error instanceof Error ? error.message : 'Unknown error occurred')
    } finally {
      setIsProcessing(false)
    }
  }

  const handleUpgrade = async () => {
    if (!ingredient || !account || !session) {
      showError('Missing Requirements', 'Ingredient and wallet connection required for upgrading')
      return
    }

    const templateId = 'template' in ingredient ? ingredient.template?.template_id : ingredient.id
    const template = getTemplateById(typeof templateId === 'string' ? parseInt(templateId) : templateId)
    if (!template || template.pxjUpgrade === 0) {
      showError('Cannot Upgrade', 'This ingredient cannot be upgraded')
      return
    }

    if (balances.pxj < template.pxjUpgrade) {
      showError('Insufficient PXJ', `You need ${template.pxjUpgrade.toLocaleString()} PXJ to upgrade this ingredient`)
      return
    }

    setIsProcessing(true)
    
    try {
      // Prepare upgrade transaction
      
      // Use centralized token transfer for upgrade
      if (!transferTokens) {
        showError('Upgrade Error', 'Transfer service not available')
        return
      }
      
      const result = await transferTokens(
        'upgradepool',
        template.pxjUpgrade.toString(),
        'PXJ',
        `Upgrade ${template.name}`
      )
      
      if (result) {
        showSuccess('Upgrade Complete!', `Successfully upgraded ${template.name}`)
        loadInventoryData()
        onClose()
      }
    } catch (error) {
      console.error('Upgrade failed:', error)
      showError('Upgrade Failed', error instanceof Error ? error.message : 'Unknown error occurred')
    } finally {
      setIsProcessing(false)
    }
  }

  if (!isOpen) return null

  return createPortal(
    <div className="modal-overlay" onClick={onClose}>
      <div className="crafting-modal" onClick={e => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Pixel Ingredient Crafting</h2>
          <button className="close-btn" onClick={onClose}>×</button>
        </div>
        
        <div className="modal-tabs">
          <button 
            className={`tab-btn ${activeTab === 'craft' ? 'active' : ''}`}
            onClick={() => setActiveTab('craft')}
          >
            Craft New
          </button>
          <button 
            className={`tab-btn ${activeTab === 'upgrade' ? 'active' : ''}`}
            onClick={() => setActiveTab('upgrade')}
            disabled={!ingredient}
          >
            Upgrade Existing
          </button>
        </div>

        <div className="modal-content">
          {activeTab === 'craft' ? (
            <div className="craft-tab">
              <div className="craft-layout">
                <div className="recipe-selection">
                  <h3>Select Recipe</h3>
                  <div className="recipe-categories">
                    <div className="recipe-category">
                      <h4>Secondary Ingredients</h4>
                      {CRAFTING_RECIPES.filter(r => r.tier === 'secondary').map(recipe => (
                        <div 
                          key={recipe.id}
                          className={`recipe-item ${selectedRecipe?.id === recipe.id ? 'selected' : ''} ${!canCraft(recipe) ? 'disabled' : ''}`}
                          onClick={() => setSelectedRecipe(recipe)}
                        >
                          <div className="recipe-name">{recipe.name}</div>
                          <div className="recipe-cost">{recipe.pxjCost.toLocaleString()} PXJ</div>
                          <div className="recipe-ingredients">
                            {recipe.ingredients.map((ing, idx) => (
                              <span key={idx} className={getInventoryQuantity(ing.templateId) >= ing.quantity ? 'available' : 'unavailable'}>
                                {ing.name} ({getInventoryQuantity(ing.templateId)}/{ing.quantity})
                              </span>
                            ))}
                          </div>
                        </div>
                      ))}
                    </div>
                    
                    <div className="recipe-category">
                      <h4>Tertiary Ingredients</h4>
                      {CRAFTING_RECIPES.filter(r => r.tier === 'tertiary').map(recipe => (
                        <div 
                          key={recipe.id}
                          className={`recipe-item ${selectedRecipe?.id === recipe.id ? 'selected' : ''} ${!canCraft(recipe) ? 'disabled' : ''}`}
                          onClick={() => setSelectedRecipe(recipe)}
                        >
                          <div className="recipe-name">{recipe.name}</div>
                          <div className="recipe-cost">{recipe.pxjCost.toLocaleString()} PXJ</div>
                          <div className="recipe-ingredients">
                            {recipe.ingredients.map((ing, idx) => (
                              <span key={idx} className={getInventoryQuantity(ing.templateId) >= ing.quantity ? 'available' : 'unavailable'}>
                                {ing.name} ({getInventoryQuantity(ing.templateId)}/{ing.quantity})
                              </span>
                            ))}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
                
                <div className="craft-controls">
                  {selectedRecipe && (
                    <div className="selected-recipe">
                      <h3>{selectedRecipe.name}</h3>
                      <p>{selectedRecipe.description}</p>
                      
                      <div className="quantity-controls">
                        <label>Quantity:</label>
                        <div className="quantity-input">
                          <button 
                            onClick={() => setCraftQuantity(Math.max(1, craftQuantity - 1))}
                            disabled={craftQuantity <= 1}
                          >
                            -
                          </button>
                          <input 
                            type="number" 
                            value={craftQuantity} 
                            onChange={(e) => setCraftQuantity(Math.min(maxCraftable, Math.max(1, parseInt(e.target.value) || 1)))}
                            min="1"
                            max={maxCraftable}
                          />
                          <button 
                            onClick={() => setCraftQuantity(Math.min(maxCraftable, craftQuantity + 1))}
                            disabled={craftQuantity >= maxCraftable}
                          >
                            +
                          </button>
                        </div>
                        <span className="max-craftable">Max: {maxCraftable}</span>
                      </div>
                      
                      <div className="total-cost">
                        <strong>Total Cost: {(selectedRecipe.pxjCost * craftQuantity).toLocaleString()} PXJ</strong>
                      </div>
                      
                      <button 
                        className="add-to-queue-btn"
                        onClick={addToQueue}
                        disabled={!canCraft(selectedRecipe, craftQuantity) || craftQuantity <= 0}
                      >
                        Add to Queue
                      </button>
                    </div>
                  )}
                  
                  <div className="inventory-section">
                    <div className="inventory-header">
                      <h3>Your Inventory</h3>
                      {!loadingInventory && inventory.length > 0 && (
                        <div className="sort-container">
                          <label htmlFor="inventory-sort">Sort by:</label>
                          <select 
                            id="inventory-sort"
                            className="sort-select"
                            value={sortBy}
                            onChange={(e) => setSortBy(e.target.value)}
                          >
                            <option value="name_asc">Name (A-Z)</option>
                            <option value="name_desc">Name (Z-A)</option>
                            <option value="quantity_asc">Quantity (Low-High)</option>
                            <option value="quantity_desc">Quantity (High-Low)</option>
                            <option value="type_asc">Type (A-Z)</option>
                            <option value="type_desc">Type (Z-A)</option>
                          </select>
                        </div>
                      )}
                    </div>
                    {loadingInventory ? (
                      <div className="loading-inventory">
                        <div className="loading-spinner"></div>
                        <span>Loading inventory...</span>
                      </div>
                    ) : (
                      <div className="inventory-grid">
                        {sortedInventory.length === 0 ? (
                          <div className="no-inventory">
                            <div className="empty-state">
                              <span>No ingredients found</span>
                              <button 
                                className="refresh-btn"
                                onClick={loadInventoryData}
                                disabled={loadingInventory}
                              >
                                Refresh Inventory
                              </button>
                            </div>
                          </div>
                        ) : (
                          sortedInventory.map(item => {
                            const template = getTemplateById(item.templateId)
                            return (
                              <div key={item.templateId} className="inventory-item">
                                <div className="item-name">{item.name}</div>
                                <div className="item-quantity">Qty: {item.quantity}</div>
                                <div className="item-tier">{template?.type || 'Unknown'}</div>
                              </div>
                            )
                          })
                        )}
                      </div>
                    )}
                  </div>
                </div>
              </div>
              
              <div className="crafting-queue">
                <div className="queue-header">
                  <h3>Crafting Queue ({craftingQueue.length})</h3>
                  {craftingQueue.length > 0 && (
                    <button className="clear-queue-btn" onClick={clearQueue}>Clear All</button>
                  )}
                </div>
                
                {craftingQueue.length > 0 ? (
                  <div className="queue-items">
                    {craftingQueue.map((item, index) => (
                      <div key={index} className="queue-item">
                        <div className="queue-item-info">
                          <span className="item-name">{item.recipe.name}</span>
                          <span className="item-quantity">x{item.quantity}</span>
                          <span className="item-cost">{(item.recipe.pxjCost * item.quantity).toLocaleString()} PXJ</span>
                        </div>
                        <button 
                          className="remove-item-btn"
                          onClick={() => removeFromQueue(item.recipe.id)}
                        >
                          Remove
                        </button>
                      </div>
                    ))}
                    
                    <div className="queue-total">
                      <strong>
                        Total: {craftingQueue.reduce((sum, item) => sum + (item.recipe.pxjCost * item.quantity), 0).toLocaleString()} PXJ
                      </strong>
                    </div>
                    
                    <button 
                      className="execute-queue-btn"
                      onClick={executeCraftingQueue}
                      disabled={isProcessing}
                    >
                      {isProcessing ? 'Crafting...' : 'Craft All'}
                    </button>
                  </div>
                ) : (
                  <div className="empty-queue">
                    <p>No items in queue. Select a recipe and add it to start crafting.</p>
                  </div>
                )}
              </div>
            </div>
          ) : (
            <div className="upgrade-tab">
              {ingredient ? (
                <div className="upgrade-content">
                  <div className="ingredient-info">
                    <h3>Upgrade: {ingredient.name}</h3>
                    <p>Template ID: {'template' in ingredient ? ingredient.template?.template_id : ingredient.id}</p>
                    
                    {(() => {
                      const templateId = 'template' in ingredient ? ingredient.template?.template_id : ingredient.id
                      const template = getTemplateById(typeof templateId === 'string' ? parseInt(templateId) : templateId)
                      if (!template) return <p>Template not found</p>
                      
                      if (template.pxjUpgrade === 0) {
                        return <p className="no-upgrade">This ingredient cannot be upgraded.</p>
                      }
                      
                      return (
                        <div className="upgrade-details">
                          <div className="upgrade-cost">
                            <strong>Upgrade Cost: {template.pxjUpgrade.toLocaleString()} PXJ</strong>
                          </div>
                          <div className="balance-info">
                            Your PXJ Balance: {balances.pxj.toLocaleString()}
                          </div>
                          
                          <button 
                            className="upgrade-btn"
                            onClick={handleUpgrade}
                            disabled={isProcessing || balances.pxj < template.pxjUpgrade}
                          >
                            {isProcessing ? 'Upgrading...' : 'Upgrade Ingredient'}
                          </button>
                        </div>
                      )
                    })()}
                  </div>
                </div>
              ) : (
                <div className="no-ingredient">
                  <p>No ingredient selected for upgrade. Please select an ingredient from your inventory.</p>
                </div>
              )}
            </div>
          )}
        </div>
        
        <div className="modal-footer">
          <div className="resource-display">
            <div className="pxj-balance">
              <strong>PXJ Balance: {balances.pxj.toLocaleString()}</strong>
            </div>
          </div>
        </div>
      </div>
    </div>,
    document.body
  )
}

export default CraftingModal