// Collection tier configuration
// This maps collection names to their tier status and associated benefits
//
// INSTRUCTIONS FOR COLLECTION OWNERS:
// ===================================
//
// To customize your collection page on PxMarket, follow these steps:
//
// 1. CONTACT US FIRST:
//    - Reach out to the PxMarket team via Discord or email
//    - Provide your collection name and desired tier level
//    - Discuss available customization options based on your tier
//
// 2. TIER LEVELS & BENEFITS:
//    - Bronze (5M PXJ): Basic verification badge, community access
//    - Silver (15M PXJ): Priority placement, custom banner, analytics
//    - Gold (30M PXJ): Homepage carousel, social media promotion
//    - Partner: Co-marketing opportunities, revenue sharing
//    - Platinum: Full customization, premium placement
//
// 3. CUSTOMIZATION OPTIONS:
//    - ownerMessage: Personal message from collection owner (all tiers)
//    - quickLinks: Custom links to your website, Discord, social media (Silver+)
//    - learnAboutGrid: Educational content grid about your project (Gold+)
//    - discordUrl: Direct Discord community link (all tiers)
//
// 4. EXAMPLE CONFIGURATION:
//    'yourcollection': {
//      tier: 'silver',
//      verified: true,
//      quickLinks: [
//        { label: 'Website', url: 'https://yoursite.com', icon: '🌐' },
//        { label: 'Discord', url: 'https://discord.gg/yourserver', icon: '💬' }
//      ],
//      benefits: ['Priority placement', 'Custom banner', 'Analytics access'],
//      ownerMessage: 'Welcome to our collection! Join our community...',
//      discordUrl: 'https://discord.gg/yourserver'
//    }
//
// 5. LEARN ABOUT GRID (Gold+ tiers):
//    Add educational content about your project:
//    learnAboutGrid: [
//      {
//        title: 'Your Feature',
//        description: 'Explain what makes your project unique...',
//        icon: '🎮', // Use emoji icons
//        url: 'https://docs.yourproject.com' // Optional link
//      }
//    ]
//
// 6. SUBMISSION PROCESS:
//    - Prepare your content following the format above
//    - Submit via our collection owner portal or Discord
//    - Our team will review and implement your customizations
//    - Changes typically go live within 24-48 hours
//
// 7. CONTENT GUIDELINES:
//    - Keep messages professional and community-friendly
//    - Ensure all URLs are valid and secure (HTTPS)
//    - Icons should be single emoji characters
//    - Descriptions should be concise but informative
//    - All content subject to moderation review
//
// For questions or to get started, contact us:
// - Discord: https://discord.gg/pxmarket
// - Email: collections@pxmarket.io
// - Documentation: https://docs.pxmarket.io/collection-customization

export interface CollectionTier {
  tier: 'platinum' | 'partner' | 'gold' | 'silver' | 'bronze'
  verified: boolean
  quickLinks?: {
    label: string
    url: string
    icon?: string
  }[]
  benefits: string[]
  ownerMessage?: string
  discordUrl?: string
  learnAboutGrid?: {
    title: string
    description: string
    icon: string
    url?: string
  }[]
}

