/**
 * Get tier information for a collection
 */
export function getCollectionTier(collectionName: string): CollectionTier | null {
  return COLLECTION_TIERS[collectionName] || null
}

/**
 * Check if a collection has a specific tier or higher
 */
export function hasMinimumTier(collectionName: string, minimumTier: CollectionTier['tier']): boolean {
  const tierHierarchy: Record<CollectionTier['tier'], number> = {
    'bronze': 1,
    'silver': 2,
    'gold': 3,
    'partner': 4,
    'platinum': 5
  }
  
  const collectionTier = getCollectionTier(collectionName)
  if (!collectionTier) return false
  
  return tierHierarchy[collectionTier.tier] >= tierHierarchy[minimumTier]
}

