import React, { useState, useRef, useEffect } from 'react'
import { API_ENDPOINTS } from '../config/marketplaceConfig'

interface ProgressiveImageProps {
  imageUrl?: string
  videoUrl?: string | null
  alt: string
  className?: string
  loading?: 'lazy' | 'eager'
  enableAnimations?: boolean
  placeholder?: string
  onLoad?: () => void
  onError?: () => void
}

const ProgressiveImage: React.FC<ProgressiveImageProps> = React.memo(({
  imageUrl,
  videoUrl,
  alt,
  className = '',
  loading = 'lazy',
  enableAnimations = true,
  placeholder,
  onLoad,
  onError
}) => {
  const [isLoaded, setIsLoaded] = useState(false)
  const [isError, setIsError] = useState(false)
  const [isInView, setIsInView] = useState(loading === 'eager')
  const imgRef = useRef<HTMLImageElement>(null)
  const videoRef = useRef<HTMLVideoElement>(null)

  // Helper function to process URLs (handle IPFS, etc.)
  const processUrl = (url: string | undefined | null): string | null => {
    if (!url) return null
    if (typeof url !== 'string') return null
    if (url.startsWith('http')) return url
    if (url.startsWith('Qm')) return `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${url}`
    return url
  }

  const processedVideoUrl = processUrl(videoUrl)
  const processedImageUrl = processUrl(imageUrl)

  // Create a loading placeholder
  const getLoadingPlaceholder = () => {
    if (placeholder) return placeholder
    
    const svg = `<svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
      <rect width="200" height="200" fill="#f8fafc"/>
      <rect x="50" y="80" width="100" height="8" rx="4" fill="#e2e8f0">
        <animate attributeName="opacity" values="0.5;1;0.5" dur="1.5s" repeatCount="indefinite"/>
      </rect>
      <rect x="60" y="100" width="80" height="6" rx="3" fill="#e2e8f0">
        <animate attributeName="opacity" values="0.5;1;0.5" dur="1.5s" begin="0.3s" repeatCount="indefinite"/>
      </rect>
      <rect x="70" y="120" width="60" height="6" rx="3" fill="#e2e8f0">
        <animate attributeName="opacity" values="0.5;1;0.5" dur="1.5s" begin="0.6s" repeatCount="indefinite"/>
      </rect>
    </svg>`
    return `data:image/svg+xml;base64,${btoa(svg)}`
  }

  // Create an error placeholder
  const getErrorPlaceholder = () => {
    const svg = `<svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
      <rect width="200" height="200" fill="#fef2f2"/>
      <circle cx="100" cy="80" r="20" fill="#fca5a5"/>
      <text x="100" y="85" text-anchor="middle" font-family="Arial" font-size="16" fill="#dc2626">!</text>
      <text x="100" y="120" text-anchor="middle" font-family="Arial" font-size="12" fill="#991b1b">Failed to load</text>
    </svg>`
    return `data:image/svg+xml;base64,${btoa(svg)}`
  }

  // Intersection Observer for lazy loading
  useEffect(() => {
    if (loading === 'eager' || isInView) return

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsInView(true)
          observer.disconnect()
        }
      },
      {
        rootMargin: '50px' // Start loading 50px before the image comes into view
      }
    )

    const currentRef = imgRef.current || videoRef.current
    if (currentRef) {
      observer.observe(currentRef)
    }

    return () => observer.disconnect()
  }, [loading, isInView])

  const handleLoad = () => {
    setIsLoaded(true)
    setIsError(false)
    onLoad?.()
  }

  const handleError = () => {
    setIsError(true)
    setIsLoaded(false)
    onError?.()
  }

  // Show loading placeholder if not in view or not loaded
  if (!isInView || (!isLoaded && !isError)) {
    return (
      <div className={`progressive-image-container ${className}`}>
        <img
          ref={imgRef}
          src={getLoadingPlaceholder()}
          alt={`Loading ${alt}`}
          className="progressive-image-placeholder"
          style={{
            width: '100%',
            height: '100%',
            objectFit: 'cover',
            filter: 'blur(2px)',
            transition: 'filter 0.3s ease'
          }}
        />
        {isInView && processedImageUrl && (
          <img
            src={processedImageUrl}
            alt={alt}
            style={{ display: 'none' }}
            onLoad={handleLoad}
            onError={handleError}
          />
        )}
        {isInView && processedVideoUrl && enableAnimations && (
          <video
            ref={videoRef}
            src={processedVideoUrl}
            style={{ display: 'none' }}
            onLoadedData={handleLoad}
            onError={handleError}
            muted
            loop
          />
        )}
      </div>
    )
  }

  // Show error placeholder if loading failed
  if (isError) {
    return (
      <img
        src={getErrorPlaceholder()}
        alt={`Error loading ${alt}`}
        className={className}
        style={{
          width: '100%',
          height: '100%',
          objectFit: 'cover'
        }}
      />
    )
  }

  // Show video if available and animations enabled
  if (processedVideoUrl && enableAnimations && isLoaded) {
    return (
      <video
        ref={videoRef}
        className={className}
        muted
        loop
        autoPlay
        poster={processedImageUrl || undefined}
        style={{
          width: '100%',
          height: '100%',
          objectFit: 'cover',
          opacity: isLoaded ? 1 : 0,
          transition: 'opacity 0.3s ease'
        }}
      >
        <source src={processedVideoUrl} type="video/mp4" />
        <source src={processedVideoUrl} type="video/webm" />
      </video>
    )
  }

  // Show image
  return (
    <img
      ref={imgRef}
      src={processedImageUrl || getErrorPlaceholder()}
      alt={alt}
      className={className}
      style={{
        width: '100%',
        height: '100%',
        objectFit: 'cover',
        opacity: isLoaded ? 1 : 0,
        transition: 'opacity 0.3s ease'
      }}
    />
  )
})

ProgressiveImage.displayName = 'ProgressiveImage'

export default ProgressiveImage