import React from 'react';
import { usePWA } from '../hooks/usePWA';
import '../css/pwa-install-banner.css';

interface PWAInstallBannerProps {
  className?: string;
}

const PWAInstallBanner: React.FC<PWAInstallBannerProps> = ({ className = '' }) => {
  const { isInstallable, isInstalled, isStandalone, showInstallPrompt, dismissInstallPrompt } = usePWA();

  // Don't show banner if app is already installed or not installable
  if (!isInstallable || isInstalled || isStandalone) {
    return null;
  }

  return (
    <div className={`pwa-install-banner ${className}`} role="banner" aria-label="Install app banner">
      <div className="pwa-banner-content">
        <div className="pwa-banner-icon">
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" aria-hidden="true">
            <rect x="3" y="3" width="18" height="18" rx="2" fill="currentColor" fillOpacity="0.1" stroke="currentColor" strokeWidth="1.5"/>
            <path d="M8 12l2 2 4-4" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </div>
        
        <div className="pwa-banner-text">
          <h3 className="pwa-banner-title">Install PxMarket</h3>
          <p className="pwa-banner-description">
            Get the full app experience with offline access and faster loading.
          </p>
        </div>
        
        <div className="pwa-banner-actions">
          <button
            type="button"
            className="pwa-install-button"
            onClick={showInstallPrompt}
            aria-label="Install PxMarket app"
          >
            <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" aria-hidden="true">
              <path d="M8 1v10M4 7l4 4 4-4" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
            Install
          </button>
          
          <button
            type="button"
            className="pwa-dismiss-button"
            onClick={dismissInstallPrompt}
            aria-label="Dismiss install banner"
          >
            <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" aria-hidden="true">
              <path d="M12 4L4 12M4 4l8 8" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
};

export default PWAInstallBanner;