// Comprehensive monitoring and analytics service
import { log } from './loggingService'

interface PerformanceMetric {
  name: string
  value: number
  timestamp: number
  context?: Record<string, unknown>
}

interface UserEvent {
  event: string
  properties: Record<string, unknown>
  timestamp: number
  userId?: string
  sessionId: string
}

interface ErrorReport {
  error: Error
  context: string
  additionalInfo?: Record<string, unknown>
  timestamp: number
  userId?: string
  sessionId: string
  userAgent: string
  url: string
  stackTrace?: string
}

interface APIMetrics {
  endpoint: string
  method: string
  duration: number
  status: number
  timestamp: number
  success: boolean
  retryCount?: number
}

interface UserSession {
  sessionId: string
  startTime: number
  lastActivity: number
  pageViews: number
  events: UserEvent[]
  userId?: string
}

class MonitoringService {
  private sessionId: string
  private userId?: string
  private session: UserSession
  private performanceMetrics: PerformanceMetric[] = []
  private apiMetrics: APIMetrics[] = []
  private errorReports: ErrorReport[] = []
  private isEnabled: boolean = true
  private batchSize: number = 50
  private flushInterval: number = 30000 // 30 seconds
  private maxStoredMetrics: number = 1000
  private pendingBatches: Array<{
    type: 'performance' | 'api' | 'events' | 'errors'
    data: (PerformanceMetric | APIMetrics | UserEvent | ErrorReport)[]
    timestamp: number
  }> = []
  private batchTimeout: ReturnType<typeof setTimeout> | null = null

  constructor() {
    this.sessionId = this.generateSessionId()
    this.session = {
      sessionId: this.sessionId,
      startTime: Date.now(),
      lastActivity: Date.now(),
      pageViews: 0,
      events: []
    }

    this.initializeMonitoring()
    this.startPerformanceObserver()
    this.setupErrorHandlers()
    this.startFlushTimer()
  }

  private generateSessionId(): string {
    return `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
  }

  private initializeMonitoring(): void {
    // Track page load performance
    if (typeof window !== 'undefined' && window.performance) {
      window.addEventListener('load', () => {
        setTimeout(() => {
          this.trackPageLoadPerformance()
        }, 0)
      })

      // Track page visibility changes
      document.addEventListener('visibilitychange', () => {
        this.trackEvent('page_visibility_change', {
          hidden: document.hidden
        })
      })

      // Track user interactions
      this.setupUserInteractionTracking()
    }
  }

  private startPerformanceObserver(): void {
    if (typeof window !== 'undefined' && 'PerformanceObserver' in window) {
      try {
        // Observe navigation timing
        const navObserver = new PerformanceObserver((list) => {
          for (const entry of list.getEntries()) {
            if (entry.entryType === 'navigation') {
              this.trackNavigationTiming(entry as PerformanceNavigationTiming)
            }
          }
        })
        navObserver.observe({ entryTypes: ['navigation'] })

        // Observe resource timing
        const resourceObserver = new PerformanceObserver((list) => {
          for (const entry of list.getEntries()) {
            if (entry.entryType === 'resource') {
              this.trackResourceTiming(entry as PerformanceResourceTiming)
            }
          }
        })
        resourceObserver.observe({ entryTypes: ['resource'] })

        // Observe largest contentful paint
        const lcpObserver = new PerformanceObserver((list) => {
          for (const entry of list.getEntries()) {
            this.trackPerformanceMetric('largest_contentful_paint', entry.startTime)
          }
        })
        lcpObserver.observe({ entryTypes: ['largest-contentful-paint'] })

        // Observe first input delay
        const fidObserver = new PerformanceObserver((list) => {
          for (const entry of list.getEntries()) {
            this.trackPerformanceMetric('first_input_delay', (entry as PerformanceEventTiming).processingStart - entry.startTime)
          }
        })
        fidObserver.observe({ entryTypes: ['first-input'] })
      } catch (error) {
        log.warn('Performance Observer not fully supported', 'MonitoringService.startPerformanceObserver', { error })
      }
    }
  }

  private setupErrorHandlers(): void {
    if (typeof window !== 'undefined') {
      // Global error handler
      window.addEventListener('error', (event) => {
        this.reportError(event.error || new Error(event.message), 'global_error', {
          filename: event.filename,
          lineno: event.lineno,
          colno: event.colno
        })
      })

      // Unhandled promise rejection handler
      window.addEventListener('unhandledrejection', (event) => {
        this.reportError(
          event.reason instanceof Error ? event.reason : new Error(String(event.reason)),
          'unhandled_promise_rejection'
        )
      })
    }
  }

  private setupUserInteractionTracking(): void {
    if (typeof window !== 'undefined') {
      // Track clicks on important elements
      document.addEventListener('click', (event) => {
        const target = event.target as HTMLElement
        if (target.matches('button, a, [role="button"], .clickable')) {
          this.trackEvent('user_click', {
            element: target.tagName.toLowerCase(),
            className: target.className,
            id: target.id,
            text: target.textContent?.slice(0, 50)
          })
        }
      })

      // Track form submissions
      document.addEventListener('submit', (event) => {
        const form = event.target as HTMLFormElement
        this.trackEvent('form_submit', {
          formId: form.id,
          formClass: form.className,
          action: form.action
        })
      })
    }
  }

  private startFlushTimer(): void {
    setInterval(() => {
      this.flushMetrics()
    }, this.flushInterval)
  }

  // Public API methods
  setUserId(userId: string): void {
    this.userId = userId
    this.session.userId = userId
  }

  trackEvent(event: string, properties: Record<string, unknown> = {}): void {
    if (!this.isEnabled) return

    const userEvent: UserEvent = {
      event,
      properties,
      timestamp: Date.now(),
      userId: this.userId,
      sessionId: this.sessionId
    }

    this.session.events.push(userEvent)
    this.session.lastActivity = Date.now()
    
    // Add to batch for processing
    this.addToBatch('events', userEvent)

    // Limit stored events to prevent memory issues
    if (this.session.events.length > this.maxStoredMetrics) {
      this.session.events = this.session.events.slice(-this.maxStoredMetrics / 2)
    }
  }

  trackPerformanceMetric(name: string, value: number, context?: Record<string, unknown>): void {
    if (!this.isEnabled) return

    const metric: PerformanceMetric = {
      name,
      value,
      timestamp: Date.now(),
      context
    }

    this.performanceMetrics.push(metric)
    
    // Add to batch for processing
    this.addToBatch('performance', metric)

    // Limit stored metrics
    if (this.performanceMetrics.length > this.maxStoredMetrics) {
      this.performanceMetrics = this.performanceMetrics.slice(-this.maxStoredMetrics / 2)
    }
  }

  trackAPICall(endpoint: string, method: string, duration: number, status: number, success: boolean, retryCount?: number): void {
    if (!this.isEnabled) return

    const apiMetric: APIMetrics = {
      endpoint,
      method,
      duration,
      status,
      timestamp: Date.now(),
      success,
      retryCount
    }

    this.apiMetrics.push(apiMetric)
    
    // Add to batch for processing
    this.addToBatch('api', apiMetric)

    // Track slow API calls as performance metrics
    if (duration > 5000) { // 5 seconds
      this.trackPerformanceMetric('slow_api_call', duration, {
        endpoint,
        method,
        status
      })
    }

    // Limit stored metrics
    if (this.apiMetrics.length > this.maxStoredMetrics) {
      this.apiMetrics = this.apiMetrics.slice(-this.maxStoredMetrics / 2)
    }
  }

  reportError(error: Error, context: string, additionalInfo?: Record<string, unknown>): void {
    if (!this.isEnabled) return

    // Use centralized logging service
    log.error(`${context}: ${error.message}`, 'monitoringService.reportError', error, { ...additionalInfo })

    const errorReport: ErrorReport = {
      error,
      context,
      additionalInfo,
      timestamp: Date.now(),
      userId: this.userId,
      sessionId: this.sessionId,
      userAgent: navigator.userAgent,
      url: window.location.href,
      stackTrace: error.stack
    }

    this.errorReports.push(errorReport)

    // Track error as event
    this.trackEvent('error_occurred', {
      context,
      errorName: error.name,
      errorMessage: error.message,
      ...additionalInfo
    })

    // Limit stored errors
    if (this.errorReports.length > this.maxStoredMetrics) {
      this.errorReports = this.errorReports.slice(-this.maxStoredMetrics / 2)
    }
  }

  // Performance tracking methods
  private trackPageLoadPerformance(): void {
    if (!window.performance || !window.performance.timing) return

    const timing = window.performance.timing
    const navigation = window.performance.navigation

    // Calculate key metrics
    const pageLoadTime = timing.loadEventEnd - timing.navigationStart
    const domContentLoadedTime = timing.domContentLoadedEventEnd - timing.navigationStart
    const firstByteTime = timing.responseStart - timing.navigationStart
    const domInteractiveTime = timing.domInteractive - timing.navigationStart

    this.trackPerformanceMetric('page_load_time', pageLoadTime)
    this.trackPerformanceMetric('dom_content_loaded_time', domContentLoadedTime)
    this.trackPerformanceMetric('first_byte_time', firstByteTime)
    this.trackPerformanceMetric('dom_interactive_time', domInteractiveTime)

    this.trackEvent('page_load', {
      pageLoadTime,
      domContentLoadedTime,
      firstByteTime,
      domInteractiveTime,
      navigationType: navigation.type,
      redirectCount: navigation.redirectCount
    })
  }

  private trackNavigationTiming(entry: PerformanceNavigationTiming): void {
    this.trackPerformanceMetric('dns_lookup_time', entry.domainLookupEnd - entry.domainLookupStart)
    this.trackPerformanceMetric('tcp_connect_time', entry.connectEnd - entry.connectStart)
    this.trackPerformanceMetric('request_response_time', entry.responseEnd - entry.requestStart)
    this.trackPerformanceMetric('dom_processing_time', entry.domComplete - entry.domContentLoadedEventStart)
  }

  private trackResourceTiming(entry: PerformanceResourceTiming): void {
    // Only track important resources to avoid noise
    if (entry.name.includes('.js') || entry.name.includes('.css') || entry.name.includes('api')) {
      this.trackPerformanceMetric('resource_load_time', entry.duration, {
        resource: entry.name,
        type: entry.initiatorType,
        size: entry.transferSize
      })
    }
  }

  // Analytics and reporting methods
  getSessionSummary(): UserSession {
    return {
      ...this.session,
      events: [...this.session.events]
    }
  }

  getPerformanceMetrics(): PerformanceMetric[] {
    return [...this.performanceMetrics]
  }

  getAPIMetrics(): APIMetrics[] {
    return [...this.apiMetrics]
  }

  getErrorReports(): ErrorReport[] {
    return [...this.errorReports]
  }

  // Data export and external service integration
  private async flushMetrics(): Promise<void> {
    if (!this.isEnabled) return

    const data = {
      session: this.getSessionSummary(),
      performance: this.getPerformanceMetrics(),
      api: this.getAPIMetrics(),
      errors: this.getErrorReports(),
      timestamp: Date.now()
    }

    // In a real application, send to analytics service
    // await this.sendToAnalyticsService(data)
    
    // For now, store in localStorage for debugging
    try {
      const existingData = JSON.parse(localStorage.getItem('monitoring_data') || '[]')
      existingData.push(data)
      
      // Keep only last 10 flushes to prevent storage overflow
      const recentData = existingData.slice(-10)
      localStorage.setItem('monitoring_data', JSON.stringify(recentData))
    } catch (error) {
      log.warn('Failed to store monitoring data', 'MonitoringService.flushMetrics', { error })
    }

    // Clear metrics after flushing
    this.performanceMetrics = []
    this.apiMetrics = []
    this.errorReports = []
    this.session.events = []
  }

  // Configuration methods
  enable(): void {
    this.isEnabled = true
  }

  disable(): void {
    this.isEnabled = false
  }

  isMonitoringEnabled(): boolean {
    return this.isEnabled
  }

  setBatchSize(size: number): void {
    this.batchSize = Math.max(10, Math.min(size, 200)) // Between 10 and 200
  }
  
  /**
   * Add data to batch for processing
   */
  private addToBatch(type: 'performance' | 'api' | 'events' | 'errors', data: PerformanceMetric | APIMetrics | UserEvent | ErrorReport): void {
    if (!this.isEnabled) return
    
    let batch = this.pendingBatches.find(b => b.type === type)
    
    if (!batch) {
      batch = {
        type,
        data: [],
        timestamp: Date.now()
      }
      this.pendingBatches.push(batch)
    }
    
    batch.data.push(data)
    
    // Process batch if it reaches the batch size
    if (batch.data.length >= this.batchSize) {
      this.processBatch(batch)
      this.pendingBatches = this.pendingBatches.filter(b => b !== batch)
    }
    
    // Set timeout to process remaining batches
    this.scheduleBatchProcessing()
  }
  
  /**
   * Schedule batch processing
   */
  private scheduleBatchProcessing(): void {
    if (this.batchTimeout) {
      clearTimeout(this.batchTimeout)
    }
    
    this.batchTimeout = setTimeout(() => {
      this.processAllPendingBatches()
    }, 5000) // Process remaining batches after 5 seconds
  }
  
  /**
   * Process a single batch
   */
  private async processBatch(batch: { type: string; data: (PerformanceMetric | APIMetrics | UserEvent | ErrorReport)[]; timestamp: number }): Promise<void> {
    try {
      // In a real implementation, send to analytics service
      // await this.sendBatchToAnalyticsService(batch)
      
      // For now, store in localStorage with batch info
      const batchData = {
        ...batch,
        batchId: this.generateBatchId(),
        processedAt: Date.now()
      }
      
      const existingBatches = JSON.parse(localStorage.getItem('monitoring_batches') || '[]')
      existingBatches.push(batchData)
      
      // Keep only last 20 batches to prevent storage overflow
      const recentBatches = existingBatches.slice(-20)
      localStorage.setItem('monitoring_batches', JSON.stringify(recentBatches))
    } catch (error) {
      console.warn('Failed to process batch:', error)
    }
  }
  
  /**
   * Process all pending batches
   */
  private async processAllPendingBatches(): Promise<void> {
    const batches = [...this.pendingBatches]
    this.pendingBatches = []
    
    for (const batch of batches) {
      await this.processBatch(batch)
    }
    
    if (this.batchTimeout) {
      clearTimeout(this.batchTimeout)
      this.batchTimeout = null
    }
  }
  
  /**
   * Generate unique batch ID
   */
  private generateBatchId(): string {
    return `batch_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
  }

  setFlushInterval(interval: number): void {
    this.flushInterval = Math.max(5000, Math.min(interval, 300000)) // 5s to 5min
  }

  // Utility methods for external service integration
  async exportData(): Promise<string> {
    const data = {
      session: this.getSessionSummary(),
      performance: this.getPerformanceMetrics(),
      api: this.getAPIMetrics(),
      errors: this.getErrorReports()
    }
    return JSON.stringify(data, null, 2)
  }

  clearAllData(): void {
    this.performanceMetrics = []
    this.apiMetrics = []
    this.errorReports = []
    this.session.events = []
    localStorage.removeItem('monitoring_data')
  }
}

// Export singleton instance
export const monitoringService = new MonitoringService()

// Export types for external use
export type {
  PerformanceMetric,
  UserEvent,
  ErrorReport,
  APIMetrics,
  UserSession
}