import React, { useState, useEffect, useCallback } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { useWallet } from '../contexts/WalletContext'
import { useNotifications } from '../hooks/useNotifications'
import { apiService } from '../services/api'
import { auctionService } from '../services/auctionService'
import MediaRenderer from './MediaRenderer'
import { API_ENDPOINTS } from '../config/marketplaceConfig'
import type { Auction } from '../types'
import '../css/auction-detail.css'

const AuctionDetail: React.FC = () => {
  const { id } = useParams<{ id: string }>()
  const navigate = useNavigate()
  const { isConnected, account, session } = useWallet()
  const { showSuccess, showError } = useNotifications()
  
  const [auction, setAuction] = useState<Auction | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [showBidModal, setShowBidModal] = useState(false)
  const [bidAmount, setBidAmount] = useState('')
  const [isPlacingBid, setIsPlacingBid] = useState(false)

  const loadAuction = useCallback(async () => {
    if (!id) return
    
    setLoading(true)
    setError(null)
    
    try {
      const response = await apiService.getAuction(id)
      
      if (!response.success || !response.data) {
        setError('Auction not found')
        return
      }
      
      setAuction(response.data as unknown as Auction)
    } catch (err) {
      console.error('Error loading auction:', err)
      setError('Failed to load auction details')
    } finally {
      setLoading(false)
    }
  }, [id])

  useEffect(() => {
    if (id) {
      loadAuction()
    }
  }, [id, loadAuction])

  // Real-time updates for this specific auction
  useEffect(() => {
    if (!auction) return

    const unsubscribe = auctionService.subscribe(auction.auction_id, (update) => {
      setAuction(prevAuction => {
        if (!prevAuction) return null
        return {
          ...prevAuction,
          current_bid: update.current_bid || prevAuction.current_bid,
          bidder: update.bidder || prevAuction.bidder,
          end_time: update.end_time
        }
      })
    })

    return () => unsubscribe()
  }, [auction])

  const handlePlaceBid = () => {
    if (!isConnected || !account) {
      showError('Wallet Required', 'Please connect your wallet to place bids')
      return
    }
    
    if (auction?.seller === account) {
      showError('Invalid Action', 'You cannot bid on your own auction')
      return
    }
    
    setBidAmount('')
    setShowBidModal(true)
  }

  const submitBid = async () => {
    if (!auction || !session || !account) {
      showError('Error', 'Please connect your wallet first')
      return
    }
    
    const bidFloat = parseFloat(bidAmount)
    if (isNaN(bidFloat) || bidFloat <= 0) {
      showError('Invalid Bid', 'Please enter a valid bid amount')
      return
    }
    
    const currentBidAmount = parseFloat(auction.current_bid || auction.price) / 100000000
    const hasExistingBids = auction.current_bid && auction.bidder
    
    if (hasExistingBids && bidFloat <= currentBidAmount) {
      showError('Invalid Bid', `Bid must be higher than current bid of ${currentBidAmount.toFixed(4)} WAX`)
      return
    }
    
    if (!hasExistingBids && bidFloat < parseFloat(auction.price) / 100000000) {
      showError('Invalid Bid', `Bid must be at least ${(parseFloat(auction.price) / 100000000).toFixed(4)} WAX`)
      return
    }
    
    setIsPlacingBid(true)
    
    try {
      const bidAmountInt = Math.floor(bidFloat * 100000000)
      
      const result = await session.transact({
        actions: [{
          account: 'atomicmarket',
          name: 'auctionbid',
          authorization: [{
            actor: account,
            permission: 'active'
          }],
          data: {
            bidder: account,
            auction_id: auction.auction_id,
            bid: `${bidAmountInt} WAX`,
            taker_marketplace: 'market.pxj'
          }
        }]
      })
      
      if (result) {
        showSuccess('Bid Placed', `Successfully placed bid of ${bidFloat.toFixed(4)} WAX`)
        setShowBidModal(false)
        setBidAmount('')
        
        // Update auction state
        setAuction(prev => prev ? {
          ...prev,
          current_bid: bidAmountInt.toString(),
          bidder: account
        } : null)
      }
    } catch (error: unknown) {
      console.error('Error placing bid:', error)
      showError('Bid Failed', error instanceof Error ? error.message : 'Failed to place bid')
    } finally {
      setIsPlacingBid(false)
    }
  }

  const formatPrice = (price: string) => {
    const waxPrice = parseFloat(price) / 100000000
    return `${waxPrice.toFixed(4)} WAX`
  }

  // Use the robust auctionService formatTimeRemaining function
  const formatTimeRemaining = (endTime: string) => {
    return auctionService.formatTimeRemaining(endTime)
  }

  const handleUserClick = (userAccount: string) => {
    navigate(`/user-profile-lookup?user=${encodeURIComponent(userAccount)}`)
  }

  if (loading) {
    return (
      <div className="auction-detail-page">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading auction details...</p>
        </div>
      </div>
    )
  }

  if (error || !auction) {
    return (
      <div className="auction-detail-page">
        <div className="error-container">
          <h2>Auction Not Found</h2>
          <p>{error || 'The requested auction could not be found.'}</p>
          <button 
            className="btn-primary"
            onClick={() => navigate('/auctions')}
          >
            Back to Auctions
          </button>
        </div>
      </div>
    )
  }

  const timeRemaining = formatTimeRemaining(auction.end_time)
  const isEnded = timeRemaining === 'Auction ended'
  const currentPrice = auction.current_bid ? formatPrice(auction.current_bid) : formatPrice(auction.price)

  return (
    <div className="auction-detail-page">
      <div className="auction-detail-container">
        {/* Back Navigation */}
        <button 
          className="back-button"
          onClick={() => navigate('/auctions')}
        >
          ← Back to Auctions
        </button>

        <div className="auction-detail-content">
          {/* NFT Image and Info */}
          <div className="auction-image-section">
            <div className="auction-image-container">
              <MediaRenderer
                imageUrl={auction.image ? `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${auction.image}` : undefined}
                videoUrl={auction.video ? `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${auction.video}` : undefined}
                alt={auction.asset_name || 'NFT'}
                className="auction-image"
                enableAnimations={true}
              />
            </div>
            
            <div className="auction-basic-info">
              <h1 className="auction-title">
                {auction.asset_name || `Asset #${auction.asset_id || 'Unknown'}`}
              </h1>
              
              <div className="auction-collection">
                Collection: {auction.collection_name || 'Unknown'}
              </div>
              
              <div className="auction-id">
                Auction ID: {auction.auction_id}
              </div>
            </div>
          </div>

          {/* Auction Details */}
          <div className="auction-info-section">
            <div className="auction-status">
              <div className={`status-badge ${isEnded ? 'ended' : 'active'}`}>
                {isEnded ? 'Ended' : 'Active'}
              </div>
              
              {!isEnded && (
                <div className="time-remaining">
                  <span className="time-label">Time Remaining:</span>
                  <span className="time-value">{timeRemaining}</span>
                </div>
              )}
            </div>

            <div className="price-section">
              <div className="current-price">
                <span className="price-label">
                  {auction.current_bid ? 'Current Bid:' : 'Starting Price:'}
                </span>
                <span className="price-value">{currentPrice}</span>
              </div>
            </div>

            <div className="participants-section">
              <div className="seller-info">
                <span className="participant-label">Seller:</span>
                <button 
                  className="participant-link"
                  onClick={() => handleUserClick(auction.seller)}
                >
                  {auction.seller}
                </button>
              </div>
              
              {auction.bidder && (
                <div className="bidder-info">
                  <span className="participant-label">Top Bidder:</span>
                  <button 
                    className="participant-link"
                    onClick={() => handleUserClick(auction.bidder || '')}
                  >
                    {auction.bidder}
                  </button>
                </div>
              )}
            </div>

            {/* Action Buttons */}
            <div className="auction-actions">
              {!isEnded && (
                <button
                  className="btn-primary bid-button"
                  onClick={handlePlaceBid}
                  disabled={!isConnected || auction.seller === account}
                >
                  {!isConnected ? 'Connect Wallet' : 
                   auction.seller === account ? 'Your Auction' : 'Place Bid'}
                </button>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Bid Modal */}
      {showBidModal && (
        <div className="modal-overlay" onClick={() => setShowBidModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>Place Bid</h3>
              <button 
                className="modal-close"
                onClick={() => setShowBidModal(false)}
              >
                ×
              </button>
            </div>
            
            <div className="modal-body">
              <div className="auction-summary">
                <MediaRenderer
                  imageUrl={auction.image ? `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${auction.image}` : undefined}
                  videoUrl={auction.video ? `${API_ENDPOINTS.IPFS.GATEWAYS[0]}${auction.video}` : undefined}
                  alt={auction.asset_name || 'NFT'}
                  className="modal-nft-image"
                  enableAnimations={true}
                />
                <div className="modal-nft-info">
                  <h4>{auction.asset_name || `Asset #${auction.asset_id || 'Unknown'}`}</h4>
                  <p>Current: {currentPrice}</p>
                </div>
              </div>
              
              {auction.bidder && (
                <div className="current-bidder">
                  <span className="label">Leading Bidder</span>
                  <button 
                    className="bidder-account"
                    onClick={() => handleUserClick(auction.bidder!)}
                  >
                    {auction.bidder}
                  </button>
                </div>
              )}
              
              <div className="bid-input-section">
                <label htmlFor="bidAmount">Your Bid (WAX)</label>
                <input
                  id="bidAmount"
                  type="number"
                  step="0.0001"
                  placeholder="Enter bid amount"
                  value={bidAmount}
                  onChange={(e) => setBidAmount(e.target.value)}
                  min={auction.current_bid && auction.bidder ? 
                    parseFloat(auction.current_bid) / 100000000 + 0.0001 : 
                    parseFloat(auction.price) / 100000000
                  }
                />
                <div className="bid-requirement">
                  Minimum: {auction.current_bid && auction.bidder ?
                    `${(parseFloat(auction.current_bid) / 100000000 + 0.0001).toFixed(4)} WAX` :
                    `${(parseFloat(auction.price) / 100000000).toFixed(4)} WAX`
                  }
                </div>
              </div>
            </div>
            
            <div className="modal-footer">
              <button 
                className="btn-secondary"
                onClick={() => setShowBidModal(false)}
                disabled={isPlacingBid}
              >
                Cancel
              </button>
              <button 
                className="btn-primary"
                onClick={submitBid}
                disabled={isPlacingBid || !bidAmount}
              >
                {isPlacingBid ? 'Placing Bid...' : 'Place Bid'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

export default AuctionDetail