import React, { useState, useMemo } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import type { InventoryNFT } from '../types/inventory'
import { log } from '../services/loggingService'

import '../css/modal.css'

interface BulkTransferModalProps {
  nfts: InventoryNFT[]
  isOpen: boolean
  onClose: () => void
  onSuccess?: () => void
}

const BulkTransferModal: React.FC<BulkTransferModalProps> = ({ nfts, isOpen, onClose, onSuccess }) => {
  const [recipient, setRecipient] = useState('')
  const [memo, setMemo] = useState('')
  const [isTransferring, setIsTransferring] = useState(false)
  const [transferProgress, setTransferProgress] = useState(0)
  const [sortBy, setSortBy] = useState('name_asc')
  const { session, bulkTransferNFT } = useWallet()
  const { showError, showSuccess } = useNotifications()

  // Sort NFTs for display
  const sortedNfts = useMemo(() => {
    const sorted = [...nfts]
    
    switch (sortBy) {
      case 'name_asc':
        return sorted.sort((a, b) => (a.name || '').localeCompare(b.name || ''))
      case 'name_desc':
        return sorted.sort((a, b) => (b.name || '').localeCompare(a.name || ''))
      case 'collection_asc':
        return sorted.sort((a, b) => a.collection.collection_name.localeCompare(b.collection.collection_name))
      case 'collection_desc':
        return sorted.sort((a, b) => b.collection.collection_name.localeCompare(a.collection.collection_name))
      case 'mint_asc':
        return sorted.sort((a, b) => (a.mint_number || 0) - (b.mint_number || 0))
      case 'mint_desc':
        return sorted.sort((a, b) => (b.mint_number || 0) - (a.mint_number || 0))
      default:
        return sorted
    }
  }, [nfts, sortBy])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!session || !recipient.trim() || nfts.length === 0) {
      showError('Please fill in all required fields', 'Validation Error')
      return
    }

    // Validate recipient account name
    const recipientTrimmed = recipient.trim().toLowerCase()
    if (recipientTrimmed.length < 1 || recipientTrimmed.length > 12) {
      showError('Account name must be between 1 and 12 characters', 'Validation Error')
      return
    }

    if (!/^[a-z1-5.]+$/.test(recipientTrimmed)) {
      showError('Account name can only contain lowercase letters, numbers 1-5, and dots', 'Validation Error')
      return
    }

    setIsTransferring(true)
    setTransferProgress(0)

    try {
      // Use bulk transfer service for efficient batch processing
      if (!bulkTransferNFT) {
        log.error('bulkTransferNFT service not available', 'BulkTransferModal')
        throw new Error('Bulk transfer service not available')
      }
      
      const assetIds = nfts.map(nft => nft.asset_id)
      await bulkTransferNFT(
        recipientTrimmed,
        assetIds,
        memo || 'Bulk transfer from PxMarket'
      )
      
      setTransferProgress(100)
      showSuccess(`Successfully transferred ${nfts.length} NFT(s) to ${recipientTrimmed}!`, 'Transfer Complete')
      onSuccess?.()
      onClose()
      setRecipient('')
      setMemo('')
      
    } catch (error) {
      log.error('Bulk transfer failed', 'BulkTransferModal.handleSubmit', error as Error, { nftCount: nfts.length, recipient })
      showError(`Transfer failed: ${error instanceof Error ? error.message : 'Unknown error'}`, 'Transfer Failed')
    } finally {
      setIsTransferring(false)
      setTransferProgress(0)
    }
  }

  if (!isOpen) return null

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content bulk-transfer-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>Bulk Transfer NFTs</h2>
          <button className="modal-close" onClick={onClose}>
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <line x1="18" y1="6" x2="6" y2="18"></line>
              <line x1="6" y1="6" x2="18" y2="18"></line>
            </svg>
          </button>
        </div>

        <div className="modal-body">
          <div className="transfer-summary">
            <h3>Transfer Summary</h3>
            <p>You are about to transfer <strong>{nfts.length} NFT{nfts.length !== 1 ? 's' : ''}</strong></p>
            
            {/* Collection Breakdown */}
            <div className="collection-breakdown">
              <h4>Collection Breakdown</h4>
              {Object.entries(
                nfts.reduce((acc, nft) => {
                  const collectionName = nft.collection.collection_name
                  acc[collectionName] = (acc[collectionName] || 0) + 1
                  return acc
                }, {} as Record<string, number>)
              ).map(([collection, count]: [string, number]) => (
                <div key={collection} className="collection-item">
                  <span className="collection-name">{collection}</span>
                  <span className="collection-count">{count} NFT{count !== 1 ? 's' : ''}</span>
                </div>
              ))}
            </div>

            {/* Rarity Breakdown */}
            <div className="rarity-breakdown">
              <h4>Rarity Distribution</h4>
              {Object.entries(
                nfts.reduce((acc, nft) => {
                  const rarity = (nft.template?.immutable_data as {rarity?: string})?.rarity || nft.data?.rarity || 'Unknown'
                  acc[rarity.toString()] = (acc[rarity.toString()] || 0) + 1
                  return acc
                }, {} as Record<string, number>)
              ).map(([rarity, count]: [string, number]) => (
                <div key={rarity} className="rarity-item">
                  <span className="rarity-name">{rarity}</span>
                  <span className="rarity-count">{count}</span>
                </div>
              ))}
            </div>
            
            <div className="nft-preview-section">
              <div className="preview-header">
                <h4>NFT Preview</h4>
                {nfts.length > 1 && (
                  <div className="sort-container">
                    <label htmlFor="transfer-sort">Sort by:</label>
                    <select 
                      id="transfer-sort"
                      className="sort-select"
                      value={sortBy}
                      onChange={(e) => setSortBy(e.target.value)}
                    >
                      <option value="name_asc">Name (A-Z)</option>
                      <option value="name_desc">Name (Z-A)</option>
                      <option value="collection_asc">Collection (A-Z)</option>
                      <option value="collection_desc">Collection (Z-A)</option>
                      <option value="mint_asc">Mint # (Low-High)</option>
                      <option value="mint_desc">Mint # (High-Low)</option>
                    </select>
                  </div>
                )}
              </div>
              <div className="nft-preview-list">
                {sortedNfts.slice(0, 5).map((nft) => (
                <div key={nft.asset_id} className="nft-preview-item">
                  <img 
                    src={nft.img?.startsWith('http') ? nft.img : `https://ipfs.io/ipfs/${nft.img}`}
                    alt={nft.name}
                    onError={(e) => {
                      const target = e.target as HTMLImageElement
                      target.src = 'https://pixeljourney.xyz/img/toppixals/10.webp'
                    }}
                  />
                  <div className="nft-info-bulk">
                    <div className="nft-details">
                      <span className="nft-name">{nft.name}</span>
                      <span className="nft-collection">{typeof nft.collection === 'string' ? nft.collection : nft.collection?.collection_name || nft.collection?.name || 'Unknown'}</span>
                      <span className="nft-mint">#{nft.mint_number || 'N/A'}</span>
                      <span className="nft-id">ID: {nft.asset_id}</span>
                    </div>
                  </div>
                </div>
              ))}
              {nfts.length > 5 && (
                <div className="nft-preview-more">
                  +{nfts.length - 5} more NFTs
                </div>
              )}
              </div>
            </div>
          </div>

          <form onSubmit={handleSubmit} className="transfer-form">
            <div className="form-group">
              <label htmlFor="recipient">Recipient Account *</label>
              <input
                type="text"
                id="recipient"
                value={recipient}
                onChange={(e) => setRecipient(e.target.value)}
                placeholder="Enter WAX account name"
                required
                disabled={isTransferring}
                maxLength={12}
              />
              <small className="form-help">
                Enter a valid WAX account name (1-12 characters, lowercase letters, numbers 1-5, dots)
              </small>
            </div>

            <div className="form-group">
              <label htmlFor="memo">Memo (Optional)</label>
              <input
                type="text"
                id="memo"
                value={memo}
                onChange={(e) => setMemo(e.target.value)}
                placeholder="Optional transfer memo"
                disabled={isTransferring}
                maxLength={256}
              />
            </div>

            {isTransferring && (
              <div className="transfer-progress">
                <div className="progress-bar">
                  <div 
                    className="progress-fill" 
                    style={{ width: `${transferProgress}%` }}
                  ></div>
                </div>
                <p>Transferring... {Math.round(transferProgress)}% complete</p>
              </div>
            )}

            <div className="modal-actions">
              <button 
                type="button" 
                className="btn-secondary" 
                onClick={onClose}
                disabled={isTransferring}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn-primary"
                disabled={isTransferring || !recipient.trim()}
              >
                {isTransferring ? 'Transferring...' : `Transfer ${nfts.length} NFT${nfts.length !== 1 ? 's' : ''}`}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default BulkTransferModal