import React, { useState, useEffect, useCallback } from 'react'
import { useParams, Link } from 'react-router-dom'
import { apiService, type TemplateData, type AssetDetails } from '../services/api'
import MediaRenderer from './MediaRenderer'
import { getAssetImageUrl, getAssetVideoUrl, getTemplateImageUrl, getTemplateVideoUrl } from '../utils/mediaUtils'
import { useDisplaySettings } from '../hooks/useDisplaySettings'
import ShareModal from './ShareModal'
import '../css/template-detail.css'



const TemplateDetail: React.FC = () => {
  const { collectionName, templateId } = useParams<{ collectionName: string; templateId: string }>()
  const { settings } = useDisplaySettings()
  const [template, setTemplate] = useState<TemplateData | null>(null)
  const [collection, setCollection] = useState<{
    collection_name: string
    name: string
    img?: string
    author: string
    allow_notify: boolean
    authorized_accounts: string[]
    notify_accounts: string[]
    market_fee: number
    created_at_block: string
    created_at_time: string
  } | null>(null)
  const [schema, setSchema] = useState<{
    schema_name: string
    format: Array<{
      name: string
      type: string
    }>
  } | null>(null)
  const [assets, setAssets] = useState<AssetDetails[]>([])
  const [loading, setLoading] = useState(false)
  const [assetsLoading, setAssetsLoading] = useState(false)
  const [currentPage, setCurrentPage] = useState(1)
  const [hasMore, setHasMore] = useState(true)
  const [showShareModal, setShowShareModal] = useState(false)
  const assetsPerPage = 20

  const loadTemplateData = useCallback(async () => {
    if (!collectionName || !templateId) return
    
    setLoading(true)
    try {
      // Get template, collection, and schema data
      const [templates, collectionData, schemasData] = await Promise.all([
        apiService.getCollectionTemplates(collectionName),
        apiService.getCollectionDetails(collectionName),
        apiService.getCollectionSchemas(collectionName)
      ])
      
      const templateData = templates.find(t => t.template_id === templateId)
      setTemplate(templateData || null)
      setCollection(collectionData)
      
      // Find the schema for this template by checking assets
      if (templateData) {
        const assets = await apiService.getCollectionAssets(collectionName, undefined, templateId, 1)
        if (assets.length > 0) {
          const schemaName = assets[0].schema_name
          const schemaData = schemasData.find(s => s.schema_name === schemaName)
          setSchema(schemaData || null)
        }
      }
    } catch (error) {
      console.error('Error loading template data:', error)
    } finally {
      setLoading(false)
    }
  }, [collectionName, templateId, setLoading, setTemplate, setCollection, setSchema])

  const loadAssets = useCallback(async (page: number) => {
    if (!collectionName || !templateId) return
    
    setAssetsLoading(true)
    try {
      const assetsData = await apiService.getCollectionAssets(
        collectionName, 
        undefined, // schemaName
        templateId, 
        assetsPerPage
      )
      
      if (page === 1) {
        setAssets(assetsData)
      } else {
        setAssets(prev => [...prev, ...assetsData])
      }
      
      setHasMore(assetsData.length === assetsPerPage)
      setCurrentPage(page)
    } catch (error) {
      console.error('Error loading assets:', error)
    } finally {
      setAssetsLoading(false)
    }
  }, [collectionName, templateId, setAssetsLoading, setAssets, setHasMore, setCurrentPage])

  useEffect(() => {
    if (collectionName && templateId) {
      loadTemplateData()
      loadAssets(1)
    }
  }, [collectionName, templateId, loadTemplateData, loadAssets])

  const loadMoreAssets = () => {
    if (!assetsLoading && hasMore) {
      loadAssets(currentPage + 1)
    }
  }





  const formatDate = (timestamp: string): string => {
    return new Date(parseInt(timestamp) * 1000).toLocaleDateString()
  }

  const renderAttributeValue = (value: string | number | boolean | null | undefined | object): string => {
    if (typeof value === 'object' && value !== null) {
      return JSON.stringify(value)
    }
    return String(value)
  }

  if (loading) {
    return (
      <div className="template-detail-container">
        <div className="loading-state">
          <div className="spinner"></div>
          <p>Loading template details...</p>
        </div>
      </div>
    )
  }

  if (!template) {
    return (
      <div className="template-detail-container">
        <div className="error-state">
          <h2>Template Not Found</h2>
          <p>The template "{templateId}" could not be found in collection "{collectionName}".</p>
          <Link to={`/collections/${collectionName}`} className="back-link">
            ← Back to Collection
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="template-detail-container">
      {/* Navigation */}
      <div className="breadcrumb">
        <Link to="/collections" className="breadcrumb-link">Collections</Link>
        <span className="breadcrumb-separator">›</span>
        <Link to={`/collections/${collectionName}`} className="breadcrumb-link">
          {collection?.name || collectionName}
        </Link>
        <span className="breadcrumb-separator">›</span>
        <span className="breadcrumb-current">{`Template #${templateId}`}</span>
      </div>

      <div className="template-content">
        {/* Template Header */}
      <div className="template-header">
        <div className="template-image-container">
          {template && (
            <MediaRenderer
              imageUrl={getTemplateImageUrl(template)}
              videoUrl={getTemplateVideoUrl(template)}
              alt="Template"
              className="template-image"
              enableAnimations={settings.enableAnimatedNFTs}
            />
          )}
        </div>
        <div className="template-info">
          <h1>{`Template #${templateId}`}</h1>
          <p className="template-id">Template ID: {template.template_id}</p>
          <p className="template-schema">Schema: {schema?.schema_name || 'Loading...'}</p>
          <p className="template-collection">
            Collection: {collection?.name || collection?.collection_name || 'Loading...'}
          </p>
          
          <div className="template-stats">
            <div className="stat">
              <span className="stat-value">{template.issued_supply}</span>
              <span className="stat-label">Minted</span>
            </div>
            <div className="stat">
              <span className="stat-value">
                {template.max_supply === '0' ? '∞' : template.max_supply}
              </span>
              <span className="stat-label">Max Supply</span>
            </div>
            <div className="stat">
              <span className="stat-value">{assets.length}</span>
              <span className="stat-label">Assets Loaded</span>
            </div>
          </div>
          
          <p className="template-created">
            Created: {formatDate(template.created_at_time)}
          </p>
          
          <button 
            className="share-btn"
            onClick={() => setShowShareModal(true)}
            style={{
              marginTop: '20px',
              padding: '12px 24px',
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              border: 'none',
              borderRadius: '12px',
              color: 'white',
              fontWeight: '600',
              cursor: 'pointer',
              transition: 'all 0.3s ease',
              display: 'flex',
              alignItems: 'center',
              gap: '8px'
            }}
          >
            <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
            </svg>
            Share Template
          </button>
        </div>
      </div>

      {/* Template Attributes */}
      {template.immutable_data && Object.keys(template.immutable_data).length > 0 && (
        <div className="template-attributes">
          <h2>Template Attributes</h2>
          <div className="attributes-grid">
            {Object.entries(template.immutable_data).map(([key, value]) => (
              <div key={key} className="attribute-item">
                <span className="attribute-name">{key}</span>
                <span className="attribute-value">{renderAttributeValue(value as string | number | boolean | object | null | undefined)}</span>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Schema Format */}
      {schema && schema.format && (
        <div className="schema-format">
          <h2>Schema Format</h2>
          <div className="format-grid">
            {schema.format.map((field: { name: string; type: string }, index: number) => (
              <div key={index} className="format-item">
                <span className="format-name">{String(field.name)}</span>
                <span className="format-type">{String(field.type)}</span>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Assets Section */}
      <div className="assets-section">
        <h2>Assets ({template.issued_supply} total)</h2>
        
        {assets.length === 0 && !assetsLoading ? (
          <div className="empty-state">
            <p>No assets found for this template.</p>
          </div>
        ) : (
          <>
            <div className="assets-grid">
              {assets.map((asset) => (
                <Link 
                  key={asset.asset_id} 
                  to={`/assets/${asset.asset_id}`}
                  className="asset-card"
                >
                  <MediaRenderer
                    imageUrl={getAssetImageUrl(asset)}
                    videoUrl={getAssetVideoUrl(asset)}
                    alt={asset.name || `Asset #${asset.asset_id}`}
                    className="asset-image"
                    enableAnimations={settings.enableAnimatedNFTs}
                  />
                  <div className="asset-info">
                    <h4>{asset.name || `Asset #${asset.asset_id}`}</h4>
                    <p className="asset-id">#{asset.asset_id}</p>
                    <p className="asset-owner">Owner: {asset.owner}</p>
                    <p className="asset-minted">Minted: {formatDate(asset.minted_at_time)}</p>
                  </div>
                </Link>
              ))}
            </div>
            
            {hasMore && (
              <div className="load-more-container">
                <button 
                  className="load-more-btn"
                  onClick={loadMoreAssets}
                  disabled={assetsLoading}
                >
                  {assetsLoading ? 'Loading...' : 'Load More Assets'}
                </button>
              </div>
            )}
          </>
        )}
      </div>
      </div>
      
      {/* Share Modal */}
      {template && (
        <ShareModal
          isOpen={showShareModal}
          onClose={() => setShowShareModal(false)}
          title={`Template #${templateId}`}
          description={`${collection?.name || collection?.collection_name} collection`}
          imageUrl={getTemplateImageUrl(template)}
          shareUrl={`${window.location.origin}/collections/${collectionName}/templates/${templateId}`}
          defaultShareText={`Check out this amazing template: Template #${templateId} from ${collection?.name || collection?.collection_name} collection! 🎨✨ View it at ${window.location.origin}/collections/${collectionName}/templates/${templateId}`}
          type="template"
        />
      )}
    </div>
  )
}

export default TemplateDetail