/*
=== API SOURCES DOCUMENTATION ===

1. AtomicAssets API - NFT data and marketplace
   - Endpoints: Multiple fallback endpoints for redundancy
   - Docs: https://github.com/pinknetworkx/eosio-contract-api-docs oor swagger: https://wax-atomic.alcor.exchange/docs/#/
   - Usage: Collections, NFTs, sales, transfers, market data

2. AtomicMarket API - NFT marketplace transactions
   - Endpoints: Same as AtomicAssets (integrated)
   - Docs: https://github.com/pinknetworkx/eosio-contract-api-docs
   - Usage: Sales data, market statistics, pricing

3. HiveBP API - Enhanced WAX blockchain data
   - Endpoint: https://wax-api.hivebp.io
   - Docs: Custom HiveBP endpoints  https://hivebp.github.io/wax-api
   - Usage: Similar collections, enhanced analytics

4. WAX Chain API - Direct blockchain queries
   - Endpoints: Multiple WAX RPC endpoints
   - Docs: https://developers.eos.io/manuals/eos/latest/nodeos/plugins/chain_api_plugin/api-reference/index
  -  full ocs: https://docs.wax.io/apis/chain-api.htm
   - Usage: Account info, token balances, chain data

5. WAX Light API - Fast token balance queries
   - Endpoint: https://wax.light-api.net
   - Docs: https://github.com/cc32d9/eosio_light_api/blob/master/README.md
   - Usage: Quick token balance lookups, account data

6. Alcor Exchange API - Token prices and DEX data
   - Endpoint: https://api.alcor.exchange
   - Docs: https://api.alcor.exchange/#introduction
   - Usage: Token prices, market data, bulk sell functionality

*/

