  async getCollections(
    options: {
      limit?: number
      page?: number
      whitelistOnly?: boolean
      author?: string
      match?: string
      order?: 'asc' | 'desc'
      sort?: 'created' | 'name' | 'collection_name'
      singleCollection?: string
    } = {}
  ): Promise<CollectionData[]> {
    try {
      const {
        limit = 20,
        page = 1,
        whitelistOnly = true,
        author,
        match,
        order = 'desc',
        sort = 'created',
        singleCollection
      } = options

      const params = new URLSearchParams()
      
      // Handle different filtering modes
      if (singleCollection) {
        // Get specific collection only
        params.append('collection_name', singleCollection)
        params.append('limit', '1')
      } else if (whitelistOnly) {
        // Get collections individually from whitelist since collection_whitelist param is not supported
        const collectionsToFetch = this.COLLECTION_WHITELIST.slice(0, Math.min(limit, 20))
        const collectionPromises = collectionsToFetch.map(async (collectionName) => {
          try {
            const response = await this.fetchWithRetry(`${this.atomicAssetsAPI}/atomicassets/v1/collections/${collectionName}`)
            const data = await response.json()
            if (data.success && data.data) {
              return data.data
            }
            return null
          } catch {
            return null
          }
        })
        
        const results = await Promise.all(collectionPromises)
        const validCollections = results.filter(col => col !== null)
        return validCollections
      } else {
        // Get all collections with optional filters
        params.append('limit', Math.min(limit, 100).toString())
        params.append('page', page.toString())
        
        if (author) params.append('author', author)
        if (match) params.append('match', match)
      }
      
      params.append('order', order)
      params.append('sort', sort)
      
      const url = `${this.atomicAssetsAPI}/atomicassets/v1/collections?${params.toString()}`
      
      const response = await this.fetchWithRetry(url)
      const data: ApiResponse<CollectionDetails[]> = await response.json()
      
      if (data.success && data.data) {
        return data.data
      }
      
      throw new Error('Invalid API response')
    } catch (error) {
      ErrorMessageService.logError('ApiService.getCollections', error, options)
      // Return fallback collections on error
      return [
        { 
          collection_name: 'pixeljourney', 
          name: 'Pixel Journey', 
          img: '', 
          author: 'pixeljourney', 
          allow_notify: true,
          authorized_accounts: [],
          notify_accounts: [],
          market_fee: 0,
          created_at_time: '', 
          created_at_block: '' 
        },
        { 
          collection_name: 'friedfactory', 
          name: 'Fried Factory', 
          img: '', 
          author: 'friedfactory', 
          allow_notify: true,
          authorized_accounts: [],
          notify_accounts: [],
          market_fee: 0,
          created_at_time: '', 
          created_at_block: '' 
        }
      ]
    }
  }

