  // Enhanced collection stats using HiveBP APIs
  async getEnhancedCollectionStats(collectionName: string): Promise<{
    marketCap: number | null
    volume24h: number | null
    volume7d: number | null
    floorPrice: number | null
  }> {
    try {
      const [marketCap, volume24h, volume7d] = await Promise.all([
        this.getHiveBPMarketCap(collectionName),
        this.getHiveBPVolumeDays(collectionName, 1),
        this.getHiveBPVolumeDays(collectionName, 7)
      ])

      // For floor price, we need to get a template ID from the collection first
      let floorPrice: number | null = null
      try {
        const templates = await this.getCollectionTemplates(collectionName, undefined, 1)
        if (templates.length > 0) {
          floorPrice = await this.getHiveBPFloorPrice(templates[0].template_id)
        }
      } catch (error) {
        logger.warn(`Could not fetch floor price for ${collectionName}`, 'API', error)
      }

      return {
        marketCap,
        volume24h,
        volume7d,
        floorPrice
      }
    } catch (error) {
      ErrorMessageService.logError('ApiService.getCollectionStats', error, { collectionName })
      return {
        marketCap: null,
        volume24h: null,
        volume7d: null,
        floorPrice: null
      }
    }
  }

