  // === Alcor Exchange API Methods ===
  
  /**
   * Get token price from Alcor Exchange
   */
  async getAlcorTokenPrice(symbol: string, contract: string): Promise<AlcorTokenPrice | null> {
    let timeoutId: NodeJS.Timeout | null = null
    try {
      // Create token ID in format: symbol-contract
      const tokenId = `${symbol.toLowerCase()}-${contract}`
      const url = `${this.alcorAPI}/tokens/${tokenId}`
      
      // Add timeout and better error handling
      const controller = new AbortController()
      timeoutId = setTimeout(() => {
        controller.abort()
      }, 8000) // Reduced to 8 second timeout
      
      const response = await this.fetchAlcorAPI(url, 2, { signal: controller.signal })
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`)
      }
      
      const tokenData: AlcorTokenPrice = await response.json()
      
      return tokenData
    } catch (error) {
      // Handle abort errors more gracefully
      if (error instanceof Error && error.name === 'AbortError') {
        logger.warn(`Alcor API request timeout for ${symbol}`, 'API')
        return null
      }
      
      logger.warn(`Error fetching Alcor token price for ${symbol}`, 'API', error)
      // Return fallback data for WAX to prevent UI crashes
      if (symbol.toLowerCase() === 'wax') {
        return {
          symbol: 'WAX',
          contract: 'eosio.token',
          id: 'wax-eosio.token',
          system_price: 0.05, // Fallback price
          usd_price: 0.05,
          decimals: 8
        }
      }
      return null
    } finally {
      // Always clear the timeout
      if (timeoutId) {
        clearTimeout(timeoutId)
      }
    }
  }

