import React, { useState, useEffect, useCallback } from 'react'
import { Link } from 'react-router-dom'
import '../css/collections.css'
import { apiService } from '../services/api'
import type { CollectionData } from '../services/api'
import TruncatedText from './TruncatedText'
import ErrorMessage from './ErrorMessage'
import { ErrorMessageService } from '../services/errorMessageService'
import { log } from '../services/loggingService'
import { CollectionImage } from './FallbackImage'
import CollectionTierModal from './CollectionTierModal'

interface Collection {
  collection_name: string
  name: string
  img?: string
  author: string
  created_at_time: string
  created_at_block: string
  floor_price?: number
  volume?: string
  market_cap?: string
  assets?: string
  loading_market_data?: boolean
}

const Collections: React.FC = () => {
  const [collections, setCollections] = useState<Collection[]>([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<Error | null>(null)
  const [showTierModal, setShowTierModal] = useState(false)
  const [searchTerm, setSearchTerm] = useState('')
  const [sortBy, setSortBy] = useState<'name' | 'author' | 'created'>('name')
  const [displayedCount, setDisplayedCount] = useState(12)
  const [loadingMore, setLoadingMore] = useState(false)
  const [showTierPerks, setShowTierPerks] = useState(false)
  const [totalFetched, setTotalFetched] = useState(0)

    // PxApproved Projects with dynamic image loading
  const [pxApprovedProjects, setPxApprovedProjects] = useState<{
    name: string
    collection: string
    image: string | null
    description: string
    tier: string
    verified: boolean
  }[]>([
    {
      name: 'Pixel Journey',
      collection: 'pixeljourney',
      image: null,
      description: 'The original Pixel Journey collection',
      tier: 'platinum',
      verified: true
    },
    {
      name: 'Fried Factory',
      collection: 'friedfactory',
      image: null,
      description: '3D NFT collection by @Friedfactory the 3D legend (and artist of our Journey Ingredients too)!',
      tier: 'partner',
      verified: true
    },
    {
      name: 'Crypto Muffins',
      collection: 'crptomuffins',
      image: null,
      description: 'Delicious crypto-themed NFT collection by AKU!',
      tier: 'gold',
      verified: true
    },
    {
      name: 'Aku Fish Heads',
      collection: 'akufishheads',
      image: null,
      description: 'Unique AKU-licious NFT collection',
      tier: 'silver',
      verified: true
    },
    {
      name: 'Pantheon',
      collection: 'pantheon',
      image: null,
      description: 'Mythological NFT collection',
      tier: 'bronze',
      verified: true
    },
    {
      name: 'Pocket People',
      collection: 'pocketsizedp',
      image: null,
      description: 'Pocket Sized PFPs by our first Partner, Kettleman!',
      tier: 'partner',
      verified: true
    },
    {
      name: 'NIMAs Odyssey',
      collection: 'nimasodyssey',
      image: null,
      description: 'NIMAs Odyssey, a Discord RPG Driven NFT Mining Game',
      tier: 'partner',
      verified: true
    }
  ])

  // Fetch collection metadata for PxApproved projects
  const fetchPxApprovedImages = useCallback(async () => {
    try {
      setPxApprovedProjects(currentProjects => {
        // Only fetch images for projects that don't have them yet
        const projectsNeedingImages = currentProjects.filter(project => !project.image)
        
        if (projectsNeedingImages.length === 0) {
          return currentProjects // No updates needed
        }
        
        // Fetch images for projects that need them
        Promise.all(
          projectsNeedingImages.map(async (project) => {
            try {
              const collectionData = await apiService.getCollectionDetails(project.collection)
              return {
                ...project,
                image: collectionData?.img || null
              }
            } catch (error) {
              log.warn(`Failed to fetch image for ${project.collection}`, 'Collections', error as Error)
              return project
            }
          })
        ).then(updatedProjectsWithImages => {
          setPxApprovedProjects(prevProjects => 
            prevProjects.map(project => {
              const updated = updatedProjectsWithImages.find(up => up.collection === project.collection)
              return updated || project
            })
          )
        })
        
        return currentProjects // Return current state immediately
      })
    } catch (error) {
      log.error('Failed to fetch PxApproved project images', 'Collections', error as Error)
    }
  }, [])

  // Fetch all collections using API service
  // Fetch market data for collections
  const fetchMarketDataForCollections = async (collections: Collection[]) => {
    const updatedCollections = [...collections]
    
    // Process collections in batches to avoid overwhelming the API
    const batchSize = 5
    for (let i = 0; i < collections.length; i += batchSize) {
      const batch = collections.slice(i, i + batchSize)
      
      const batchResults = await Promise.allSettled(batch.map(async (collection, batchIndex) => {
        const collectionIndex = i + batchIndex
        try {
          // Fetch live floor price and market stats in parallel
          const [floorPriceData, marketStats] = await Promise.all([
            apiService.getCollectionFloorPrice(collection.collection_name),
            apiService.getCollectionMarketStats({ collection_name: collection.collection_name })
          ])
          
          return {
            index: collectionIndex,
            data: {
              ...updatedCollections[collectionIndex],
              floor_price: floorPriceData.floorPrice,
              volume: marketStats.data?.volume,
              market_cap: marketStats.data?.volume ? 
                (floorPriceData.floorPrice * parseFloat(marketStats.data.volume)).toFixed(2) : undefined,
              assets: marketStats.data?.sales,
              loading_market_data: false
            }
          }
        } catch (error) {
          ErrorMessageService.logError('Collections.fetchMarketDataForCollections', error, { 
            collection_name: collection.collection_name 
          })
          return {
            index: collectionIndex,
            data: {
              ...updatedCollections[collectionIndex],
              loading_market_data: false
            }
          }
        }
      }))
      
      // Update all batch results at once to minimize re-renders
      batchResults.forEach(result => {
        if (result.status === 'fulfilled') {
          updatedCollections[result.value.index] = result.value.data
        }
      })
      
      // Small delay between batches to be respectful to the API
       if (i + batchSize < collections.length) {
         await new Promise(resolve => setTimeout(resolve, 100))
       }
     }
     
     // Update state once after all batches are processed
     setCollections([...updatedCollections])
   }

  const fetchCollections = async (limit = 50) => {
    setLoading(true)
    setError(null)
    try {
      const data = await apiService.getCollections({ limit, whitelistOnly: true })
      const mappedCollections: Collection[] = (data || []).map((col: CollectionData) => ({
        collection_name: col.collection_name,
        name: col.name,
        img: col.img,
        author: col.author,
        created_at_time: col.created_at_time,
        created_at_block: col.created_at_block,
        loading_market_data: true
      }))
      setCollections(mappedCollections)
      setTotalFetched(data?.length || 0)
      
      // Fetch market data for each collection
      fetchMarketDataForCollections(mappedCollections)
    } catch (error) {
      const errorObj = error instanceof Error ? error : new Error('Failed to fetch collections')
      setError(errorObj)
      ErrorMessageService.logError('Collections.fetchCollections', errorObj, { limit })
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchCollections()
    fetchPxApprovedImages()
  }, [fetchPxApprovedImages]) // Safe to depend on memoized function with no dependencies

  // Filter and sort collections based on search term and sort option
  const filteredCollections = collections
    .filter(collection => 
      collection.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      collection.collection_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      collection.author.toLowerCase().includes(searchTerm.toLowerCase())
    )
    .sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.name.localeCompare(b.name)
        case 'author':
          return a.author.localeCompare(b.author)
        case 'created':
          return new Date(b.created_at_time).getTime() - new Date(a.created_at_time).getTime()
        default:
          return 0
      }
    })

  // Filter PxApproved projects based on search term
  const filteredPxApproved = pxApprovedProjects.filter(project =>
    project.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    project.collection.toLowerCase().includes(searchTerm.toLowerCase())
  )

  // Get displayed collections (limited by displayedCount)
  const displayedCollections = filteredCollections.slice(0, displayedCount)
  const hasMoreCollections = filteredCollections.length > displayedCount || (totalFetched >= 50 && displayedCount >= filteredCollections.length)

  // Load more collections function
  const loadMoreCollections = async () => {
    setLoadingMore(true)
    try {
      // If we have more collections already fetched, just show more
      if (displayedCount < collections.length) {
        setDisplayedCount(prev => prev + 12)
      } else {
        // Fetch more collections from API
        const newLimit = totalFetched + 50
        const data = await apiService.getCollections({ limit: newLimit, whitelistOnly: true })
        if (data && data.length > totalFetched) {
          const mappedCollections: Collection[] = data.map((col: CollectionData) => ({
            collection_name: col.collection_name,
            name: col.name,
            img: col.img,
            author: col.author,
            created_at_time: col.created_at_time,
            created_at_block: col.created_at_block,
            loading_market_data: true
          }))
          setCollections(mappedCollections)
          setTotalFetched(data.length)
          setDisplayedCount(prev => prev + 12)
          
          // Fetch market data for new collections
          const newCollections = mappedCollections.slice(totalFetched)
          if (newCollections.length > 0) {
            fetchMarketDataForCollections(newCollections)
          }
        }
      }
    } catch (error) {
      const errorObj = error instanceof Error ? error : new Error('Failed to load more collections')
      ErrorMessageService.logError('Collections.loadMoreCollections', errorObj, { 
        displayedCount, 
        totalFetched 
      })
    } finally {
      setLoadingMore(false)
    }
  }



  return (
    
    <div className="collections-container">

      {/* Collections Header & PxApproved Info Section */}
      <div className="perk-tiers-section">
        <div className="collections-header">
          <h1>Collections</h1>
          <p className="collections-subtitle">
            Discover amazing NFT collections on the WAX blockchain
          </p>
        </div>
        
        <div className="info-card">
          <h3>PxApproved Collections</h3>
          <p>
            Only PxApproved collections are listed on our marketplace by default. 
            This ensures quality and authenticity for our community.
          </p>
          <div className="info-actions">
            <a 
              href="https://discord.gg/pixeljourney" 
              target="_blank" 
              rel="noopener noreferrer"
              className="discord-link"
            >
              <svg className="discord-icon" viewBox="0 0 24 24" fill="currentColor">
                <path d="M20.317 4.37a19.791 19.791 0 0 0-4.885-1.515a.074.074 0 0 0-.079.037c-.21.375-.444.864-.608 1.25a18.27 18.27 0 0 0-5.487 0a12.64 12.64 0 0 0-.617-1.25a.077.077 0 0 0-.079-.037A19.736 19.736 0 0 0 3.677 4.37a.07.07 0 0 0-.032.027C.533 9.046-.32 13.58.099 18.057a.082.082 0 0 0 .031.057a19.9 19.9 0 0 0 5.993 3.03a.078.078 0 0 0 .084-.028a14.09 14.09 0 0 0 1.226-1.994a.076.076 0 0 0-.041-.106a13.107 13.107 0 0 1-1.872-.892a.077.077 0 0 1-.008-.128a10.2 10.2 0 0 0 .372-.292a.074.074 0 0 1 .077-.01c3.928 1.793 8.18 1.793 12.062 0a.074.074 0 0 1 .078.01c.12.098.246.198.373.292a.077.077 0 0 1-.006.127a12.299 12.299 0 0 1-1.873.892a.077.077 0 0 0-.041.107c.36.698.772 1.362 1.225 1.993a.076.076 0 0 0 .084.028a19.839 19.839 0 0 0 6.002-3.03a.077.077 0 0 0 .032-.054c.5-5.177-.838-9.674-3.549-13.66a.061.061 0 0 0-.031-.03zM8.02 15.33c-1.183 0-2.157-1.085-2.157-2.419c0-1.333.956-2.419 2.157-2.419c1.21 0 2.176 1.096 2.157 2.42c0 1.333-.956 2.418-2.157 2.418zm7.975 0c-1.183 0-2.157-1.085-2.157-2.419c0-1.333.955-2.419 2.157-2.419c1.21 0 2.176 1.096 2.157 2.42c0 1.333-.946 2.418-2.157 2.418z"/>
              </svg>
              Join Discord for More Info
            </a>
            <button 
              className="payment-button"
              onClick={() => setShowTierModal(true)}
            >
              Upgrade Collection Tier
            </button>
            <button 
              className="toggle-tiers-button"
              onClick={() => setShowTierPerks(!showTierPerks)}
            >
              {showTierPerks ? 'Hide Tier Details' : 'Show Tier Details'}
              <svg 
                className={`toggle-icon ${showTierPerks ? 'rotated' : ''}`} 
                viewBox="0 0 24 24" 
                fill="currentColor"
              >
                <path d="M7.41 8.59L12 13.17l4.59-4.58L18 10l-6 6-6-6 1.41-1.41z"/>
              </svg>
            </button>
          </div>
        </div>
        
        <p className="tiers-description">
          Elevate your collection's status on PxMarket with our tier system. Each tier unlocks exclusive benefits and promotional opportunities.
        </p>
        
        {showTierPerks && (
        <div className="tiers-content">
          <div className="tiers-header">
            <h2>PxApproved Perk Tiers</h2>
          </div>
          <div className="tiers-grid">
          <div className="tier-card tier-bronze">
            <div className="tier-header">
              <span className="tier-icon">🥉</span>
              <h3>Bronze</h3>
              <div className="tier-cost">5M PXJ</div>
            </div>
            <div className="tier-benefits">
              <h4>Benefits:</h4>
              <ul>
                <li>Basic marketplace listing</li>
                <li>Collection verification badge</li>
                <li>Community Discord access</li>
                <li>Monthly featured spot rotation</li>
              </ul>
            </div>
          </div>

          <div className="tier-card tier-silver">
            <div className="tier-header">
              <span className="tier-icon">🥈</span>
              <h3>Silver</h3>
              <div className="tier-cost">15M PXJ</div>
            </div>
            <div className="tier-benefits">
              <h4>Benefits:</h4>
              <ul>
                <li>All Bronze benefits</li>
                <li>Priority marketplace placement</li>
                <li>Weekly newsletter mentions</li>
                <li>Custom collection banner</li>
                <li>Analytics dashboard access</li>
              </ul>
            </div>
          </div>

          <div className="tier-card tier-gold">
            <div className="tier-header">
              <span className="tier-icon">🥇</span>
              <h3>Gold</h3>
              <div className="tier-cost">30M PXJ</div>
            </div>
            <div className="tier-benefits">
              <h4>Benefits:</h4>
              <ul>
                <li>All Silver benefits</li>
                <li>Homepage featured carousel</li>
                <li>Social media promotion</li>
                <li>Exclusive events participation</li>
                <li>Direct community manager contact</li>
              </ul>
            </div>
          </div>

          <div className="tier-card tier-partner">
            <div className="tier-header">
              <span className="tier-icon">🤝</span>
              <h3>Partner</h3>
              <div className="tier-cost">Special Agreement</div>
            </div>
            <div className="tier-benefits">
              <h4>Benefits:</h4>
              <ul>
                <li>All Gold benefits</li>
                <li>Co-marketing opportunities</li>
                <li>Revenue sharing programs</li>
                <li>Joint event hosting</li>
                <li>Custom integration support</li>
              </ul>
            </div>
          </div>

          <div className="tier-card tier-platinum">
            <div className="tier-header">
              <span className="tier-icon">💎</span>
              <h3>Platinum</h3>
              <div className="tier-cost">50M PXJ</div>
            </div>
            <div className="tier-benefits">
              <h4>Benefits:</h4>
              <ul>
                <li>All Partner benefits</li>
                <li>Permanent homepage presence</li>
                <li>Dedicated marketing campaigns</li>
                <li>WaxRock Council consideration</li>
                <li>Platform governance participation</li>
                <li>White-label marketplace options</li>
              </ul>
            </div>
          </div>
          </div>
        </div>
        )}

        {showTierPerks && (
        <div className="council-note">
          <div className="council-info">
            <span className="council-icon">🏛️</span>
            <div>
              <h4>WaxRock Council Authority</h4>
              <p>
                The WaxRock Council reserves the right to grant special tier status to exceptional collections 
                through community proposals and voting. Collections may also lobby for tier upgrades through 
                formal governance processes.
              </p>
            </div>
          </div>
        </div>
        )}

        {showTierPerks && (
        <div className="apply-section">
          <h3>Ready to Upgrade Your Collection?</h3>
          <p>Contact our team to discuss tier upgrades and promotional opportunities.</p>
          <div className="apply-buttons">
            <a href="https://discord.gg/pixeljourney" target="_blank" rel="noopener noreferrer" className="apply-btn discord-btn">
              Apply via Discord
            </a>
            <button className="apply-btn payment-btn" onClick={() => setShowTierModal(true)}>
              Instant Tier Upgrade
            </button>
          </div>
        </div>
        )}
      </div>

      {/* Collection Tier Modal */}
      <CollectionTierModal 
        isOpen={showTierModal}
        onClose={() => setShowTierModal(false)}
      />

      {/* Search and Filter Section */}
      <div className="search-filter-section">
        <div className="search-controls">
          <div className="search-input-container">
            <svg className="search-icon" viewBox="0 0 24 24" fill="currentColor">
              <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
            </svg>
            <input
              type="text"
              placeholder="Search collections, authors, or names..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="search-input"
            />
            {searchTerm && (
              <button 
                onClick={() => setSearchTerm('')}
                className="clear-search"
                aria-label="Clear search"
              >
                <svg viewBox="0 0 24 24" fill="currentColor">
                  <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/>
                </svg>
              </button>
            )}
          </div>
          <div className="sort-container">
            <label htmlFor="sortBy">Sort by:</label>
            <select
              id="sortBy"
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as 'name' | 'author' | 'created')}
              className="sort-select"
            >
              <option value="name">Name</option>
              <option value="author">Author</option>
              <option value="created">Created Date</option>
            </select>
          </div>
        </div>
        {searchTerm && (
          <div className="search-results-info">
            <p>Found {filteredCollections.length + filteredPxApproved.length} results for "{searchTerm}"</p>
          </div>
        )}
      </div>

      {/* PxApproved Projects Section - Only show if there are filtered results or no search */}
      {(!searchTerm || filteredPxApproved.length > 0) && (
        <div className="pxapproved-section">
          <h2>PxApproved {searchTerm && `(${filteredPxApproved.length})`}</h2>
          <div className="approved-projects-grid">
            {filteredPxApproved.map((project, index) => (
              <Link key={index} to={`/collections/${project.collection}`} className="project-card-link">
                <div className={`project-card project-card-${project.tier}`}>
                  <div className="project-tier-badge">
                    <span className={`tier-label tier-${project.tier}`}>{project.tier.toUpperCase()}</span>
                    {project.verified && <span className="verified-badge">✓</span>}
                  </div>
                  <img 
                    src={project.image && typeof project.image === 'string' ? 
                      (project.image.startsWith('http') ? project.image : `https://ipfs.io/ipfs/${project.image}`) 
                      : '/img/placeholder-collection.svg'
                    } 
                    alt={project.name} 
                    className="project-image"
                    onError={(e) => {
                      e.currentTarget.src = '/img/placeholder-collection.svg'
                    }}
                  />
                  <div className="project-info">
                    <h3>{project.name}</h3>
                    <p className="project-collection">{project.collection}</p>
                    <TruncatedText 
                      text={project.description} 
                      maxLength={240}
                      className="project-description"
                    />
                  </div>
                </div>
              </Link>
            ))}
          </div>
        </div>
      )}

      {/* All Collections Section */}
      <div className="all-collections-section">
        <h2>More Collections on WAX {searchTerm && `(${filteredCollections.length})`}</h2>
        {error ? (
          <ErrorMessage
            errorType="COLLECTION_NOT_FOUND"
            onRetry={() => fetchCollections()}
            showRetry={true}
          />
        ) : loading ? (
          <div className="loading-spinner">
            <div className="spinner"></div>
            <p>Loading collections...</p>
          </div>
        ) : (
          <>
            <div className="collections-grid">
              {displayedCollections.length > 0 ? (
                displayedCollections.map((collection) => (
                  <Link 
                    key={collection.collection_name} 
                    to={`/collections/${collection.collection_name}`}
                    className="collection-card"
                  >
                    <CollectionImage 
                      src={collection.img ? 
                        (collection.img.startsWith('http') ? collection.img : `https://ipfs.io/ipfs/${collection.img}`) 
                        : undefined
                      } 
                      alt={collection.collection_name}
                      width={150}
                      height={150}
                      fallbackText={collection.collection_name}
                      className="collection-image"
                    />
                    <div className="collection-info">
                      <div className="collection-row-1">
                        <h3>{collection.name}</h3>
                      </div>
                      <div className="collection-row-2">
                        <p className="collection-name">{collection.collection_name}</p>
                      </div>
                      <div className="collection-market-data">
                        {collection.loading_market_data ? (
                          <div className="market-data-loading">
                            <div className="mini-spinner"></div>
                            <span>Loading market data...</span>
                          </div>
                        ) : (
                          <>
                            {collection.floor_price !== undefined && collection.floor_price > 0 && (
                              <div className="market-stat">
                                <span className="stat-label">Floor:</span>
                                <span className="stat-value">{collection.floor_price.toFixed(4)} WAX</span>
                              </div>
                            )}
                            {collection.volume && (
                              <div className="market-stat">
                                <span className="stat-label">Volume:</span>
                                <span className="stat-value">{parseFloat(collection.volume).toLocaleString()} WAX</span>
                              </div>
                            )}
                            {collection.assets && (
                              <div className="market-stat">
                                <span className="stat-label">Assets:</span>
                                <span className="stat-value">{collection.assets}</span>
                              </div>
                            )}
                          </>
                        )}
                      </div>
                    </div>
                  </Link>
                ))
              ) : searchTerm ? (
                <div className="no-results">
                  <svg className="no-results-icon" viewBox="0 0 24 24" fill="currentColor">
                    <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
                  </svg>
                  <h3>No collections found</h3>
                  <p>Try adjusting your search terms or browse all collections</p>
                  <button 
                    onClick={() => setSearchTerm('')}
                    className="clear-search-button"
                  >
                    Clear Search
                  </button>
                </div>
              ) : null}
            </div>
            
            {/* Load More Button */}
            {hasMoreCollections && !searchTerm && (
              <div className="load-more-container">
                <button 
                  onClick={loadMoreCollections}
                  disabled={loadingMore}
                  className="load-more-button"
                >
                  {loadingMore ? (
                    <>
                      <div className="button-spinner"></div>
                      Loading...
                    </>
                  ) : (
                    `Load More Collections (${filteredCollections.length - displayedCount} remaining)`
                  )}
                </button>
              </div>
            )}
          </>
        )}
      </div>
    </div>
  )
}

export default Collections