/**
 * Production-ready logging service
 * Replaces console.log statements with structured logging
 * 
 * USAGE GUIDELINES:
 * 
 * 1. ALWAYS use the centralized log service instead of console.log/warn/error
 *    ✅ Good: log.info('User logged in', 'AuthService.login', { userId })
 *    ❌ Bad:  console.log('User logged in')
 * 
 * 2. Choose appropriate log levels:
 *    - log.debug(): Development info, detailed tracing (filtered in production)
 *    - log.info():  Important events, successful operations, state changes
 *    - log.warn():  Recoverable errors, fallbacks, deprecated usage
 *    - log.error(): Critical errors, exceptions, failed operations
 * 
 * 3. ALWAYS provide context as the second parameter:
 *    - Format: 'ServiceName.methodName' or 'ComponentName.functionName'
 *    - Example: 'RWax.handleTokenization', 'InventoryService.loadFullInventory'
 * 
 * 4. Include relevant data as the third parameter when helpful:
 *    - Use objects for structured data: { userId, assetId, count }
 *    - Avoid logging sensitive information (passwords, tokens, keys)
 * 
 * 5. For errors, pass the error object in the data parameter:
 *    - log.error('Failed to fetch data', 'ApiService.getData', { error, endpoint })
 * 
 * 6. Migration from console calls:
 *    - console.log() → log.debug() or log.info() (depending on importance)
 *    - console.warn() → log.warn()
 *    - console.error() → log.error()
 * 
 * 7. Import and use:
 *    import { log } from './loggingService'
 *    log.info('Operation completed', 'MyService.myMethod')
 */

export const LogLevel = {
  DEBUG: 'debug',
  INFO: 'info',
  WARN: 'warn',
  ERROR: 'error'
} as const

export type LogLevel = typeof LogLevel[keyof typeof LogLevel]

interface LogEntry {
  timestamp: string;
  level: LogLevel;
  message: string;
  context?: string;
  data?: unknown;
  error?: Error;
}

class LoggingService {
  private logLevel: LogLevel = LogLevel.INFO;
  private isDevelopment = import.meta.env.DEV;
  private logs: LogEntry[] = [];
  private maxLogs = 1000;

  constructor() {
    // Set log level based on environment
    this.logLevel = this.isDevelopment ? LogLevel.DEBUG : LogLevel.WARN;
  }

  private createLogEntry(level: LogLevel, message: string, context?: string, data?: unknown, error?: Error): LogEntry {
    return {
      timestamp: new Date().toISOString(),
      level,
      message,
      context,
      data,
      error
    };
  }

  private shouldLog(level: LogLevel): boolean {
    return level >= this.logLevel;
  }

  private storeLog(entry: LogEntry): void {
    this.logs.push(entry);
    if (this.logs.length > this.maxLogs) {
      this.logs.shift(); // Remove oldest log
    }
  }

  private formatMessage(entry: LogEntry): string {
    const levelName = entry.level.toUpperCase();
    const context = entry.context ? `[${entry.context}]` : '';
    return `${entry.timestamp} ${levelName} ${context} ${entry.message}`;
  }

  debug(message: string, context?: string, data?: unknown): void {
    if (!this.shouldLog(LogLevel.DEBUG)) return;
    
    const entry = this.createLogEntry(LogLevel.DEBUG, message, context, data);
    this.storeLog(entry);
    
    if (this.isDevelopment) {
      console.debug(this.formatMessage(entry), data || '');
    }
  }

  info(message: string, context?: string, data?: unknown): void {
    if (!this.shouldLog(LogLevel.INFO)) return;
    
    const entry = this.createLogEntry(LogLevel.INFO, message, context, data);
    this.storeLog(entry);
    
    if (this.isDevelopment) {
      console.info(this.formatMessage(entry), data || '');
    }
  }

  warn(message: string, context?: string, data?: unknown): void {
    if (!this.shouldLog(LogLevel.WARN)) return;
    
    const entry = this.createLogEntry(LogLevel.WARN, message, context, data);
    this.storeLog(entry);
    
    console.warn(this.formatMessage(entry), data || '');
  }

  error(message: string, context?: string, error?: Error, data?: unknown): void {
    if (!this.shouldLog(LogLevel.ERROR)) return;
    
    const entry = this.createLogEntry(LogLevel.ERROR, message, context, data, error);
    this.storeLog(entry);
    
    console.error(this.formatMessage(entry), error || '', data || '');
    
    // In production, send to error reporting service
    if (!this.isDevelopment && error) {
      this.reportError(error, { message, context, data });
    }
  }

  private async reportError(error: Error, metadata: Record<string, unknown>): Promise<void> {
    try {
      // Send to multiple error reporting services for redundancy
      const reportingPromises = [
        this.sendToErrorReportingAPI(error, metadata),
        this.sendToLocalStorage(error, metadata),
        this.sendToConsole(error, metadata)
      ];

      await Promise.allSettled(reportingPromises);
    } catch (reportingError) {
      // Fallback to console if all reporting methods fail
      console.error('Failed to report error:', reportingError);
      console.error('Original error:', error);
    }
  }

  private async sendToErrorReportingAPI(error: Error, metadata: Record<string, unknown>): Promise<void> {
    try {
      const errorReport = {
        message: error.message,
        stack: error.stack,
        timestamp: new Date().toISOString(),
        userAgent: navigator.userAgent,
        url: window.location.href,
        userId: this.getUserId(),
        sessionId: this.getSessionId(),
        buildVersion: this.getBuildVersion(),
        environment: this.getEnvironment(),
        metadata
      };

      // Try primary error reporting endpoint
      const response = await fetch('/api/errors/report', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.getAuthToken()}`
        },
        body: JSON.stringify(errorReport)
      });

      if (!response.ok) {
        throw new Error(`Error reporting failed: ${response.status} ${response.statusText}`);
      }

      console.log('Error successfully reported to server');
    } catch {
      // Try fallback error reporting service
      await this.sendToFallbackErrorService(error, metadata);
    }
  }

  private async sendToFallbackErrorService(error: Error, metadata: Record<string, unknown>): Promise<void> {
    try {
      // Use a third-party error reporting service as fallback
      const errorData = {
        error: {
          message: error.message,
          stack: error.stack,
          timestamp: new Date().toISOString()
        },
        context: {
          ...metadata,
          userAgent: navigator.userAgent,
          url: window.location.href
        }
      };

      // This could be Sentry, LogRocket, Bugsnag, etc.
      if (typeof window !== 'undefined' && (window as { Sentry?: { captureException: (error: Error, options: { extra: unknown; level: string }) => void } }).Sentry) {
        (window as unknown as { Sentry: { captureException: (error: Error, options: { extra: unknown; level: string }) => void } }).Sentry.captureException(error, {
          extra: errorData.context,
          level: 'error'
        });
      }
    } catch (fallbackError) {
      console.warn('Fallback error reporting also failed:', fallbackError);
    }
  }

  private async sendToLocalStorage(error: Error, metadata: Record<string, unknown>): Promise<void> {
    try {
      const storageKey = 'error_reports';
      const maxStoredErrors = 50;
      
      const errorReport = {
        message: error.message,
        stack: error.stack,
        timestamp: new Date().toISOString(),
        userAgent: navigator.userAgent,
        url: window.location.href,
        metadata
      };
      
      let storedErrors: unknown[] = [];
      const existing = localStorage.getItem(storageKey);
      
      if (existing) {
        try {
          storedErrors = JSON.parse(existing);
        } catch (parseError) {
          console.warn('Failed to parse stored errors:', parseError);
        }
      }

      // Add new error and limit storage
      storedErrors.unshift(errorReport);
      storedErrors = storedErrors.slice(0, maxStoredErrors);

      localStorage.setItem(storageKey, JSON.stringify(storedErrors));
    } catch (storageError) {
      console.warn('Failed to store error in localStorage:', storageError);
    }
  }

  private sendToConsole(error: Error, metadata: Record<string, unknown>): void {
    const style = 'color: #ff4444; font-weight: bold; background: #ffe6e6; padding: 2px 4px; border-radius: 3px;';
    console.group(`%cERROR: ${error.message}`, style);
    
    if (metadata) {
      console.log('Metadata:', metadata);
    }
    
    if (error.stack) {
      console.log('Stack:', error.stack);
    }
    
    console.log('Timestamp:', new Date().toISOString());
    console.groupEnd();
  }

  private getUserId(): string {
    return localStorage.getItem('user_id') || sessionStorage.getItem('user_id') || 'anonymous';
  }

  private getSessionId(): string {
    let sessionId = sessionStorage.getItem('session_id');
    if (!sessionId) {
      sessionId = `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
      sessionStorage.setItem('session_id', sessionId);
    }
    return sessionId;
  }

  private getAuthToken(): string {
    return localStorage.getItem('auth_token') || sessionStorage.getItem('auth_token') || '';
  }

  private getBuildVersion(): string {
    return import.meta.env.VITE_APP_VERSION || 'unknown';
  }

  private getEnvironment(): string {
    return import.meta.env.MODE || 'development';
  }

  getLogs(level?: LogLevel): LogEntry[] {
    if (level !== undefined) {
      return this.logs.filter(log => log.level >= level);
    }
    return [...this.logs];
  }

  clearLogs(): void {
    this.logs = [];
  }

  exportLogs(): string {
    return JSON.stringify(this.logs, null, 2);
  }
}

// Create singleton instance
export const logger = new LoggingService();

// Convenience functions for backward compatibility
export const log = {
  debug: (message: string, context?: string, data?: unknown) => logger.debug(message, context, data),
  info: (message: string, context?: string, data?: unknown) => logger.info(message, context, data),
  warn: (message: string, context?: string, data?: unknown) => logger.warn(message, context, data),
  error: (message: string, context?: string, error?: Error, data?: unknown) => logger.error(message, context, error, data)
};