import React, { useState } from 'react'
import '../css/truncated-text.css'

interface TruncatedTextProps {
  text: string
  maxLength?: number
  className?: string
}

const TruncatedText: React.FC<TruncatedTextProps> = React.memo(({ 
  text, 
  maxLength = 240, 
  className = '' 
}) => {
  const [isExpanded, setIsExpanded] = useState(false)
  
  if (!text || text.length <= maxLength) {
    return <span className={className}>{text}</span>
  }
  
  const truncatedText = text.slice(0, maxLength).trim()
  
  return (
    <span className={className}>
      {isExpanded ? (
        <>
          {text}
          <button 
            className="read-more-btn collapse-btn"
            onClick={(e) => {
              e.preventDefault()
              e.stopPropagation()
              setIsExpanded(false)
            }}
          >
            read less
          </button>
        </>
      ) : (
        <>
          {truncatedText}
          {truncatedText.endsWith('.') ? '' : '...'}
          <button 
            className="read-more-btn expand-btn"
            onClick={(e) => {
              e.preventDefault()
              e.stopPropagation()
              setIsExpanded(true)
            }}
          >
            read more
          </button>
        </>
      )}
    </span>
  )
})

TruncatedText.displayName = 'TruncatedText'

export default TruncatedText