import React from 'react'

interface ImageZoomProps {
  isOpen: boolean
  imageUrl: string
  imageName: string
  onClose: () => void
}

const ImageZoom: React.FC<ImageZoomProps> = ({ isOpen, imageUrl, imageName, onClose }) => {
  if (!isOpen) return null

  const handleKeyDown = (e: KeyboardEvent) => {
    if (e.key === 'Escape') {
      onClose()
    }
  }

  React.useEffect(() => {
    if (isOpen) {
      document.addEventListener('keydown', handleKeyDown)
      document.body.style.overflow = 'hidden'
    }

    return () => {
      document.removeEventListener('keydown', handleKeyDown)
      document.body.style.overflow = 'unset'
    }
  }, [isOpen])

  return (
    <div className="image-zoom-modal" onClick={onClose}>
      <div className="zoom-modal-content" onClick={(e) => e.stopPropagation()}>
        <button 
          className="zoom-modal-close"
          onClick={onClose}
        >
          ×
        </button>
        <img 
          src={imageUrl} 
          alt={imageName}
          className="zoom-modal-image"
        />
        <div className="zoom-modal-info">
          <h3>{imageName}</h3>
        </div>
      </div>
    </div>
  )
}

export default ImageZoom