import { log } from './loggingService'

export interface FeaturedTokenPromo {
  id: string
  tokenSymbol: string
  tokenContract: string
  description: string
  imageUrl?: string
  websiteUrl?: string
  paidAmount: number
  startDate: Date
  endDate: Date
  transactionId: string
  isActive: boolean
}

export interface PromoPayment {
  amount: number // PXJ amount (500K to 5M)
  duration: number // days (30 to 365)
  tokenSymbol: string
  tokenContract: string
  description: string
  imageUrl?: string
  websiteUrl?: string
}

class FeaturedTokensService {
  private readonly FEATURE_ACCOUNT = 'feature.pxj'
  private readonly MIN_AMOUNT = 500000 // 500K PXJ
  private readonly MAX_AMOUNT = 5000000 // 5M PXJ
  private readonly MIN_DAYS = 30
  private readonly MAX_DAYS = 365
  
  /**
   * Calculate promotion duration based on PXJ amount
   */
  calculateDuration(pxjAmount: number): number {
    // Linear scale: 500K PXJ = 30 days, 5M PXJ = 365 days
    const ratio = (pxjAmount - this.MIN_AMOUNT) / (this.MAX_AMOUNT - this.MIN_AMOUNT)
    return Math.floor(this.MIN_DAYS + (ratio * (this.MAX_DAYS - this.MIN_DAYS)))
  }
  
  /**
   * Calculate required PXJ amount for desired duration
   */
  calculateRequiredAmount(days: number): number {
    const ratio = (days - this.MIN_DAYS) / (this.MAX_DAYS - this.MIN_DAYS)
    return Math.floor(this.MIN_AMOUNT + (ratio * (this.MAX_AMOUNT - this.MIN_AMOUNT)))
  }
  
  /**
   * Generate compact memo for promotion payment
   * Format: TOKEN@CONTRACT|DESCRIPTION|IMAGE|WEBSITE
   */
  generatePromoMemo(payment: PromoPayment): string {
    const token = `${payment.tokenSymbol}@${payment.tokenContract}`
    
    // Calculate available space for description
    const baseLength = token.length + 1 // 1 pipe after token
    const imageLength = payment.imageUrl ? payment.imageUrl.length + 1 : 0 // +1 for pipe
    const websiteLength = payment.websiteUrl ? payment.websiteUrl.length + 1 : 0 // +1 for pipe
    const usedLength = baseLength + imageLength + websiteLength
    const maxDescLength = Math.min(100, 235 - usedLength) // Leave 5 chars buffer
    
    // Truncate description if needed
    let description = payment.description
    if (description.length > maxDescLength) {
      description = description.substring(0, maxDescLength - 3) + '...'
    }
    
    // Build compact memo: TOKEN@CONTRACT|DESCRIPTION|IMAGE|WEBSITE
    let memo = `${token}|${description}`
    
    if (payment.imageUrl) {
      memo += `|${payment.imageUrl}`
    }
    
    if (payment.websiteUrl) {
      memo += `|${payment.websiteUrl}`
    }
    
    return memo
  }
  
  /**
   * Parse memo from blockchain transaction
   * Supports both new compact format and old JSON format for backward compatibility
   */
  parseMemo(memo: string): any {
    if (!memo) return null
    
    try {
      // Try parsing new compact format first: TOKEN@CONTRACT|DESCRIPTION|IMAGE|WEBSITE
      if (memo.includes('|') && memo.includes('@') && !memo.startsWith('{')) {
        const parts = memo.split('|')
        if (parts.length >= 2) {
          const [token, description, image = '', website = ''] = parts
          const [tokenSymbol, tokenContract] = token.split('@')
          
          return {
            type: 'feature_token',
            token,
            tokenSymbol,
            tokenContract,
            description,
            image,
            website
          }
        }
      }
      
      // Fall back to old JSON format
      const parsed = JSON.parse(memo)
      if (parsed.type === 'feature_token') {
        return parsed
      }
      
      return null
    } catch (error) {
      log.error('Failed to parse memo:', 'FeaturedTokensService.parseMemo', error as Error)
      return null
    }
  }
  
  /**
   * Fetch featured token promotions from blockchain
   */
  async getFeaturedTokens(): Promise<FeaturedTokenPromo[]> {
    try {
      // Query transfers to feature.pxj account
      const response = await fetch(`https://wax.eosrio.io/v2/history/get_actions?account=${this.FEATURE_ACCOUNT}&filter=eosio.token:transfer&limit=100&sort=desc`)
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`)
      }
      
      const data = await response.json()
      const promos: FeaturedTokenPromo[] = []
      
      for (const action of data.actions || []) {
        try {
          const transfer = action.act.data
          const memo = this.parseMemo(transfer.memo)
          
          if (!memo || memo.type !== 'feature_token') continue
          
          const pxjAmount = parseFloat(transfer.quantity.split(' ')[0])
          const startDate = new Date(action.timestamp)
          const endDate = new Date(startDate.getTime() + (memo.duration * 24 * 60 * 60 * 1000))
          const isActive = new Date() <= endDate
          
          // Only include valid promotions with sufficient payment
          if (pxjAmount >= this.MIN_AMOUNT && isActive) {
            const [tokenSymbol, tokenContract] = memo.token.split('@')
            
            promos.push({
              id: action.trx_id,
              tokenSymbol,
              tokenContract,
              description: memo.description,
              imageUrl: memo.image,
              websiteUrl: memo.website,
              paidAmount: pxjAmount,
              startDate,
              endDate,
              transactionId: action.trx_id,
              isActive
            })
          }
        } catch (error) {
          log.error('Error processing featured token action:', 'FeaturedTokensService.getFeaturedTokens', error as Error)
        }
      }
      
      // Sort by paid amount (highest first) and then by start date (newest first)
      return promos.sort((a, b) => {
        if (a.paidAmount !== b.paidAmount) {
          return b.paidAmount - a.paidAmount
        }
        return b.startDate.getTime() - a.startDate.getTime()
      })
      
    } catch (error) {
      log.error('Failed to fetch featured tokens:', 'FeaturedTokensService.getFeaturedTokens', error as Error)
      return []
    }
  }
  
  /**
   * Validate promotion payment parameters
   */
  validatePayment(payment: PromoPayment): { valid: boolean; error?: string } {
    if (payment.amount < this.MIN_AMOUNT || payment.amount > this.MAX_AMOUNT) {
      return {
        valid: false,
        error: `Amount must be between ${this.MIN_AMOUNT.toLocaleString()} and ${this.MAX_AMOUNT.toLocaleString()} PXJ`
      }
    }
    
    if (payment.duration < this.MIN_DAYS || payment.duration > this.MAX_DAYS) {
      return {
        valid: false,
        error: `Duration must be between ${this.MIN_DAYS} and ${this.MAX_DAYS} days`
      }
    }
    
    if (!payment.tokenSymbol || !payment.tokenContract) {
      return {
        valid: false,
        error: 'Token symbol and contract are required'
      }
    }
    
    if (!payment.description || payment.description.length < 10) {
      return {
        valid: false,
        error: 'Description must be at least 10 characters'
      }
    }
    
    // Calculate max description length based on other fields
    const token = `${payment.tokenSymbol}@${payment.tokenContract}`
    const baseLength = token.length + 1 // 1 pipe after token
    const imageLength = payment.imageUrl ? payment.imageUrl.length + 1 : 0
    const websiteLength = payment.websiteUrl ? payment.websiteUrl.length + 1 : 0
    const usedLength = baseLength + imageLength + websiteLength
    const maxDescLength = Math.min(100, 235 - usedLength)
    
    if (payment.description.length > maxDescLength) {
      return {
        valid: false,
        error: `Description too long. Maximum ${maxDescLength} characters allowed with current image/website URLs.`
      }
    }
    
    return { valid: true }
  }
}

export const featuredTokensService = new FeaturedTokensService()