  /**
   * Get detailed staking information for a user from farms.waxdao contract tables
   */
  async getStakingInfo(account: string, farmName: string = 'pixeljourney'): Promise<{
    staked_nfts: NFTStakeInfo[]
    pending_rewards: StakingReward[]
    total_staking_power: number
    estimated_daily_rewards: StakingReward[]
  }> {
    try {
      // Query the farms.waxdao contract tables for staked assets
      const stakedResponse = await fetch(
        `${API_ENDPOINTS.WAX_CHAIN.GREYMASS}/v1/chain/get_table_rows`,
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
             json: true,
             code: this.FARMS_CONTRACT,
             scope: this.FARMS_CONTRACT,
             table: 'stakers',
             lower_bound: account,
             upper_bound: account,
             limit: 1000
           })
        }
      )
      
      if (!stakedResponse.ok) {
        throw new Error(`Failed to fetch staking info: ${stakedResponse.statusText}`)
      }
      
      const stakedData = await stakedResponse.json()
      
      // Extract staked assets and rewards from the staker data
       const userStakeData = stakedData.rows.find((row: StakerDataRow) => row.user === account) || {} as Partial<StakerDataRow>
       const stakedAssets = userStakeData.staked_assets || []
       const pendingRewards = userStakeData.pending_rewards || []
       
       const stakedNfts: NFTStakeInfo[] = stakedAssets.map((assetId: string) => ({
         asset_id: assetId,
         template_id: '0', // Would need to fetch from AtomicAssets API
         farm_name: farmName,
         stake_time: Date.now() / 1000, // Would need actual stake time from contract
         rewards_per_hour: { pxj: 0, wuf: 0 }, // Would calculate based on template
         multiplier: 1
       })) || []
      
      // Transform pending rewards from contract format
       const rewards: StakingReward[] = pendingRewards.map((reward: PendingReward) => ({
         token: reward.quantity.split(' ')[1], // Extract token symbol from asset format
         amount: parseFloat(reward.quantity.split(' ')[0]), // Extract amount
         contract: reward.contract,
         precision: 4
       }))
      
      const totalStakingPower = stakedNfts.reduce((sum, nft) => {
        const hourlyRewards = Object.values(nft.rewards_per_hour).reduce((a, b) => a + b, 0)
        return sum + (hourlyRewards * nft.multiplier)
      }, 0)
      
      const estimatedDailyRewards: StakingReward[] = [
        {
          token: 'PXJ',
          amount: stakedNfts.reduce((sum, nft) => sum + (nft.rewards_per_hour.pxj || 0) * 24, 0),
          contract: this.PXJ_CONTRACT,
          precision: 4
        },
        {
          token: 'WUF',
          amount: stakedNfts.reduce((sum, nft) => sum + (nft.rewards_per_hour.wuf || 0) * 24, 0),
          contract: this.WUF_CONTRACT,
          precision: 4
        }
      ]
      
      return {
         staked_nfts: stakedNfts,
         pending_rewards: rewards,
         total_staking_power: totalStakingPower,
         estimated_daily_rewards: estimatedDailyRewards
       }
      
    } catch (error) {
      logger.error('getStakingInfo failed', 'StakingService', error as Error, {
        account,
        farm_name: farmName
      })
      
      return {
        staked_nfts: [],
        pending_rewards: [],
        total_staking_power: 0,
        estimated_daily_rewards: []
      }
    }
  }

  /**
   * Stake tokens to tf.waxdao farm
   */
  async stakeTokens(
    session: Session,
    amount: string,
    farmName: string = this.PXJ_FARM_NAME
  ): Promise<TransactionResult> {
    try {
      const actions = [{
        account: this.PXJ_CONTRACT,
        name: 'transfer',
        authorization: [{
          actor: WharfKitService.safeGetActor(session),
          permission: WharfKitService.safeGetPermission(session)
        }],
        data: {
          from: WharfKitService.safeGetActor(session),
          to: this.TOKEN_FARMS_CONTRACT,
          quantity: amount,
          memo: `stake:${farmName}`
        }
      }]

      const result = await this.executeTransactionWithRetry(session, actions)
      
      if (result.success) {
        logger.info('Token staked successfully', 'StakingService', {
          type: 'token_stake',
          user: WharfKitService.safeGetActor(session),
          amount,
          farm_name: farmName
        })
      }
      
      return result
    } catch (error) {
      logger.error('stakeTokens failed', 'StakingService', error as Error, {
        user: WharfKitService.safeGetActor(session),
        amount,
        farm_name: farmName
      })
      
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Unknown error'
      }
    }
  }

