import { useState, useCallback, useRef } from 'react'
import { useWallet } from './useWallet'
import { log } from '../services/loggingService'
import type { TransactionData, TransactionType } from '../services/transactionService'

interface TransactionManagerState {
  activeTransactions: Map<string, TransactionData>
  completedTransactions: TransactionData[]
  isProcessing: boolean
}

interface UseTransactionManagerReturn {
  // State
  activeTransactions: TransactionData[]
  completedTransactions: TransactionData[]
  isProcessing: boolean
  
  // Actions
  startTransaction: (type: TransactionType, data: any) => Promise<string | null>
  getTransaction: (id: string) => TransactionData | null
  cancelTransaction: (id: string) => Promise<boolean>
  clearCompleted: () => void
  
  // Status tracking
  getActiveCount: () => number
  getCompletedCount: () => number
  hasFailedTransactions: () => boolean
}

export const useTransactionManager = (): UseTransactionManagerReturn => {
  const [state, setState] = useState<TransactionManagerState>({
    activeTransactions: new Map(),
    completedTransactions: [],
    isProcessing: false
  })
  
  const { 
    executeTransaction, 
    getTransaction: getWalletTransaction,
    cancelTransaction: cancelWalletTransaction 
  } = useWallet()
  
  const pollIntervals = useRef<Map<string, NodeJS.Timeout>>(new Map())

  const updateTransaction = useCallback((id: string, updates: Partial<TransactionData>) => {
    setState(prev => {
      const newActiveTransactions = new Map(prev.activeTransactions)
      const existing = newActiveTransactions.get(id)
      
      if (existing) {
        const updated = { ...existing, ...updates }
        
        // If transaction is completed, move to completed list
        if (updated.status === 'success' || updated.status === 'failed' || updated.status === 'cancelled') {
          newActiveTransactions.delete(id)
          
          // Clear polling interval
          const interval = pollIntervals.current.get(id)
          if (interval) {
            clearInterval(interval)
            pollIntervals.current.delete(id)
          }
          
          return {
            ...prev,
            activeTransactions: newActiveTransactions,
            completedTransactions: [updated, ...prev.completedTransactions.slice(0, 49)], // Keep last 50
            isProcessing: newActiveTransactions.size > 0
          }
        } else {
          newActiveTransactions.set(id, updated)
          return {
            ...prev,
            activeTransactions: newActiveTransactions
          }
        }
      }
      
      return prev
    })
  }, [])

  const pollTransactionStatus = useCallback(async (id: string) => {
    try {
      if (!getWalletTransaction) {
        log.warn('getWalletTransaction not available')
        return
      }
      const txData = await getWalletTransaction(id)
      if (txData) {
        updateTransaction(id, txData)
      }
    } catch (error) {
      log.error(`Error polling transaction ${id}:`, 'useTransactionManager.pollTransactionStatus', error as Error)
      updateTransaction(id, { 
        status: 'failed', 
        error: 'Failed to check transaction status' 
      })
    }
  }, [getWalletTransaction, updateTransaction])

  const startPolling = useCallback((id: string) => {
    // Clear existing interval if any
    const existingInterval = pollIntervals.current.get(id)
    if (existingInterval) {
      clearInterval(existingInterval)
    }
    
    // Start new polling interval
    const interval = setInterval(() => {
      pollTransactionStatus(id)
    }, 2000) // Poll every 2 seconds
    
    pollIntervals.current.set(id, interval)
    
    // Auto-cleanup after 5 minutes
    setTimeout(() => {
      const currentInterval = pollIntervals.current.get(id)
      if (currentInterval === interval) {
        clearInterval(interval)
        pollIntervals.current.delete(id)
        updateTransaction(id, { 
          status: 'failed', 
          error: 'Transaction timeout - please check manually' 
        })
      }
    }, 5 * 60 * 1000)
  }, [pollTransactionStatus, updateTransaction])

  const startTransaction = useCallback(async (type: TransactionType, data: any): Promise<string | null> => {
    try {
      setState(prev => ({ ...prev, isProcessing: true }))
      
      if (!executeTransaction) {
        throw new Error('executeTransaction not available - wallet not connected')
      }
      
      const transactionId = await executeTransaction(type, data)
      
      if (transactionId) {
        // Add to active transactions
        const newTransaction: TransactionData = {
          id: transactionId,
          type,
          status: 'pending',
          timestamp: Date.now(),
          account: '', // Will be set by the transaction service
          actions: [], // Will be populated by the transaction service
          metadata: data // Store the data as metadata
        }
        
        setState(prev => {
          const newActiveTransactions = new Map(prev.activeTransactions)
          newActiveTransactions.set(transactionId, newTransaction)
          
          return {
            ...prev,
            activeTransactions: newActiveTransactions
          }
        })
        
        // Start polling for status updates
        startPolling(transactionId)
        
        return transactionId
      }
      
      return null
    } catch (error) {
      log.error('Error starting transaction:', 'useTransactionManager.startTransaction', error as Error)
      setState(prev => ({ ...prev, isProcessing: false }))
      throw error
    }
  }, [executeTransaction, startPolling])

  const getTransaction = useCallback((id: string): TransactionData | null => {
    // Check active transactions first
    const active = state.activeTransactions.get(id)
    if (active) return active
    
    // Check completed transactions
    const completed = state.completedTransactions.find(tx => tx.id === id)
    return completed || null
  }, [state.activeTransactions, state.completedTransactions])

  const cancelTransaction = useCallback(async (id: string): Promise<boolean> => {
    try {
      if (!cancelWalletTransaction) {
        log.warn('cancelWalletTransaction not available')
        return false
      }
      
      const success = await cancelWalletTransaction(id)
      
      if (success) {
        updateTransaction(id, { 
          status: 'cancelled',
          error: 'Transaction cancelled by user'
        })
      }
      
      return success
    } catch (error) {
      log.error('Error cancelling transaction:', 'useTransactionManager.cancelTransaction', error as Error)
      return false
    }
  }, [cancelWalletTransaction, updateTransaction])

  const clearCompleted = useCallback(() => {
    setState(prev => ({
      ...prev,
      completedTransactions: []
    }))
  }, [])

  const getActiveCount = useCallback(() => {
    return state.activeTransactions.size
  }, [state.activeTransactions.size])

  const getCompletedCount = useCallback(() => {
    return state.completedTransactions.length
  }, [state.completedTransactions.length])

  const hasFailedTransactions = useCallback(() => {
    return state.completedTransactions.some(tx => tx.status === 'failed')
  }, [state.completedTransactions])

  return {
    // State
    activeTransactions: Array.from(state.activeTransactions.values()),
    completedTransactions: state.completedTransactions,
    isProcessing: state.isProcessing,
    
    // Actions
    startTransaction,
    getTransaction,
    cancelTransaction,
    clearCompleted,
    
    // Status tracking
    getActiveCount,
    getCompletedCount,
    hasFailedTransactions
  }
}

export default useTransactionManager