  private static ATOMIC_ASSETS_CONTRACT = 'atomicassets'
  private static PXJ_CONTRACT = 'pixeljourney'

  private static RECYCLE_BLEND_ID = 37700
  private static RECYCLE_COST = '12000.00000000 PXJ'
  
  // Pack unpack blend IDs - Updated with correct values
  private static PACK_TEMPLATE_ID = '820134' // NeftyBlocks pack template ID
  private static PACK_RECIPE_ID = 37701 // Recipe ID for unpacking
  private static GENERIC_PACK_BLEND_ID = 37702 // Generic pack blend ID

  static async recyclePixal(assetId: string, userAccount: string): Promise<BlendTransaction> {
    // Get current blockchain info for transaction
    const blockInfo = await this.getBlockInfo()
    
    const transaction: BlendTransaction = {
      expiration: this.getExpirationTime(),
      ref_block_num: blockInfo.ref_block_num,
      ref_block_prefix: blockInfo.ref_block_prefix,
      max_net_usage_words: 0,
      max_cpu_usage_ms: 0,
      delay_sec: 0,
      context_free_actions: [],
      actions: [
        // Skip CPU pay action as mentioned in requirements
        {
          account: this.BLEND_CONTRACT,
          name: 'openbal',
          authorization: [{
            actor: userAccount,
            permission: 'active'
          }],
          data: {
            owner: userAccount,
            token_symbol: '8,PXJ'
          }
        },
        {
          account: this.PXJ_CONTRACT,
          name: 'transfer',
          authorization: [{
            actor: userAccount,
            permission: 'active'
          }],
          data: {
            from: userAccount,
            to: this.BLEND_CONTRACT,
            quantity: this.RECYCLE_COST,
            memo: 'deposit'
          }
        },
        {
          account: this.BLEND_CONTRACT,
          name: 'announcedepo',
          authorization: [{
            actor: userAccount,
            permission: 'active'
          }],
          data: {
            owner: userAccount,
            count: 1
          }
        },
        {
          account: this.ATOMIC_ASSETS_CONTRACT,
          name: 'transfer',
          authorization: [{
            actor: userAccount,
            permission: 'active'
          }],
          data: {
            from: userAccount,
            to: this.BLEND_CONTRACT,
            asset_ids: [assetId],
            memo: 'deposit'
          }
        },
        {
          account: this.BLEND_CONTRACT,
          name: 'nosecfuse',
          authorization: [{
            actor: userAccount,
            permission: 'active'
          }],
          data: {
            claimer: userAccount,
            blend_id: this.RECYCLE_BLEND_ID,
            transferred_assets: [assetId],
            own_assets: []
          }
        }
      ],
      transaction_extensions: []
    }

    return transaction
  }

