  private static async getBlockInfo(): Promise<{ ref_block_num: number; ref_block_prefix: number }> {
    try {
      // Fetch real block info from WAX blockchain
      const waxEndpoints = [
        API_ENDPOINTS.WAX_CHAIN.EOSRIO,
        API_ENDPOINTS.WAX_CHAIN.WAXSWEDEN,
        API_ENDPOINTS.WAX_CHAIN.GREYMASS
      ]
      
      for (const endpoint of waxEndpoints) {
        try {
          const response = await fetch(`${endpoint}/v1/chain/get_info`, {
            method: 'GET',
            headers: {
              'Accept': 'application/json'
            }
          })
          
          if (!response.ok) continue
          
          const chainInfo = await response.json()
          
          // Extract block number and prefix from head block ID
          const headBlockId = chainInfo.head_block_id
          
          // Use last irreversible block for safety
          const refBlockNum = chainInfo.last_irreversible_block_num & 0xFFFF
          
          // Extract prefix from block ID (first 4 bytes after reversing)
          const blockIdBytes = headBlockId.match(/.{2}/g)?.slice(4, 8) || ['00', '00', '00', '00']
          const refBlockPrefix = parseInt(blockIdBytes.reverse().join(''), 16)
          
          return {
            ref_block_num: refBlockNum,
            ref_block_prefix: refBlockPrefix
          }
        } catch (endpointError) {
          logger.warn('Failed to fetch block info from endpoint', 'NEFTY_BLEND', { endpoint, error: endpointError })
          continue
        }
      }
      
      // Fallback if all endpoints fail
      throw new Error('All WAX endpoints failed')
    } catch (error) {
      ErrorMessageService.logError('NeftyBlendService.getBlockInfo', error)
      
      // Return reasonable fallback values based on current time
      const now = Date.now()
      return {
        ref_block_num: (now / 500) & 0xFFFF, // Approximate block number
        ref_block_prefix: now & 0xFFFFFFFF   // Use timestamp as prefix
      }
    }
  }

  private static getExpirationTime(): string {
    const now = new Date()
    const expiration = new Date(now.getTime() + 30 * 60 * 1000) // 30 minutes from now
    return expiration.toISOString().slice(0, -5) // Remove milliseconds and Z
  }

  static async executeRecycle(transaction: BlendTransaction, wallet: Session): Promise<boolean> {
    try {
      if (!wallet || !wallet.transact) {
        throw new Error('Wallet not connected or does not support transactions')
      }

      const result = await wallet.transact({
        actions: transaction.actions
      })

      logger.info('Recycle transaction successful', 'NEFTY_BLEND', { result })
      return true
    } catch (error) {
      ErrorMessageService.logError('NeftyBlendService.executeRecycle', error, { 
          blendId: this.RECYCLE_BLEND_ID, 
          recycleCost: this.RECYCLE_COST,
          actionsCount: transaction.actions.length,
          walletActor: wallet.actor
        })
      throw error
    }
  }

  static async executeUnpack(transaction: BlendTransaction, wallet: Session): Promise<unknown> {
    try {
      if (!wallet || !wallet.transact) {
        throw new Error('Wallet not connected or does not support transactions')
      }

      const result = await wallet.transact({
        actions: transaction.actions
      })

      logger.info('Unpack transaction successful', 'NEFTY_BLEND', { result })
      return result
    } catch (error) {
      ErrorMessageService.logError('NeftyBlendService.executeUnpack', error, {
        actionsCount: transaction.actions.length,
        walletActor: wallet.actor
      })
      throw error
    }
  }

  static isPackNFT(nft: {
    template?: {
      name?: string
      template_id?: string
    }
  }): boolean {
    if (!nft || !nft.template) return false
    
    // Check if it's a known pack type
    const packKeywords = ['pack', 'container', 'box', 'chest']
    const templateName = (nft.template.name || '').toLowerCase()
    const hasPackKeyword = packKeywords.some(keyword => templateName.includes(keyword))
    
    // Check if it's the specific NeftyBlocks pack template
    const isNeftyBlocksPack = nft.template?.template_id === this.PACK_TEMPLATE_ID
    
    return hasPackKeyword || isNeftyBlocksPack
  }

  static getPackType(nft: {
    template?: {
      name?: string
      template_id?: string
    }
  }): 'pixel_ingredient' | 'generic' | null {
    if (!this.isPackNFT(nft)) return null
    
    // Check if it's the NeftyBlocks pack template
    if (nft.template?.template_id === this.PACK_TEMPLATE_ID) {
      return 'pixel_ingredient'
    }
    
    return 'generic'
  }

  static async unpackPixelPack(assetId: string, userAccount: string): Promise<BlendTransaction> {
    const blockInfo = await this.getBlockInfo()
    
    const transaction: BlendTransaction = {
      expiration: this.getExpirationTime(),
      ref_block_num: blockInfo.ref_block_num,
      ref_block_prefix: blockInfo.ref_block_prefix,
      max_net_usage_words: 0,
      max_cpu_usage_ms: 0,
      delay_sec: 0,
      context_free_actions: [],
      actions: [
        {
          account: this.ATOMIC_ASSETS_CONTRACT,
          name: 'transfer',
          authorization: [{
            actor: userAccount,
            permission: 'active'
          }],
          data: {
            from: userAccount,
            to: 'neftyblocksp',
            asset_ids: [assetId],
            memo: 'unbox'
          }
        }
      ],
      transaction_extensions: []
    }

    return transaction
  }

  static async unpackPixelPacksBulk(assetIds: string[], userAccount: string): Promise<BlendTransaction[]> {
    const transactions: BlendTransaction[] = []
    
    // Batch assets in groups of 20
    for (let i = 0; i < assetIds.length; i += 20) {
      const batch = assetIds.slice(i, i + 20)
      const blockInfo = await this.getBlockInfo()
      
      const transaction: BlendTransaction = {
        expiration: this.getExpirationTime(),
        ref_block_num: blockInfo.ref_block_num,
        ref_block_prefix: blockInfo.ref_block_prefix,
        max_net_usage_words: 0,
        max_cpu_usage_ms: 0,
        delay_sec: 0,
        context_free_actions: [],
        actions: batch.map(assetId => ({
          account: this.ATOMIC_ASSETS_CONTRACT,
          name: 'transfer',
          authorization: [{
            actor: userAccount,
            permission: 'active'
          }],
          data: {
            from: userAccount,
            to: 'neftyblocksp',
            asset_ids: [assetId],
            memo: 'unbox'
          }
        })),
        transaction_extensions: []
      }
      
      transactions.push(transaction)
    }
    
    return transactions
  }

  static isPixelPack(nft: {
    template?: {
      template_id?: string
    }
  }): boolean {
    return nft.template?.template_id === this.PACK_TEMPLATE_ID
  }
