import React, { useState, useEffect, useCallback } from 'react'
import { useWallet } from '../contexts/WalletContext'
import { useNotifications } from '../hooks/useNotifications'
import { alcorService } from '../services/alcorService'

import { apiService } from '../services/api'
import { log } from '../services/loggingService'
import TokenDropdown from './TokenDropdown'
import '../css/swap-interface.css'

interface SwapInterfaceProps {
  isModal?: boolean
  onClose?: () => void
  initialSellAmount?: number
  initialBuyAmount?: number
}

const SwapInterface: React.FC<SwapInterfaceProps> = ({ 
  isModal = false, 
  onClose, 
  initialSellAmount = 0,
  initialBuyAmount = 1
}) => {
  const { showSuccess, showError } = useNotifications()
  const { account, session } = useWallet()
  const [sellAmount, setSellAmount] = useState<string>(initialSellAmount.toString())
  const [buyAmount, setBuyAmount] = useState<string>(initialBuyAmount.toString())
  const [sellToken, setSellToken] = useState<string>('WAX')
  const [buyToken, setBuyToken] = useState<string>('WPIXAL')
  const [loading, setLoading] = useState(false)
  const [quoteData, setQuoteData] = useState<any>(null)
  const [slippage, setSlippage] = useState<number>(0.5)
  const [showSlippageSettings, setShowSlippageSettings] = useState(false)
  const [balances, setBalances] = useState<Record<string, string>>({})
  const [isLoadingBalances, setIsLoadingBalances] = useState(false)
  const [alcorTokens, setAlcorTokens] = useState<any[]>([])

  // Load Alcor tokens
  const loadAlcorTokens = useCallback(async () => {
    try {
      const tokens = await alcorService.getAllTokens()
      setAlcorTokens(tokens)
      return tokens
    } catch (error) {
      log.error('Failed to load Alcor tokens', 'SwapInterface.loadAlcorTokens', error as Error)
      return []
    }
  }, [])

  // Favored tokens that should appear first and have priority
   const favoredTokenConfigs: Record<string, { contract: string; precision: number; priority: number }> = {
     'PXJ': { contract: 'pixeljourney', precision: 8, priority: 1 },
     'WPIXAL': { contract: 'pixeljourney', precision: 8, priority: 2 },
     'WAX': { contract: 'eosio.token', precision: 8, priority: 3 },
     'KING': { contract: 'alpha.waxfun', precision: 8, priority: 4 },
     'WAXUSDC': { contract: 'eth.token', precision: 8, priority: 5 },
     'WAXWBTC': { contract: 'eth.token', precision: 8, priority: 6 },
     'WUF': { contract: 'wuffi', precision: 4, priority: 7 },
     'LSWAX': { contract: 'token.defi', precision: 8, priority: 8 },
     'BRWL': { contract: 'brawlertoken', precision: 4, priority: 9 },
     'TLM': { contract: 'alien.worlds', precision: 4, priority: 10 },
     'WOMBAT': { contract: 'wombattoken', precision: 4, priority: 11 },
     'TACO': { contract: 't.taco', precision: 8, priority: 12 },
     'SHING': { contract: 't.taco', precision: 8, priority: 13 },
     'YEET': { contract: 'token.yeet', precision: 4, priority: 14 },
     'DMT': { contract: 'defiminingtk', precision: 4, priority: 15 },
     'FGL': { contract: 'fgltokenmgmt', precision: 4, priority: 16 },
     'SOREN': { contract: 'farfromhomes', precision: 4, priority: 17 }
   }

   // Get token info from favored tokens first, then Alcor tokens
   const getTokenInfo = useCallback((symbol: string) => {
     // Check favored tokens first
     const favoredToken = favoredTokenConfigs[symbol]
     if (favoredToken) {
       return {
         symbol,
         contract: favoredToken.contract,
         precision: favoredToken.precision,
         token_id: `${favoredToken.contract}-${symbol}`,
         priority: favoredToken.priority
       }
     }
     
     // Then check Alcor tokens
     const token = alcorTokens.find(t => t.symbol === symbol)
     if (token) {
       return {
         symbol: token.symbol,
         contract: token.contract,
         precision: token.precision,
         token_id: token.id,
         priority: 999 // Lower priority for non-favored tokens
       }
     }
     
     return null
   }, [alcorTokens])

  // Load user balances with live updates
  const loadBalances = useCallback(async () => {
    if (!account) {
      setBalances({})
      return
    }
    
    setIsLoadingBalances(true)
    try {
      // Ensure Alcor tokens are loaded
      if (alcorTokens.length === 0) {
        await loadAlcorTokens()
      }
      
      const lightApiResponse = await apiService.getLightApiBalances(account)
      const newBalances: Record<string, string> = {}
      
      if (lightApiResponse && lightApiResponse.balances) {
        // Process balances from Light API
        for (const balance of lightApiResponse.balances) {
          const tokenInfo = getTokenInfo(balance.currency)
          if (tokenInfo && tokenInfo.contract === balance.contract) {
            newBalances[balance.currency] = `${balance.amount} ${balance.currency}`
          }
        }
        
        // Ensure favored tokens are included even with 0 balance
        Object.keys(favoredTokenConfigs).forEach(symbol => {
          if (!newBalances[symbol]) {
            newBalances[symbol] = `0.0000 ${symbol}`
          }
        })
      } else {
        // Fallback to favored tokens with 0 balance
        Object.keys(favoredTokenConfigs).forEach(symbol => {
          newBalances[symbol] = `0.0000 ${symbol}`
        })
      }
      
      setBalances(newBalances)
    } catch (error) {
      log.error('Error loading balances', 'SwapInterface.loadBalances', error as Error)
      showError('Failed to load token balances')
      
      // Fallback to favored tokens with 0 balance on error
      const defaultBalances: Record<string, string> = {}
      Object.keys(favoredTokenConfigs).forEach(symbol => {
        defaultBalances[symbol] = `0.0000 ${symbol}`
      })
      setBalances(defaultBalances)
    } finally {
      setIsLoadingBalances(false)
    }
  }, [account, showError, alcorTokens, loadAlcorTokens])

  useEffect(() => {
    loadAlcorTokens()
  }, [])

  useEffect(() => {
    loadBalances()
    
    // Set up interval for live balance updates every 30 seconds
    const balanceInterval = setInterval(loadBalances, 30000)
    
    return () => clearInterval(balanceInterval)
  }, [loadBalances])

  // Manual quote refresh function
  const getQuote = useCallback(async () => {
    if (!sellAmount || parseFloat(sellAmount) <= 0) {
      setQuoteData(null)
      setBuyAmount('0')
      return
    }

    try {
      setLoading(true)
      
      // Get token info for both sell and buy tokens
      const sellTokenInfo = getTokenInfo(sellToken)
      const buyTokenInfo = getTokenInfo(buyToken)
      
      if (!sellTokenInfo || !buyTokenInfo) {
        throw new Error(`Token configuration not found for ${!sellTokenInfo ? sellToken : buyToken}`)
      }
      
      // Use generic Alcor swap quote for any token pair
      const quote = await alcorService.getSwapQuoteForPair(
        sellTokenInfo,
        buyTokenInfo,
        sellAmount,
        (slippage / 100).toString()
      )
      
      if (quote && quote.output_amount) {
        setQuoteData(quote)
        setBuyAmount(parseFloat(quote.output_amount).toFixed(sellTokenInfo.precision || 4))
      } else {
        throw new Error('No valid quote received')
      }
    } catch (error) {
      log.error('Error getting swap quote', 'SwapInterface.getQuote', error as Error)
      setQuoteData(null)
      setBuyAmount('0')
    } finally {
      setLoading(false)
    }
  }, [sellAmount, sellToken, buyToken, account, slippage, getTokenInfo])

  // Auto-refresh quote when tokens change (but not on amount changes)
  useEffect(() => {
    if (sellToken && buyToken && sellToken !== buyToken) {
      getQuote()
    }
  }, [sellToken, buyToken, getQuote])

  const handleSwapTokens = () => {
    const tempToken = sellToken
    const tempAmount = sellAmount
    
    setSellToken(buyToken)
    setBuyToken(tempToken)
    setSellAmount(buyAmount)
    setBuyAmount(tempAmount)
  }

  const handleMaxClick = () => {
    const maxBalance = balances[sellToken] || `0.0000 ${sellToken}`
    const amount = maxBalance.split(' ')[0]
    setSellAmount(amount)
  }

  const handlePercentageClick = (percentage: number) => {
    const balanceStr = balances[sellToken] || `0.0000 ${sellToken}`
    const maxBalance = parseFloat(balanceStr.split(' ')[0])
    const amount = (maxBalance * percentage / 100).toFixed(4)
    setSellAmount(amount)
  }

  const executeSwap = async () => {
    if (!account || !session) {
      showError('Please connect your wallet first')
      return
    }

    if (!quoteData) {
      showError('No quote available')
      return
    }

    try {
      setLoading(true)
      
      // Get token info for both sell and buy tokens
      const sellTokenInfo = getTokenInfo(sellToken)
      const buyTokenInfo = getTokenInfo(buyToken)
      
      if (!sellTokenInfo || !buyTokenInfo) {
        throw new Error(`Token configuration not found for ${!sellTokenInfo ? sellToken : buyToken}`)
      }
      
      // Use generic Alcor swap execution for any token pair
      const swapTransaction: any = await alcorService.executeSwapForPair(
        sellTokenInfo,
        buyTokenInfo,
        sellAmount,
        account,
        'EXACT_INPUT',
        slippage / 100
      )
      
      const result: any = await session.transact({ actions: swapTransaction.actions })
      
      showSuccess(`Successfully swapped ${sellAmount} ${sellToken} for ${buyAmount} ${buyToken}`)
      log.info('Swap transaction completed', 'SwapInterface.executeSwap', {
        sellAmount,
        sellToken,
        buyAmount,
        buyToken,
        transactionId: result?.transaction?.id || result?.transactionId || result?.id || 'unknown'
      })
      
      // Refresh balances after successful swap
      await loadBalances()
      
      // Reset form
      setSellAmount('0')
      setBuyAmount('0')
      setQuoteData(null)
      
      if (isModal && onClose) {
        onClose()
      }
    } catch (error) {
      log.error('Error executing swap', 'SwapInterface.executeSwap', error as Error)
      showError('Failed to execute swap transaction')
    } finally {
      setLoading(false)
    }
  }

  const content = (
    <div className={`swap-interface ${isModal ? 'modal-content' : ''}`}>
      {isModal && (
        <div className="swap-header">
          <h2>Swap Tokens</h2>
          <button className="close-button" onClick={onClose}>×</button>
        </div>
      )}
      
      <div className="swap-container">
        {/* Sell Section */}
        <div className="swap-section sell-section">
          <div className="swap-section-header">
            <span className="section-label">Sell</span>
            <span className="balance">
              Balance: {isLoadingBalances ? 'Loading...' : (balances[sellToken] || `0.0000 ${sellToken}`)}
            </span>
          </div>
          
          <div className="input-container">
            <input
              type="number"
              value={sellAmount}
              onChange={(e) => setSellAmount(e.target.value)}
              placeholder="0"
              className="amount-input"
            />
            
            <div className="token-selector">
              <TokenDropdown
                selectedToken={sellToken}
                onTokenSelect={setSellToken}
                filterByAvailable={false}
                className="sell-token-dropdown"
              />
            </div>
          </div>
          
          <div className="percentage-buttons">
            <button onClick={() => handlePercentageClick(15)}>15%</button>
            <button onClick={() => handlePercentageClick(25)}>25%</button>
            <button onClick={() => handlePercentageClick(50)}>50%</button>
            <button onClick={() => handlePercentageClick(75)}>75%</button>
            <button onClick={handleMaxClick} className="max-button">MAX</button>
          </div>
        </div>

        {/* Swap Arrow */}
        <div className="swap-arrow-container">
          <button className="swap-arrow" onClick={handleSwapTokens}>
            ↓
          </button>
          <button 
            className="refresh-quote-button" 
            onClick={getQuote}
            disabled={loading || !sellAmount || parseFloat(sellAmount) <= 0}
            title="Refresh price quote"
          >
            {loading ? '⟳' : '🔄'}
          </button>
        </div>

        {/* Buy Section */}
        <div className="swap-section buy-section">
          <div className="swap-section-header">
            <span className="section-label">Buy</span>
            <span className="balance">
              Balance: {isLoadingBalances ? 'Loading...' : (balances[buyToken] || `0.0000 ${buyToken}`)}
            </span>
          </div>
          
          <div className="input-container">
            <input
              type="number"
              value={buyAmount}
              readOnly
              placeholder="0"
              className="amount-input"
              title="Output amount is calculated automatically"
            />
            
            <div className="token-selector">
              <TokenDropdown
                selectedToken={buyToken}
                onTokenSelect={setBuyToken}
                filterByAvailable={false}
                className="buy-token-dropdown"
              />
            </div>
          </div>
          
          <div className="percentage-buttons">
            <button onClick={() => handlePercentageClick(15)}>15%</button>
            <button onClick={() => handlePercentageClick(25)}>25%</button>
            <button onClick={() => handlePercentageClick(50)}>50%</button>
            <button onClick={() => handlePercentageClick(75)}>75%</button>
          </div>
        </div>

        {/* Quote Information */}
        {quoteData && (
          <div className="quote-info">
            <div className="quote-row">
              <span className="quote-label">Rate</span>
              <span className="quote-value">
                {quoteData.rate || '0.00340412'} WPIXAL per WAX (0.02%)
              </span>
            </div>
            <div className="quote-row">
              <span className="quote-label">Expected Output</span>
              <span className="quote-value">{quoteData.expectedOutput || buyAmount}</span>
            </div>
            <div className="quote-row">
              <span className="quote-label">Price Impact</span>
              <span className="quote-value">{quoteData.priceImpact || '< 0.01%'}</span>
            </div>
            <div className="quote-row">
              <span className="quote-label">Maximum Send after slippage</span>
              <span className="quote-value">
                {quoteData.maxSent || '309.45013892'} WAX (0.50%)
              </span>
            </div>
          </div>
        )}

        {/* Slippage Settings */}
        <div className="slippage-container">
          <button 
            className="slippage-toggle"
            onClick={() => setShowSlippageSettings(!showSlippageSettings)}
          >
            ⚙️ Multiroute
          </button>
          
          {showSlippageSettings && (
            <div className="slippage-settings">
              <label>Slippage Tolerance</label>
              <div className="slippage-options">
                <button 
                  className={slippage === 0.1 ? 'active' : ''}
                  onClick={() => setSlippage(0.1)}
                >
                  0.1%
                </button>
                <button 
                  className={slippage === 0.5 ? 'active' : ''}
                  onClick={() => setSlippage(0.5)}
                >
                  0.5%
                </button>
                <button 
                  className={slippage === 1.0 ? 'active' : ''}
                  onClick={() => setSlippage(1.0)}
                >
                  1.0%
                </button>
                <input
                  type="number"
                  value={slippage}
                  onChange={(e) => setSlippage(parseFloat(e.target.value) || 0.5)}
                  step="0.1"
                  min="0.1"
                  max="50"
                  className="custom-slippage"
                />
              </div>
            </div>
          )}
        </div>

        {/* Swap Button */}
        <button 
          className="swap-button"
          onClick={executeSwap}
          disabled={loading || !account || !quoteData || parseFloat(sellAmount) <= 0}
        >
          {loading ? 'Processing ...' : `Swap ${sellToken} To ${buyToken}`}
        </button>
      </div>


    </div>
  )

  if (isModal) {
    return (
      <div className="swap-modal-overlay" onClick={onClose}>
        <div onClick={(e) => e.stopPropagation()}>
          {content}
        </div>
      </div>
    )
  }

  return content
}

export default SwapInterface