import React, { useState, useCallback, useEffect } from 'react'
import '../css/attribute-filter.css'

interface PixalAttributeFilterProps {
  onFiltersChange: (filters: Record<string, string[]>) => void
  initialFilters?: Record<string, string[]>
}

interface FilterState {
  [attributeName: string]: {
    isExpanded: boolean
    selectedValues: Set<string>
    searchTerm: string
  }
}

// Hardcoded Pixal attributes to prevent API refresh loops
const PIXAL_ATTRIBUTES = {
  'Background': [
    'Nefty Spot',
    'Blue Sky',
    'Forest',
    'Desert',
    'Ocean',
    'Mountain',
    'City',
    'Space',
    'Sunset',
    'Rainbow'
  ],
  'Body': [
    'Pink-Blue Overalls',
    'Red Shirt',
    'Blue Hoodie',
    'Green Jacket',
    'Yellow Dress',
    'Purple Suit',
    'Orange Tank',
    'Black Tee',
    'White Polo',
    'Gray Sweater'
  ],
  'Eyes': [
    'Kawaii Green',
    'Brown',
    'Blue',
    'Hazel',
    'Gray',
    'Amber',
    'Violet',
    'Red',
    'Black',
    'Gold'
  ],
  'Head': [
    'Grey',
    'Black Hair',
    'Brown Hair',
    'Blonde Hair',
    'Red Hair',
    'Blue Hair',
    'Green Hair',
    'Purple Hair',
    'Bald',
    'Hat'
  ]
}

export const PixalAttributeFilter: React.FC<PixalAttributeFilterProps> = ({
  onFiltersChange,
  initialFilters = {}
}) => {
  const [filterState, setFilterState] = useState<FilterState>({})

  // Initialize filter state
  useEffect(() => {
    const newFilterState: FilterState = {}
    
    Object.keys(PIXAL_ATTRIBUTES).forEach(attributeName => {
      newFilterState[attributeName] = {
        isExpanded: false,
        selectedValues: new Set(initialFilters[attributeName] || []),
        searchTerm: ''
      }
    })
    
    setFilterState(newFilterState)
  }, []) // Only run once on mount

  // Handle attribute value selection
  const handleValueToggle = useCallback((attributeName: string, value: string) => {
    setFilterState(prev => {
      const newState = { ...prev }
      if (!newState[attributeName]) {
        newState[attributeName] = {
          isExpanded: false,
          selectedValues: new Set(),
          searchTerm: ''
        }
      }
      
      const selectedValues = new Set(newState[attributeName].selectedValues)
      if (selectedValues.has(value)) {
        selectedValues.delete(value)
      } else {
        selectedValues.add(value)
      }
      
      newState[attributeName] = {
        ...newState[attributeName],
        selectedValues
      }
      
      return newState
    })
  }, [])

  // Handle attribute expansion toggle
  const handleAttributeToggle = useCallback((attributeName: string) => {
    setFilterState(prev => ({
      ...prev,
      [attributeName]: {
        ...prev[attributeName],
        isExpanded: !prev[attributeName]?.isExpanded
      }
    }))
  }, [])

  // Handle search term change
  const handleSearchChange = useCallback((attributeName: string, searchTerm: string) => {
    setFilterState(prev => ({
      ...prev,
      [attributeName]: {
        ...prev[attributeName],
        searchTerm
      }
    }))
  }, [])

  // Clear all filters
  const clearAllFilters = useCallback(() => {
    const clearedState: FilterState = {}
    Object.keys(PIXAL_ATTRIBUTES).forEach(attributeName => {
      clearedState[attributeName] = {
        isExpanded: false,
        selectedValues: new Set(),
        searchTerm: ''
      }
    })
    setFilterState(clearedState)
  }, [])

  // Emit filter changes
  useEffect(() => {
    const filters: Record<string, string[]> = {}
    Object.entries(filterState).forEach(([attributeName, state]) => {
      if (state.selectedValues.size > 0) {
        filters[attributeName] = Array.from(state.selectedValues)
      }
    })
    onFiltersChange(filters)
  }, [filterState, onFiltersChange])

  // Get filtered attribute values based on search term
  const getFilteredValues = (attributeName: string) => {
    const searchTerm = filterState[attributeName]?.searchTerm?.toLowerCase() || ''
    const values = PIXAL_ATTRIBUTES[attributeName as keyof typeof PIXAL_ATTRIBUTES] || []
    
    if (!searchTerm) return values
    
    return values.filter(value => 
      value.toLowerCase().includes(searchTerm)
    )
  }

  // Calculate total selected filters
  const totalSelected = Object.values(filterState).reduce(
    (total, state) => total + state.selectedValues.size,
    0
  )

  return (
    <div className="attribute-filter-container">
      <div className="filter-header">
        <h3>Pixal Traits</h3>
        {totalSelected > 0 && (
          <button className="clear-all-button" onClick={clearAllFilters}>
            Clear All ({totalSelected})
          </button>
        )}
      </div>

      {Object.entries(PIXAL_ATTRIBUTES).map(([attributeName]) => {
        const state = filterState[attributeName]
        const filteredValues = getFilteredValues(attributeName)
        const selectedCount = state?.selectedValues.size || 0

        return (
          <div key={attributeName} className="filter-group">
            <div 
              className="filter-group-header"
              onClick={() => handleAttributeToggle(attributeName)}
            >
              <div className="attribute-name">
                {attributeName}
                {selectedCount > 0 && (
                  <span className="selected-count">{selectedCount}</span>
                )}
              </div>
              <div className={`expand-icon ${state?.isExpanded ? 'expanded' : ''}`}>
                ▼
              </div>
            </div>

            {state?.isExpanded && (
              <div className="filter-group-content">
                <input
                  type="text"
                  placeholder={`Search ${attributeName.toLowerCase()}...`}
                  value={state.searchTerm}
                  onChange={(e) => handleSearchChange(attributeName, e.target.value)}
                  className="attribute-search"
                />

                <div className="attribute-values">
                  {filteredValues.length > 0 ? (
                    filteredValues.map(value => {
                      const isSelected = state.selectedValues.has(value)
                      return (
                        <div
                          key={value}
                          className={`attribute-value-item ${isSelected ? 'selected' : ''}`}
                          onClick={() => handleValueToggle(attributeName, value)}
                        >
                          <span className="attribute-value-text">{value}</span>
                        </div>
                      )
                    })
                  ) : (
                    <div className="no-results">
                      No {attributeName.toLowerCase()} found matching "{state.searchTerm}"
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
        )
      })}
    </div>
  )
}