// Real-time auction service for AtomicMarket integration
import { ErrorMessageService } from './errorMessageService'
import { apiService } from './api'
import { logger } from './loggingService'
import { API_ENDPOINTS } from '../config/marketplaceConfig'

export interface AuctionBid {
  bidder: string
  bid_amount: string
  timestamp: string
  transaction_id?: string
}

export interface AuctionUpdate {
  auction_id: string
  current_bid?: string
  bidder?: string
  end_time: string
  status: 'active' | 'ended' | 'cancelled'
  bid_count: number
  last_bid_time?: string
}

export interface AuctionSubscription {
  auction_id: string
  callback: (update: AuctionUpdate) => void
}

class AuctionService {
  private subscriptions: Map<string, AuctionSubscription[]> = new Map()
  private pollInterval: NodeJS.Timeout | null = null
  private isPolling = false
  private readonly POLL_INTERVAL = 30000 // 30 seconds - reduced frequency
  private readonly MAX_REQUESTS_PER_MINUTE = 30
  private readonly REQUEST_DELAY = 2000 // 2 seconds between requests
  private lastRequestTime = 0
  private requestCount = 0
  private requestResetTime = Date.now() + 60000
  private auctionCache: Map<string, { data: AuctionUpdate; timestamp: number }> = new Map()
  private readonly CACHE_DURATION = 15000 // 15 seconds cache

  // Subscribe to auction updates
  subscribe(auctionId: string, callback: (update: AuctionUpdate) => void): () => void {
    if (!this.subscriptions.has(auctionId)) {
      this.subscriptions.set(auctionId, [])
    }
    
    const subscription: AuctionSubscription = { auction_id: auctionId, callback }
    this.subscriptions.get(auctionId)!.push(subscription)
    
    // Start polling if not already started
    this.startPolling()
    
    // Return unsubscribe function
    return () => {
      const subs = this.subscriptions.get(auctionId)
      if (subs) {
        const index = subs.indexOf(subscription)
        if (index > -1) {
          subs.splice(index, 1)
        }
        
        // Remove auction from subscriptions if no more subscribers
        if (subs.length === 0) {
          this.subscriptions.delete(auctionId)
        }
      }
      
      // Stop polling if no more subscriptions
      if (this.subscriptions.size === 0) {
        this.stopPolling()
      }
    }
  }

  // Start polling for auction updates
  private startPolling(): void {
    if (this.isPolling) return
    
    this.isPolling = true
    this.pollInterval = setInterval(() => {
      this.pollAuctionUpdates()
    }, this.POLL_INTERVAL)
  }

  // Stop polling
  private stopPolling(): void {
    if (this.pollInterval) {
      clearInterval(this.pollInterval)
      this.pollInterval = null
    }
    this.isPolling = false
  }

  // Poll for auction updates
  private async pollAuctionUpdates(): Promise<void> {
    const auctionIds = Array.from(this.subscriptions.keys())
    
    if (auctionIds.length === 0) {
      this.stopPolling()
      return
    }

    try {
      // Clear expired cache entries periodically
      this.clearExpiredCache()
      
      // Fetch updates for all subscribed auctions
      const updates = await this.fetchAuctionUpdates(auctionIds)
      
      // Notify subscribers
      updates.forEach(update => {
        const subscribers = this.subscriptions.get(update.auction_id)
        if (subscribers) {
          subscribers.forEach(sub => {
            try {
              sub.callback(update)
            } catch (error) {
              ErrorMessageService.logError('AuctionService.pollAuctionUpdates.callback', error, {
                auctionId: update.auction_id
              })
            }
          })
        }
      })
    } catch (error) {
      ErrorMessageService.logError('AuctionService.pollAuctionUpdates', error, {
        auctionIds
      })
    }
  }

  // Fetch auction updates from API with rate limiting and caching
  private async fetchAuctionUpdates(auctionIds: string[]): Promise<AuctionUpdate[]> {
    const updates: AuctionUpdate[] = []
    const now = Date.now()
    
    // Reset request count if minute has passed
    if (now > this.requestResetTime) {
      this.requestCount = 0
      this.requestResetTime = now + 60000
    }
    
    for (const auctionId of auctionIds) {
      // Check cache first
      const cached = this.auctionCache.get(auctionId)
      if (cached && (now - cached.timestamp) < this.CACHE_DURATION) {
        updates.push(cached.data)
        continue
      }
      
      // Check rate limits
      if (this.requestCount >= this.MAX_REQUESTS_PER_MINUTE) {
        logger.warn('Rate limit reached, skipping auction', 'AUCTION', { auctionId })
        // Use cached data if available, even if expired
        if (cached) {
          updates.push(cached.data)
        }
        continue
      }
      
      // Ensure minimum delay between requests
      const timeSinceLastRequest = now - this.lastRequestTime
      if (timeSinceLastRequest < this.REQUEST_DELAY) {
        await new Promise(resolve => setTimeout(resolve, this.REQUEST_DELAY - timeSinceLastRequest))
      }
      
      try {
        const url = `${apiService.getCurrentAtomicEndpoint()}/atomicmarket/v1/auctions/${auctionId}`
        const response = await apiService.fetchWithRetry(url)
        const data = await response.json()
        
        this.requestCount++
        this.lastRequestTime = Date.now()
        
        if (data.success && data.data) {
          const auction = data.data
          const update: AuctionUpdate = {
            auction_id: auctionId,
            current_bid: auction.current_bid,
            bidder: auction.current_bidder,
            end_time: auction.end_time,
            status: this.getAuctionStatus(auction),
            bid_count: auction.bid_count || 0,
            last_bid_time: auction.updated_at_time
          }
          
          // Cache the result
          this.auctionCache.set(auctionId, {
            data: update,
            timestamp: Date.now()
          })
          
          updates.push(update)
        }
      } catch (error) {
        ErrorMessageService.logError('AuctionService.fetchAuctionUpdates.single', error, {
          auctionId
        })
        
        // Use cached data if available on error
        if (cached) {
          updates.push(cached.data)
        }
      }
    }
    
    return updates
  }

  // Determine auction status
  private getAuctionStatus(auction: { end_time: string; state: number }): 'active' | 'ended' | 'cancelled' {
    const now = Date.now()
    const endTime = new Date(auction.end_time + 'Z').getTime()
    
    if (auction.state === 3) return 'cancelled'
    if (now > endTime) return 'ended'
    return 'active'
  }

  // Get auction bid history
  async getAuctionBids(auctionId: string): Promise<AuctionBid[]> {
    try {
      // Get auction details first to find the seller and asset info
      const auctionResponse = await apiService.getAuctions({ 
        limit: 1 
      })
      
      if (!auctionResponse.success || !auctionResponse.data.length) {
        return []
      }
      
      const auction = auctionResponse.data[0]
      const assetIds = auction.assets.map(asset => asset.asset_id)
      
      // Query Hyperion for auction bid transactions
      const bidHistory = await this.fetchAuctionBidHistory(auctionId, assetIds)
      
      return bidHistory.sort((a, b) => 
        new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime()
      )
    } catch (error) {
      ErrorMessageService.logError('AuctionService.getAuctionBids', error, { auctionId })
      return []
    }
  }

  // Fetch real auction bid history from blockchain
  private async fetchAuctionBidHistory(auctionId: string, assetIds: string[]): Promise<AuctionBid[]> {
    try {
      // Query Hyperion API for atomicmarket auction bid actions
      const hyperionEndpoints = [
        API_ENDPOINTS.WAX_CHAIN.EOSRIO,
        API_ENDPOINTS.WAX_CHAIN.WAXSWEDEN,
        API_ENDPOINTS.HYPERION[2] // eosdetroit hyperion endpoint
      ]
      
      for (const endpoint of hyperionEndpoints) {
        try {
          const response = await fetch(`${endpoint}/v2/history/get_actions?account=atomicmarket&filter=atomicmarket:auctionbid&limit=100&sort=desc&after=${new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).toISOString()}`, {
            method: 'GET',
            headers: {
              'Accept': 'application/json'
            }
          })
          
          if (!response.ok) continue
          
          const data = await response.json()
          const bids: AuctionBid[] = []
          
          if (data.actions) {
            for (const action of data.actions) {
              if (action.act?.data?.auction_id === auctionId) {
                bids.push({
                  bidder: action.act.data.bidder || action.act.authorization?.[0]?.actor || 'unknown',
                  bid_amount: action.act.data.bid || '0.0000 WAX',
                  timestamp: action.timestamp,
                  transaction_id: action.trx_id
                })
              }
            }
          }
          
          return bids
        } catch (endpointError) {
          logger.warn('Failed to fetch from endpoint', 'AUCTION', { endpoint, error: endpointError })
          continue
        }
      }
      
      // If all endpoints fail, return empty array
      return []
    } catch (error) {
      ErrorMessageService.logError('AuctionService.fetchAuctionBidHistory', error, { auctionId, assetIds })
      return []
    }
  }

  // Calculate time remaining for auction
  getTimeRemaining(endTime: string): {
    total: number
    days: number
    hours: number
    minutes: number
    seconds: number
    isExpired: boolean
  } {
    const now = Date.now()
    
    // Handle different date formats and validate
    let end: number
    if (!endTime) {
      // If no endTime provided, consider expired
      return {
        total: 0,
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
        isExpired: true
      }
    }
    
    // Try different date parsing approaches
    if (endTime.includes('T') && !endTime.endsWith('Z')) {
      // ISO format without Z
      end = new Date(endTime + 'Z').getTime()
    } else if (endTime.includes('T') && endTime.endsWith('Z')) {
      // ISO format with Z
      end = new Date(endTime).getTime()
    } else {
      // Unix timestamp or other format
      const timestamp = parseInt(endTime)
      if (!isNaN(timestamp)) {
        // If it's a valid number, treat as unix timestamp
        end = timestamp > 1000000000000 ? timestamp : timestamp * 1000
      } else {
        // Try parsing as regular date
        end = new Date(endTime).getTime()
      }
    }
    
    // Validate the parsed date
    if (isNaN(end)) {
      logger.warn('Invalid endTime format', 'AUCTION', { endTime })
      return {
        total: 0,
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
        isExpired: true
      }
    }
    
    const total = end - now
    
    if (total <= 0) {
      return {
        total: 0,
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
        isExpired: true
      }
    }
    
    const days = Math.floor(total / (1000 * 60 * 60 * 24))
    const hours = Math.floor((total % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
    const minutes = Math.floor((total % (1000 * 60 * 60)) / (1000 * 60))
    const seconds = Math.floor((total % (1000 * 60)) / 1000)
    
    return {
      total,
      days,
      hours,
      minutes,
      seconds,
      isExpired: false
    }
  }

  // Format time remaining as string
  formatTimeRemaining(endTime: string): string {
    const time = this.getTimeRemaining(endTime)
    
    if (time.isExpired) {
      return 'Auction ended'
    }
    
    if (time.days > 0) {
      return `${time.days}d ${time.hours}h ${time.minutes}m`
    } else if (time.hours > 0) {
      return `${time.hours}h ${time.minutes}m ${time.seconds}s`
    } else if (time.minutes > 0) {
      return `${time.minutes}m ${time.seconds}s`
    } else {
      return `${time.seconds}s`
    }
  }

  // Check if auction is ending soon (less than 5 minutes)
  isEndingSoon(endTime: string): boolean {
    const time = this.getTimeRemaining(endTime)
    return !time.isExpired && time.total < 5 * 60 * 1000 // 5 minutes in milliseconds
  }

  // Clear expired cache entries
  private clearExpiredCache(): void {
    const now = Date.now()
    for (const [auctionId, cached] of this.auctionCache.entries()) {
      if (now - cached.timestamp > this.CACHE_DURATION * 2) {
        this.auctionCache.delete(auctionId)
      }
    }
  }

  // Cleanup all subscriptions
  cleanup(): void {
    this.subscriptions.clear()
    this.stopPolling()
    this.auctionCache.clear()
  }
}

// Export singleton instance
export const auctionService = new AuctionService()