import { wharfKitService } from './wharfkit'
import { log } from './loggingService'

// Transaction Types
export type TransactionType = 
  | 'nft_purchase'
  | 'nft_sale'
  | 'nft_transfer'
  | 'nft_listing'
  | 'nft_cancel_listing'
  | 'token_transfer'
  | 'token_receive'
  | 'crafting'
  | 'blend'
  | 'burn'
  | 'recycle'
  | 'unpack'
  | 'auction_bid'
  | 'auction_create'
  | 'tokenization'
  | 'bulk_operation'

// Transaction Status
export type TransactionStatus = 
  | 'pending'
  | 'processing'
  | 'confirming'
  | 'success'
  | 'failed'
  | 'cancelled'

// Transaction Data Interface
export interface TransactionData {
  id: string
  type: TransactionType
  status: TransactionStatus
  timestamp: number
  account: string
  actions: TransactionAction[]
  metadata?: Record<string, unknown>
  error?: string
  transactionId?: string
  blockNum?: number
  cpuUsage?: number
  netUsage?: number
  retryCount?: number
  maxRetries?: number
}

// Transaction Action Interface
export interface TransactionAction {
  account: string
  name: string
  authorization: Array<{ actor: string; permission: string }>
  data: Record<string, unknown>
}

// Transaction Result Interface
export interface TransactionResult {
  success: boolean
  transactionId?: string
  blockNum?: number
  error?: string
  cpuUsage?: number
  netUsage?: number
}

// Transaction Options
export interface TransactionOptions {
  maxRetries?: number
  retryDelay?: number
  timeout?: number
  skipConfirmation?: boolean
  metadata?: Record<string, unknown>
}

// Transaction Event Types
export type TransactionEventType = 
  | 'transaction_started'
  | 'transaction_processing'
  | 'transaction_confirming'
  | 'transaction_success'
  | 'transaction_failed'
  | 'transaction_cancelled'
  | 'transaction_retry'

// Transaction Event Interface
export interface TransactionEvent {
  type: TransactionEventType
  transaction: TransactionData
  timestamp: number
}

// Transaction Listener
export type TransactionListener = (event: TransactionEvent) => void

/**
 * Centralized Transaction Service
 * Handles all blockchain transactions with consistent state management,
 * error handling, retry logic, and event notifications
 */
class TransactionService {
  private transactions = new Map<string, TransactionData>()
  private listeners = new Set<TransactionListener>()
  private retryTimeouts = new Map<string, NodeJS.Timeout>()

  /**
   * Execute a transaction with full state management
   */
  async executeTransaction(
    type: TransactionType,
    actions: TransactionAction[],
    options: TransactionOptions = {}
  ): Promise<TransactionResult> {
    const transactionId = this.generateTransactionId()
    const account = wharfKitService.getAccountName()
    
    if (!account) {
      throw new Error('Wallet not connected')
    }

    const transaction: TransactionData = {
      id: transactionId,
      type,
      status: 'pending',
      timestamp: Date.now(),
      account,
      actions,
      metadata: options.metadata,
      retryCount: 0,
      maxRetries: options.maxRetries ?? 3
    }

    // Store transaction and notify listeners
    this.transactions.set(transactionId, transaction)
    this.emitEvent('transaction_started', transaction)

    try {
      // Update status to processing
      this.updateTransactionStatus(transactionId, 'processing')
      
      // Execute the transaction
      const result = await this.performTransaction(actions, options.timeout)
      
      // Update transaction with result
      transaction.transactionId = result.transactionId
      transaction.blockNum = result.blockNum
      transaction.cpuUsage = result.cpuUsage
      transaction.netUsage = result.netUsage
      
      if (result.success) {
        // Update status to confirming if not skipping confirmation
        if (!options.skipConfirmation) {
          this.updateTransactionStatus(transactionId, 'confirming')
          await this.waitForConfirmation(result.transactionId!)
        }
        
        // Mark as successful
        this.updateTransactionStatus(transactionId, 'success')
        
        log.info('Transaction successful', 'TransactionService', {
          transactionId: result.transactionId,
          type,
          account
        })
        
        return result
      } else {
        throw new Error(result.error || 'Transaction failed')
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Unknown error'
      transaction.error = errorMessage
      
      log.error('Transaction failed', 'TransactionService', error instanceof Error ? error : new Error(errorMessage), {
        transactionId,
        type,
        account
      })
      
      // Attempt retry if configured and maxRetries > 0
      if (transaction.maxRetries! > 0 && transaction.retryCount! < transaction.maxRetries!) {
        return this.retryTransaction(transactionId, options)
      } else {
        this.updateTransactionStatus(transactionId, 'failed')
        return {
          success: false,
          error: errorMessage
        }
      }
    }
  }

  /**
   * Retry a failed transaction
   */
  private async retryTransaction(
    transactionId: string,
    options: TransactionOptions
  ): Promise<TransactionResult> {
    const transaction = this.transactions.get(transactionId)
    if (!transaction) {
      throw new Error('Transaction not found')
    }

    transaction.retryCount = (transaction.retryCount || 0) + 1
    this.emitEvent('transaction_retry', transaction)

    // Wait before retry
    const retryDelay = options.retryDelay ?? 2000 * transaction.retryCount
    await new Promise(resolve => {
      const timeout = setTimeout(resolve, retryDelay)
      this.retryTimeouts.set(transactionId, timeout)
    })

    // Remove retry timeout
    this.retryTimeouts.delete(transactionId)

    // Create new transaction ID for retry to avoid conflicts
    const newTransactionId = this.generateTransactionId()
    const newTransaction: TransactionData = {
      ...transaction,
      id: newTransactionId,
      status: 'pending',
      timestamp: Date.now(),
      error: undefined,
      transactionId: undefined,
      blockNum: undefined,
      cpuUsage: undefined,
      netUsage: undefined
    }

    // Store new transaction
    this.transactions.set(newTransactionId, newTransaction)
    this.emitEvent('transaction_started', newTransaction)

    try {
      // Update status to processing
      this.updateTransactionStatus(newTransactionId, 'processing')
      
      // Execute the transaction
      const result = await this.performTransaction(transaction.actions, options.timeout)
      
      // Update transaction with result
      newTransaction.transactionId = result.transactionId
      newTransaction.blockNum = result.blockNum
      newTransaction.cpuUsage = result.cpuUsage
      newTransaction.netUsage = result.netUsage
      
      if (result.success) {
        // Update status to confirming if not skipping confirmation
        if (!options.skipConfirmation) {
          this.updateTransactionStatus(newTransactionId, 'confirming')
          await this.waitForConfirmation(result.transactionId!)
        }
        
        // Mark as successful
        this.updateTransactionStatus(newTransactionId, 'success')
        
        log.info('Transaction retry successful', 'TransactionService', {
          transactionId: result.transactionId,
          type: transaction.type,
          account: transaction.account,
          retryCount: transaction.retryCount
        })
        
        return result
      } else {
        throw new Error(result.error || 'Transaction failed')
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Unknown error'
      newTransaction.error = errorMessage
      
      log.error('Transaction retry failed', 'TransactionService', error instanceof Error ? error : new Error(errorMessage), {
        transactionId: newTransactionId,
        type: transaction.type,
        account: transaction.account,
        retryCount: transaction.retryCount
      })
      
      // Check if we should retry again
      if (transaction.maxRetries! > 0 && transaction.retryCount! < transaction.maxRetries!) {
        return this.retryTransaction(transactionId, options)
      } else {
        this.updateTransactionStatus(newTransactionId, 'failed')
        return {
          success: false,
          error: errorMessage
        }
      }
    }
  }

  /**
   * Cancel a pending transaction
   */
  cancelTransaction(transactionId: string): boolean {
    const transaction = this.transactions.get(transactionId)
    if (!transaction || transaction.status !== 'pending') {
      return false
    }

    // Clear retry timeout if exists
    const timeout = this.retryTimeouts.get(transactionId)
    if (timeout) {
      clearTimeout(timeout)
      this.retryTimeouts.delete(transactionId)
    }

    this.updateTransactionStatus(transactionId, 'cancelled')
    return true
  }

  /**
   * Get transaction by ID
   */
  getTransaction(transactionId: string): TransactionData | undefined {
    return this.transactions.get(transactionId)
  }

  /**
   * Get all transactions for an account
   */
  getTransactionsByAccount(account: string): TransactionData[] {
    return Array.from(this.transactions.values())
      .filter(tx => tx.account === account)
      .sort((a, b) => b.timestamp - a.timestamp)
  }

  /**
   * Get transactions by type
   */
  getTransactionsByType(type: TransactionType): TransactionData[] {
    return Array.from(this.transactions.values())
      .filter(tx => tx.type === type)
      .sort((a, b) => b.timestamp - a.timestamp)
  }

  /**
   * Get pending transactions
   */
  getPendingTransactions(): TransactionData[] {
    return Array.from(this.transactions.values())
      .filter(tx => ['pending', 'processing', 'confirming'].includes(tx.status))
      .sort((a, b) => b.timestamp - a.timestamp)
  }

  /**
   * Clear old transactions (keep last 100)
   */
  clearOldTransactions(): void {
    const allTransactions = Array.from(this.transactions.values())
      .sort((a, b) => b.timestamp - a.timestamp)
    
    if (allTransactions.length > 100) {
      const toRemove = allTransactions.slice(100)
      toRemove.forEach(tx => this.transactions.delete(tx.id))
    }
  }

  /**
   * Add transaction event listener
   */
  addListener(listener: TransactionListener): void {
    this.listeners.add(listener)
  }

  /**
   * Remove transaction event listener
   */
  removeListener(listener: TransactionListener): void {
    this.listeners.delete(listener)
  }

  /**
   * Clear all listeners
   */
  clearListeners(): void {
    this.listeners.clear()
  }

  // Private helper methods

  private generateTransactionId(): string {
    return `tx_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
  }

  private updateTransactionStatus(transactionId: string, status: TransactionStatus): void {
    const transaction = this.transactions.get(transactionId)
    if (transaction) {
      transaction.status = status
      this.emitEvent(`transaction_${status}` as TransactionEventType, transaction)
    }
  }

  private emitEvent(type: TransactionEventType, transaction: TransactionData): void {
    const event: TransactionEvent = {
      type,
      transaction,
      timestamp: Date.now()
    }
    
    this.listeners.forEach(listener => {
      try {
        listener(event)
      } catch (error) {
        log.error('Transaction listener error', 'TransactionService', error instanceof Error ? error : new Error(String(error)))
      }
    })
  }

  private async performTransaction(
    actions: TransactionAction[],
    _timeout?: number
  ): Promise<TransactionResult> {
    try {
      // Use WharfKit service to execute the transaction
      const result = await wharfKitService.transact(actions)
      
      // Extract transaction details from result
      // Note: This depends on WharfKit's response format
      if (result && typeof result === 'object') {
        const txResult = result as any
        return {
          success: true,
          transactionId: txResult.transaction_id || txResult.id,
          blockNum: txResult.block_num,
          cpuUsage: txResult.cpu_usage_us,
          netUsage: txResult.net_usage_words
        }
      }
      
      return { success: true }
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Transaction failed'
      }
    }
  }

  private async waitForConfirmation(_transactionId: string): Promise<void> {
    // Wait for transaction confirmation
    // This is a simplified implementation - in production you'd want to
    // poll the blockchain to confirm the transaction was included in a block
    await new Promise(resolve => setTimeout(resolve, 3000))
  }
}

// Export singleton instance
export const transactionService = new TransactionService()
export default transactionService