import { createContext } from 'react'
import type { Session } from '@wharfkit/session'
import type { TransactionType } from '../services/transactionService'

interface TransactionAction {
  account: string
  name: string
  authorization: Array<{ actor: string; permission: string }>
  data: Record<string, unknown>
}

export interface WalletContextType {
  isConnected: boolean
  account: string | null
  session: Session | null
  login: () => Promise<Session | null>
  logout: () => Promise<void>
  transact: (actions: TransactionAction[]) => Promise<unknown>
  purchaseNFT: (saleId: string, price: string) => Promise<unknown>
  batchPurchaseNFTs: (purchases: Array<{saleId: string, price: string}>) => Promise<unknown>
  executeCraftingTransaction: (craftingData: any) => Promise<unknown>
  isLoading: boolean
  // Transaction management methods
  executeTransaction: (type: TransactionType, data: any) => Promise<string>
  getTransaction?: (transactionId: string) => any
  cancelTransaction?: (transactionId: string) => boolean
  getTransactionHistory?: () => Promise<any[]>
  transferNFT?: (assetId: string, recipient: string) => Promise<unknown>
  bulkTransferNFT?: (recipient: string, assetIds: string[], memo?: string) => Promise<unknown>
  transferTokens?: (to: string, amount: string, currency?: string, memo?: string) => Promise<unknown>
}

const WalletContext = createContext<WalletContextType | undefined>(undefined)

export default WalletContext