export const getTemplateById = (id: number): IngredientTemplate | undefined => {
  return INGREDIENT_TEMPLATES.find(template => template.id === id)
}

export const getRecipeById = (id: string): CraftingRecipe | undefined => {
  return CRAFTING_RECIPES.find(recipe => recipe.id === id)
}

export const getPrimaryIngredients = (): IngredientTemplate[] => {
  return INGREDIENT_TEMPLATES.filter(template => template.type === 'primary')
}

export const getSecondaryIngredients = (): IngredientTemplate[] => {
  return INGREDIENT_TEMPLATES.filter(template => template.type === 'secondary')
}

export const getTertiaryIngredients = (): IngredientTemplate[] => {
  return INGREDIENT_TEMPLATES.filter(template => template.type === 'tertiary')
}