import React, { useState, useEffect, useCallback, useMemo, memo } from 'react'
import { useNavigate } from 'react-router-dom'
import { useWallet } from '../hooks/useWallet'
import { apiService, type NFTAsset } from '../services/api'
import NFTDetailsModal from './NFTDetailsModal'
import ListingModal from './ListingModal'
import TransferModal from './TransferModal'
import BurnModal from './BurnModal'
import BulkListingModal from './BulkListingModal'
import BulkTransferModal from './BulkTransferModal'
import DistributeModal from './DistributeModal'
import BulkBurnModal from './BulkBurnModal'
import CraftingModal from './CraftingModal'
import ViewOffersModal from './ViewOffersModal'
import UnpackModal from './UnpackModal'
import BulkAuctionModal from './BulkAuctionModal'
import TokenizeFeeModal from './TokenizeFeeModal'
import AdvancedSearch from './AdvancedSearch'
import { NeftyBlendService } from '../services/neftyBlendService'
import { MarketplaceConfigService } from '../config/marketplaceConfig'
import { stakingService } from '../services/stakingService'

import ProgressiveImage from './ProgressiveImage'
import { getAssetImageUrl, getAssetVideoUrl } from '../utils/mediaUtils'

import { useDisplaySettings } from '../hooks/useDisplaySettings'
