  // Generate search suggestions based on inventory
  const generateSearchSuggestions = useCallback((query: string): SearchSuggestion[] => {
    if (!query || query.length < 2) return []
    
    const suggestions = new Map<string, SearchSuggestion>()
    const queryLower = query.toLowerCase()
    
    inventory.forEach(nft => {
      // NFT name suggestions
      if (nft.name.toLowerCase().includes(queryLower)) {
        const key = `name:${nft.name}`
        if (!suggestions.has(key)) {
          suggestions.set(key, {
            text: nft.name,
            type: 'name',
            count: 1
          })
        } else {
          const existing = suggestions.get(key)!
          existing.count = (existing.count || 0) + 1
        }
      }
      
      // Collection suggestions
      if (nft.collection.collection_name.toLowerCase().includes(queryLower)) {
        const key = `collection:${nft.collection.collection_name}`
        if (!suggestions.has(key)) {
          suggestions.set(key, {
            text: nft.collection.collection_name,
            type: 'collection',
            count: 1
          })
        } else {
          const existing = suggestions.get(key)!
          existing.count = (existing.count || 0) + 1
        }
      }
      
      // Schema suggestions
      if (nft.schema.schema_name.toLowerCase().includes(queryLower)) {
        const key = `schema:${nft.schema.schema_name}`
        if (!suggestions.has(key)) {
          suggestions.set(key, {
            text: nft.schema.schema_name,
            type: 'schema',
            count: 1
          })
        } else {
          const existing = suggestions.get(key)!
          existing.count = (existing.count || 0) + 1
        }
      }
    })
    
    return Array.from(suggestions.values())
      .sort((a, b) => (b.count || 0) - (a.count || 0))
      .slice(0, 8)
  }, [inventory])

  // Update search suggestions when search term changes
