// Push notification service for managing subscriptions and notifications
import { ErrorMessageService } from './errorMessageService'
import { monitoringService } from './monitoringService'
import { log } from './loggingService'

interface NotificationPayload {
  title: string
  body: string
  icon?: string
  badge?: string
  tag?: string
  data?: Record<string, unknown>
  requireInteraction?: boolean
  silent?: boolean
  timestamp?: number
}

interface PushSubscriptionData {
  endpoint: string
  keys: {
    p256dh: string
    auth: string
  }
  userId?: string
  subscriptionTime: number
}

interface NotificationPermissionState {
  permission: NotificationPermission
  supported: boolean
  serviceWorkerReady: boolean
  subscribed: boolean
}

interface NotificationPreferences {
  priceAlerts: boolean
  auctionUpdates: boolean
  newListings: boolean
  marketTrends: boolean
  systemNotifications: boolean
}

class PushNotificationService {
  private serviceWorkerRegistration: ServiceWorkerRegistration | null = null
  private pushSubscription: PushSubscription | null = null
  private vapidPublicKey: string = '' // Should be set from environment or config
  private isInitialized: boolean = false
  private permissionState: NotificationPermissionState = {
    permission: 'default',
    supported: false,
    serviceWorkerReady: false,
    subscribed: false
  }

  constructor() {
    this.checkSupport()
  }

  private checkSupport(): void {
    this.permissionState.supported = 
      'serviceWorker' in navigator &&
      'PushManager' in window &&
      'Notification' in window

    if (this.permissionState.supported) {
      this.permissionState.permission = Notification.permission
    }
  }

  async initialize(): Promise<boolean> {
    if (this.isInitialized) {
      return true
    }

    try {
      if (!this.permissionState.supported) {
        log.warn('Push notifications not supported in this browser')
        return false
      }

      // Register service worker
      await this.registerServiceWorker()
      
      // Check existing subscription
      await this.checkExistingSubscription()
      
      this.isInitialized = true
      
      monitoringService.trackEvent('push_notification_service_initialized', {
        supported: this.permissionState.supported,
        permission: this.permissionState.permission,
        subscribed: this.permissionState.subscribed
      })
      
      return true
    } catch (error) {
      log.error('Failed to initialize push notification service', 'PushNotificationService.initialize', error as Error)
      monitoringService.reportError(error as Error, 'push_notification_init_error')
      return false
    }
  }

  private async registerServiceWorker(): Promise<void> {
    try {
      // Detect environment based on hostname and protocol
      const isDevelopment = location.hostname === 'localhost' || location.hostname === '127.0.0.1' || location.protocol === 'http:'
      const swFile = isDevelopment ? '/sw-dev.js' : '/sw.js'
      
      this.serviceWorkerRegistration = await navigator.serviceWorker.register(swFile, {
        scope: '/'
      })

      // Wait for service worker to be ready
      await navigator.serviceWorker.ready
      this.permissionState.serviceWorkerReady = true

      log.info(`Service Worker registered successfully: ${swFile}`)
      
      // Listen for service worker messages
      navigator.serviceWorker.addEventListener('message', this.handleServiceWorkerMessage.bind(this))
      
    } catch (error) {
      log.error('Failed to register service worker', 'PushNotificationService.registerServiceWorker', error as Error)
      throw error
    }
  }

  private handleServiceWorkerMessage(event: MessageEvent): void {
    const { type, data } = event.data || {}
    
    switch (type) {
      case 'NOTIFICATION_CLICKED':
        monitoringService.trackEvent('notification_clicked', data)
        break
      case 'NOTIFICATION_CLOSED':
        monitoringService.trackEvent('notification_closed', data)
        break
      default:
        log.debug('Unknown service worker message:', type)
    }
  }

  private async checkExistingSubscription(): Promise<void> {
    if (!this.serviceWorkerRegistration) {
      return
    }

    try {
      this.pushSubscription = await this.serviceWorkerRegistration.pushManager.getSubscription()
      this.permissionState.subscribed = !!this.pushSubscription
      
      if (this.pushSubscription) {
        log.info('Existing push subscription found')
        // Optionally validate subscription with server
        await this.validateSubscription()
      }
    } catch (error) {
      log.error('Failed to check existing subscription', 'PushNotificationService.checkExistingSubscription', error as Error)
    }
  }

  async requestPermission(): Promise<NotificationPermission> {
    if (!this.permissionState.supported) {
      throw new Error('Push notifications not supported')
    }

    try {
      const permission = await Notification.requestPermission()
      this.permissionState.permission = permission
      
      monitoringService.trackEvent('notification_permission_requested', {
        permission,
        previousPermission: this.permissionState.permission
      })
      
      return permission
    } catch (error) {
      ErrorMessageService.logError('PushNotificationService.requestPermission', error as Error)
      monitoringService.reportError(error as Error, 'notification_permission_error')
      throw error
    }
  }

  async subscribe(userId?: string): Promise<PushSubscription | null> {
    if (!this.isInitialized) {
      await this.initialize()
    }

    if (!this.serviceWorkerRegistration) {
      throw new Error('Service worker not registered')
    }

    if (this.permissionState.permission !== 'granted') {
      const permission = await this.requestPermission()
      if (permission !== 'granted') {
        throw new Error('Notification permission denied')
      }
    }

    try {
      // Unsubscribe existing subscription if any
      if (this.pushSubscription) {
        await this.unsubscribe()
      }

      // Create new subscription
      const subscribeOptions: PushSubscriptionOptionsInit = {
        userVisibleOnly: true,
        applicationServerKey: this.vapidPublicKey ? this.urlBase64ToUint8Array(this.vapidPublicKey) : undefined
      }

      this.pushSubscription = await this.serviceWorkerRegistration.pushManager.subscribe(subscribeOptions)
      this.permissionState.subscribed = true

      // Store subscription data
      const subscriptionData: PushSubscriptionData = {
        endpoint: this.pushSubscription.endpoint,
        keys: {
          p256dh: this.arrayBufferToBase64(this.pushSubscription.getKey('p256dh')!),
          auth: this.arrayBufferToBase64(this.pushSubscription.getKey('auth')!)
        },
        userId,
        subscriptionTime: Date.now()
      }

      // Store in localStorage for persistence
      localStorage.setItem('push_subscription', JSON.stringify(subscriptionData))

      // Send subscription to server (implement based on your backend)
      await this.sendSubscriptionToServer(subscriptionData)

      monitoringService.trackEvent('push_notification_subscribed', {
        userId,
        endpoint: this.pushSubscription.endpoint
      })

      log.info('Push notification subscription successful')
      return this.pushSubscription
    } catch (error) {
      log.error('Failed to subscribe to push notifications', 'PushNotificationService.subscribe', error as Error)
      monitoringService.reportError(error as Error, 'push_subscription_error', { userId })
      throw error
    }
  }

  async unsubscribe(): Promise<boolean> {
    if (!this.pushSubscription) {
      return true
    }

    try {
      const success = await this.pushSubscription.unsubscribe()
      
      if (success) {
        this.pushSubscription = null
        this.permissionState.subscribed = false
        
        // Remove from localStorage
        localStorage.removeItem('push_subscription')
        
        // Notify server about unsubscription
        await this.removeSubscriptionFromServer()
        
        monitoringService.trackEvent('push_notification_unsubscribed')
        log.info('Push notification unsubscribed successfully')
      }
      
      return success
    } catch (error) {
      ErrorMessageService.logError('PushNotificationService.unsubscribe', error as Error)
      monitoringService.reportError(error as Error, 'push_unsubscription_error')
      return false
    }
  }

  async showLocalNotification(payload: NotificationPayload): Promise<void> {
    if (!this.permissionState.supported || this.permissionState.permission !== 'granted') {
      throw new Error('Notifications not available')
    }

    try {
      const options: NotificationOptions = {
        body: payload.body,
        icon: payload.icon || '/logo.png',
        badge: payload.badge || '/logo.png',
        tag: payload.tag,
        data: payload.data,
        requireInteraction: payload.requireInteraction || false,
        silent: payload.silent || false
      }

      const notification = new Notification(payload.title, options)
      
      // Track notification events
      notification.onclick = () => {
        monitoringService.trackEvent('local_notification_clicked', {
          tag: payload.tag,
          title: payload.title
        })
        notification.close()
      }

      notification.onclose = () => {
        monitoringService.trackEvent('local_notification_closed', {
          tag: payload.tag,
          title: payload.title
        })
      }

      monitoringService.trackEvent('local_notification_shown', {
        tag: payload.tag,
        title: payload.title
      })
      
    } catch (error) {
      log.error('Failed to show local notification', 'PushNotificationService.showLocalNotification', error as Error)
      monitoringService.reportError(error as Error, 'local_notification_error', { title: payload.title })
      throw error
    }
  }

  // Utility methods
  private urlBase64ToUint8Array(base64String: string): Uint8Array {
    const padding = '='.repeat((4 - base64String.length % 4) % 4)
    const base64 = (base64String + padding)
      .replace(/-/g, '+')
      .replace(/_/g, '/')

    const rawData = window.atob(base64)
    const outputArray = new Uint8Array(rawData.length)

    for (let i = 0; i < rawData.length; ++i) {
      outputArray[i] = rawData.charCodeAt(i)
    }
    return outputArray
  }

  private arrayBufferToBase64(buffer: ArrayBuffer): string {
    const bytes = new Uint8Array(buffer)
    let binary = ''
    for (let i = 0; i < bytes.byteLength; i++) {
      binary += String.fromCharCode(bytes[i])
    }
    return window.btoa(binary)
  }

  private getAuthToken(): string {
    // Get authentication token from localStorage or session storage
    return localStorage.getItem('auth_token') || sessionStorage.getItem('auth_token') || ''
  }

  private getNotificationPreferences(): NotificationPreferences {
    const stored = localStorage.getItem('notification_preferences')
    if (stored) {
      try {
        return JSON.parse(stored)
      } catch (error) {
        log.warn('Failed to parse notification preferences:', 'pushNotificationService.getNotificationPreferences', error)
      }
    }
    
    // Return default preferences
    return {
      priceAlerts: true,
      auctionUpdates: true,
      newListings: false,
      marketTrends: false,
      systemNotifications: true
    }
  }

  private async validateSubscription(): Promise<boolean> {
    if (!this.pushSubscription) {
      return false
    }

    try {
      const subscriptionData = {
        endpoint: this.pushSubscription.endpoint,
        keys: {
          p256dh: this.arrayBufferToBase64(this.pushSubscription.getKey('p256dh')!),
          auth: this.arrayBufferToBase64(this.pushSubscription.getKey('auth')!)
        }
      }

      const response = await fetch('/api/push/validate-subscription', {
        method: 'POST',
        headers: { 
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.getAuthToken()}`
        },
        body: JSON.stringify({ subscription: subscriptionData })
      })

      if (!response.ok) {
        throw new Error(`Validation failed: ${response.status} ${response.statusText}`)
      }

      const result = await response.json()
      return result.valid === true
    } catch (error) {
      log.warn('Subscription validation failed:', 'pushNotificationService.validateSubscription', error as Error)
      return false
    }
  }

  private async sendSubscriptionToServer(subscriptionData: PushSubscriptionData): Promise<void> {
    try {
      const response = await fetch('/api/push/subscribe', {
        method: 'POST',
        headers: { 
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.getAuthToken()}`
        },
        body: JSON.stringify({
          ...subscriptionData,
          userAgent: navigator.userAgent,
          timestamp: Date.now(),
          preferences: this.getNotificationPreferences()
        })
      })

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}))
        throw new Error(`Failed to save subscription: ${response.status} ${response.statusText} - ${errorData.message || 'Unknown error'}`)
      }

      const result = await response.json()
      log.info('Subscription successfully saved to server:', result.subscriptionId)
      
      // Store server subscription ID for future reference
      localStorage.setItem('push_subscription_id', result.subscriptionId)
      
    } catch (error) {
      log.error('Failed to send subscription to server:', 'pushNotificationService.sendSubscriptionToServer', error as Error, {
        endpoint: subscriptionData.endpoint,
        userId: subscriptionData.userId
      })
      // Re-throw to handle in calling function
      throw error
    }
  }

  private async removeSubscriptionFromServer(): Promise<void> {
    try {
      const subscriptionId = localStorage.getItem('push_subscription_id')
      const subscriptionData = localStorage.getItem('push_subscription')
      
      if (!subscriptionId && !subscriptionData) {
        log.info('No subscription to remove from server')
        return
      }

      const response = await fetch('/api/push/unsubscribe', {
        method: 'DELETE',
        headers: { 
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.getAuthToken()}`
        },
        body: JSON.stringify({
          subscriptionId,
          endpoint: this.pushSubscription?.endpoint,
          timestamp: Date.now()
        })
      })

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}))
        throw new Error(`Failed to remove subscription: ${response.status} ${response.statusText} - ${errorData.message || 'Unknown error'}`)
      }

      // Clean up local storage
      localStorage.removeItem('push_subscription_id')
      log.info('Subscription successfully removed from server')
      
    } catch (error) {
      log.error('Failed to remove subscription from server:', 'pushNotificationService.removeSubscriptionFromServer', error as Error)
      // Don't throw here to avoid breaking the unsubscription process
    }
  }

  // Public API methods
  getPermissionState(): NotificationPermissionState {
    return { ...this.permissionState }
  }

  isSubscribed(): boolean {
    return this.permissionState.subscribed
  }

  getSubscription(): PushSubscription | null {
    return this.pushSubscription
  }

  setVapidKey(key: string): void {
    this.vapidPublicKey = key
  }

  async testNotification(): Promise<void> {
    await this.showLocalNotification({
      title: 'PxMarket Test Notification',
      body: 'Push notifications are working correctly!',
      tag: 'test-notification',
      data: { test: true }
    })
  }

  // Integration with watchlist service for price alerts
  async sendPriceAlert(assetName: string, currentPrice: string, targetPrice: string): Promise<void> {
    await this.showLocalNotification({
      title: 'Price Alert',
      body: `${assetName} is now ${currentPrice} (target: ${targetPrice})`,
      tag: 'price-alert',
      icon: '/logo.png',
      requireInteraction: true,
      data: {
        type: 'price_alert',
        assetName,
        currentPrice,
        targetPrice
      }
    })
  }

  // Integration with cart service for transaction notifications
  async sendTransactionNotification(type: 'success' | 'error', message: string, transactionId?: string): Promise<void> {
    await this.showLocalNotification({
      title: type === 'success' ? 'Transaction Successful' : 'Transaction Failed',
      body: message,
      tag: `transaction-${type}`,
      icon: '/logo.png',
      data: {
        type: 'transaction',
        status: type,
        transactionId
      }
    })
  }
}

// Export singleton instance
export const pushNotificationService = new PushNotificationService()

// Export types
export type {
  NotificationPayload,
  PushSubscriptionData,
  NotificationPermissionState,
  NotificationPreferences
}