import React, { useState, useEffect, useRef } from 'react'
import { imageService, type ImageConfig, type ImageLoadResult } from '../services/imageService'

interface FallbackImageProps {
  src?: string
  alt: string
  className?: string
  style?: React.CSSProperties
  width?: number
  height?: number
  type?: 'nft' | 'collection' | 'avatar' | 'token' | 'generic'
  fallbackText?: string
  backgroundColor?: string
  textColor?: string
  onLoad?: (result: ImageLoadResult) => void
  onError?: (error: Error) => void
  loading?: 'lazy' | 'eager'
  placeholder?: boolean
}

const FallbackImage: React.FC<FallbackImageProps> = ({
  src,
  alt,
  className = '',
  style = {},
  width,
  height,
  type = 'generic',
  fallbackText,
  backgroundColor,
  textColor,
  onLoad,
  onError,
  loading = 'lazy',
  placeholder = true
}) => {
  const [imageSrc, setImageSrc] = useState<string>('')
  const [isLoading, setIsLoading] = useState(true)
  const [loadResult, setLoadResult] = useState<ImageLoadResult | null>(null)
  const imgRef = useRef<HTMLImageElement>(null)
  const loadingRef = useRef<boolean>(false)

  useEffect(() => {
    if (loadingRef.current) return
    loadingRef.current = true
    
    const loadImage = async () => {
      setIsLoading(true)
      
      const config: ImageConfig = {
        width,
        height,
        type,
        text: fallbackText,
        backgroundColor,
        textColor
      }
      
      try {
        const result = await imageService.loadImage(src || '', config)
        setImageSrc(result.src)
        setLoadResult(result)
        
        if (onLoad) {
          onLoad(result)
        }
        
        if (!result.success && result.error && onError) {
          onError(result.error)
        }
      } catch (error) {
        const fallbackSrc = imageService.generateFallbackSVG(config)
        setImageSrc(fallbackSrc)
        setLoadResult({
          success: false,
          src: fallbackSrc,
          fallbackUsed: true,
          error: error as Error
        })
        
        if (onError) {
          onError(error as Error)
        }
      } finally {
        setIsLoading(false)
        loadingRef.current = false
      }
    }
    
    loadImage()
  }, [src, width, height, type, fallbackText, backgroundColor, textColor, onLoad, onError])

  const handleImageLoad = () => {
    setIsLoading(false)
  }

  const handleImageError = () => {
    if (loadResult && !loadResult.fallbackUsed) {
      // If the original image failed and we haven't used fallback yet
      const config: ImageConfig = {
        width,
        height,
        type,
        text: fallbackText,
        backgroundColor,
        textColor
      }
      const fallbackSrc = imageService.generateFallbackSVG(config)
      setImageSrc(fallbackSrc)
      setLoadResult({
        success: false,
        src: fallbackSrc,
        fallbackUsed: true
      })
    }
    setIsLoading(false)
  }

  const combinedStyle: React.CSSProperties = {
    ...style,
    ...(width && { width }),
    ...(height && { height })
  }

  const combinedClassName = `fallback-image ${className} ${
    isLoading ? 'loading' : ''
  } ${
    loadResult?.fallbackUsed ? 'fallback-used' : ''
  }`.trim()

  return (
    <div className="fallback-image-container" style={{ position: 'relative', display: 'inline-block' }}>
      {isLoading && placeholder && (
        <div 
          className="image-placeholder"
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            width: width || '100%',
            height: height || '100%',
            backgroundColor: backgroundColor || '#f3f4f6',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRadius: '4px',
            zIndex: 1
          }}
        >
          <div className="loading-spinner" style={{
            width: '20px',
            height: '20px',
            border: '2px solid #e5e7eb',
            borderTop: '2px solid #3b82f6',
            borderRadius: '50%',
            animation: 'spin 1s linear infinite'
          }} />
        </div>
      )}
      
      <img
        ref={imgRef}
        src={imageSrc || ''}
        alt={alt}
        className={combinedClassName}
        style={{
          ...combinedStyle,
          opacity: isLoading ? 0 : 1,
          transition: 'opacity 0.3s ease'
        }}
        loading={loading}
        onLoad={handleImageLoad}
        onError={handleImageError}
      />
      
      <style>{`
        @keyframes spin {
          0% { transform: rotate(0deg); }
          100% { transform: rotate(360deg); }
        }
        
        .fallback-image {
          display: block;
          max-width: 100%;
          height: auto;
        }
        
        .fallback-image.loading {
          opacity: 0;
        }
        
        .fallback-image.fallback-used {
          filter: grayscale(0.1);
        }
        
        .image-placeholder {
          background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
          background-size: 200% 100%;
          animation: shimmer 1.5s infinite;
        }
        
        @keyframes shimmer {
          0% { background-position: -200% 0; }
          100% { background-position: 200% 0; }
        }
      `}</style>
    </div>
  )
}

export default FallbackImage

// Export utility component for common use cases
export const NFTImage: React.FC<Omit<FallbackImageProps, 'type'>> = (props) => (
  <FallbackImage {...props} type="nft" />
)

export const CollectionImage: React.FC<Omit<FallbackImageProps, 'type'>> = (props) => (
  <FallbackImage {...props} type="collection" />
)

export const AvatarImage: React.FC<Omit<FallbackImageProps, 'type'>> = (props) => (
  <FallbackImage {...props} type="avatar" />
)

export const TokenImage: React.FC<Omit<FallbackImageProps, 'type'>> = (props) => (
  <FallbackImage {...props} type="token" />
)