import React, { useState, useEffect, useCallback } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import { stakingService, type TransactionResult, type TokenStakeInfo, type TokenFarmInfo } from '../services/stakingService'
import { TokenImage } from './FallbackImage'
import '../css/modal.css'

// Helper function to get token icon URL
const getTokenIcon = (tokenSymbol: string): string => {
  const tokenIcons: Record<string, string> = {
    'PXJ': '/img/tokens/pxj.png',
    'WUF': '/img/tokens/wuf.png',
    'WAX': '/img/tokens/wax.png',
    'PIXAL': '/img/tokens/pixal.png'
  };
  
  return tokenIcons[tokenSymbol.toUpperCase()] || '/img/tokens/default.png';
};

interface TokenStakingModalProps {
  isOpen: boolean
  onClose: () => void
  action: 'stake' | 'unstake' | 'claim'
  farmName?: string
  onSuccess?: () => void
}

const TokenStakingModal: React.FC<TokenStakingModalProps> = ({
  isOpen,
  onClose,
  action,
  farmName = 'pxjfarm1',
  onSuccess
}) => {
  const { session } = useWallet()
  const { showSuccess, showError } = useNotifications()
  const [amount, setAmount] = useState('')
  const [isProcessing, setIsProcessing] = useState(false)
  const [farmInfo, setFarmInfo] = useState<TokenFarmInfo | null>(null)
  const [userStakeInfo, setUserStakeInfo] = useState<TokenStakeInfo | null>(null)
  const [userBalance, setUserBalance] = useState(0)
  const [loading, setLoading] = useState(false)

  // Fetch farm and user data when modal opens
  const fetchData = useCallback(async () => {
    if (!session) return
    
    setLoading(true)
    try {
      // Fetch farm info
      const farm = await stakingService.getTokenFarmInfo(farmName)
      setFarmInfo(farm)
      
      // Fetch user staking info
      const userStake = await stakingService.getTokenStakingInfo(session.actor.toString(), farmName)
      setUserStakeInfo(userStake)
      
      // Fetch user PXJ balance
      const balanceResponse = await fetch('https://wax.greymass.com/v1/chain/get_currency_balance', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          code: 'pixeljourney',
          account: session.actor.toString(),
          symbol: 'PXJ'
        })
      })
      
      if (balanceResponse.ok) {
        const balanceData = await balanceResponse.json()
        const balance = balanceData.length > 0 ? parseFloat(balanceData[0].split(' ')[0]) : 0
        setUserBalance(balance)
      }
    } catch (error) {
      console.error('Error fetching data:', error)
      showError('Failed to load staking data')
    } finally {
      setLoading(false)
    }
  }, [session, setLoading, farmName, setFarmInfo, setUserStakeInfo, setUserBalance, showError])

  useEffect(() => {
    if (isOpen && session) {
      fetchData()
    }
  }, [isOpen, session, farmName, fetchData])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!session || isProcessing) return

    if (action !== 'claim' && (!amount || parseFloat(amount) <= 0)) {
      showError('Please enter a valid amount')
      return
    }

    if (action === 'stake' && parseFloat(amount) > userBalance) {
      showError('Insufficient PXJ balance')
      return
    }

    if (action === 'unstake' && userStakeInfo && parseFloat(amount) > userStakeInfo.staked_amount) {
      showError('Amount exceeds staked balance')
      return
    }

    setIsProcessing(true)
    try {
      let result: TransactionResult
      const formattedAmount = `${parseFloat(amount).toFixed(4)} PXJ`

      switch (action) {
        case 'stake':
          result = await stakingService.stakeTokens(session, formattedAmount, farmName)
          break
        case 'unstake':
          result = await stakingService.unstakeTokens(session, formattedAmount, farmName)
          break
        case 'claim':
          result = await stakingService.claimTokenFarmRewards(session, farmName)
          break
        default:
          throw new Error('Invalid action')
      }

      if (result.success) {
        showSuccess(`Successfully ${action === 'stake' ? 'staked' : action === 'unstake' ? 'unstaked' : 'claimed'} tokens!`)
        onSuccess?.()
        onClose()
      } else {
        showError(result.error || `Failed to ${action} tokens`)
      }
    } catch (error) {
      console.error(`Error ${action}ing tokens:`, error)
      showError(error instanceof Error ? error.message : `Failed to ${action} tokens`)
    } finally {
      setIsProcessing(false)
    }
  }

  const formatTokenAmount = (amount: number) => {
    return amount.toLocaleString(undefined, { minimumFractionDigits: 4, maximumFractionDigits: 4 })
  }

  const formatTimeRemaining = (timestamp: number) => {
    const now = Date.now() / 1000
    const remaining = timestamp - now
    
    if (remaining <= 0) return 'Available now'
    
    const days = Math.floor(remaining / 86400)
    const hours = Math.floor((remaining % 86400) / 3600)
    const minutes = Math.floor((remaining % 3600) / 60)
    
    if (days > 0) return `${days}d ${hours}h remaining`
    if (hours > 0) return `${hours}h ${minutes}m remaining`
    return `${minutes}m remaining`
  }

  if (!isOpen) return null

  const getModalTitle = () => {
    switch (action) {
      case 'stake': return 'Stake PXJ Tokens'
      case 'unstake': return 'Unstake PXJ Tokens'
      case 'claim': return 'Claim Rewards'
      default: return 'Token Staking'
    }
  }

  const getActionButtonText = () => {
    if (isProcessing) return 'Processing...'
    switch (action) {
      case 'stake': return 'Stake Tokens'
      case 'unstake': return 'Unstake Tokens'
      case 'claim': return 'Claim Rewards'
      default: return 'Submit'
    }
  }

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content token-staking-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>{getModalTitle()}</h3>
          <button className="close-btn" onClick={onClose}>×</button>
        </div>
        
        <div className="modal-body">
          {loading ? (
            <div className="loading-state">
              <div className="spinner"></div>
              <p>Loading staking data...</p>
            </div>
          ) : (
            <>
              {/* Farm Information */}
              {farmInfo && (
                <div className="farm-info-section">
                  <h4>Farm Information</h4>
                  <div className="info-grid">
                    <div className="info-item">
                      <span className="label">Farm:</span>
                      <span className="value">{farmInfo.farm_name}</span>
                    </div>
                    <div className="info-item">
                      <span className="label">Total Staked:</span>
                      <span className="value">{formatTokenAmount(farmInfo.total_staked)} PXJ</span>
                    </div>
                    <div className="info-item">
                      <span className="label">Vesting Period:</span>
                      <span className="value">{Math.floor(farmInfo.vesting_time / 86400)} days</span>
                    </div>
                  </div>
                </div>
              )}

              {/* User Staking Information */}
              {userStakeInfo && (
                <div className="user-stake-info">
                  <h4>Your Staking Position</h4>
                  <div className="info-grid">
                    <div className="info-item">
                      <span className="label">Staked Amount:</span>
                      <span className="value">{formatTokenAmount(userStakeInfo.staked_amount)} PXJ</span>
                    </div>
                    <div className="info-item">
                      <span className="label">Pending Rewards:</span>
                      <span className="value">
                        {userStakeInfo.pending_rewards.map(reward => 
                          `${formatTokenAmount(reward.amount)} ${reward.token}`
                        ).join(', ') || 'None'}
                      </span>
                    </div>
                    <div className="info-item">
                      <span className="label">Unlock Time:</span>
                      <span className="value">{formatTimeRemaining(userStakeInfo.vesting_time)}</span>
                    </div>
                  </div>
                </div>
              )}

              {/* Balance Information */}
              <div className="balance-info">
                <div className="balance-item">
                  <TokenImage src={getTokenIcon('PXJ')} alt="PXJ" />
                  <span>Available: {formatTokenAmount(userBalance)} PXJ</span>
                </div>
              </div>

              {/* Action Form */}
              <form onSubmit={handleSubmit} className="staking-form">
                {action !== 'claim' && (
                  <div className="form-group">
                    <label htmlFor="amount">Amount (PXJ)</label>
                    <div className="input-group">
                      <input
                        type="number"
                        id="amount"
                        value={amount}
                        onChange={(e) => setAmount(e.target.value)}
                        placeholder="0.0000"
                        step="0.0001"
                        min="0"
                        max={action === 'stake' ? userBalance : userStakeInfo?.staked_amount || 0}
                        required
                        disabled={isProcessing}
                      />
                      <button
                        type="button"
                        className="max-btn"
                        onClick={() => {
                          const maxAmount = action === 'stake' ? userBalance : userStakeInfo?.staked_amount || 0
                          setAmount(maxAmount.toFixed(4))
                        }}
                        disabled={isProcessing}
                      >
                        MAX
                      </button>
                    </div>
                  </div>
                )}

                {action === 'claim' && userStakeInfo && userStakeInfo.pending_rewards.length > 0 && (
                  <div className="rewards-preview">
                    <h4>Rewards to Claim:</h4>
                    <div className="rewards-list">
                      {userStakeInfo.pending_rewards.map((reward, index) => (
                        <div key={index} className="reward-item">
                          <TokenImage src={getTokenIcon(reward.token)} alt={reward.token} />
                          <span>{formatTokenAmount(reward.amount)} {reward.token}</span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                <div className="form-actions">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    onClick={onClose}
                    disabled={isProcessing}
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    className="btn btn-primary"
                    disabled={isProcessing || (action !== 'claim' && !amount)}
                  >
                    {getActionButtonText()}
                  </button>
                </div>
              </form>
            </>
          )}
        </div>
      </div>
    </div>
  )
}

export default TokenStakingModal