import React, { useState, useEffect } from 'react'
import { useWallet } from '../hooks/useWallet'
import { useNotifications } from '../hooks/useNotifications'
import { log } from '../services/loggingService'
import { WharfKitService } from '../services/wharfkit'
import '../css/modal.css'

interface RAMModalProps {
  isOpen: boolean
  onClose: () => void
  action: 'buy' | 'sell'
  onSuccess?: () => void
}

const RAMModal: React.FC<RAMModalProps> = ({ 
  isOpen, 
  onClose, 
  action, 
  onSuccess 
}) => {
  const [amount, setAmount] = useState('')
  const [isProcessing, setIsProcessing] = useState(false)
  const [ramPrice, setRamPrice] = useState<number | null>(null)
  const [userRamBytes, setUserRamBytes] = useState<number | null>(null)
  const [isLoadingData, setIsLoadingData] = useState(false)
  const { session, transact } = useWallet()
  const { showSuccess, showError } = useNotifications()

  // Fetch RAM market data and user account info
  useEffect(() => {
    if (isOpen && session) {
      fetchRAMData()
    }
  }, [isOpen, session])

  const fetchRAMData = async () => {
    if (!session) return
    
    setIsLoadingData(true)
    try {
      // Fetch RAM price from WAX blockchain
      const ramMarketResponse = await fetch('https://wax.greymass.com/v1/chain/get_table_rows', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          json: true,
          code: 'eosio',
          scope: 'eosio',
          table: 'rammarket',
          limit: 1
        })
      })
      
      if (ramMarketResponse.ok) {
        const ramMarketData = await ramMarketResponse.json()
        if (ramMarketData.rows && ramMarketData.rows.length > 0) {
          const ramMarket = ramMarketData.rows[0]
          // Calculate RAM price: WAX balance / RAM supply
          const waxBalance = parseFloat(ramMarket.quote.balance.split(' ')[0])
          const ramSupply = parseFloat(ramMarket.base.balance.split(' ')[0])
          const price = waxBalance / ramSupply
          setRamPrice(price)
        }
      }
      
      // Fetch user account data
      const accountResponse = await fetch('https://wax.greymass.com/v1/chain/get_account', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ account_name: session.actor.toString() })
      })
      
      if (accountResponse.ok) {
        const accountData = await accountResponse.json()
        setUserRamBytes(accountData.ram_quota - accountData.ram_usage)
      }
    } catch (error) {
      log.error('Failed to fetch RAM market data', 'RAMModal.fetchRAMData', error as Error, { account: session?.actor.toString() })
    } finally {
      setIsLoadingData(false)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!session || !amount || parseFloat(amount) <= 0) return

    // Validation
    if (action === 'sell' && userRamBytes !== null) {
      const bytesToSell = Math.floor(parseFloat(amount))
      if (bytesToSell > userRamBytes) {
        showError('Insufficient RAM', `You only have ${userRamBytes.toLocaleString()} bytes available to sell.`)
        return
      }
    }

    setIsProcessing(true)
    try {
      if (action === 'buy') {
        // Buy RAM using buyram
        const waxAmount = `${parseFloat(amount).toFixed(8)} WAX`
        const actions = [{
          account: 'eosio',
          name: 'buyram',
          authorization: [{
            actor: WharfKitService.safeGetActor(session),
            permission: WharfKitService.safeGetPermission(session)
          }],
          data: {
            payer: WharfKitService.safeGetActor(session),
            receiver: WharfKitService.safeGetActor(session),
            quant: waxAmount
          }
        }]
        
        await transact(actions)
        showSuccess('RAM Purchase Successful', `Successfully purchased RAM with ${waxAmount}!`)
      } else {
        // Sell RAM using sellram
        const bytes = Math.floor(parseFloat(amount))
        const actions = [{
          account: 'eosio',
          name: 'sellram',
          authorization: [{
            actor: WharfKitService.safeGetActor(session),
            permission: WharfKitService.safeGetPermission(session)
          }],
          data: {
            account: WharfKitService.safeGetActor(session),
            bytes: bytes
          }
        }]
        
        await transact(actions)
        showSuccess('RAM Sale Successful', `Successfully sold ${bytes.toLocaleString()} bytes of RAM!`)
      }
      
      onSuccess?.()
      onClose()
      setAmount('')
    } catch (error) {
      log.error(`Failed to ${action} RAM`, 'RAMModal.handleSubmit', error as Error, { action, amount, account: WharfKitService.safeGetActor(session) })
      const errorMessage = error instanceof Error ? error.message : 'Unknown error occurred'
      showError(`RAM ${action === 'buy' ? 'Purchase' : 'Sale'} Failed`, errorMessage)
    } finally {
      setIsProcessing(false)
    }
  }

  if (!isOpen) return null

  const actionName = action === 'buy' ? 'Buy' : 'Sell'
  const inputLabel = action === 'buy' ? 'Amount (WAX)' : 'Bytes to Sell'
  const inputPlaceholder = action === 'buy' ? 'Enter WAX amount' : 'Enter bytes amount'

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>{actionName} RAM</h2>
          <button className="modal-close" onClick={onClose}>
            ×
          </button>
        </div>
        
        <div className="modal-body">
          <div className="resource-info">
            <div className="resource-icon">
              💾
            </div>
            <div className="resource-description">
              <h3>RAM {actionName}</h3>
              <p>
                {action === 'buy' 
                  ? 'Purchase RAM with WAX tokens. RAM is used to store account data and smart contract state.'
                  : 'Sell RAM bytes back to the system for WAX tokens. Only sell RAM you don\'t need.'
                }
              </p>
              
              {/* RAM Market Information */}
              <div className="ram-market-info">
                {isLoadingData ? (
                  <div className="loading-info">
                    <span>Loading RAM market data...</span>
                  </div>
                ) : (
                  <>
                    {ramPrice !== null && (
                      <div className="market-stat">
                        <span className="stat-label">Current RAM Price:</span>
                        <span className="stat-value">{(ramPrice * 1024).toFixed(8)} WAX/KB</span>
                      </div>
                    )}
                    {action === 'sell' && userRamBytes !== null && (
                      <div className="market-stat">
                        <span className="stat-label">Available RAM:</span>
                        <span className="stat-value">{userRamBytes.toLocaleString()} bytes</span>
                      </div>
                    )}
                    {action === 'buy' && ramPrice !== null && amount && (
                      <div className="market-stat">
                        <span className="stat-label">Estimated RAM:</span>
                        <span className="stat-value">
                          {Math.floor(parseFloat(amount) / ramPrice).toLocaleString()} bytes
                        </span>
                      </div>
                    )}
                    {action === 'sell' && ramPrice !== null && amount && (
                      <div className="market-stat">
                        <span className="stat-label">Estimated WAX:</span>
                        <span className="stat-value">
                          {(parseFloat(amount) * ramPrice).toFixed(8)} WAX
                        </span>
                      </div>
                    )}
                  </>
                )}
              </div>
            </div>
          </div>
          
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="amount">{inputLabel}</label>
              <input
                type="number"
                id="amount"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                placeholder={inputPlaceholder}
                min={action === 'buy' ? '0.00000001' : '1'}
                step={action === 'buy' ? '0.00000001' : '1'}
                required
                disabled={isProcessing}
              />
              {action === 'buy' && (
                <small className="form-hint">
                  Minimum: 0.00000001 WAX
                </small>
              )}
              {action === 'sell' && (
                <small className="form-hint">
                  Enter the number of bytes to sell
                </small>
              )}
            </div>
            
            <div className="form-actions">
              <button 
                type="button" 
                className="btn-secondary" 
                onClick={onClose}
                disabled={isProcessing}
              >
                Cancel
              </button>
              <button 
                type="submit" 
                className="btn-primary"
                disabled={isProcessing || !amount || parseFloat(amount) <= 0}
              >
                {isProcessing ? 'Processing...' : `${actionName} RAM`}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default RAMModal