import React, { useState, useEffect, useCallback, useMemo } from 'react'
import { useNavigate } from 'react-router-dom'
import { apiService } from '../services/api'
import { auctionService } from '../services/auctionService'
import { useWallet } from '../contexts/WalletContext'
import { useNotifications } from "../hooks/useNotifications";
import MediaRenderer from './MediaRenderer'
import { formatWAXPrice } from '../utils/formatUtils'
import { getAssetImageUrl, getAssetVideoUrl } from '../utils/mediaUtils'
import { log } from '../services/loggingService'
import { WharfKitService } from '../services/wharfkit'

import type { Auction } from '../types'
import '../css/auctions.css'

const Auctions: React.FC = React.memo(() => {
  const navigate = useNavigate()
  const { isConnected, account, session } = useWallet()
  const { showSuccess, showError } = useNotifications()
  const [auctions, setAuctions] = useState<Auction[]>([])
  const [loading, setLoading] = useState(true)
  const [loadingMore, setLoadingMore] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [currentPage, setCurrentPage] = useState(1)
  const [hasMore, setHasMore] = useState(true)
  const [totalPages, setTotalPages] = useState(1)
  const [filter, setFilter] = useState<'all' | 'ending-soon' | 'new'>('all')
  const [sortBy, setSortBy] = useState<'price_asc' | 'price_desc' | 'created_desc' | 'created_asc'>('created_desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCollection, setSelectedCollection] = useState('')
  const [hideEmptyAuctions, setHideEmptyAuctions] = useState(false)
  const [showCreateModal, setShowCreateModal] = useState(false)
  const [showBidModal, setShowBidModal] = useState(false)
  const [selectedAuction, setSelectedAuction] = useState<Auction | null>(null)
  const [bidAmount, setBidAmount] = useState('')
  const [isPlacingBid, setIsPlacingBid] = useState(false)
  const [isCreatingAuction, setIsCreatingAuction] = useState(false)
  
  // Create Auction form states
  const [selectedAssets, setSelectedAssets] = useState<Set<string>>(new Set())
  const [userAssets, setUserAssets] = useState<Array<{asset_id: string, name: string, image: string, collection_name: string, template_mint?: string}>>([])
  const [isLoadingAssets, setIsLoadingAssets] = useState(false)
  const [startingPrice, setStartingPrice] = useState('')
  const [durationWeeks, setDurationWeeks] = useState(0)
  const [durationDays, setDurationDays] = useState(1)
  const [durationHours, setDurationHours] = useState(0)
  const [durationMinutes, setDurationMinutes] = useState(0)
  const [auctionMemo, setAuctionMemo] = useState('')
  const [collections, setCollections] = useState<Array<{collection_name: string, name: string}>>([])  

  const getSortField = useCallback(() => {
    switch (sortBy) {
      case 'price_asc':
      case 'price_desc':
        return 'price'
      case 'created_desc':
      case 'created_asc':
      default:
        return 'created'
    }
  }, [sortBy])

  const getSortOrder = useCallback(() => {
    switch (sortBy) {
      case 'price_asc':
      case 'created_asc':
        return 'asc'
      case 'price_desc':
      case 'created_desc':
      default:
        return 'desc'
    }
  }, [sortBy])

  const loadAuctions = useCallback(async (page: number = 1, append: boolean = false) => {
    try {
      if (page === 1) {
        setLoading(true)
        setCurrentPage(1)
        setHasMore(true)
      } else {
        setLoadingMore(true)
      }
      setError(null)
      
      const response = await apiService.getAuctions({
        state: 1, // Active auctions
        limit: 36,
        offset: (page - 1) * 36,
        sort: getSortField(),
        order: getSortOrder(),
        hide_empty_auctions: hideEmptyAuctions,
        search: searchTerm || undefined,
        collection_name: selectedCollection || undefined,
        collection_blacklist: 'flinttffunko,onlyfanzgaly,farmerpetsco,fantaspeop',
        seller_blacklist: 'dximg.wam,xpvrs.wam,5rrrc.wam,nnmnmnmnn.gm',
        symbol: 'WAX'
      })
      
      if (!response.success || !response.data) {
        throw new Error('Failed to fetch auctions data')
      }
      
      // Use the formatted auction data from the API service
      const formattedAuctions: Auction[] = response.data.map((auction) => ({
        auction_id: auction.auction_id,
        asset_id: auction.asset_id,
        seller: auction.seller,
        price: auction.price,
        end_time: auction.end_time,
        collection_name: auction.collection_name,
        template_id: auction.template_id,
        asset_name: auction.asset_name,
        image: auction.image,
        current_bid: auction.current_bid || null,
        bidder: auction.bidder || null,
        collection_info: undefined // Remove individual collection fetching to prevent API spam
      }))
      
      if (append && page > 1) {
        setAuctions(prev => [...prev, ...formattedAuctions])
      } else {
        setAuctions(formattedAuctions)
      }
      
      // Update pagination state
      const itemsPerPage = 36
      const hasMoreData = formattedAuctions.length === itemsPerPage
      setHasMore(hasMoreData)
      setCurrentPage(page)
      
      if (hasMoreData) {
        setTotalPages(page + 1)
      } else {
        setTotalPages(page)
      }
      
    } catch (err) {
      log.error('Error loading auctions', 'Auctions', err as Error)
      setError('Failed to load auctions. Please try again later.')
    } finally {
      setLoading(false)
      setLoadingMore(false)
    }
  }, [getSortField, getSortOrder, hideEmptyAuctions, searchTerm, selectedCollection])

  const loadMoreAuctions = useCallback(() => {
    if (!loadingMore && hasMore) {
      loadAuctions(currentPage + 1, true)
    }
  }, [loadAuctions, currentPage, hasMore, loadingMore])

  const resetAndLoadAuctions = useCallback(() => {
    setCurrentPage(1)
    setHasMore(true)
    loadAuctions(1, false)
  }, [loadAuctions])

  useEffect(() => {
    resetAndLoadAuctions()
  }, [resetAndLoadAuctions, filter, searchTerm, selectedCollection, hideEmptyAuctions, sortBy])

  // Load collections for filtering
  useEffect(() => {
    const loadCollections = async () => {
      try {
        const collectionsData = await apiService.getCollections({ limit: 50, whitelistOnly: true })
        if (collectionsData && collectionsData.length > 0) {
          const mappedCollections = collectionsData.map(col => ({
            collection_name: col.collection_name,
            name: col.name || col.collection_name
          }))
          setCollections(mappedCollections)
        }
      } catch (error) {
        log.error('Failed to load collections', 'Auctions', error as Error)
        // Fallback to default collections
        setCollections([
          { collection_name: 'pixeljourney', name: 'Pixel Journey' },
          { collection_name: 'friedfactory', name: 'Fried Factory' }
        ])
      }
    }
    loadCollections()
  }, [])

  // Load user assets for auction creation
  const loadUserAssets = async () => {
    if (!account) return
    
    setIsLoadingAssets(true)
    try {
      const url = `${apiService.atomicAssetsAPI}/atomicassets/v1/assets?owner=${account}&limit=100`
      const response = await apiService.fetchWithRetry(url)
      const data = await response.json()
      
      if (data.success && data.data) {
        const assets = data.data.map((asset: {asset_id: string, name?: string, collection: {collection_name: string}, template_mint?: string, data?: {img?: string}}) => ({
          asset_id: asset.asset_id,
          name: asset.name || `${asset.collection.collection_name} #${asset.template_mint || asset.asset_id}`,
          collection_name: asset.collection.collection_name,
          image: asset.data?.img ? `https://ipfs.io/ipfs/${asset.data.img}` : undefined,
          template_mint: asset.template_mint,
          isSelected: false
        }))
        setUserAssets(assets)
      }
    } catch (error) {
      log.error('Error loading user assets', 'Auctions', error as Error)
      setUserAssets([])
    } finally {
      setIsLoadingAssets(false)
    }
  }



  const formatPrice = (price: string) => {
    const waxPrice = parseFloat(price) / 100000000
    return formatWAXPrice(waxPrice)
  }

  // Optimized filtering with useMemo
  const filteredAuctions = useMemo(() => {
    return auctions.filter(auction => {
      // Search filter
      if (searchTerm) {
        const searchLower = searchTerm.toLowerCase()
        const matchesSearch = 
          auction.asset_name?.toLowerCase().includes(searchLower) ||
          auction.asset_id?.toString().includes(searchLower) ||
          auction.collection_name?.toLowerCase().includes(searchLower)
        if (!matchesSearch) return false
      }

      // Collection filter
      if (selectedCollection && auction.collection_name !== selectedCollection) {
        return false
      }

      // Hide empty auctions filter - check if auction has actual bids
      if (hideEmptyAuctions && (!auction.bidder || !auction.current_bid || auction.current_bid === '0')) {
        return false
      }

      return true
    })
  }, [auctions, searchTerm, selectedCollection, hideEmptyAuctions])

  // Real-time auction updates
  useEffect(() => {
    const unsubscribeFunctions: (() => void)[] = []

    // Subscribe to updates for all visible auctions
    filteredAuctions.forEach(auction => {
      const unsubscribe = auctionService.subscribe(auction.auction_id, (update) => {
        setAuctions(prevAuctions => 
          prevAuctions.map(prevAuction => 
            prevAuction.auction_id === update.auction_id
              ? {
                  ...prevAuction,
                  current_bid: update.current_bid || prevAuction.current_bid,
                  bidder: update.bidder || prevAuction.bidder,
                  end_time: update.end_time
                }
              : prevAuction
          )
        )
      })
      unsubscribeFunctions.push(unsubscribe)
    })

    // Cleanup subscriptions when component unmounts or auctions change
    return () => {
      unsubscribeFunctions.forEach(unsubscribe => unsubscribe())
    }
  }, [filteredAuctions])

  const handlePlaceBid = (auction: Auction) => {
    if (!isConnected || !account) {
      showError('Wallet Required', 'Please connect your wallet to place bids')
      return
    }
    
    if (auction.seller === account) {
      showError('Invalid Action', 'You cannot bid on your own auction')
      return
    }
    
    setSelectedAuction(auction)
    setBidAmount('')
    setShowBidModal(true)
  }
  
  const submitBid = async () => {
    if (!selectedAuction || !session || !account) {
      showError('Error', 'Please connect your wallet first')
      return
    }
    
    const bidFloat = parseFloat(bidAmount)
    if (isNaN(bidFloat) || bidFloat <= 0) {
      showError('Invalid Bid', 'Please enter a valid bid amount')
      return
    }
    
    // Check if bid is valid
    const currentBidAmount = parseFloat(selectedAuction.current_bid || selectedAuction.price) / 100000000
    const hasExistingBids = selectedAuction.current_bid && selectedAuction.bidder
    
    if (hasExistingBids && bidFloat <= currentBidAmount) {
      showError('Invalid Bid', `Bid must be higher than current bid of ${formatWAXPrice(currentBidAmount)}`)
      return
    } else if (!hasExistingBids && bidFloat < currentBidAmount) {
      showError('Invalid Bid', `Bid must be at least the starting price of ${formatWAXPrice(currentBidAmount)}`)
      return
    }
    
    setIsPlacingBid(true)
    
    try {
      // Format bid amount with WAX precision (8 decimals)
      const formattedBid = bidFloat.toFixed(8)
      const bidString = `${formattedBid} WAX`
      
      // Create the bid action for AtomicMarket
      const action = {
        account: 'atomicmarket',
        name: 'auctionbid',
        authorization: [{
          actor: account,
          permission: WharfKitService.safeGetPermission(session)
        }],
        data: {
          bidder: account,
          auction_id: selectedAuction.auction_id,
          bid: bidString,
          taker_marketplace: 'market.pxj'
        }
      }
      
      // Execute the transaction
      const result = await session.transact({ actions: [action] })
      
      if (result) {
        showSuccess('Bid Placed', `Successfully placed bid of ${formatWAXPrice(bidFloat)}`)
        setShowBidModal(false)
        setBidAmount('')
        setSelectedAuction(null)
        
        // Reload auctions to show updated bid
        loadAuctions()
      }
    } catch (error: unknown) {
      log.error('Error placing bid', 'Auctions', error as Error)
      const errorMessage = error instanceof Error ? error.message : 'Failed to place bid. Please try again.'
      showError('Bid Failed', errorMessage)
    } finally {
      setIsPlacingBid(false)
    }
  }

  const handleViewDetails = (auction: Auction) => {
    // Navigate to asset details page
    navigate(`/assets/${auction.asset_id}`)
  }

  const handleCreateAuction = () => {
    setShowCreateModal(true)
    if (account) {
      loadUserAssets()
    }
  }

  const toggleAssetSelection = (assetId: string) => {
    const newSelected = new Set(selectedAssets)
    if (newSelected.has(assetId)) {
      newSelected.delete(assetId)
    } else {
      newSelected.add(assetId)
    }
    setSelectedAssets(newSelected)
  }

  // Calculate total duration in seconds
  const calculateTotalDuration = () => {
    return (durationWeeks * 7 * 24 * 60 * 60) + 
           (durationDays * 24 * 60 * 60) + 
           (durationHours * 60 * 60) + 
           (durationMinutes * 60)
  }

  // Update duration handlers
  const updateDurationComponent = (component: 'weeks' | 'days' | 'hours' | 'minutes', value: number) => {
    const clampedValue = Math.max(0, Math.min(value, getMaxValue(component)))
    
    switch (component) {
      case 'weeks':
        setDurationWeeks(clampedValue)
        break
      case 'days':
        setDurationDays(clampedValue)
        break
      case 'hours':
        setDurationHours(clampedValue)
        break
      case 'minutes':
        setDurationMinutes(clampedValue)
        break
    }
  }

  const getMaxValue = (component: 'weeks' | 'days' | 'hours' | 'minutes') => {
    switch (component) {
      case 'weeks': return 4 // Max 4 weeks
      case 'days': return 30 // Max 30 days
      case 'hours': return 23 // Max 23 hours
      case 'minutes': return 59 // Max 59 minutes
      default: return 0
    }
  }

  const formatDurationDisplay = () => {
    const parts = []
    if (durationWeeks > 0) parts.push(`${durationWeeks} week${durationWeeks > 1 ? 's' : ''}`)
    if (durationDays > 0) parts.push(`${durationDays} day${durationDays > 1 ? 's' : ''}`)
    if (durationHours > 0) parts.push(`${durationHours} hour${durationHours > 1 ? 's' : ''}`)
    if (durationMinutes > 0) parts.push(`${durationMinutes} minute${durationMinutes > 1 ? 's' : ''}`)
    
    if (parts.length === 0) return 'No duration set'
    return parts.join(', ')
  }

  const handleCreateAuctionSubmit = async () => {
    if (selectedAssets.size === 0) {
      showError('No Assets Selected', 'Please select at least one asset to auction')
      return
    }
    
    if (!startingPrice || parseFloat(startingPrice) <= 0) {
      showError('Invalid Price', 'Please enter a valid starting price')
      return
    }
    
    const totalDurationSeconds = calculateTotalDuration()
    if (totalDurationSeconds < 3600) { // Minimum 1 hour
      showError('Invalid Duration', 'Auction duration must be at least 1 hour')
      return
    }
    
    if (totalDurationSeconds > 2592000) { // Maximum 30 days
      showError('Invalid Duration', 'Auction duration cannot exceed 30 days')
      return
    }
    
    try {
      setIsCreatingAuction(true)
      
      // Create auction transaction using AtomicMarket announceauct action
      const assetIds = Array.from(selectedAssets).map(id => parseInt(id))
      const startingBidAmount = `${parseFloat(startingPrice).toFixed(8)} WAX`
      
      const actions = [{
        account: 'atomicmarket',
        name: 'announceauct',
        authorization: [{
          actor: account || '',
          permission: session?.permission || 'active'
        }],
        data: {
          seller: account || '',
          asset_ids: assetIds,
          starting_bid: startingBidAmount,
          duration: totalDurationSeconds,
          maker_marketplace: 'market.pxj'
        }
      }]
      
      if (session) {
        await session.transact({ actions })
      }
      
      showSuccess('Auction Created', `Successfully created auction for ${selectedAssets.size} asset(s). Remember to transfer your assets to the atomicmarket contract to activate the auction.`)
      setShowCreateModal(false)
      
      // Reset form
      setSelectedAssets(new Set())
      setStartingPrice('')
      setDurationWeeks(0)
      setDurationDays(1)
      setDurationHours(0)
      setDurationMinutes(0)
      setAuctionMemo('')
      
      // Refresh auctions list
      loadAuctions()
    } catch (error) {
      log.error('Failed to create auction', 'Auctions', error as Error)
      showError('Auction Creation Failed', error instanceof Error ? error.message : 'Failed to create auction')
    } finally {
      setIsCreatingAuction(false)
    }
  }

  return (
    <div className="auctions-page">
      <div className="page-header">
        <div className="header-content">
          <div className="header-text">
            <h1>Auctions</h1>
            <p>Discover and bid on unique NFTs from the WAX ecosystem</p>
          </div>
          <button className="create-auction-btn" onClick={handleCreateAuction}>
            Create Auction
          </button>
        </div>
      </div>

      <div className="auctions-controls">
        <div className="search-section">
          <div className="search-input-wrapper">
              <svg className="search-icon" viewBox="0 0 24 24" fill="currentColor">
                <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
              </svg>
              <input
                type="text"
                placeholder="Search auctions by name or asset ID..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="search-input"
              />
            </div>
        </div>

        <div className="filters-section">
          <div className="filter-group">
            <label>Collection:</label>
            <select 
              value={selectedCollection} 
              onChange={(e) => setSelectedCollection(e.target.value)}
              className="collection-filter"
            >
              <option value="">All Collections</option>
              {collections.map(collection => (
                <option key={collection.collection_name} value={collection.collection_name}>
                  {collection.name}
                </option>
              ))}
            </select>
          </div>

          <div className="filter-group">
            <label>Sort by:</label>
            <select 
              value={sortBy} 
              onChange={(e) => setSortBy(e.target.value as typeof sortBy)}
              className="sort-filter"
            >
              <option value="price_asc">Price: Low to High</option>
              <option value="price_desc">Price: High to Low</option>
              <option value="created_desc">Newest First</option>
              <option value="created_asc">Oldest First</option>
            </select>
          </div>

          <div className="filter-group">
            <label className="checkbox-label">
              <input
                type="checkbox"
                checked={hideEmptyAuctions}
                onChange={(e) => setHideEmptyAuctions(e.target.checked)}
              />
              <span className="checkmark"></span>
              Hide auctions without bids
            </label>
          </div>
        </div>

        <div className="auctions-filters">
          <button 
            className={`filter-btn ${filter === 'all' ? 'active' : ''}`}
            onClick={() => setFilter('all')}
          >
            All Auctions
          </button>
          <button 
            className={`filter-btn ${filter === 'ending-soon' ? 'active' : ''}`}
            onClick={() => setFilter('ending-soon')}
          >
            Ending Soon
          </button>
          <button 
            className={`filter-btn ${filter === 'new' ? 'active' : ''}`}
            onClick={() => setFilter('new')}
          >
            New Auctions
          </button>
        </div>
      </div>

      {loading && (
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading auctions...</p>
        </div>
      )}

      {error && (
        <div className="error-container">
          <p className="error-message">{error}</p>
          <button onClick={() => loadAuctions()} className="retry-btn">
            Try Again
          </button>
        </div>
      )}

      {!loading && !error && filteredAuctions.length === 0 && auctions.length > 0 && (
        <div className="empty-state">
          <h3>No Matching Auctions</h3>
          <p>No auctions match your current filters. Try adjusting your search criteria.</p>
        </div>
      )}

      {!loading && !error && auctions.length === 0 && (
        <div className="empty-state">
          <h3>No Active Auctions</h3>
          <p>There are currently no active auctions. Check back later!</p>
        </div>
      )}

      {!loading && !error && filteredAuctions.length > 0 && (
        <div className="auctions-grid">
          {filteredAuctions.map((auction) => (
            <div 
              key={auction.auction_id} 
              className="auction-card clickable-card"
              onClick={() => navigate(`/auctions/${auction.auction_id}`)}
            >
              <div className="auction-image">
                <MediaRenderer
                  imageUrl={auction.image}
                  videoUrl={auction.video}
                  alt={auction.asset_name}
                  className="auction-media"
                  enableAnimations={true}
                />
                <div className="auction-timer">
                  {auctionService.formatTimeRemaining(auction.end_time)}
                </div>
              </div>
              
              <div className="auction-info">
                <h3 className="auction-title">{auction.asset_name}</h3>
                
                <div className="auction-collection-info">
                  {auction.collection_info?.img && (
                    <img 
                      src={auction.collection_info.img} 
                      alt={auction.collection_info.name}
                      className="collection-avatar"
                    />
                  )}
                  <div className="collection-details">
                    <p className="collection-name">
                      {auction.collection_info?.name || auction.collection_name}
                    </p>
                    {auction.collection_info?.description && (
                      <p className="collection-description">
                        {auction.collection_info.description.length > 60 
                          ? `${auction.collection_info.description.substring(0, 60)}...`
                          : auction.collection_info.description
                        }
                      </p>
                    )}
                  </div>
                </div>
                
                <div className="seller-info">
                  <span className="label">Seller: </span>
                  <button 
                    onClick={(e) => {
                      e.stopPropagation();
                      navigate(`/user-profile-lookup?user=${encodeURIComponent(auction.seller)}`);
                    }}
                    className="seller-account"
                  >
                    {auction.seller}
                  </button>
                </div>
                
                <div className="auction-pricing">
                  <div className="current-bid">
                    <span className="label">{auction.current_bid ? 'Current Bid:' : 'Starting Price:'}</span>
                    <span className="price">
                      {auction.current_bid ? formatPrice(auction.current_bid) : formatPrice(auction.price)}
                    </span>
                  </div>
                  
                  {auction.bidder && (
                    <div className="bidder">
                      <span className="label">Top Bidder:</span>
                      <button 
                        onClick={(e) => {
                          e.stopPropagation();
                          navigate(`/user-profile-lookup?user=${encodeURIComponent(auction.bidder || '')}`);
                        }}
                        className="account"
                      >
                        {auction.bidder}
                      </button>
                    </div>
                  )}
                </div>
                
                <div className="auction-actions">
                  <button 
                    className="bid-btn"
                    onClick={(e) => {
                      e.stopPropagation();
                      handlePlaceBid(auction);
                    }}
                    disabled={!isConnected || auction.seller === account}
                  >
                    {!isConnected ? 'Connect Wallet' : 
                     auction.seller === account ? 'Your Auction' : 'Place Bid'}
                  </button>
                  <button 
                    className="view-btn"
                    onClick={(e) => {
                      e.stopPropagation();
                      handleViewDetails(auction);
                    }}
                  >
                    View Details
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Pagination Controls */}
      {!loading && !error && filteredAuctions.length > 0 && (
        <div className="pagination-container">
          {hasMore && (
            <button 
              className="load-more-btn"
              onClick={loadMoreAuctions}
              disabled={loadingMore}
            >
              {loadingMore ? (
                <>
                  <div className="loading-spinner small"></div>
                  Loading more...
                </>
              ) : (
                'Load More Auctions'
              )}
            </button>
          )}
          
          <div className="pagination-info">
            <span>Page {currentPage} of {totalPages}</span>
            <span>{filteredAuctions.length} auctions loaded</span>
          </div>
        </div>
      )}

      {/* Coming Soon Notice */}
      <div className="coming-soon-notice">
        <h3>🚧 Integration in Progress</h3>
        <p>
          Full AtomicMarket auction integration is currently being implemented. 
          This page will soon display live auction data and allow bidding functionality.
        </p>
      </div>

      {/* Enhanced Bid Modal */}
      {showBidModal && selectedAuction && (
        <div className="modal-overlay" onClick={() => setShowBidModal(false)}>
          <div className="bid-modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="bid-modal-header">
              <h2>Place Your Bid</h2>
              <button 
                className="close-btn"
                onClick={() => setShowBidModal(false)}
              >
                ×
              </button>
            </div>
            
            <div className="bid-modal-body">
              {/* Enhanced Auction Summary */}
              <div className="bid-auction-summary">
                <div className="bid-asset-image">
                  <MediaRenderer
                    imageUrl={selectedAuction.image}
                    videoUrl={selectedAuction.video}
                    alt={selectedAuction.asset_name}
                    className="bid-modal-media"
                    enableAnimations={true}
                  />
                  <div className="auction-timer-small">
                    {auctionService.formatTimeRemaining(selectedAuction.end_time)}
                  </div>
                </div>
                <div className="bid-asset-info">
                  <h3>{selectedAuction.asset_name}</h3>
                  <div className="bid-collection-info">
                    {selectedAuction.collection_info?.img && (
                      <img 
                        src={selectedAuction.collection_info.img} 
                        alt={selectedAuction.collection_info.name}
                        className="collection-avatar-small"
                      />
                    )}
                    <span className="collection-name">{selectedAuction.collection_info?.name || selectedAuction.collection_name}</span>
                  </div>
                  <div className="bid-current-info">
                    <div className="current-bid-display">
                      <span className="label">{selectedAuction.current_bid ? 'Current Bid' : 'Starting Price'}</span>
                      <span className="amount">{formatPrice(selectedAuction.current_bid || selectedAuction.price)}</span>
                    </div>
                    {selectedAuction.bidder && (
                      <div className="current-bidder">
                        <span className="label">Leading Bidder</span>
                        <button 
                          onClick={(e) => {
                            e.stopPropagation();
                            navigate(`/user-profile-lookup?user=${encodeURIComponent(selectedAuction.bidder || '')}`);
                          }}
                          className="bidder-account"
                        >
                          {selectedAuction.bidder}
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              </div>

              {/* Bid Input Section */}
              <div className="bid-input-section">
                <div className="bid-form-group">
                  <label>Your Bid Amount</label>
                  <div className="bid-input-wrapper">
                    <input 
                      type="number" 
                      value={bidAmount}
                      onChange={(e) => setBidAmount(e.target.value)}
                      placeholder="0.0000"
                      step="0.0001"
                      min={selectedAuction.current_bid && selectedAuction.bidder ? parseFloat(selectedAuction.current_bid) / 100000000 + 0.0001 : parseFloat(selectedAuction.price) / 100000000}
                      className="bid-amount-input"
                      disabled={isPlacingBid}
                    />
                    <span className="currency-label">WAX</span>
                  </div>
                  <div className="bid-requirements">
                    <p className="minimum-bid">
                      {selectedAuction.current_bid && selectedAuction.bidder ? 
                        `Minimum: ${formatWAXPrice(parseFloat(selectedAuction.current_bid) / 100000000 + 0.0001)}` :
                `Starting Price: ${formatWAXPrice(parseFloat(selectedAuction.price) / 100000000)}`
                      }
                    </p>
                    {bidAmount && (
                      <p className="bid-total">
                        Total: {formatWAXPrice(parseFloat(bidAmount))}
                      </p>
                    )}
                  </div>
                </div>
              </div>

              {/* Previous Bids Section */}
              <div className="previous-bids-section">
                <h4>Bid History</h4>
                <div className="bids-list">
                  {selectedAuction.bidder ? (
                    <div className="bid-item">
                      <div className="bidder-info">
                        <button 
                          onClick={(e) => {
                            e.stopPropagation();
                            navigate(`/user-profile-lookup?user=${encodeURIComponent(selectedAuction.bidder || '')}`);
                          }}
                          className="bidder"
                        >
                          {selectedAuction.bidder}
                        </button>
                        <span className="bid-time">Current leading bid</span>
                      </div>
                      <span className="bid-amount">{formatPrice(selectedAuction.current_bid || selectedAuction.price)}</span>
                    </div>
                  ) : (
                    <div className="no-bids">
                      <p>No bids yet - be the first to bid!</p>
                    </div>
                  )}
                </div>
              </div>

              {/* Bid Warning */}
              <div className="bid-notice">
                <div className="notice-icon">💡</div>
                <div className="notice-text">
                  <p><strong>Important:</strong> Ensure you have sufficient WAX balance for your bid. Bids are binding and cannot be cancelled.</p>
                </div>
              </div>
            </div>
            
            <div className="bid-modal-footer">
              <button 
                className="cancel-bid-btn"
                onClick={() => setShowBidModal(false)}
                disabled={isPlacingBid}
              >
                Cancel
              </button>
              <button 
                className="submit-bid-btn"
                onClick={submitBid}
                disabled={isPlacingBid || !bidAmount || 
                  (selectedAuction.current_bid && selectedAuction.bidder ? 
                    parseFloat(bidAmount) <= parseFloat(selectedAuction.current_bid) / 100000000 :
                    parseFloat(bidAmount) < parseFloat(selectedAuction.price) / 100000000
                  )}
              >
                {isPlacingBid ? (
                  <>
                    <span className="loading-spinner-small"></span>
                    Placing Bid...
                  </>
                ) : (
                  `Place Bid • ${bidAmount || '0.0000'} WAX`
                )}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Create Auction Modal */}
      {showCreateModal && (
        <div className="modal-overlay auction-modal" onClick={() => setShowCreateModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2>Create Auction</h2>
              <button 
                className="close-btn"
                onClick={() => setShowCreateModal(false)}
              >
                ×
              </button>
            </div>
            
            <div className="modal-body">
              <div className="asset-selection-section">
                {/* Asset Selection Section */}
                <div className="form-group">
                  <label>Select Assets to Auction</label>
                  <div className="asset-selection-container">
                    {isLoadingAssets ? (
                      <div className="loading-assets">
                        <div className="loading-spinner"></div>
                        <span>Loading your assets...</span>
                      </div>
                    ) : userAssets.length > 0 ? (
                      <div className="assets-grid">
                        {userAssets.map(asset => (
                          <div 
                            key={asset.asset_id}
                            className={`asset-item ${selectedAssets.has(asset.asset_id) ? 'selected' : ''}`}
                            onClick={() => toggleAssetSelection(asset.asset_id)}
                          >
                            <div className="asset-image">
                              <MediaRenderer
                                imageUrl={getAssetImageUrl(asset)}
                                videoUrl={getAssetVideoUrl(asset)}
                                alt={asset.name || `Asset #${asset.asset_id}`}
                                className="asset-media"
                                enableAnimations={true}
                                loading="lazy"
                              />
                              {selectedAssets.has(asset.asset_id) && (
                                <div className="selection-indicator">
                                  <svg viewBox="0 0 24 24" fill="currentColor">
                                    <path d="M9,20.42l-6.21,-6.21l2.83,-2.83L9,14.77l9.88,-9.89l2.83,2.83L9,20.42z"/>
                                  </svg>
                                </div>
                              )}
                            </div>
                            <div className="asset-info">
                              <div className="asset-name">{asset.name || `Asset #${asset.asset_id}`}</div>
                              <div className="asset-collection">{asset.collection_name}</div>
                              {asset.template_mint && (
                                <div className="asset-mint-info">Mint #{asset.template_mint}</div>
                              )}
                            </div>
                          </div>
                        ))}
                      </div>
                    ) : (
                      <div className="no-assets">
                        <svg className="no-assets-icon" viewBox="0 0 24 24" fill="currentColor">
                          <path d="M20,7l-8,-4l-8,4m16,0l-8,4m8,-4v10l-8,4m0,-10L4,7m8,4v10M4,7v10l8,4"/>
                        </svg>
                        <h3>No Assets Found</h3>
                        <p>You don't have any assets to auction. Visit the marketplace to acquire some NFTs first.</p>
                      </div>
                    )}
                  </div>
                  {selectedAssets.size > 0 && (
                    <div className="selection-summary">
                      <span>{selectedAssets.size} asset{selectedAssets.size > 1 ? 's' : ''} selected</span>
                    </div>
                  )}
                </div>
              </div>
              
              <div className="auction-form-section">
              
              <div className="form-group">
                <label>Starting Price (WAX)</label>
                <input 
                  type="number" 
                  placeholder="0.0000"
                  step="0.0001"
                  min="0"
                  value={startingPrice}
                  onChange={(e) => setStartingPrice(e.target.value)}
                  className="form-input"
                />
              </div>
              
              <div className="form-group">
                <label>Auction Duration</label>
                <div className="duration-inputs">
                  <div className="duration-field">
                    <label className="duration-label">Weeks</label>
                    <input 
                      type="number"
                      min="0"
                      max="4"
                      value={durationWeeks}
                      onChange={(e) => updateDurationComponent('weeks', parseInt(e.target.value) || 0)}
                      className="duration-input"
                    />
                  </div>
                  <div className="duration-field">
                    <label className="duration-label">Days</label>
                    <input 
                      type="number"
                      min="0"
                      max="30"
                      value={durationDays}
                      onChange={(e) => updateDurationComponent('days', parseInt(e.target.value) || 0)}
                      className="duration-input"
                    />
                  </div>
                  <div className="duration-field">
                    <label className="duration-label">Hours</label>
                    <input 
                      type="number"
                      min="0"
                      max="23"
                      value={durationHours}
                      onChange={(e) => updateDurationComponent('hours', parseInt(e.target.value) || 0)}
                      className="duration-input"
                    />
                  </div>
                  <div className="duration-field">
                    <label className="duration-label">Minutes</label>
                    <input 
                      type="number"
                      min="0"
                      max="59"
                      value={durationMinutes}
                      onChange={(e) => updateDurationComponent('minutes', parseInt(e.target.value) || 0)}
                      className="duration-input"
                    />
                  </div>
                </div>
                <div className="duration-summary">
                  <span className="duration-display">{formatDurationDisplay()}</span>
                  <span className="duration-total">Total: {Math.floor(calculateTotalDuration() / 3600)}h {Math.floor((calculateTotalDuration() % 3600) / 60)}m</span>
                </div>
              </div>
              
              <div className="form-group">
                <label>Memo (Optional)</label>
                <textarea 
                  placeholder="Add a description for your auction"
                  className="form-textarea"
                  rows={3}
                  value={auctionMemo}
                  onChange={(e) => setAuctionMemo(e.target.value)}
                />
              </div>
              </div>
            </div>
            
            <div className="modal-footer">
              <button 
                className="cancel-btn"
                onClick={() => setShowCreateModal(false)}
              >
                Cancel
              </button>
              <button 
                className="create-btn"
                onClick={handleCreateAuctionSubmit}
                disabled={selectedAssets.size === 0 || !startingPrice || parseFloat(startingPrice) <= 0 || isCreatingAuction}
              >
                {isCreatingAuction ? (
                  <>
                    <span className="loading-spinner-small"></span>
                    Creating Auction...
                  </>
                ) : (
                  `Create Auction${selectedAssets.size > 0 ? ` (${selectedAssets.size} asset${selectedAssets.size > 1 ? 's' : ''})` : ''}`
                )}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
})

export default Auctions